const epsilon = 1e-6,
  pi = Math.PI,
  tau = 2 * pi,
  Z = 'Z',
  BLANK = '',
  tauEpsilon = tau - epsilon;

class Path {
  constructor () {
    this._clear();
  }

  moveTo (x, y) {
    this._x0 = this._x1 = +x;
    this._y0 = this._y1 = +y;
    this.pathStr += `M${this._x0},${this._y0}`;
  }

  lineTo (x, y) {
    this._x1 = +x;
    this._y1 = +y;
    this.pathStr += `L${this._x1},${this._y1}`;
  }

  quadraticCurveTo (_cpx, _cpy, x, y) {
    this._x1 = +x;
    this._y1 = +y;
    let cpx = +_cpx,
      cpy = +_cpy;
    this.pathStr += `Q${cpx},${cpy},${this._x1},${this._y1}`;
  }

  bezierCurveTo (_cp1x, _cp1y, _cp2x, _cp2y, x, y) {
    this._x1 = +x;
    this._y1 = +y;
    let cp1x = +_cp1x,
      cp1y = +_cp1y,
      cp2x = +_cp2x,
      cp2y = +_cp2y;

    this.pathStr += `C${cp1x},${cp1y},${cp2x},${cp2y},${this._x1},${this._y1}`;
  }

  rect (x, y, _w, _h) {
    let path = this,
      w = +_w,
      h = +_h;

    path._x0 = path._x1 = +x;
    path._y0 = path._y1 = +y;

    path.pathStr += `M${path._x0},${path._y0}h${w}v${h}h${-w}Z`;
  }

  arcTo (_x1, _y1, _x2, _y2, _radius) {
    let path = this,
      dx20,
      dy20,
      dx21,
      dy21,
      dx01,
      dy01,
      xy012,
      xy01,
      xy202,
      xy212,
      xy21,
      t01,
      t21,
      xy,
      x0,
      y0,
      x1 = +_x1,
      x2 = +_x2,
      y1 = +_y1,
      y2 = +_y2,
      radius = +_radius;

    x0 = path._x1;
    y0 = path._y1;
    dx21 = x2 - x1;
    dy21 = y2 - y1;
    dx01 = x0 - x1;
    dy01 = y0 - y1;
    xy012 = dx01 * dx01 + dy01 * dy01;

    // Is the radius negative? Set it to zero.
    radius = Math.max(radius, 0);

    if (this._x1 === null) {
      // Is the path empty? Move to (x1, y1).
      this._x1 = x1;
      this._y1 = y1;
      this.pathStr += `M${this._x1},${this._y1}`;
    } else if (!(xy012 > epsilon)) {
      // Else, is (x1,y1) coincident with (x0,y0)? Do nothing.
    } else if (!(Math.abs(dy01 * dx21 - dy21 * dx01) > epsilon) || !radius) {
      // Else, are (x0,y0), (x1,y1) and (x2,y2) collinear?
      // And, equivalently, is (x1,y1) coincident with (x2,y2)?
      // Or, is the radius zero? Line to (x1,y1).
      this._x1 = x1;
      this._y1 = y1;
      this.pathStr += `L${this._x1},${this._y1}`;
    } else {
      // Otherwise draw an arc.
      dx20 = x2 - x0;
      dy20 = y2 - y0;
      xy212 = dx21 * dx21 + dy21 * dy21;
      xy202 = dx20 * dx20 + dy20 * dy20;
      xy21 = Math.sqrt(xy212);
      xy01 = Math.sqrt(xy012);
      xy = radius * Math.tan((pi - Math.acos((xy212 + xy012 - xy202) / (2 * xy21 * xy01))) / 2);
      t01 = xy / xy01;
      t21 = xy / xy21;

      // If the start tangent is not coincident with (x0,y0), draw a line.
      if (Math.abs(t01 - 1) > epsilon) {
        this.pathStr += `L${x1 + t01 * dx01},${y1 + t01 * dy01}`;
      }

      this._x1 = x1 + t21 * dx21;
      this._y1 = y1 + t21 * dy21;
      this.pathStr += `A${radius},${radius},0,0,${+(dy01 * dx20 > dx01 * dy20)},${this._x1},${this._y1}`;
    }
  }

  arc (_x, _y, _radius, _startAngle, _endAngle, _counterclockwise = false) {
    let x = +_x,
      y = +_y,
      radius = +_radius,
      startAngle = +_startAngle,
      endAngle = +_endAngle,
      counterclockwise = !!_counterclockwise,
      path = this,
      dx = radius * Math.cos(startAngle),
      dy = radius * Math.sin(startAngle),
      x0 = x + dx,
      y0 = y + dy,
      cw = Number(!counterclockwise),
      da = counterclockwise ? startAngle - endAngle : endAngle - startAngle;

    // Is the radius negative? Set it to zero.
    Math.max(radius, 0);

    if (path._x1 === null) {
      // Is this path empty? Move to (x0,y0).
      path.pathStr += `M${x0},${y0}`;
    } else if (Math.abs(path._x1 - x0) > epsilon || Math.abs(path._y1 - y0) > epsilon) {
      // Else, is (x0,y0) not coincident with the previous point? Line to (x0,y0).
      path.pathStr += `L${x0},${y0}`;
    }

    // Is this arc empty? Nothing to be done.
    if (!radius) return;

    // Does the angle go the wrong way? Flip the direction.
    if (da < 0) da = da % tau + tau;

    if (da > tauEpsilon) {
      // Is this a complete circle? Draw two arcs to complete the circle.
      path._x1 = x0;
      path._y1 = y0;
      path.pathStr += `A${radius},${radius},0,1,${cw},${x - dx},${y - dy}`;
      path.pathStr += `A${radius},${radius},0,1,${cw},${path._x1},${path._y1}`;
    } else if (da > epsilon) {
      // Is this arc non-empty? Finally, draw an arc.
      path._x1 = x + radius * Math.cos(endAngle);
      path._y1 = y + radius * Math.sin(endAngle);
      path.pathStr += `A${radius},${radius},0,${+(da >= pi)},${cw},${path._x1},${path._y1}`;
    }
  }

  closePath () {
    if (this._x1 !== null) {
      this._x1 = this._x0;
      this._y1 = this._y0;
      this.pathStr += Z;
    }
  }

  toString () {
    return this.pathStr;
  }

  _clear () {
    this._x0 = this._y0 = this._x1 = this._y1 = null;
    this.pathStr = BLANK;
  }
}

export default Path;
