
import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { extend2 } from '@fusioncharts/core/src/lib';

const HORIZONTAL = 'horizontal';
class GridBand extends SmartRenderer {
  __setDefaultConfig () {
    super.__setDefaultConfig();
    this.config.direction = HORIZONTAL;
    this.config.style = {};
    this.config.defaultStyle = {
      'stroke': '#EFEFEF',
      'stroke-width': '0',
      'fill': '#EFEFEF',
      'fill-opacity': '1'
    };
  }

  configureAttributes (obj = {}) {
    super.configureAttributes(obj);
    let selfConfig = this.config;
    for (let key in obj) {
      if (obj.hasOwnProperty(key)) {
        selfConfig[key] = obj[key];
      }
    }
  }

  /**
   * Method that creates all the gridline paths
   * @return {Array} array of paths
   */
  getBands () {
    let gridBand = this,
      canvasConfig = gridBand.getLinkedParent().config,
      bands = [],
      axisConfig,
      x,
      y,
      yScaleRangeFunction,
      width,
      height,
      scale,
      ticks,
      tickArguments = [],
      i,
      len;

    // grid-bands are drawn with respect to the primary y-axis
    // thus using the first yAxis config
    axisConfig = canvasConfig.yConfigs[0];
    scale = axisConfig.scale;
    x = canvasConfig.canvasBGLeft;
    yScaleRangeFunction = scale.getRangeValue.bind(scale);
    width = canvasConfig.canvasBGLeft + canvasConfig.canvasBGWidth;

    // If tickArguments is present, use it for calculation of ticks
    if (axisConfig.tickarguments) {
      tickArguments = axisConfig.tickarguments;
    }

    // If skipInfo is provided, use it to calculate ticks
    tickArguments.push(axisConfig.skipinfo || {});
    tickArguments.push(axisConfig.dayinmajor || false);
    ticks = scale.ticks(...tickArguments);
    height = yScaleRangeFunction(ticks[0]) - yScaleRangeFunction(ticks[1]);
    for (i = 0, len = ticks.length; i < len; i += 2) {
      y = yScaleRangeFunction(ticks[i]) - height;
      bands.push({
        x,
        y,
        width,
        height
      });
    }
    return bands;
  }

  draw () {
    let gridBand = this,
      selfConfig = gridBand.config,
      mergedStyle,
      { canvasLeft, canvasTop, canvasWidth, canvasHeight, padding } = gridBand.getLinkedParent().config,
      // Canvas strato groups are translated considering canvas padding
      // thus while creating the clip path we need to account for that
      clipPath = [`M${canvasLeft},${canvasTop - padding.top}`, `h${canvasWidth}`, `v${canvasHeight}`, `h${-canvasWidth}`, `Z`];

    // store style of gridbands
    selfConfig.style = gridBand.getFromEnv('getStyleDef')(selfConfig.style);
    mergedStyle = extend2(extend2({}, selfConfig.defaultStyle), selfConfig.style);
    // We dont want bands to have strokes
    mergedStyle['stroke-width'] = 0;

    // create the grid line group
    gridBand.addGraphicalElement({
      el: 'group',
      container: {
        id: 'strato',
        label: 'group',
        isParent: true
      },
      component: gridBand,
      label: 'gridBand',
      attr: {
        name: 'grid-band-group',
        'clip-path': clipPath
      },
      id: 'gridBand'
    });
    gridBand.getBands().forEach((band, index) => {
      band.opacity = mergedStyle.opacity;
      gridBand.addGraphicalElement({
        el: 'rect',
        attr: band,
        css: mergedStyle,
        container: {
          label: 'gridBand'
        },
        id: 'grid-band-' + index,
        component: gridBand,
        label: 'band'
      });
    });
  }
}
export default GridBand;
