import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
class Background extends SmartRenderer {
  __setDefaultConfig () {
    this.config.backgroundCss = {
      fill: '#ffffff'
    };
  }
  /**
   * configures the caption component
   *
   * @param {any} [options={}] options used to configure
   * @memberof Caption
   */
  configureAttributes (options = {}) {
    super.configureAttributes(options);
    let background = this,
      config = background.config,
      chartAttr = background.getFromEnv('chart-attrib'),
      backgroundStyle = chartAttr.style && chartAttr.style.background;

    Object.keys(options).forEach(key => (config[key] = options[key]));

    config.backgroundCss = Object.assign(config.backgroundCss, background.getFromEnv('getStyleDef')(backgroundStyle));
  }
  setDimension (dim = {}) {
    Object.assign(this.config, dim);
  }
  draw () {
    let background = this,
      config = background.config;
    background.addGraphicalElement({
      el: 'rect',
      attr: {
        x: 0,
        y: 0,
        height: config.height,
        width: config.width,
        transform: config.translate,
        opacity: config.backgroundCss.opacity
      },
      container: {
        id: 'tropo',
        label: 'group',
        isParent: true
      },
      css: config.backgroundCss,
      component: background,
      id: 'background',
      label: 'rect'
    });
  }
}
export default Background;
