import GridBand from '../components/grid-bands';
export default canvas => {
  let canvasConfig = canvas.config,
    gridBand;

  if (canvasConfig.gridBandSupported && canvasConfig.showGridBand) {
    gridBand = canvas.attachChild(GridBand, 'gridBand');
    gridBand.configure({
      style: canvasConfig.gridBandStyle
    });
  }
};
