import { getMouseCoordinate } from '@fusioncharts/core/src/lib';
import { clampWithinContextRange } from '../../_utils/pixel-validator-utils';

const VERTICAL = 'vertical',
  HORIZONTAL = 'horizontal',
  UP = 'up',
  DOWN = 'down',
  wheelZoomDeltaModeLinePixels = 17,
  wheelZoomDeltaModeScreenPixels = 53,
  getWheelEventDeltaYInPixels = function (deltaMode, deltaVal) {
    // The WheelEvent.deltaMode read-only property returns the length unit of scroll amount.
    // When wheeling on a mouse,
    // - chrome by default uses deltaY = 53, deltaMode = 0 i.e. DOM_DELTA_PIXEL(pixel)
    // - firefox by default uses deltaY = 3, deltaMode = 1 i.e. DOM_DELTA_LINE(line)
    // - chrome and firefox on windows alternatively can also be set to scroll by page, and
    //   use deltaY = 1, deltaMode = 2 i.e. DOM_DELTA_PAGE(screen)
    // Note that when when wheeling on a touchpad, deltaY depends on how fast
    // you swipe, but the deltaMode is still different between the browsers.
    // Normalize everything so that zooming speed is approximately the same in all cases
    switch (deltaMode) {
      case WheelEvent.DOM_DELTA_LINE: // 1 (Delta values measured in lines)
        return deltaVal * wheelZoomDeltaModeLinePixels;
      case WheelEvent.DOM_DELTA_PAGE: // 2 (Delta values measured in pages or screens)
        return deltaVal * wheelZoomDeltaModeScreenPixels;
      default: // 0 (Delta values measured in pixels)
        return deltaVal;
    }
  },

  wheelInteraction = function (canvas) {
    return function (e) {
      let chart = canvas.getLinkedParent(),
        scale = canvas.config.xConfigs[0].scale,
        // Getting the range in pixel wrt to the scale attached to the drawing canvas
        [currStartRange, currEndRange] = scale.getRange(),
        originalEvent = e.originalEvent,
        direction,
        leftClampDirection,
        rightClampDirection,
        offset,
        coordinate,
        translationObj,
        translationX,
        chartX,
        r1,
        r2,
        sum;

      if (Math.abs(originalEvent.deltaY) > Math.abs(originalEvent.deltaX)) {
        direction = VERTICAL;
        offset = getWheelEventDeltaYInPixels(originalEvent.originalEvent.deltaMode, originalEvent.deltaY);
      } else {
        direction = HORIZONTAL;
        offset = getWheelEventDeltaYInPixels(originalEvent.originalEvent.deltaMode, originalEvent.deltaX);
      }

      if (direction === VERTICAL) {
        coordinate = getMouseCoordinate(canvas.getFromEnv('chart-container'), e.originalEvent,
          chart);
        translationObj = canvas.getTranslation();
        translationX = translationObj ? translationObj.x : 0;
        chartX = coordinate.chartX - translationX;
        // Finding the ratio in which the zooming has to be done such that zooming is done keeping the
        // category hovered at the center
        r1 = chartX - currStartRange;
        r2 = currEndRange - chartX;
        sum = r1 + r2;
        // Changing the Range wrt to the wheel value such that the gap changes (zoom)
        currStartRange -= 2 * offset * r1 / sum;
        currEndRange += 2 * offset * r2 / sum;
        [currStartRange, currEndRange] = clampWithinContextRange([currStartRange, currEndRange], scale, canvas.getFromEnv('contextScalesX')[0]);
        leftClampDirection = offset < 0 ? UP : DOWN;
        rightClampDirection = offset >= 0 ? UP : DOWN;
        chart.setPixelLimit(
          [currStartRange, currEndRange],
          scale,
          [leftClampDirection, rightClampDirection],
          {
            name: 'canvasWheel'
          }
        );
      } else {
        currEndRange += offset;
        currStartRange += offset;
        leftClampDirection = offset >= 0 ? UP : DOWN;
        rightClampDirection = leftClampDirection;
        chart.setPixelLimit(
          [currStartRange, currEndRange],
          scale,
          [leftClampDirection, rightClampDirection],
          {
            name: 'canvasWheel'
          }
        );
      }
    };
  };

export default function (canvas) {
  let config = canvas.config;
  // Adding wheel event only once for the main canvas
  if (config.enableInteraction && !config.wheelEventAdded) {
    canvas.addEventListener('fc-wheel', wheelInteraction(canvas));
    config.wheelEventAdded = true;
  }
}
