import SmartRenderer from '@fusioncharts/core/src/component-interface/smart-renderer';

const MOUSEOVER = 'fc-mouseover',
  MOUSEDOWN = 'fc-mousedown',
  MOUSEUP = 'fc-mouseup',
  MOUSEMOVE = 'fc-mousemove',
  MOUSEOUT = 'fc-mouseout',
  CLICK = 'fc-click',
  xShift = 5,
  BLANK = '',
  createDivString = (styleStr = '', string = '') => string && `<div ${styleStr}>${string}</div>`;

let UNDEF;

/**
 * This class is responsible for the tooltip and hover effect of dataplots and datamarkers
 */
class MouseAction extends SmartRenderer {
  configure () {
    super.configure();
    let mouseAction = this,
      config = mouseAction.config,
      chartConfig = mouseAction.getFromEnv('chartConfig'),
      mouseTracker = mouseAction.getFromEnv('mouseTracker');
    config.showTooltip = chartConfig.showTooltip;
    // Attaching external event listener on mouseTracker for showing tooltip
    !config.eventAdded && mouseAction.addExtEventListener('canvasHovered', (function (_mouseAction) {
      config.eventAdded = true;
      return function () {
        switch (arguments[1].hoveredInfo && arguments[1].hoveredInfo.component && arguments[1].hoveredInfo.component.getType()) {
          case 'timeMarker':
            // if hovered on the time marker for the first time then
            // hoverout on the dataset first and then hover in on the
            // time marker
            if (arguments[1].e.type === MOUSEOVER) {
              // send empty data to hide highlighted/hovered plots
              _mouseAction._triggerMouseActions({
                data: {
                  hoveredInfo: {},
                  e: {}
                }
              });
            }
            _mouseAction._triggerMarkerMouseActions(...arguments);
            break;
          default:
            _mouseAction._triggerMouseActions(...arguments);
        }
      };
    })(mouseAction), mouseTracker);
  }

  /**
   * Function to manage the mouseout event
   * @param {Object} e The actual mouse event object
   * @param {Number} _lastDatasetIndex The last datasetIndex hovered
   * @param {Number} _lastPointIndex The last data of the datasetIndex hovered
   * @param {Object} hoveredInfo The object conataining the hovered information
   * @param {Object} dataset The dataset instance hovered
   */
  mouseoutHandler (e, _lastDatasetIndex, _lastPointIndex, lastComponentHovered) {
    var mouseAction = this,
      config = mouseAction.config;

    // Triggering hover effect
    // Checking if lastComponentHovered's config property is present dataset might be disposed inbetween
    // hover in and hover out via wheel gesture.
    if (lastComponentHovered && lastComponentHovered.config) {
      lastComponentHovered.setHoverOutEffect([_lastPointIndex]);
      lastComponentHovered._firePlotEvent && lastComponentHovered._firePlotEvent(MOUSEOUT, _lastPointIndex, e);
    }
    // Hiding the tooltip
    config.showTooltip && mouseAction.getFromEnv('toolTipController').hide(config.currentToolTip);
    // delete stored last component details
    delete config._lastDatasetIndex;
    delete config._lastPointIndex;
  }
  /**
   * Call back function of canvasHovered event responsible for hover effect and tooltip of time markers
   * @param {Object} customObj The event object of canvasHovered events
   */
  _triggerMarkerMouseActions (customObj) {
    let mouseAction = this,
      data = customObj.data,
      e = data.e,
      toolTipController = mouseAction.getFromEnv('toolTipController'),
      tooltipStyle = mouseAction.getFromEnv('tooltipStyle'),
      chartX = data.chartX,
      chartY = data.chartY,
      parent = mouseAction.getLinkedParent(),
      startX,
      startY,
      translation = parent.getTranslation(),
      hoveredInfo = data.hoveredInfo,
      hovered = hoveredInfo.hovered,
      derivedEvents,
      config = mouseAction.config,
      showToolip = config.showTooltip,
      timeMarker = hoveredInfo.component,
      hoveredMarkerDim = hoveredInfo.pointObj.hoveredMarkerDim || {},
      toolText = BLANK,
      tooltextConfig = hoveredInfo.hovered && timeMarker.getToolTextConfiguration(hoveredMarkerDim.toolTextArr, hoveredInfo.pointObj.type),
      isWithinCanvas = parent.isWithinCanvas(chartX - (hoveredMarkerDim.thresholdX || 0), chartY - (hoveredMarkerDim.thresholdY || 0)),
      i,
      l;
    if (hovered && isWithinCanvas) {
      const { header, body } = tooltextConfig;
      toolText = createDivString(tooltipStyle.headerStyleString, header) +
      createDivString(tooltipStyle.bodyStyleString, body);
      startX = chartX;
      startY = hoveredMarkerDim.y - tooltextConfig.dimensions.height + translation.y;
      showToolip && (config.currentToolTip = toolTipController.drawAt(startX, startY, toolText, config.currentToolTip, parent));
      timeMarker.setHoverInEffect(hoveredInfo.pointIndex, hoveredInfo.pointObj.index, false);
      derivedEvents = mouseAction.getMakerMouseEvents(e, hoveredInfo.pointIndex, hoveredInfo.pointObj.index);
      if (l = derivedEvents.length) {
        config._lastMarkerIndex = hoveredInfo.pointIndex;
        config._lastDomainIndex = hoveredInfo.pointObj.index;
        for (i = 0; i < l; i++) {
          timeMarker.fireMarkerEvents(derivedEvents[i], hoveredInfo.pointIndex, hoveredInfo.pointObj.index);
        }
      }
    } else {
      showToolip && toolTipController.hide(config.currentToolTip);
      timeMarker.setHoverOutEffect();
      if (config._lastMarkerIndex !== UNDEF && config._lastDomainIndex !== UNDEF) {
        timeMarker.fireMarkerEvents(MOUSEOUT, hoveredInfo.pointIndex, hoveredInfo.pointObj.index);
      }
      config._lastMarkerIndex = UNDEF;
      config._lastDomainIndex = UNDEF;
    }
  }
  /**
   * Call back function of canvasHovered event responsible for hover effect and tooltip
   * @param {Object} customObj The event object of canvasHovered events
   */
  _triggerMouseActions (customObj) {
    var mouseAction = this,
      data = customObj.data,
      toolTipController = mouseAction.getFromEnv('toolTipController'),
      tooltipStyle = mouseAction.getFromEnv('tooltipStyle'),
      dataSource = mouseAction.getFromEnv('dataSource'),
      customToolText = dataSource.tooltip ? dataSource.tooltip.tooltext : null,
      componentIndex = data.i,
      e = data.e,
      chartX = data.chartX,
      chartY = data.chartY,
      hoveredInfo = data.hoveredInfo,
      pointObj,
      pointFound = componentIndex >= 0,
      parent = mouseAction.getLinkedParent(),
      datasets = parent.getChild(UNDEF, 'dataset'),
      config = mouseAction.config,
      _lastDatasetIndex = config._lastDatasetIndex,
      _lastPointIndex = config._lastPointIndex,
      translation = parent.getTranslation(),
      l,
      j,
      header = BLANK,
      body = BLANK,
      tooltext = BLANK,
      currTooltext,
      validPointObj,
      xRange,
      currPointIndex = hoveredInfo && hoveredInfo.pointIndex,
      targetComponent,
      datasetToEnquire,
      currDsConfig,
      isWithinCanvas = parent.isWithinCanvas(chartX, chartY),
      derivedEvensInfo;

    if (!hoveredInfo) {
      // if hoveredInfo is undefined, no valid data point is found in the canvas. Hence,
      // no tooltips and hover effects are applicable.
      return;
    }

    if (pointFound) {
      hoveredInfo.datasetIndex = componentIndex;
      derivedEvensInfo = mouseAction.getMouseEvents(e, hoveredInfo.datasetIndex, currPointIndex,
        hoveredInfo.component.getName(), hoveredInfo.markerObj);
    }

    // fire out on last hovered plot
    // Always fire out hover out if point is not within canvas
    if (!isWithinCanvas || ((!pointFound ||
        (derivedEvensInfo && derivedEvensInfo.fireOut)) && typeof _lastDatasetIndex !== 'undefined')) {
      // when mouseout is fired and there aren't any events that need to be fired over the current plot,
      // call mouseouthamndler through settimeout.
      // It handles situation where there is no actual element but mouseOver/mouseOut is triggered,
      // example line plot without anchors
      if (derivedEvensInfo && !derivedEvensInfo.events.length) {
        config.mouseoutTimer = setTimeout(function () {
          mouseAction.mouseoutHandler(e, _lastDatasetIndex, _lastPointIndex, config._lastComponentHovered);
        }, 20);
      } else {
        mouseAction.mouseoutHandler(e, _lastDatasetIndex, _lastPointIndex, config._lastComponentHovered);
        clearTimeout(config.mouseoutTimer);
      }
    }

    // Always show the tooltip if mouse pointer is inside the canvas
    if (isWithinCanvas && datasets.length && config.showTooltip) {
      xRange = hoveredInfo.component.getFromEnv('xScale').getRange();
      // Iterarting over all the datasets to trigger tooltip
      datasets.forEach((dataset, index) => {
        currDsConfig = dataset.config;
        if (currDsConfig.visibility === 'visible') {
          currTooltext = dataset._getTooltext(pointFound, componentIndex === index,
            dataset._getHoveredBin());
          // If custom tooltext is provided then no need to append
          if (customToolText) {
            tooltext = currTooltext;
          } else {
            tooltext += currTooltext;
          }

          // Fetching a valid dataset from which date info can be obtained
          if (currDsConfig.dataInfo[dataset._getHoveredBin()] && currTooltext) {
            validPointObj = currDsConfig.dataInfo[dataset._getHoveredBin()];
            datasetToEnquire = dataset;
          };
        }
      });
      // Date to be shown in the tooltip is fetched from the dataset having a valid data at the given point

      if (datasetToEnquire) {
        ({ header, body } = datasetToEnquire._getDateForToolText(datasetToEnquire._getHoveredBin()));
      }

      if (customToolText) {
        tooltext = createDivString('', body + tooltext);
      } else {
        tooltext = createDivString(tooltipStyle.headerStyleString, header) +
        createDivString(tooltipStyle.bodyStyleString, body + tooltext);
      }

      // If data is not actually hovered then we take the data from the hoveredBinIndex
      pointObj = pointFound ? hoveredInfo.pointObj : validPointObj;
      if (pointObj) {
        // Drawing the tooltip at specific position
        tooltext !== '' && pointObj.endXPosition > xRange[0] && pointObj.x < xRange[1] ? (config.currentToolTip =
          // colY is applicable for candlestick
          toolTipController.drawAt(pointObj.x + (pointObj.width || 0) / 2 + xShift + translation.x,
            (pointObj.colY || pointObj.y) + translation.y, tooltext, config.currentToolTip, parent))
          : toolTipController.hide(config.currentToolTip);
      } else {
        toolTipController.hide(config.currentToolTip);
      }
    }

    // fire remaining events
    if (pointFound) {
      l = derivedEvensInfo.events && derivedEvensInfo.events.length;
      // Update only when events for current plot are present.
      // Reason: When cursor is present in territory of line/area plot but not directly over it, then
      // the fireOut of current plot was happening instead of the actual last plot.
      if (l) {
        // store the index of the hovered DS and plot
        config._lastDatasetIndex = hoveredInfo.datasetIndex;
        config._lastPointIndex = currPointIndex;
        targetComponent = config._lastComponentHovered = hoveredInfo.component;
        config._lastComponentHoveredName = targetComponent && targetComponent.getName();

        // triggering the hover effect
        targetComponent.setHoverInEffect([currPointIndex]);
        // Triggering tooltip
        // If hovered over dataMarker then tooltip of a specific dataMarker is shown else
        // consolidated tooltip is shown
        if (config.showTooltip && targetComponent.getType() === 'dataMarker') {
          ({ header, body } = targetComponent._getTooltext(currPointIndex));
          tooltext = createDivString(tooltipStyle.headerStyleString, header) +
       createDivString(tooltipStyle.bodyStyleString, body);
          config.currentToolTip = toolTipController.draw(e, tooltext, config.currentToolTip);
        }

        // Firing corresponding events related to the actual mousevents
        for (j = 0; j < l; j += 1) {
          if (targetComponent && targetComponent._firePlotEvent) {
            targetComponent && targetComponent._firePlotEvent && targetComponent._firePlotEvent(derivedEvensInfo.events[j],
              currPointIndex, e);
          }
        }
      }
    }
  }

  /**
   * Given the event object, the index of the dataset and the index of the plot in the dataset, this
   * method determines which event has occured on a dataset's plot
   * @param {Event} e original mouse event that is being trapped
   * @param {number} datasetIndex Index of the dataset that is being interacted
   * @param {number} plotIndex Index of the plot that is being interacted
   * @param {String} componentName The component's name that is hovered
   * @return {DerivedEvent} An object containg the details of the mouse events that need to be fired
   * @todo has scope to reduce no of line
   */
  getMouseEvents (e, datasetIndex, plotIndex, componentName) {
    var mouseAction = this,
      config = mouseAction.config,
      lastDatasetIndex = config._lastDatasetIndex,
      lastPointIndex = config._lastPointIndex,
      lastComponentHovered = config._lastComponentHoveredName,
      eventType = e.type,
      derivedEvensInfo = {
        fireOut: false,
        events: []
      };
    switch (eventType) {
      case CLICK:
        derivedEvensInfo.events.push(CLICK);
        break;
      case MOUSEMOVE:
        if (lastComponentHovered === componentName && lastDatasetIndex === datasetIndex &&
            lastPointIndex === plotIndex) {
          // clear mouseout timer(if any) if mousemove happens over the same plot
          clearTimeout(config.mouseoutTimer);
          derivedEvensInfo.events.push(MOUSEMOVE);
        } else {
          derivedEvensInfo.events.push(MOUSEOVER);
          derivedEvensInfo.fireOut = true;
        }

        break;
      case MOUSEDOWN:
        if (lastComponentHovered !== componentName || lastDatasetIndex !== datasetIndex ||
            lastPointIndex !== plotIndex) {
          derivedEvensInfo.fireOut = true;
          derivedEvensInfo.events.push(MOUSEOVER);
        }
        derivedEvensInfo.events.push(MOUSEDOWN);
        break;

      case MOUSEOVER:
        if (lastComponentHovered === componentName && lastDatasetIndex === datasetIndex &&
            lastPointIndex === plotIndex) {
          // clear mouseout timer(if any) if mouseover happens over the same plot
          clearTimeout(config.mouseoutTimer);
        } else {
          //  when hovering over an element that lies over the last hovered element the fireout the last hovered element
          derivedEvensInfo.fireOut = true;
          derivedEvensInfo.events.push(MOUSEOVER);
        }
        break;

      case MOUSEUP:
        derivedEvensInfo.events.push(MOUSEUP);
        // @todo: implement clcik from mouseend as well
        break;

      case MOUSEOUT :
        derivedEvensInfo.fireOut = true;
    }
    return derivedEvensInfo;
  }
  /**
   * Given the event object and the index of the time marker, this
   * method determines which event has occured on an event markers
   * @param {Event} e original mouse event that is being trapped
   * @param {number} markerIndex Index of the marker that is being interacted
   * @param {number} domainIndex Sub-Index of the marker
   * @return {DerivedEvents} An array containg the details of the mouse events that need to be fired
   */
  getMakerMouseEvents (e, markerIndex, domainIndex) {
    var mouseAction = this,
      config = mouseAction.config,
      lastMarkerIndex = config._lastMarkerIndex,
      lastDomainIndex = config._lastDomainIndex,
      eventType = e.type,
      derivedEvents = [];
    switch (eventType) {
      case CLICK:
        derivedEvents.push(CLICK);
        break;
      case MOUSEMOVE:
        if (lastMarkerIndex === markerIndex && lastDomainIndex === domainIndex) {
          derivedEvents.push(MOUSEMOVE);
        } else {
          derivedEvents.push(MOUSEOVER);
        }
        break;
      case MOUSEOVER:
        if (lastMarkerIndex !== markerIndex && lastDomainIndex !== domainIndex) {
          derivedEvents.push(MOUSEOVER);
        }
        break;
    }
    return derivedEvents;
  }
}

export default MouseAction;
export { createDivString };
