import equals from 'ramda/es/equals';

import {
  pluck,
  UNDEF,
  defined,
  extend2,
  pluckNumber,
  polyPathToPath
} from '@fusioncharts/core/src/lib';
import Chunker from '@fusioncharts/utils/src/chunker/index.js';
import { addDep } from '@fusioncharts/core/src/dependency-manager';
import { convertColor } from '@fusioncharts/core/src/lib/lib-graphics';
import isValidNumber from '@fusioncharts/utils/src/type/is-valid-number';
import AreaGenerator from '@fusioncharts/utils/src/shape-generators/area-generator';
import LineGenerator from '@fusioncharts/utils/src/shape-generators/line-generator';
import StepCurve from '@fusioncharts/utils/src/shape-generators/curve-factories/step';
import LinearCurve from '@fusioncharts/utils/src/shape-generators/curve-factories/linear';
import CurveMonotoneX from '@fusioncharts/utils/src/shape-generators/curve-factories/monotone';
import TimeConverter from '@fusioncharts/utils/src/time-converter';
import timeHour from '@fusioncharts/utils/src/time-intervals/hour';

import Column from './column';
import lineAnimation from './line.animation';

const HOVERED_ANCHOR_RADIUS = 3.5,
  PLOT_HOVERED_ANCHOR_RADIUS = 5.5,
  DEFAULT_LINE_LENGTH = 10,
  DEFAULT_GAP_LENGTH = 5,
  CRISP_CONSTANT = 0.0625,
  NONE = 'none',
  DEFAULT_COLOR = '9194CC',
  VISIBLE = 'visible',
  SHOW = 'show',
  HIDE = 'hide',
  HIGHLIGHT = 'DataPlotHighlight',
  calculateMid = (a, b) => (a + b) / 2,
  _reducerFn = (s, t) => Object.assign(s, t),
  /**
   * If given a blank string, returns a valid path string by converting the
   * blank string to a M0,0 command. Otherwise, it assumes the the input path
   * string is valid and returns the input.
   *
   * @param {string} [pathString=''] The path string to be processed.
   *
   * @returns {string} The processed path string.
   */
  unBlankify = (pathString = '') => pathString === '' ? 'M0,0' : pathString,
  /**
   * Attempts to 'crisp' the left and right edges of an input object with x, y,
   * width and height properties (i.e. a rectangle). Crisping is the act of
   * removing sub-pixel rendering artifacts by adjusting the edges by some
   * constant.
   *
   * @param {Object} rect The rectangle to be crisped.
   * @param {number} rect.x The co-ordinate of left edge of the rectangle.
   * @param {number} rect.y The co-ordinate of the top edge of the rectangle.
   * @param {number} rect.width The width of the rectangle.
   * @param {number} rect.height The height of the rectangle.
   *
   * @returns {Object} A crisped rectangle.
   */
  crisper = ({ x, y, width, height }) => ({
    y,
    height,
    x: x - CRISP_CONSTANT,
    width: width + (CRISP_CONSTANT * 2)
  }),
  /**
   * Reduces a series of rectangle objects (i.e. objects with a x, y, width and
   * height) into a path string which visually correspond to exactly the same
   * series of rectangles.
   *
   * @param {string} acc The string into which the reducer will accumulate the
   * individual path strings
   * @param {Object} rect The rectangle to be reduced to its corresponding
   * path string
   * @param {number} rect.x The co-ordinate of left edge of the rectangle.
   * @param {number} rect.y The co-ordinate of the top edge of the rectangle.
   * @param {number} rect.width The width of the rectangle.
   * @param {number} rect.height The height of the rectangle.
   *
   * @returns {string} The input rectangle object, converted into a path string
   * and concatenated with the input accumulator.
   */
  toPathRect = function toPathRect (acc, rect) {
  // FTV3-1076 Added padding to pathRect so that anchor points are rendered properly at the border
    let padding = 2;
    return acc + 'M' + rect.x + ',' + rect.y + 'L' + (rect.x + rect.width + padding) + ',' + rect.y + 'L' + (rect.x + rect.width + padding) + ',' + (rect.y + rect.height + padding) + 'L' + rect.x + ',' + (rect.y + rect.height + padding) + 'Z';
  };

let maxRadius = 5,
  LINE = 'line',
  AREA = 'area',
  WHITE = '#ffffff',
  /**
   * Function to check if the coordinate is inside the radius of the anchor
   * @param   {Object} pointObj The object containing the info about the data
   * point hovered
   * @param   {number} pX x-axis position of the mouse cordinate
   * @param   {number} x the chartX where hovered
   * @param   {number} y the chartY where hovered
   * @param   {String} component component hovered
   * @return {Object} return an object with details of nearest plot and whether
   * it is hovered or not
   */
  isWithinShape = (pointObj, pX, x, y, component) => {
    var xPos,
      yPos,
      dx,
      dy,
      diff;

    if (!pointObj) {
      return {
        pointIndex: pX,
        hovered: false,
        component: component
      };
    }

    xPos = pointObj.x;
    yPos = pointObj.y;

    dx = (x - xPos);
    dy = (y - yPos);
    // Mathematical function to find a point inside a cirlce of given radius
    diff = Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2));

    return {
      pointIndex: pX,
      hovered: diff <= maxRadius,
      pointObj: pointObj,
      component: component
    };
  };

// helper function to get the generator depending on the type of dataset
function getGenerator (type) {
  switch (type) {
    case 'area':
    case 'smooth-area':
    case 'step-area':
      return AreaGenerator;
    case 'line':
    case 'smooth-line':
    case 'step-line':
    default:
      return LineGenerator;
  }
}
// helper function to get the curve generator class depending on the type of
// dataset
function getCurve (type) {
  switch (type) {
    case 'smooth-line':
    case 'smooth-area':
      return CurveMonotoneX;
    case 'step-area':
    case 'step-line':
      return StepCurve;
    case 'line':
    case 'area':
    default:
      return LinearCurve;
  }
}

function isArea (type) {
  return type === 'area' || type === 'smooth-area' || type === 'step-area';
}

function isLine (type) {
  return type === 'line' || type === 'smooth-line' || type === 'step-line';
}

addDep({
  name: 'continuousAnimation',
  type: 'animationRule',
  extension: lineAnimation
});

class Line extends Column {
  constructor () {
    super();

    /**
     * returns the style which is to be applied on the hovered plot
     *
     * @param {Object} hoverIndices indices of hovered plots
     * @returns {Object} configuration
     * @memberof Column
     */
    this.getHoverInConfig = function (hoverIndices, isDsHovered, isOtherDsHovered = false) {
      return {
        hoverIndices,
        hovered: true,
        radius: isDsHovered
          ? PLOT_HOVERED_ANCHOR_RADIUS
          : HOVERED_ANCHOR_RADIUS,
        isDsHovered,
        isOtherDsHovered
      };
    };

    /**
     * returns the style which is to be applied on the hovered out plot
     *
     * @param {Object} hoverIndices indices of hovered out plots
     * @returns {Object} configuration
     * @memberof Column
     */
    this.getHoverOutConfig = function (hoverIndices) {
      return {
        hoverIndices,
        hovered: false
      };
    };
  }
  /**
   * Sets the name of the component
   * @return {string} name
   */
  getName () {
    return 'continuous';
  }
  /**
   * returns the data plot indices which falls within the range
   * @param {Object} range min and max value range
   * @returns {Array} array of data points
   */
  getHighlightedIndices (range) {
    let dataset = this,
      config = dataset.config,
      dataInfo = config.dataInfo,
      hightlightedPoints = [];
    dataInfo.forEach((data, index) => {
      if (data.y <= range.minValue && data.y >= range.maxValue) {
        hightlightedPoints.push(index);
      }
    });
    return hightlightedPoints;
  }
  __setDefaultConfig () {
    super.__setDefaultConfig();
    let config = this.config;

    config['default-stroke'] = DEFAULT_COLOR;
    config['default-fill'] = DEFAULT_COLOR;
    config['default-anchor-stroke'] = convertColor(WHITE);
    config.type = LINE;
    config.plotStyle = {};
    config.nullPlotStyle = {};
    config.linePlotStyle = {};
    config.nullLinePlotStyle = {};
    config.areaPlotStyle = {};
    config.nullAreaPlotStyle = {};
    config.anchorStyle = {};
    config.anchorHoverInStyle = {};
    config.anchorHoverOutStyle = {};
    config.anchorHighlightObj = {};
    config.defaultLineStyle = {
      'stroke-opacity': 1,
      'stroke-width': 2,
      'stroke-dasharray': 'none',
      'fill': 'none'
    };
    config.defaultNullLineStyle = {
      'stroke-opacity': 1,
      'stroke-width': 2,
      'stroke-dasharray': [DEFAULT_LINE_LENGTH, DEFAULT_GAP_LENGTH],
      'fill': 'none'
    };
    config.defaultLinePredictiveStyle = {
      'stroke-opacity': 1,
      'stroke-width': 1,
      'stroke-dasharray': 5,
      'fill': 'none'
    };
    config.defaultAreaStyle = {
      'stroke-opacity': 1,
      'stroke-width': 0,
      'fill-opacity': 0.6
    };
    config.defaultNullAreaStyle = {
      'stroke-opacity': 1,
      'stroke-width': 0,
      'fill-opacity': 0.3
    };
    config.defaultAreaPredictiveStyle = {
      'stroke-opacity': 1,
      'opacity': 0.5
    };
    config.defaultAnchorStyle = {
      'fill-opacity': 1,
      'stroke-width': 1
    };
    config.willInjectNulls = true;
  }

  /**
   * Function to configure the cosmemtics of the plots
   */
  configureAttributes (dataObj) {
    const { config } = this,
      id = this.getId(),
      getStyleDef = this.getFromEnv('getStyleDef');

    let isNullAndNonNullStylesSame = true;

    // Setting the given configurations
    if (dataObj.hasOwnProperty('hoverIndices')) {
      // if a particular anchor is to be shown, enable it's showAnchor property
      if (dataObj.hovered) {
        config.mode = SHOW;
        config.sharedAnchorIndices = dataObj.hoverIndices;
      } else {
        config.mode = HIDE;
        // Storing infor for hiding the previously shown anchor
        config.hideIndices = config.lastShownIndices;
      }
      config.radius = dataObj.radius;
      config.isDsHovered = dataObj.isDsHovered;
      config.isOtherDsHovered = dataObj.isOtherDsHovered;
      config.hoverMode = true;
    } else {
      Object.entries(dataObj).forEach(([prop, val]) => {
        if (prop === 'primaryColor') {
          this.parsePrediction(dataObj);
          let primaryColor = val,
            plotConfig = dataObj.plotCosmetics,
            genericConfig = dataObj.genericCosmetics,
            styleConfig = dataObj.styleConfig,
            dsType = dataObj.type,
            plotStyleObj = config.plotStyle,
            predictivePlotStyleObj = config.predictiveStyleAttributes,
            nullPlotStyleObj = config.nullPlotStyle,
            linePlotStyleObj = config.linePlotStyle,
            predictiveStyleAttributesLine = config.predictiveStyleAttributesLine,
            predictiveStyleAttributesArea = config.predictiveStyleAttributesArea,
            nullLinePlotStyleObj = config.nullLinePlotStyle,
            areaPlotStyleObj = config.areaPlotStyle,
            nullAreaPlotStyleObj = config.nullAreaPlotStyle,
            anchorStyleObj = config.anchorStyle,
            anchorHoverInStyleObj = config.anchorHoverInStyle,
            anchorHighlightObj = config.anchorHighlightObj,
            applicableClassStyleGeneric = getStyleDef(
              genericConfig.style && genericConfig.style.plot
            ),
            applicableClassStyleGenericNull = getStyleDef(
              genericConfig.style && genericConfig.style['plot.null']
            ),
            applicableClassStyleGenericHover = getStyleDef(
              genericConfig.style && genericConfig.style['plot:hover']
            ),
            applicableClassStyleGenericHighlight = getStyleDef(
              genericConfig.style &&
              genericConfig.style['plot:highlight']
            ),
            applicableClassStyleGenericLine = getStyleDef(
              genericConfig.style && genericConfig.style.line
            ),
            applicableClassStyleGenericLineNull = getStyleDef(
              genericConfig.style && genericConfig.style['line.null']
            ),
            applicableClassStyleGenericArea = getStyleDef(
              genericConfig.style && genericConfig.style.area
            ),
            applicableClassStyleGenericAreaNull = getStyleDef(
              genericConfig.style && genericConfig.style['area.null']
            ),
            applicableClassStyleGenericAnchor = getStyleDef(
              genericConfig.style && genericConfig.style.anchor
            ),
            applicableClassStyleGenericAnchorHover = getStyleDef(
              genericConfig.style &&
              genericConfig.style['anchor:hover']
            ),
            applicableClassStyleGenericAnchorHighlight = getStyleDef(
              genericConfig.style &&
              genericConfig.style['anchor:highlight']
            ),
            applicableClassStyleSpecific = getStyleDef(
              plotConfig.style && plotConfig.style.plot
            ),
            applicableClassStyleSpecificNull = getStyleDef(
              plotConfig.style && plotConfig.style['plot.null']
            ),
            applicableClassStyleSpecificPredictive = getStyleDef(
              plotConfig.style && plotConfig.style['plot.predictive']
            ),
            applicableClassStyleSpecificHover = getStyleDef(
              plotConfig.style && plotConfig.style['plot:hover']
            ),
            applicableClassStyleSpecificHighlight = getStyleDef(
              plotConfig.style && plotConfig.style['plot:highlight']
            ),
            applicableClassStyleSpecificLine = getStyleDef(
              plotConfig.style && plotConfig.style.line
            ),
            applicableClassStyleSpecificLineNull = getStyleDef(
              plotConfig.style && plotConfig.style['line.null']
            ),
            applicableClassStyleSpecificLinePredictive = getStyleDef(
              plotConfig.style && plotConfig.style['line.predictive']
            ),
            applicableClassStyleSpecificArea = getStyleDef(
              plotConfig.style && plotConfig.style.area
            ),
            applicableClassStyleSpecificAreaNull = getStyleDef(
              plotConfig.style && plotConfig.style['area.null']
            ),
            applicableClassStyleSpecificAreaPredictive = getStyleDef(
              plotConfig.style && plotConfig.style['area.predictive']
            ),
            applicableClassStyleSpecificAnchor = getStyleDef(
              plotConfig.style && plotConfig.style.anchor
            ),
            applicableClassStyleSpecificAnchorHover = getStyleDef(
              plotConfig.style && plotConfig.style['anchor:hover']
            ),
            applicableClassStyleSpecificAnchorHighlight = getStyleDef(
              plotConfig.style && plotConfig.style['anchor:highlight']
            ),
            applicableInlineStyle = getStyleDef(styleConfig.plot),
            applicableInlineStyleNull = getStyleDef(
              styleConfig['plot.null']
            ),
            applicableInlineStylePredictive = getStyleDef(
              styleConfig['plot.predictive']
            ),
            applicableInlineStyleHover = getStyleDef(
              styleConfig['plot:hover']
            ),
            applicableInlineStyleHighlight = getStyleDef(
              styleConfig['plot:highlight']
            ),
            applicableInlineStyleLine = getStyleDef(styleConfig.line),
            applicableInlineStyleLineNull = getStyleDef(
              styleConfig['line.null']
            ),
            applicableInlineStyleLinePredictive = getStyleDef(
              styleConfig['line.predictive']
            ),
            applicableInlineStyleArea = getStyleDef(styleConfig.area),
            applicableInlineStyleAreaNull = getStyleDef(
              styleConfig['area.null']
            ),
            applicableInlineStyleAreaPredictive = getStyleDef(
              styleConfig['area.predictive']
            ),
            applicableInlineStyleAnchor = getStyleDef(
              styleConfig.anchor
            ),
            applicableInlineStyleAnchorHover = getStyleDef(
              styleConfig['anchor:hover']
            ),
            applicableInlineStyleAnchorHighlight = getStyleDef(
              styleConfig['anchor:highlight']
            );

          // config.defaultLineStyle['stroke-linecap'] = isArea(dsType)
          // ? 'round'
          // : 'round';

          config.defaultLineStyle['stroke-linecap'] = 'round';

          config.defaultNullLineStyle['stroke-linecap'] = isArea(dsType)
            ? 'butt'
            : 'round';

          config.defaultLinePredictiveStyle['stroke-linecap'] = isArea(dsType)
            ? 'butt'
            : 'round';

          [
            linePlotStyleObj,
            config.defaultLineStyle,
            applicableClassStyleGeneric,
            applicableClassStyleGenericLine,
            applicableInlineStyle,
            applicableClassStyleSpecific,
            applicableClassStyleSpecificLine,
            applicableInlineStyleLine
          ].reduce(_reducerFn);
          [
            nullLinePlotStyleObj,
            config.defaultNullLineStyle,
            applicableClassStyleGenericNull,
            applicableClassStyleGenericLineNull,
            applicableInlineStyleNull,
            applicableClassStyleSpecificNull,
            applicableClassStyleSpecificLineNull,
            applicableInlineStyleLineNull
          ].reduce(_reducerFn);
          [
            predictiveStyleAttributesLine,
            applicableInlineStylePredictive,
            applicableClassStyleSpecificPredictive,
            applicableClassStyleSpecificLinePredictive,
            applicableInlineStyleLinePredictive
          ].reduce(_reducerFn);
          [
            areaPlotStyleObj,
            config.defaultAreaStyle,
            applicableClassStyleGeneric,
            applicableClassStyleGenericArea,
            applicableInlineStyle,
            applicableClassStyleSpecific,
            applicableClassStyleSpecificArea,
            applicableInlineStyleArea
          ].reduce(_reducerFn);
          [
            nullAreaPlotStyleObj,
            config.defaultNullAreaStyle,
            applicableClassStyleGenericNull,
            applicableClassStyleGenericAreaNull,
            applicableInlineStyleNull,
            applicableClassStyleSpecificNull,
            applicableClassStyleSpecificAreaNull,
            applicableInlineStyleAreaNull
          ].reduce(_reducerFn);
          [
            predictiveStyleAttributesArea,
            applicableInlineStylePredictive,
            applicableClassStyleSpecificPredictive,
            applicableClassStyleSpecificAreaPredictive,
            applicableInlineStyleAreaPredictive
          ].reduce(_reducerFn);
          [
            anchorStyleObj,
            config.defaultAnchorStyle,
            applicableClassStyleGeneric,
            applicableClassStyleGenericAnchor,
            applicableInlineStyle,
            applicableClassStyleSpecific,
            applicableClassStyleSpecificAnchor,
            applicableInlineStyleAnchor
          ].reduce(_reducerFn);

          // Applying primaryColor to stroke and fill
          // When area fill is taken only from primary color.
          // When line stroke is taken only from primary color
          plotStyleObj.fill = areaPlotStyleObj.fill = convertColor(
            pluck(primaryColor, config['default-fill']),
            areaPlotStyleObj['fill-opacity'] * 100
          );
          // Same for null plots
          nullPlotStyleObj.fill = nullAreaPlotStyleObj.fill = convertColor(
            pluck(
              nullAreaPlotStyleObj.fill,
              primaryColor,
              config['default-fill']
            ),
            nullAreaPlotStyleObj['fill-opacity'] * 100
          );
          // For predictive plots
          predictivePlotStyleObj.fill = predictiveStyleAttributesArea.fill = convertColor(
            pluck(
              predictiveStyleAttributesArea.fill,
              primaryColor,
              config['default-fill']
            ),
            predictiveStyleAttributesArea['fill-opacity'] * 100
          );

          // line should not take fill even if user gives it, as it connects
          // terminal points and become a different shape.
          linePlotStyleObj.fill = NONE;
          // same for null plots
          nullLinePlotStyleObj.fill = NONE;
          // same for predictive plots
          predictiveStyleAttributesLine.fill = NONE;

          areaPlotStyleObj.stroke = convertColor(
            pluck(primaryColor, config['default-stroke']),
            areaPlotStyleObj['stroke-opacity'] * 100
          );
          nullAreaPlotStyleObj.stroke = convertColor(
            pluck(
              nullAreaPlotStyleObj.stroke,
              primaryColor,
              config['default-stroke']
            ),
            nullAreaPlotStyleObj['stroke-opacity'] * 100
          );
          predictiveStyleAttributesArea.stroke = convertColor(
            pluck(
              predictiveStyleAttributesArea.stroke,
              primaryColor,
              config['default-stroke']
            ),
            predictiveStyleAttributesArea['stroke-opacity'] * 100
          );
          linePlotStyleObj.stroke = convertColor(
            pluck(primaryColor, config['default-stroke']),
            linePlotStyleObj['stroke-opacity'] * 100
          );
          nullLinePlotStyleObj.stroke = convertColor(
            pluck(
              nullLinePlotStyleObj.stroke,
              primaryColor,
              config['default-stroke']
            ),
            nullLinePlotStyleObj['stroke-opacity'] * 100
          );
          predictiveStyleAttributesLine.stroke = convertColor(
            pluck(
              predictiveStyleAttributesLine.stroke,
              primaryColor,
              config['default-stroke']
            ),
            predictiveStyleAttributesLine['stroke-opacity'] * 100
          );
          plotStyleObj.stroke = isArea(dsType)
            ? areaPlotStyleObj.stroke
            : linePlotStyleObj.stroke;
          nullPlotStyleObj.stroke = isArea(dsType)
            ? nullAreaPlotStyleObj.stroke
            : nullLinePlotStyleObj.stroke;
          predictivePlotStyleObj.stroke = isArea(dsType)
            ? predictiveStyleAttributesArea.stroke
            : predictiveStyleAttributesLine.stroke;

          // Applying primaryColor to stroke and fill
          anchorStyleObj.fill = convertColor(
            pluck(primaryColor, config['default-stroke'])
          );
          anchorStyleObj.stroke = config['default-anchor-stroke'];

          [
            anchorHoverInStyleObj,
            anchorStyleObj,
            applicableClassStyleGenericHover,
            applicableClassStyleGenericAnchorHover,
            applicableClassStyleSpecificHover,
            applicableInlineStyleHover,
            applicableClassStyleSpecificAnchorHover,
            applicableInlineStyleAnchorHover
          ].reduce(_reducerFn);
          [
            anchorHighlightObj,
            anchorStyleObj,
            applicableClassStyleGenericHighlight,
            applicableClassStyleGenericAnchorHighlight,
            applicableClassStyleSpecificHighlight,
            applicableInlineStyleHighlight,
            applicableClassStyleSpecificAnchorHighlight,
            applicableInlineStyleAnchorHighlight
          ].reduce(_reducerFn);

          // Parsing the hoverOut styles from hover style and highlight style
          Object.keys(Object.assign(
            Object.assign({}, anchorHoverInStyleObj),
            anchorHighlightObj
          )).forEach(styles => {
            config.anchorHoverOutStyle[styles] = anchorStyleObj[styles] || '';
          });

          // If no highlight style is given at any level then set highlight
          // styles to hover out styles.
          if (!Object.keys(anchorHighlightObj).length) {
            anchorHighlightObj = config.anchorHighlightObj = extend2(
              {},
              config.anchorHoverOutStyle
            );
          }

          config.connectNullData = pluckNumber(
            config.connectNullData,
            plotConfig.connectnulldata,
            genericConfig.connectnulldata,
            0
          );

          // Applied individually in stroke and fill
          delete areaPlotStyleObj['fill-opacity'];
          delete nullAreaPlotStyleObj['fill-opacity'];
          delete areaPlotStyleObj['stroke-opacity'];
          delete nullAreaPlotStyleObj['stroke-opacity'];
          delete linePlotStyleObj['stroke-opacity'];
          delete nullLinePlotStyleObj['stroke-opacity'];
          delete config.predictiveStyleAttributesArea['fill-opacity'];
          delete config.predictiveStyleAttributesArea['stroke-opacity'];
          delete config.predictiveStyleAttributesLine['stroke-opacity'];
        } else {
          config[prop] = val;
        }
      });

      // If we are not supposed to use different null styles (e.g. if the
      // dataset is being represented in the time navigator), make the null
      // and non-null styles the same
      if (!config.useNullStyles) {
        // Not deep copying here since the style objects are expected to be
        // at most one level deep. Instead, simply using Object.assign to break
        // the reference to the non-null styles
        config.nullAreaPlotStyle = Object.assign({}, config.areaPlotStyle);
        config.nullLinePlotStyle = Object.assign({}, config.linePlotStyle);
      }

      // Null and non null styles are same if BOTH the line's null and
      // non-null styles AND the area's null and non-null styles match
      isNullAndNonNullStylesSame = equals(
        config.areaPlotStyle,
        config.nullAreaPlotStyle
      ) && equals(
        config.linePlotStyle,
        config.nullLinePlotStyle
      );

      // Even if null data is to be connected, nulls should NOT be injected if
      // the null and non-null styles are same.
      // Why? Because if the styles are the same and we still inject nulls,
      // a banding/striation effect appears on the rendered paths due to
      // sub-pixel aliasing effects on adjacent clip paths.
      if (config.connectNullData && isNullAndNonNullStylesSame) {
        config.willInjectNulls = false;
      } else {
        // If null data is to not be connected and the null and non null styles
        // are different, nulls should be injected into the data so that the
        // generated line paths appear 'broken'
        config.willInjectNulls = true;
      }

      const chart = this.getFromEnv('chart');

      config.limit = this._calculateLimits();

      chart.setYScaleLimit(
        config.scaleY.getId(),
        id,
        config.limit.y,
        config.limit.baseRequired
      );
      chart.setXScaleLimit(
        config.scaleX.getId(),
        id,
        config.limit.x
      );
    }
  }

  parsePrediction (dataObject) {
    this.config.prediction = dataObject.prediction || this.config.prediction;
    this.parsePredictiveStyleAttributes();
  }

  parsePredictiveStyleAttributes () {
    const predictiveStyleAttributes = {},
      config = this.config;

    config.predictiveStyleAttributesLine = {};
    config.predictiveStyleAttributesArea = {};

    if (config.prediction.enabled) {
      Object.assign(
        predictiveStyleAttributes,
        config.prediction.style.plot
      );
      Object.assign(
        config.predictiveStyleAttributesLine,
        config.defaultLinePredictiveStyle,
        config.prediction.style.plot,
        config.prediction.style.line
      );
      Object.assign(
        config.predictiveStyleAttributesArea,
        config.defaultAreaPredictiveStyle,
        config.prediction.style.plot,
        config.prediction.style.area
      );
    }

    config.predictiveStyleAttributes = predictiveStyleAttributes;
  }

  // Method to get padding in data limits
  getPadding () {
    let dataset = this,
      canvasConfig = dataset.getLinkedParent().config,
      config = dataset.config,
      anchorPadding,
      dataMarkerPadding;

    anchorPadding = dataset.getValueFromPx(PLOT_HOVERED_ANCHOR_RADIUS);
    dataMarkerPadding = canvasConfig.enableMarkers
      ? dataset.getValueFromPx(config.dataMarkerPadding)
      : 0;

    return Math.ceil(Math.max(anchorPadding, dataMarkerPadding));
  }
  /**
   * Helper function of data markers
   * @return {Object} contains positioning info and the dataInfo
   */
  _getRelevantInfo () {
    var config = this.config;
    return {
      firstTimeStamp: config.firstTimeStamp,
      timeStampGap: config.timeStampGap,
      dataInfo: config.dataInfo,
      fill: config.plotStyle.stroke
    };
  }

  /**
   * Function to allocation the position of the dataplots
   */
  allocatePosition () {
    let dataset = this,
      config = dataset.config,
      [xIdx, yIdx, baseIdx, totalStackSum] = config.indices,
      seriesInfo = config.seriesInfo,
      dateColumn = dataset.getFromEnv('dateColumn'),
      isUTC = dataset.getFromEnv('isUTC'),
      dsType = config.type,
      data = config.data,
      dataInfo = config.dataInfo,
      dataInfoNull = [],
      dataObj,
      binDecider = dataset.getFromEnv('binDecider'),
      xScale = dataset.getFromEnv('xScale'),
      yScale = dataset.getFromEnv('yScale'),
      xThresholdDuration = binDecider.getRangeThreshold()[2],
      nullClipAdjustment = Number.parseFloat(
        config.nullLinePlotStyle['stroke-width']
      ),
      predictionClipAdjustment = Number.parseFloat(
        config.predictiveStyleAttributesLine['stroke-width']
      ),
      clipAdjustment = Number.parseFloat(config.linePlotStyle['stroke-width']),
      // isAggregated = config.isAggregated,
      pathGenerator,
      startDate,
      endDate,
      midDate,
      nextData,
      nextMidDate,
      yValue,
      // firstTimeStamp,
      pX,
      base,
      y,
      yBaseValue,
      paddingInTimestamp,
      dataLen = data.length,
      [startDomain, endDomain] = xScale.getDomain(),
      bins = xScale.bins,
      i,
      len,
      xValue,
      duration,
      eventArgs,
      seriesName,
      format = dateColumn.format,
      formatter = isUTC ? TimeConverter.utcFormatter(format) : TimeConverter.formatter(format),
      isPlotOverTick = xScale.showPlotOverTick();
      // Storing the 1st time stam
    config.firstTimeStamp = data && data[0] && data[0][xIdx] && data[0][xIdx].start;
    if ((config.repositioningDone = dataset._isRepositioningNeeded())) {
      // skip drawing if dataset is not visible
      if (config.visibility !== 'visible') {
        return;
      }
      dataInfo = (config.dataInfo = []);

      // Store data related operations
      config.timeStampGap = xThresholdDuration;

      config.availableWidth = 0;
      // Storing info for dataMarker detection
      for (i = 0, len = bins.length; i < len; i++) {
        if (bins[i].clipType !== 2) {
          config.availableWidth = xScale.getRangeValue(bins[i].end) - xScale.getRangeValue(bins[i].start);
          break;
        }
      }

      paddingInTimestamp =
        +xScale.getDomainValue(PLOT_HOVERED_ANCHOR_RADIUS) -
        +xScale.getDomainValue(0);

      // Storing value for tooltip
      config.actualStartDomain = +startDomain - paddingInTimestamp;
      // Actual end domain is the scale domain minus the space taken up by the
      // plotspacepercent
      config.actualEndDomain = +endDomain + paddingInTimestamp;

      // Storing the x and y for plotting the data which is later used for
      // tooltip detection
      data.forEach((d, index, dataArr) => {
        xValue = d[xIdx];
        yValue = d[yIdx];
        duration = xValue.config.duration;
        startDate = xValue.start;
        endDate = xValue.end;
        const tempDate = new Date(startDate),
          winterOffset = new Date(tempDate.getFullYear(), 0).getTimezoneOffset(),
          summerOffset = new Date(tempDate.getFullYear(), 6).getTimezoneOffset();

        // In case of daylight-saving update the start date
        if (winterOffset > summerOffset) {
          startDate = timeHour.offset(startDate, 1);
        }
        midDate = calculateMid(startDate, endDate);
        nextData = dataArr[index + 1];
        yBaseValue = d[baseIdx];
        if (Math.abs(yBaseValue) > Math.abs(yValue)) {
          // FTV3-1076 swapping values of yValue and yBaseValue.
          // generally yValue and yBaseValue should be present at indices yIdx and baseIdx respectively
          // but in certain cases they are present in baseIdx and yIdx respectively
          // this swap is to handle the above cases.
          yValue = d[baseIdx];
          yBaseValue = d[yIdx];
        }
        base = yScale.getRangeValue(
          yBaseValue || Math.max(yScale.getDomain()[0], 0)
        );
        y = yScale.getRangeValue(yValue);
        // Storing the 1st time stamp
        if (!index) {
          config.firstTimeStamp = startDate;
        }
        if (dsType === AREA && !isValidNumber(base)) {
          return;
        }
        if (isValidNumber(yValue) && isValidNumber(y)) {
          // Get the data index corresponding to the fetched timeStamp
          pX = xScale.getBinIndex(startDate);
          dataObj = {
            startDate,
            endDate,
            timeInstant: isPlotOverTick ? startDate : midDate,
            value: yValue,
            paddingInTimestamp,
            yBaseValue: dsType === AREA ? yBaseValue : UNDEF,
            x: xScale.getRangeValue(new Date(startDate), new Date(endDate)),
            endXPosition: xScale.getRangeValue(
              new Date(startDate),
              new Date(endDate)
            ),
            totalStackSum: defined(d[totalStackSum]) ? d[totalStackSum] : UNDEF,
            y,
            base
          };

          eventArgs = {
            index,
            dataValue: yValue,
            start: startDate,
            startText: formatter.format(startDate),
            end: endDate,
            endText: formatter.format(endDate),
            binUnit: duration.Unit,
            binMultiplier: duration.number,
            plotType: config.type,
            timeFormatter: format,
            aggregation: config.aggregation,
            measure: config.measures[0],
            binValue: yValue,
            binValueFormatted: config.formatterFn && config.formatterFn({
              value: yValue,
              type: 'tooltip',
              prefix: config.prefix,
              suffix: config.suffix
            })
          };
          seriesName = Object.keys(seriesInfo)[0];
          if (seriesName) {
            eventArgs.series = seriesName;
            eventArgs.seriesValue = seriesInfo[seriesName];
          }
          dataObj.eventArgs = eventArgs;
          dataInfo[pX] = dataObj;
          // Storing metadata
          if (nextData) {
            const nextPX = xScale.getBinIndex(nextData[xIdx].start);
            dataInfoNull.push(dataObj);
            nextMidDate = calculateMid(
              nextData[xIdx].start,
              nextData[xIdx].end
            );
            // Pushing null if the gap between 2 consequtive data is more than
            // the threshold handling the uneven number of days in each month
            // with the 1.5 multiplier
            if (
              config.willInjectNulls &&
              nextMidDate - midDate - (xScale.clips[nextPX] - xScale.clips[pX]) > xThresholdDuration * 1.5
            ) {
              dataInfoNull.push(null);
            }
          } else if (dataLen - index === 1) { // For the last data
            dataInfoNull.push(dataObj);
          }
        } else if (config.willInjectNulls) {
          // when value is not present, insert null to show gaps in data
          dataInfoNull.push(null);
        }
      });

      pathGenerator = new Chunker(getGenerator(dsType))
        .setConnectUndefineds(config.connectNullData)
        .setChunks({
          def: { clipAdjustment },
          undef: { clipAdjustment: nullClipAdjustment },
          predictive: { clipAdjustment: predictionClipAdjustment }
        })
        // Uncomment to create a line instead of a smooth line
        .setCurve(getCurve(dsType))
        // Below function determines what will happen incase of null data
        .setDefined(d => !!d)
        .setChunkDecider(d => {
          return d.startDate >= config.prediction.dateMs ? 'predictive' : 'def';
        })
        // Below function determines the x location of the plot
        .setXAccessor(d => d.x)
        // Below function determines the y location of the plot
        .setYAccessor(d => d.y);

      // applicable only for area charts.
      if (isArea(dsType)) {
        // Below function determines the y base location of the plot
        pathGenerator.setYBaseAccessor(d => d.base);
      }

      // Below function generates the path
      config.chunks = pathGenerator.generate(dataInfoNull);

      config.path = unBlankify(config.chunks.def.path);
      config.clipPath = unBlankify(
        config.chunks.def.clipRects
          .map(crisper)
          .reduce(toPathRect, '')
      );

      config.nullPath = unBlankify(config.chunks.undef.path);
      config.nullClipPath = unBlankify(
        config.chunks.undef.clipRects
          .map(crisper)
          .reduce(toPathRect, '')
      );

      config.predictivePath = unBlankify(config.chunks.predictive.path);
      config.predictiveClipPath = unBlankify(
        config.chunks.predictive.clipRects
          .map(crisper)
          .reduce(toPathRect, '')
      );

      if (isArea(dsType)) {
        // Below function generates only the top path
        config.topChunks = pathGenerator
          .getLineChunkerYTop()
          .generate(dataInfoNull);

        config.topPath = unBlankify(config.topChunks.def.path);
        config.topClipPath = unBlankify(
          config.topChunks.def.clipRects
            .map(crisper)
            .reduce(toPathRect, '')
        );

        config.topNullPath = unBlankify(config.topChunks.undef.path);
        config.topNullClipPath = unBlankify(
          config.topChunks.undef.clipRects
            .map(crisper)
            .reduce(toPathRect, '')
        );

        config.topPredictivePath = unBlankify(config.topChunks.predictive.path);
        config.topPredictiveClipPath = unBlankify(
          config.topChunks.predictive.clipRects
            .map(crisper)
            .reduce(toPathRect, '')
        );
      }
    }
  }

  /**
   * Funtion that determines if tooltext is possible for the current data
   * @param {Object} currData contains info about the current hovered index
   */
  _isInvalidTooltext (currData) {
    let dataset = this,
      config = dataset.config;
    // Checking the data's mid date to be inside the range of the visbible axis
    if (
      !currData ||
      currData.timeInstant < config.actualStartDomain ||
      currData.timeInstant > config.actualEndDomain
    ) {
      return true;
    }
  }

  /**
   * Function to draw the line plot
   */
  _drawPlot () {
    let dataset = this,
      config = dataset.config,
      linePlotStyleObj = { ...config.linePlotStyle, fill: 'none' },
      areaPlotStyleObj = config.areaPlotStyle,
      nullLinePlotStyleObj = { ...config.nullLinePlotStyle, fill: 'none' },
      nullAreaPlotStyleObj = config.nullAreaPlotStyle,
      predictiveStyleAttributesLine = { ...config.predictiveStyleAttributesLine, fill: 'none' },
      predictiveStyleAttributesArea = config.predictiveStyleAttributesArea,
      dataInfo = config.dataInfo,
      data;
    // dataset meso parent group
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'meso-line',
      attr: {
        name: 'line-common-meso',
        visibility: config.visibility
      }
    });
    // dataset thermo parent group
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'thermo',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'thermo-line',
      attr: {
        name: 'line-common-thermo',
        visibility: config.visibility
      }
    });

    // Meso group for line path plot
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso-line',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'meso-plot',
      attr: {
        name: 'line-plot-meso',
        visibility: config.visibility
      }
    }, true);
    // Thermo group for line path plot
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'thermo-line',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'thermo-plot',
      attr: {
        name: 'line-plot-thermo',
        visibility: config.visibility
      }
    }, true);

    // Group for anchor (thermo)
    // As anchors should drawn on top of area path
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'thermo-line',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'thermo-anchor',
      attr: {
        name: 'line-anchor-thermo',
        visibility: config.visibility
      }
    }, true);

    if (config.connectNullData) {
      // drawing null area fill/line path in meso-plot group
      dataset.addGraphicalElement({
        el: 'path',
        container: {
          label: 'group',
          id: 'meso-plot'
        },
        attr: Object.assign(
          {
            path: config.nullPath,
            'clip-path': config.nullClipPath
          },
          isLine(config.type) ? nullLinePlotStyleObj : nullAreaPlotStyleObj
        ),
        label: 'path',
        component: dataset
      }, true);
    }
    // drawing area fill/line path in meso-plot group
    dataset.addGraphicalElement({
      el: 'path',
      container: {
        label: 'group',
        id: 'meso-plot'
      },
      attr: Object.assign({
        path: config.path,
        'clip-path': config.clipPath
      }, (isLine(config.type) ? linePlotStyleObj : areaPlotStyleObj)),
      label: 'path',
      component: dataset
    }, true);

    if (config.prediction.enabled) {
      // drawing null area fill/line path in meso-plot group
      dataset.addGraphicalElement({
        el: 'path',
        container: {
          label: 'group',
          id: 'meso-plot'
        },
        attr: Object.assign(
          {
            path: config.predictivePath,
            'clip-path': config.predictiveClipPath
          },
          isLine(config.type) ? predictiveStyleAttributesLine : predictiveStyleAttributesArea
        ),
        label: 'path',
        component: dataset
      }, true);
    }

    // draw the top path of dataset in thermo group.
    if (config.topPath) {
      if (config.connectNullData) {
        dataset.addGraphicalElement({
          el: 'path',
          container: {
            label: 'group',
            id: 'thermo-plot'
          },
          attr: Object.assign(
            {
              path: config.topNullPath,
              'clip-path': config.topNullClipPath
            },
            nullLinePlotStyleObj
          ),
          label: 'topPath',
          component: dataset
        }, true);
      }

      dataset.addGraphicalElement({
        el: 'path',
        container: {
          label: 'group',
          id: 'thermo-plot'
        },
        attr: Object.assign({
          path: config.topPath,
          'clip-path': config.topClipPath
        }, linePlotStyleObj),
        label: 'topPath',
        component: dataset
      }, true);

      if (config.prediction.enabled) {
        // drawing predictive area fill/line path in meso-plot group
        dataset.addGraphicalElement({
          el: 'path',
          container: {
            label: 'group',
            id: 'meso-plot'
          },
          attr: Object.assign(
            {
              path: config.topPredictivePath,
              'clip-path': config.topPredictiveClipPath
            },
            predictiveStyleAttributesLine
          ),
          label: 'path',
          component: dataset
        }, true);
      }
    }

    if (config.mode === 'show') {
      // Only draw the anchors whose showAnchor property are enabled
      // by default only the anchor on which mouse is hovered will be shown
      if (config.sharedAnchorIndices && config.sharedAnchorIndices.length) {
        config.sharedAnchorIndices.forEach((index) => {
          data = dataInfo[index];
          config.lastShownIndices = config.sharedAnchorIndices;
          data && !isNaN(data.value) && dataset.addGraphicalElement({
            el: 'path',
            container: {
              label: 'group',
              id: 'thermo-anchor'
            },
            attr: Object.assign({
              path: polyPathToPath([2, data.x, data.y, config.radius, 0, 0]),
              visibility: 'show'
            }, config.isDsHovered ? config.anchorHoverInStyle : config.isOtherDsHovered ? config.anchorStyle : config.anchorHighlightObj),
            id: 'shared-anchor-' + index,
            label: config.dsType + '-anchor'
          });
        });
      }
    } else {
      // Hiding an anchor
      if (config.hideIndices && config.hideIndices.length) {
        config.hideIndices.forEach((index) => {
          data = dataInfo[index];
          data && !isNaN(data.value) && dataset.addGraphicalElement({
            el: 'path',
            container: {
              label: 'group',
              id: 'meso-anchor'
            },
            attr: {
              visibility: 'hidden'
            },
            id: 'shared-anchor-' + index,
            label: config.dsType + '-anchor'
          });
        });
      }
    }
  }

  /**
   * Function to set hover in effect
   * @param {number} indices data indices
   */
  setHoverInEffect (indices, isDsHovered, isOtherDsHovered) {
    let dataset = this,
      chart = dataset.getFromEnv('chart'),
      dataInfo = dataset.config.dataInfo,
      eventArgs;
    dataset.getFromEnv('animationManager').setAnimationState('mouseOver');
    if (!isDsHovered) {
      indices.forEach((index) => {
        if (dataInfo[index]) {
          eventArgs = dataInfo[index].eventArgs;
          chart.fireChartInstanceEvent(HIGHLIGHT, eventArgs);
        }
      });
    }
    this.setData(this.getHoverInConfig(indices, isDsHovered, isOtherDsHovered), true);
  }

  /**
   * Function that return the nearest plot details
   * @param   {number} chartX x-axis position of the mouse cordinate
   * @param   {number} chartY y-axis position of the mouse cordinate
   * @return {Object} return an object with details of nearest plot and whether
   * it is hovered or not
   */
  _getHoveredPlot (chartXArg, chartYArg) {
    var dataset = this,
      chartX = chartXArg,
      chartY = chartYArg,
      xScale = dataset.getFromEnv('xScale'),
      xScaleRange = xScale.getRange(),
      dataMarkers = [
        dataset.getChildren('dataMarker')
      ],
      config = dataset.config,
      visible = config.visibility === VISIBLE,
      dataInfo = config.dataInfo,
      len = dataInfo.length,
      canvas = dataset.getLinkedParent(),
      translationObj = canvas.getTranslation(),
      translationX = translationObj.x,
      translationY = translationObj.y,
      pointObj,
      xMin,
      xMax,
      returnValue,
      j,
      pX;
    // Readjusting the chart as the canvas group is translated
    chartX -= translationX;
    chartY -= translationY;
    // if chartX is outside the range of scale, no plots will be hovered
    // as because tootlip expects a proper object or undefined component property is sent
    // if undefined it sent then it will freeze tooltip in its last position.
    if (chartX < xScaleRange[0] || chartX > xScaleRange[1]) {
      return {
        component: dataset
      };
    }

    pX = xScale.getBinIndex(xScale.getDomainValue(chartX));
    // Finding the lower limit on the xscale of the search domain
    xMin = Math.floor(Math.max(pX, 0));
    // Finding the upper limit on the xscale of the search domain
    xMax = Math.ceil(Math.min(pX, len - 1));

    // Iteraring over the datamarkers to check if they are hovered
    dataMarkers.forEach(dataMarkerArg => {
      let dataMarker = dataMarkerArg;
      if (!returnValue && dataMarker && (dataMarker = dataMarker[0])) {
        returnValue = dataMarker._checkPointOverMarker(
          pX,
          chartX,
          chartY,
          config.availableWidth
        );
      }
    });

    // If a datamarker is not hovered
    if (!returnValue) {
      for (j = xMax; j >= xMin; j--) {
        pointObj = dataInfo[j];
        returnValue = visible ? isWithinShape(pointObj, j, chartX, chartY, dataset) : {};
        if (returnValue.hovered) {
          break;
        }
      }
    }
    // To avoid dirty checking at multiple instances later in the code
    !returnValue && (returnValue = {});

    // if not hovered on any plot then get the nearest plot info.
    if (!returnValue.hovered) {
      // Storing info for crossline, x is calculated without considering the
      // radius
      returnValue.binIndexHovered = xScale.getBinIndex(
        xScale.getDomainValue(chartX)
      );

      returnValue.pointObj =
        config.dataInfo[returnValue.binIndexHovered] ||
        xScale.getBinBounds(pX);
      returnValue.component = dataset;
    } else {
      returnValue.binIndexHovered = returnValue.pointIndex;
    }
    config.binIndexHovered = returnValue.binIndexHovered;
    return returnValue;
  }
}

export default Line;
