import Path from './path';
import constant from '../scale-utils/constant';
import CurveLinear from './curve-factories/linear';
import Line from './line-generator';

class AreaGenerator {
  constructor () {
    this.xTopAccessor = d => d[0];
    this.xBaseAccessor = null;
    this.yTopAccessor = constant(0);
    this.yBaseAccessor = d => d[1];

    this.isDefined = constant(true);

    this.Curve = CurveLinear;

    this.ctx = null;
    this._output = null;
  }

  generate (data = []) {
    let n = data.length,
      defined0 = false,
      xBases = new Array(n),
      yBases = new Array(n),
      i,
      j,
      k,
      d,
      buffer;

    if (this.ctx === null || typeof this.ctx === 'undefined') {
      buffer = new Path();
      this._output = new this.Curve(buffer);
    }

    for (i = 0; i <= n; ++i) {
      d = data[i];
      if (!(i < n && this.isDefined(d, i, data)) === defined0) {
        defined0 = !defined0;
        if (defined0) {
          j = i;
          this._output.areaStart();
          this._output.lineStart();
        } else {
          this._output.lineEnd();
          this._output.lineStart();
          for (k = i - 1; k >= j; --k) {
            this._output.point(xBases[k], yBases[k]);
          }
          this._output.lineEnd();
          this._output.areaEnd();
        }
      }
      if (defined0) {
        xBases[i] = +this.xTopAccessor(d, i, data);
        yBases[i] = +this.yTopAccessor(d, i, data);
        this._output.point(
          this.xBaseAccessor ? +this.xBaseAccessor(d, i, data) : xBases[i],
          this.yBaseAccessor ? +this.yBaseAccessor(d, i, data) : yBases[i]
        );
      }
    }

    // Reference: https://www.w3.org/TR/SVG/paths.html#PathDataBNF
    // Quote: "Note that the EBNF allows the path data string in the d property to be empty. This is not an error, instead it disables rendering of the path. Rendering is also disabled when the d property has the value none."
    // Going with the empty string option since "none" or null does not exactly match the given EBNF in the SVG spec (although "none" is a part of the written specification)

    if (buffer) {
      this._output = null;
      return buffer.toString();
    }
  }

  setXAccessor (accessor = d => d[0]) {
    this.xTopAccessor = typeof accessor === 'function' ? accessor : constant(+accessor);
    this.xBaseAccessor = null;

    return this;
  }

  getXAccessor () {
    return this.xTopAccessor;
  }

  setXTopAccessor (accessor = d => d[0]) {
    this.xTopAccessor = typeof accessor === 'function' ? accessor : constant(+accessor);

    return this;
  }

  getXTopAccessor () {
    return this.xTopAccessor;
  }

  setXBaseAccessor (accessor) {
    if (accessor === null || typeof accessor === 'undefined') {
      this.xBaseAccessor = null;
    } else {
      this.xBaseAccessor = typeof accessor === 'function' ? accessor : constant(+accessor);
    }

    return this;
  }

  getXBaseAccessor () {
    return this.xBaseAccessor;
  }

  setYAccessor (accessor = d => d[1]) {
    this.yTopAccessor = typeof accessor === 'function' ? accessor : constant(+accessor);
    this.yBaseAccessor = null;

    return this;
  }

  getYAccessor () {
    return this.yTopAccessor;
  }

  setYTopAccessor (accessor = d => d[1]) {
    this.yTopAccessor = typeof accessor === 'function' ? accessor : constant(+accessor);

    return this;
  }

  getYTopAccessor () {
    return this.yTopAccessor;
  }

  setYBaseAccessor (accessor) {
    if (accessor === null || typeof accessor === 'undefined') {
      this.yBaseAccessor = null;
    } else {
      this.yBaseAccessor = typeof accessor === 'function' ? accessor : constant(+accessor);
    }

    return this;
  }

  getYBaseAccessor () {
    return this.yBaseAccessor;
  }

  setDefined (isDefined = constant(true)) {
    this.isDefined = typeof isDefined === 'function' ? isDefined : constant(!!isDefined);

    return this;
  }

  getDefined () {
    return this.isDefined;
  }

  setCurve (Curve = CurveLinear) {
    this.Curve = Curve;

    if (this.ctx !== null && typeof this.ctx !== 'undefined') {
      this._output = new this.Curve(this.ctx);
    }

    return this;
  }

  getCurve () {
    return this.Curve;
  }

  setContext (ctx) {
    if (ctx === null || typeof ctx === 'undefined') {
      this.ctx = null;
      this._output = this._ctx;
    } else {
      this.ctx = ctx;
      this._output = new this.Curve(this.ctx);
    }

    return this;
  }

  getContext () {
    return this.ctx;
  }

  _areaLine () {
    return new Line().setDefined(this.isDefined).setCurve(this.Curve).setContext(this.ctx);
  }

  getLineXBase () {
    return this._areaLine().setXAccessor(this.xTopAccessor).setYAccessor(this.yTopAccessor);
  }

  getLineYBase () {
    return this._areaLine().setXAccessor(this.xTopAccessor).setYAccessor(this.yTopAccessor);
  }

  getLineYTop () {
    return this._areaLine().setXAccessor(this.xTopAccessor).setYAccessor(this.yTopAccessor);
  }

  getLineXTop () {
    return this._areaLine().setXAccessor(this.xBaseAccessor).setYAccessor(this.yBaseAccessor);
  }
}

export default AreaGenerator;
