import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import Handle from './handle';
import Label from './label';
import Selection from './selection';

class Brush extends SmartRenderer {
  constructor () {
    super();
    this._maxDimensions = {};
    this._currDimensions = {};
  }
  __setDefaultConfig (obj = {}) {
    super.__setDefaultConfig(obj);
    let config = this.config;
    // set default dimensions of itself and its child.
    config.handleWidth = 12;
    config.handleHeight = 20;
  }
  configureAttributes (obj = {}) {
    super.configureAttributes(obj);
    let brush = this;
    brush.config.style = obj.style;
    brush.createChildComponents();
  }
  /**
   * Method that sets dimensions of its child components
   */
  setChildDimension () {
    let brush = this,
      selfConfig = brush.config,
      selection = brush.getChildren('selection')[0],
      leftHandle = brush.getChildren('leftHandle')[0],
      rightHandle = brush.getChildren('rightHandle')[0],
      leftLabel = brush.getChildren('leftLabel')[0],
      rightLabel = brush.getChildren('rightLabel')[0],
      currDimension = brush.getCurrentBrushDimensions(),
      startPx = currDimension.x,
      endPx = currDimension.x + currDimension.width;
    // set dimension of selection
    selection.setDimension({
      x: startPx,
      y: currDimension.y,
      width: currDimension.width,
      height: currDimension.height
    });
    // set dimension of left handle
    leftHandle.setDimension({
      x: startPx - selfConfig.handleWidth,
      y: currDimension.y + currDimension.height / 2 - selfConfig.handleHeight / 2,
      width: selfConfig.handleWidth,
      height: selfConfig.handleHeight
    });
    // set dimension of right handle
    rightHandle.setDimension({
      x: endPx,
      y: currDimension.y + currDimension.height / 2 - selfConfig.handleHeight / 2,
      width: selfConfig.handleWidth,
      height: selfConfig.handleHeight
    });

    // set dimension of left label
    leftLabel.setDimension({
      x: startPx,
      y: currDimension.y
    });
    // set dimension of right label
    rightLabel.setDimension({
      x: endPx,
      y: currDimension.y
    });
  }
  /**
   * Method that returns the maximum space taken by the labels.
   * @return {Number} maximum space taken by labels.
   */
  getLabelSpace () {
    let brush = this,
      leftLabel = brush.getChildren('leftLabel')[0],
      rightLabel = brush.getChildren('rightLabel')[0];

    // return the maximum height taken by the labels
    return Math.max(leftLabel.getLabelDimension().height, rightLabel.getLabelDimension().height);
  }
  draw () {
    let brush = this;
    // parent brush group.
    brush.addGraphicalElement({
      el: 'group',
      attr: {
        name: 'brush-group'
      },
      id: 'brushGroup',
      label: 'brushGroup',
      component: brush,
      container: {
        id: 'exo',
        label: 'group',
        isParent: true
      }
    });
  }
  /**
   * API that hides both the dragable labels.
   */
  hideAllLabels () {
    let brush = this,
      leftLabel = brush.getChildren('leftLabel')[0],
      rightLabel = brush.getChildren('rightLabel')[0];
    // hide only when the mouse pointer is outside time navigator.
    if (!brush.config.pointerReleasedOnParent) {
      leftLabel.config.showLabel = rightLabel.config.showLabel = false;
      leftLabel.asyncDraw();
      rightLabel.asyncDraw();
    }
  }

  setBrushValue (value) {
    const brush = this,
      leftLabel = brush.getChildren('leftLabel')[0],
      rightLabel = brush.getChildren('rightLabel')[0];

    leftLabel.setValue(value[0]);
    rightLabel.setValue(value[1]);
  }

  /**
   * Method that creates all the child components of brush.
   */
  createChildComponents () {
    let brush = this,
      brushConfig = brush.config,
      getStyleDef = brush.getFromEnv('getStyleDef'),
      leftHandle,
      rightHandle,
      leftLabel,
      rightLabel,
      selection;
    // selection box
    selection = brush.attachChild(Selection, 'selection');
    selection.attachHandlers();
    selection.configure({
      orientation: 'horizontal',
      style: {
        mask: getStyleDef(brushConfig.style.mask)
      }
    });

    // left handle
    leftHandle = brush.attachChild(Handle, 'leftHandle');
    leftHandle.attachHandlers();
    leftHandle.configure({
      orientation: 'horizontal',
      type: 'leftHandle',
      style: {
        handle: getStyleDef(brushConfig.style.handle),
        'handle-grip': getStyleDef(brushConfig.style['handle-grip'])
      }
    });

    // right handle
    rightHandle = brush.attachChild(Handle, 'rightHandle');
    rightHandle.attachHandlers();
    rightHandle.configure({
      orientation: 'horizontal',
      type: 'rightHandle',
      style: {
        handle: getStyleDef(brushConfig.style.handle),
        'handle-grip': getStyleDef(brushConfig.style['handle-grip'])
      }
    });

    // left label
    leftLabel = brush.attachChild(Label, 'leftLabel');
    leftLabel.attachHandlers();
    leftLabel.configure({
      orientation: 'horizontal',
      type: 'leftLabel',
      style: {
        label: getStyleDef(brushConfig.style.label),
        labelBg: getStyleDef(brushConfig.style['label-background'])
      }
    });

    // right label
    rightLabel = brush.attachChild(Label, 'rightLabel');
    rightLabel.attachHandlers();
    rightLabel.configure({
      orientation: 'horizontal',
      type: 'rightLabel',
      style: {
        label: getStyleDef(brushConfig.style.label),
        labelBg: getStyleDef(brushConfig.style['label-background'])
      }
    });
  }
  /**
   * Method that sets the maximum play dimension of brush component as a whole
   * @param {Object} dim maximum dimension
   */
  setMaxBrushDimensions (dim) {
    this._maxDimensions = dim;
  }
  /**
   * Method to get the maximum brush play dimension.
   * @return {Object} maximum dimension.
   */
  getMaxBrushDimensions () {
    return this._maxDimensions;
  }
  /**
   * Method that sets the current brush dimension of brush component.
   * @param {Object} dim current dimension
   */
  setCurrentBrushDimensions (dim) {
    this._currDimensions = dim;
    // now set the dimensions of its childs.
    this.setChildDimension();
  }
  /**
   * Method to get the current brush dimension.
   * @return {Object} current dimension
   */
  getCurrentBrushDimensions () {
    return this._currDimensions;
  }
}

export default Brush;
