import { MILLISECONDS_IN_SECOND, SECONDS_IN_MINUTE, MINUTES_IN_HOUR, HOURS_IN_DAY, DAYS_IN_MONTH, MONTHS_IN_YEAR, DAYS_IN_YEAR } from '../scale-utils';

const durationMilliSecond = 1,
  durationSecond = MILLISECONDS_IN_SECOND * durationMilliSecond,
  durationMinute = durationSecond * SECONDS_IN_MINUTE,
  durationHour = durationMinute * MINUTES_IN_HOUR,
  durationDay = durationHour * HOURS_IN_DAY,
  durationMonth = durationDay * DAYS_IN_MONTH,
  durationYear = durationDay * DAYS_IN_YEAR;
/**
 * @description This function santizes given bin to valid integers, common multiplies of max unit given and sorts then in ascending order
 * @param {Integer} maxUnit holds thje max value which a bin unit consists of. eg. for sec it will be 60 as one minute consist of 60 secs
 * @param {*} slotArr Array of given bin units eg. second: [1, 2, 20]
 */
function santizeBin (maxUnit, slotArr) {
  let sanitizedArr = [];
  for (let index = 0; index < slotArr.length; index++) {
    const bin = slotArr[index];
    // eslint-disable-next-line eqeqeq
    bin && (parseInt(bin, 10) == bin) && (bin > 0 && bin <= maxUnit / 2) && (maxUnit % bin === 0) && sanitizedArr.push(Number(bin));
  }
  return sanitizedArr.sort(function sorAsc (a, b) {
    return a - b;
  });
};
// sanitization function for year as it does not have any maxunit
function santizeYear (slotArr) {
  let sanitizedArr = [];
  for (let index = 0; index < slotArr.length; index++) {
    const bin = slotArr[index];
    // eslint-disable-next-line eqeqeq
    bin && (parseInt(bin, 10) == bin) && bin > 0 && sanitizedArr.push(Number(bin));
  }
  return sanitizedArr.sort(function sorAsc (a, b) {
    return a - b;
  });
};
// common sanitization function for all bins
function sanitizeBins (customBin) {
  // returning blank array when custombin not provided
  if (!customBin) {
    return [];
  }
  let sanitizdeCustomBin = customBin,
    binMaxUnitObj = {
      millisecond: MILLISECONDS_IN_SECOND,
      second: SECONDS_IN_MINUTE,
      minute: MINUTES_IN_HOUR,
      hour: HOURS_IN_DAY,
      day: DAYS_IN_MONTH,
      month: MONTHS_IN_YEAR
    };
  for (const key in binMaxUnitObj) {
    if (binMaxUnitObj.hasOwnProperty(key)) {
      if (Array.isArray(customBin[key])) {
        sanitizdeCustomBin[key] = santizeBin(binMaxUnitObj[key], customBin[key]);
      } else {
        sanitizdeCustomBin[key] = false;
      }
    }
  }
  if (Array.isArray(customBin.year)) {
    sanitizdeCustomBin.year = santizeYear(customBin.year);
  } else {
    sanitizdeCustomBin.year = false;
  }
  return sanitizdeCustomBin;
};
/**
 * @description This function returns all standardbins in a form of object, This function is solely developed
 * considering to keep single source of standard bins. Chaning here will affect bins used everywhere.
 * @author Dip Bhattacharjee
 * @since 3.14.0
 * @param {Object} year holds year interval info
 * @param {Object} month holds month interval info
 * @param {Object} week holds week interval info
 * @param {Object} day holds day interval info
 * @param {Object} hour holds hour interval info
 * @param {Object} minute holds minute interval info
 * @param {Object} second holds second interval info
 * @param {Object} millisecond holds millisecond interval info
 * @returns {object} returns object containing all standard bin unit name as key and all possible combination in
 * an array
 */
function getAllStandardBins (year, month, week, day, hour, minute, second, millisecond) {
  // eslint-disable-next-line good-practices/no-single-usage-variable
  let standardMiliseconds = [
      [millisecond, 1, durationMilliSecond],
      [millisecond, 2, 2 * durationMilliSecond],
      [millisecond, 4, 4 * durationMilliSecond],
      [millisecond, 5, 5 * durationMilliSecond],
      [millisecond, 10, 10 * durationMilliSecond],
      [millisecond, 20, 20 * durationMilliSecond],
      [millisecond, 25, 25 * durationMilliSecond],
      [millisecond, 40, 40 * durationMilliSecond],
      [millisecond, 50, 50 * durationMilliSecond],
      [millisecond, 100, 100 * durationMilliSecond],
      [millisecond, 200, 200 * durationMilliSecond],
      [millisecond, 250, 250 * durationMilliSecond],
      [millisecond, 500, 500 * durationMilliSecond]
    ],
    // eslint-disable-next-line good-practices/no-single-usage-variable
    standardSeconds = [
      [second, 1, durationSecond, true],
      [second, 2, 2 * durationSecond],
      [second, 3, 3 * durationSecond],
      [second, 4, 4 * durationSecond],
      [second, 5, 5 * durationSecond],
      [second, 6, 6 * durationSecond],
      [second, 10, 10 * durationSecond],
      [second, 12, 12 * durationSecond],
      [second, 15, 15 * durationSecond],
      [second, 20, 20 * durationSecond],
      [second, 30, 30 * durationSecond]
    ],
    // eslint-disable-next-line good-practices/no-single-usage-variable
    standardMinutes = [
      [minute, 1, durationMinute, true],
      [minute, 2, 2 * durationMinute],
      [minute, 3, 3 * durationMinute],
      [minute, 4, 4 * durationMinute],
      [minute, 5, 5 * durationMinute],
      [minute, 6, 6 * durationMinute],
      [minute, 10, 10 * durationMinute],
      [minute, 12, 12 * durationMinute],
      [minute, 15, 15 * durationMinute],
      [minute, 20, 20 * durationMinute],
      [minute, 30, 30 * durationMinute]
    ],
    // eslint-disable-next-line good-practices/no-single-usage-variable
    standardHours = [
      [hour, 1, durationHour, true],
      [hour, 2, 2 * durationHour],
      [hour, 3, 3 * durationHour],
      [hour, 4, 4 * durationHour],
      [hour, 6, 6 * durationHour],
      [hour, 8, 8 * durationHour],
      [hour, 12, 12 * durationHour]
    ],
    // eslint-disable-next-line good-practices/no-single-usage-variable
    standardDays = [
      [day, 1, durationDay, true],
      [day, 2, 2 * durationDay],
      [day, 3, 3 * durationDay],
      [day, 5, 5 * durationDay],
      [day, 6, 6 * durationDay],
      [day, 10, 10 * durationDay],
      [day, 15, 15 * durationDay]
    ],
    // eslint-disable-next-line good-practices/no-single-usage-variable
    standardMonths = [
      [month, 1, durationMonth],
      [month, 2, 2 * durationMonth],
      [month, 3, 3 * durationMonth],
      [month, 4, 4 * durationMonth],
      [month, 6, 6 * durationMonth]
    ],
    // eslint-disable-next-line good-practices/no-single-usage-variable
    standardYears = [
      [year, 1, durationYear],
      [year, 2, 2 * durationYear],
      [year, 3, 3 * durationYear],
      [year, 5, 5 * durationYear]
    ];

  return [
    { binUnitName: millisecond, duration: durationMilliSecond, standardBinVal: standardMiliseconds },
    { binUnitName: second, duration: durationSecond, standardBinVal: standardSeconds },
    { binUnitName: minute, duration: durationMinute, standardBinVal: standardMinutes },
    { binUnitName: hour, duration: durationHour, standardBinVal: standardHours },
    { binUnitName: day, duration: durationDay, standardBinVal: standardDays },
    { binUnitName: month, duration: durationMonth, standardBinVal: standardMonths },
    { binUnitName: year, duration: durationYear, standardBinVal: standardYears }
  ];
}
/**
 * @description This function generates custom bin in desired format
 * @author Dip Bhattacharjee
 * @since 3.14.0
 * @param {*} binUnit TimeInterval instance of an bin
 * @param {*} binArr Valid Array of custom bins
 * @param {*} duration duration of a bin unit
 */
function generateBins (binUnit, binArr, duration) {
  let updatedBins = [];
  for (let index = 0; index < binArr.length; index++) {
    const bin = binArr[index];
    updatedBins.push([binUnit, bin, bin * duration]);
  }
  return updatedBins;
}
/**
 * @description This function returns standardbins in an array of array format sorted in ascending order
 * from lower bin values to higher. If user has provided any custom bin configration, this function modfies
 * the standard bin such a way that bins get restricts to the custom format provided.
 * Desired format to provide custom bin is written below
 * xAxis:{
 *  binning:{
 *   "month": [1,2];
 *   "day": [1,2,3,5,6,10,15];
 *   "hour": [2,4,8]; // [1] is removed as minBin is 2hr
 *   "minute": []; // Empty array to specify minBin
 *   "millisecond": [] // Empty array to specify minBin
 *  }
 * }
 * custom values should be common multipliers of the bin unit.By Providing blank array user can specify that
 * no binning is required for the particular bin unit.
 * eg. If user has provided valid bins for month, dat and hour blank array(option to override) for the other
 *  bin options like millisecond, minute then the standard does not contain minute, millisecond. Though user
 * has not provided any configuration for year. Hence we will consider the default value for year.
 * This function aslo sanitizes the given input to valid integers and common multiplier of bin units, also
 * sorts them in ascending order.
 * @author Dip Bhattacharjee
 * @since 3.14.0
 *
 * @param {Object} year holds year interval info
 * @param {Object} month holds month interval info
 * @param {Object} week holds week interval info
 * @param {Object} day holds day interval info
 * @param {Object} hour holds hour interval info
 * @param {Object} minute holds minute interval info
 * @param {Object} second holds second interval info
 * @param {Object} millisecond holds millisecond interval info
 * @param {Object} customBin holds custombin config if user has given custom bin
 * @returns {Array} returns the standard bins in array of array format
 */
function getCustomBins (year, month, week, day, hour, minute, second, millisecond, customBin) {
  let sanitizdeCustomBin = customBin,
    binNameDurationArr = getAllStandardBins(year, month, week, day, hour, minute, second, millisecond),
    updatedBins = [];
  // returning if custombin is not provided as it does not need further execution
  sanitizdeCustomBin = sanitizeBins(Object.assign({}, customBin));

  for (let index = 0; index < binNameDurationArr.length; index++) {
    const binUnit = binNameDurationArr[index];
    if (sanitizdeCustomBin[binUnit.binUnitName.name()]) {
      // case: when configuration provided and not a blank array
      if (sanitizdeCustomBin[binUnit.binUnitName.name()].length > 0) {
        updatedBins = updatedBins.concat(generateBins(binUnit.binUnitName, sanitizdeCustomBin[binUnit.binUnitName.name()], binUnit.duration));
      }
    } else {
      // default case when configuration not provided
      updatedBins = updatedBins.concat(binUnit.standardBinVal);
    }
  }
  // case when all binunit is provided blank array, falling back to default binning
  if (updatedBins.length === 0) {
    updatedBins = getDefaultBins(year, month, week, day, hour, minute, second, millisecond);
  }
  // returning defaultbinning and custombinning
  return updatedBins;
};

/**
 * @description This function returns defaultbins in an array of array format sorted in ascending order
 * from lower bin values to higher. This defaultbins are specified to provide maximum possible smooth transition
 * while zooming in or out in timeseries chart visual.
 * @since 3.14.0
 *
 * @param {Object} year holds year interval info
 * @param {Object} month holds month interval info
 * @param {Object} week holds week interval info
 * @param {Object} day holds day interval info
 * @param {Object} hour holds hour interval info
 * @param {Object} minute holds minute interval info
 * @param {Object} second holds second interval info
 * @param {Object} millisecond holds millisecond interval info
 * @returns {Array} returns the default bins in array of array format
 */
function getDefaultBins (year, month, week, day, hour, minute, second, millisecond) {
  let binNameDurationArr = getAllStandardBins(year, month, week, day, hour, minute, second, millisecond),
    defaultStandardBin = [];

  for (let index = 0; index < binNameDurationArr.length; index++) {
    defaultStandardBin = defaultStandardBin.concat(binNameDurationArr[index].standardBinVal);
  }
  return defaultStandardBin;
};

export {
  getDefaultBins,
  getCustomBins
};
