export default (series, order) => {
  const ii = series.length;

  if (ii > 1) {
    let s1 = series[order[0]],
      jj = s1.length,
      s0,
      i,
      j;

    for (i = 0; i < s1.length; i++) {
      s1[i][0] = null;
    }

    for (i = 1; i < ii; ++i) {
      s0 = s1;
      s1 = series[order[i]];

      for (j = 0; j < jj; ++j) {
        s1[j][0] = isNaN(s0[j][1])
          ? s0[j][0]
          : s0[j][1];

        s1[j][1] += s1[j][0];
      }
    }
  }
};
