import bisector from '../array/bisector';
import { tickStep } from '../array/ticks';

const DURATION_YEAR = 31536000000, // 1000 ms * 60 s * 60 m * 24 h * 365 d
  tickInterval = (start, stop, interval = 10, count, tickIntervals, timeIntervals) => {
    let i = interval,
      c = count,
      idx,
      target,
      tickNum;

    // If tick count is specified, use a reasonable tick interval
    // based on the domain's extent and an estimated tick size.
    // Else assume that the interval is already a time interval and use it.
    if (typeof i === 'number') {
      target = Math.abs(stop - start) / i;
      tickNum = bisector(n => n[2]).right(tickIntervals, target);

      if (tickNum === tickIntervals.length) {
        c = tickStep(start / DURATION_YEAR, stop / DURATION_YEAR, i);
        i = timeIntervals.year;
      } else if (tickNum) {
        if ((target / tickIntervals[tickNum - 1][2]) < (tickIntervals[tickNum][2] / target)) {
          idx = tickNum - 1;
        } else {
          idx = tickNum;
        }

        tickNum = tickIntervals[idx];
        c = tickNum[1];
        i = tickNum[0];
      } else {
        c = Math.max(tickStep(start, stop, i), 1);
        i = timeIntervals.millisecond;
      }
    }

    return c === null || typeof c === 'undefined' ? i : i.every(c);
  },
  timeTicks = (_start, _stop, interval, count, tickIntervals, timeIntervals) => {
    let start = _start,
      stop = _stop,
      isReverse = stop < start,
      temp,
      ticks;

    if (isReverse) {
      temp = start;
      start = stop;
      stop = temp;
    }

    ticks = tickInterval(start, stop, interval, count, tickIntervals, timeIntervals);
    ticks = ticks ? ticks.range(start, stop + 1) : [];

    return isReverse ? ticks.reverse() : ticks;
  },
  tickFormat = (intervals, formatters, date) => {
    let formatter;

    if (intervals.second.floor(date) < date) {
      formatter = formatters.millisecond;
    } else if (intervals.minute.floor(date) < date) {
      formatter = formatters.second;
    } else if (intervals.hour.floor(date) < date) {
      formatter = formatters.minute;
    } else if (intervals.day.floor(date) < date) {
      formatter = formatters.hour;
    } else if (intervals.month.floor(date) < date) {
      if (intervals.week.floor(date) < date) {
        formatter = formatters.day;
      } else {
        formatter = formatters.week;
      }
    } else if (intervals.year.floor(date) < date) {
      formatter = formatters.month;
    } else {
      formatter = formatters.year;
    }

    return formatter.format(date);
  };

export { tickInterval, tickFormat };
export default timeTicks;
