import {
  UNDEF,
  pluck,
  defined,
  extend2,
  VISIBLE,
  pluckNumber,
  convertColor,
  DEFAULT_FT_FONT,
  BLANKSTRING,
  rgbaToHex,
  parseUnsafeString,
  parseTooltext
} from '@fusioncharts/core/src/lib';
import safeMax from '@fusioncharts/utils/src/array/safe-max';
import safeMin from '@fusioncharts/utils/src/array/safe-min';
import extent from '@fusioncharts/utils/src/array/extent';
import { addDep } from '@fusioncharts/core/src/dependency-manager';
import isValidNumber from '@fusioncharts/utils/src/type/is-valid-number';
import TimeConverter from '@fusioncharts/utils/src/time-converter';

import Column from './column';
import candlestickAnimation from './candlestick.animation';

const STROKE_COLOR = '#464E56',
  BEAR = 'bear',
  BULL = 'bull',
  _reducerFn = (s, t) => Object.assign(s, t);

addDep({
  name: 'timeseriesCandlestickAnimation',
  type: 'animationRule',
  extension: candlestickAnimation
});

class CandleStick extends Column {
  /**
   * returns the style which is to be applied on the hovered plot
   *
   * @param {Object} hoverIndices index of hovered plots
   * @returns {Object} configuration
   * @memberof Column
   */
  getHoverInConfig (hoverIndices) {
    let dsConfig = this.config;
    return {
      bullStyle: dsConfig.bullHoverInStyle,
      bearStyle: dsConfig.bearHoverInStyle,
      hoverIndices,
      hovered: true
    };
  };

  /**
   * returns the style which is to be applied on the highlighted plot
   *
   * @param {Object} hoverIndices index of highlighted plots
   * @returns {Object} configuration
   * @memberof Column
   */
  getHighlightConfig (hoverIndices) {
    let dsConfig = this.config;
    return {
      bullStyle: dsConfig.bullHighlightStyle,
      bearStyle: dsConfig.bearHighlightStyle,
      hoverIndices,
      hovered: true
    };
  }

  /**
   * returns the style which is to be applied on the hovered out plot
   *
   * @param {Object} hoverIndices index of hovered out plots
   * @returns {Object} configuration
   * @memberof Column
   */
  getHoverOutConfig (hoverIndices) {
    let dsConfig = this.config,
      bullStyle = Object.assign({}, dsConfig.bullHoverOutStyle),
      bearStyle = Object.assign({}, dsConfig.bearHoverOutStyle),
      key;

    for (key in bullStyle) {
      bullStyle[key] = null;
    }
    for (key in bearStyle) {
      bearStyle[key] = null;
    }
    return {
      bullStyle,
      bearStyle,
      hoverIndices,
      hovered: false
    };
  };

  __setDefaultConfig () {
    var dataset = this,
      config = dataset.config;
    config.isStock = true;
    config.bullStyle = {};
    config.bullHoverInStyle = {};
    config.bullHighlightStyle = {};
    config.bullHoverOutStyle = {};

    config.bearStyle = {};
    config.bearHoverInStyle = {};
    config.bearHighlightStyle = {};
    config.bearHoverOutStyle = {};

    config.plotSpacePercent = 40;
    config.dataMarkerPadding = 0;
    config.visibility = VISIBLE;
    config.prevBoundaryInfo = {};
    config.defaultBullStyle = {
      'stroke-width': 1,
      'opacity': 1,
      'stroke': '000000',
      'fill': 'ffffff'
    };
    config.defaultBearStyle = {
      'stroke-width': 1,
      'opacity': 1,
      'stroke': '000000',
      'fill': '868AC8'
    };
    config.defaultBullHighlightStyle = {
      'stroke-width': 2
    };
    config.defaultBearHighlightStyle = {
      'stroke-width': 2
    };
    config.defaultBearHoverInStyle = {
      'stroke-width': 2
    };
    config.defaultBullHoverInStyle = {
      'stroke-width': 2
    };
    config.legendDefaultTextStyle = {
      'font-family': DEFAULT_FT_FONT,
      'font-weight': 'normal',
      'font-style': 'normal',
      'font-size': '12px',
      'fill': '#5F5F5F'
    };
    config.defaultBearPredictiveStyle = {
      fill: '#62B58F'
    };
    config.defaultBullPredictiveStyle = {
      fill: '#F76363'
    };
    // used to decide the fill and stroke of legend item
    config.strokeColor = STROKE_COLOR;
    config.hoverInfo = [];
    config.prevPerBinYExtents = [];
    config.dataIndices = [1, 2, 3, 4];
  }

  /**
   * Function to configure the cosmemtics of the plots
   */
  configureAttributes (dataObj) {
    let dataset = this,
      prop,
      dataSource = dataset.getFromEnv('dataSource'),
      getStyleDef = dataset.getFromEnv('getStyleDef'),
      legendConf = dataSource.legend,
      legendStyle = getStyleDef(legendConf && legendConf.item &&
        legendConf.item.style && legendConf.item.style.text),
      config = dataset.config;

    config.mergedLegendStyle = extend2(extend2({}, config.legendDefaultTextStyle), legendStyle);
    // Setting the given configurations
    if (dataObj.hasOwnProperty('hoverIndices')) {
      config.hoverInfo.push(dataObj);
      config.hoverMode = true;
    } else {
      for (prop in dataObj) {
        if (prop === 'primaryColor') {
          let plotConfig = dataObj.plotCosmetics,
            genericConfig = dataObj.genericCosmetics,
            bullStyle = config.bullStyle,
            bearStyle = config.bearStyle,
            bullHoverInStyle = config.bullHoverInStyle,
            bearHoverInStyle = config.bearHoverInStyle,
            bullHighlightStyle = config.bullHighlightStyle,
            bearHighlightStyle = config.bearHighlightStyle,
            bullHoverOutStyle = config.bullHoverOutStyle,
            bearHoverOutStyle = config.bearHoverOutStyle,
            styles,
            applicableClassBearStyle = getStyleDef(plotConfig.style && plotConfig.style.bear),
            applicableClassBullStyle = getStyleDef(plotConfig.style && plotConfig.style.bull),

            applicableClassStyleGeneric = getStyleDef(genericConfig.style && genericConfig.style.plot),
            applicableClassStyleGenericHover = getStyleDef(genericConfig.style && genericConfig.style['plot:hover']),
            applicableClassStyleGenericHighlight = getStyleDef(genericConfig.style && genericConfig.style['plot:highlight']),

            applicableClassBullStyleGeneric = getStyleDef(genericConfig.style && genericConfig.style.bull),
            applicableClassBullStyleGenericHover = getStyleDef(genericConfig.style && genericConfig.style['bull:hover']),
            applicableClassBullStyleGenericHighlight = getStyleDef(genericConfig.style && genericConfig.style['bull:highlight']),

            applicableClassBearStyleGeneric = getStyleDef(genericConfig.style && genericConfig.style.bear),
            applicableClassBearStyleGenericHover = getStyleDef(genericConfig.style && genericConfig.style['bear:hover']),
            applicableClassBearStyleGenericHighlight = getStyleDef(genericConfig.style && genericConfig.style['bear:highlight']),

            applicableClassBearHoverStyle = getStyleDef(plotConfig.style && plotConfig.style['bear:hover']),
            applicableClassBearHighlightStyle = getStyleDef(plotConfig.style && plotConfig.style['bear:highlight']),
            applicableClassBullHoverStyle = getStyleDef(plotConfig.style && plotConfig.style['bull:hover']),
            applicableClassBullHighlightStyle = getStyleDef(plotConfig.style && plotConfig.style['bull:highlight']),

            applicableInlineBearStyle = getStyleDef(dataObj.styleConfig && dataObj.styleConfig.bear),
            applicableInlineBullStyle = getStyleDef(dataObj.styleConfig && dataObj.styleConfig.bull),

            applicableInlineBearHoverStyle = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['bear:hover']),
            applicableInlineBearHighlightStyle = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['bear:highlight']),
            applicableInlineBullHoverStyle = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['bull:hover']),
            applicableInlineBullHighlightStyle = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['bull:highlight']),

            applicableClassStylePlot = getStyleDef(plotConfig.style && plotConfig.style.plot),
            applicableInlineStylePlot = getStyleDef(dataObj.styleConfig && dataObj.styleConfig.plot),

            applicableClassStyleHoverPlot = getStyleDef(plotConfig.style && plotConfig.style['generic:hover']),
            applicableClassStyleHighlightPlot = getStyleDef(plotConfig.style && plotConfig.style['generic:highlight']),
            applicableInlineStyleHoverPlot = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['plot:hover']),
            applicableInlineStyleHighlightPlot = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['plot:highlight']);

          // Stroke and fill is applicable when applied from bull and bear configurations and not the general ones
          delete applicableClassStyleGeneric.fill;
          delete applicableClassStylePlot.fill;
          delete applicableInlineStylePlot.fill;
          if (dataset.getName() === 'timeseries-ohlc') {
            delete applicableInlineStylePlot.stroke;
            delete applicableClassStyleGeneric.stroke;
            delete applicableClassStylePlot.stroke;
          }

          // Parsing the plot styling properties with increasing preference
          [bullStyle, config.defaultBullStyle, applicableClassStyleGeneric, applicableClassStylePlot, applicableInlineStylePlot, applicableClassBullStyleGeneric, applicableClassBullStyle, applicableInlineBullStyle].reduce(_reducerFn);
          [bearStyle, config.defaultBearStyle, applicableClassStyleGeneric, applicableClassStylePlot, applicableInlineStylePlot, applicableClassBearStyleGeneric, applicableClassBearStyle, applicableInlineBearStyle].reduce(_reducerFn);
          [bullHoverInStyle, config.defaultBullHoverInStyle, applicableClassStyleGenericHover, applicableClassStyleHoverPlot, applicableInlineStyleHoverPlot, applicableClassBullStyleGenericHover, applicableClassBullHoverStyle, applicableInlineBullHoverStyle].reduce(_reducerFn);
          [bullHighlightStyle, config.defaultBullHighlightStyle, applicableClassStyleGenericHighlight, applicableClassStyleHighlightPlot, applicableInlineStyleHighlightPlot, applicableClassBullStyleGenericHighlight, applicableClassBullHighlightStyle, applicableInlineBullHighlightStyle].reduce(_reducerFn);
          [bearHoverInStyle, config.defaultBearHoverInStyle, applicableClassStyleGenericHover, applicableClassStyleHoverPlot, applicableInlineStyleHoverPlot, applicableClassBearStyleGenericHover, applicableClassBearHoverStyle, applicableInlineBearHoverStyle].reduce(_reducerFn);
          [bearHighlightStyle, config.defaultBearHighlightStyle, applicableClassStyleGenericHighlight, applicableClassStyleHighlightPlot, applicableInlineStyleHighlightPlot, applicableClassBearStyleGenericHighlight, applicableClassBearHighlightStyle, applicableInlineBearHighlightStyle].reduce(_reducerFn);

          // Parsing the plot colors for bull
          bullStyle.stroke = convertColor(bullStyle.stroke, pluckNumber(bullStyle['stroke-opacity'], 1) * 100);
          bullStyle.fill = convertColor(bullStyle.fill, pluckNumber(bullStyle['fill-opacity'], 1) * 100);

          bearStyle.stroke = convertColor(bearStyle.stroke, pluckNumber(bearStyle['fill-opacity'], 1) * 100);
          bearStyle.fill = convertColor(bearStyle.fill, pluckNumber(bearStyle['fill-opacity'], 1) * 100);

          // Formatting the hover in style style
          bullHoverInStyle.fill && (bullHoverInStyle.fill = convertColor(bullHoverInStyle.fill, pluckNumber(bullHoverInStyle['fill-opacity'], bullStyle['fill-opacity']) * 100));
          bullHoverInStyle.stroke && (bullHoverInStyle.stroke = convertColor(bullHoverInStyle.stroke, pluckNumber(bullHoverInStyle['stroke-opacity'], bullStyle['stroke-opacity']) * 100));

          bearHoverInStyle.fill && (bearHoverInStyle.fill = convertColor(bearHoverInStyle.fill, pluckNumber(bearHoverInStyle['fill-opacity'], bearStyle['fill-opacity']) * 100));
          bearHoverInStyle.stroke && (bearHoverInStyle.stroke = convertColor(bearHoverInStyle.stroke, pluckNumber(bearHoverInStyle['stroke-opacity'], bearStyle['stroke-opacity']) * 100));

          // Formatting the highlight style
          bullHighlightStyle.fill && (bullHighlightStyle.fill = convertColor(bullHighlightStyle.fill, pluckNumber(bullHighlightStyle['fill-opacity'], bullStyle['fill-opacity']) * 100));
          bullHighlightStyle.stroke && (bullHighlightStyle.stroke = convertColor(bullHighlightStyle.stroke, pluckNumber(bullHighlightStyle['stroke-opacity'], bullStyle['stroke-opacity']) * 100));

          bearHoverInStyle.fill && (bearHoverInStyle.fill = convertColor(bearHoverInStyle.fill, pluckNumber(bearHoverInStyle['fill-opacity'], bearStyle['fill-opacity']) * 100));
          bearHoverInStyle.stroke && (bearHoverInStyle.stroke = convertColor(bearHoverInStyle.stroke, pluckNumber(bearHoverInStyle['stroke-opacity'], bearStyle['stroke-opacity']) * 100));

          config.plotBorderThickness =
            bearStyle['stroke-width'] =
            bullStyle['stroke-width'] = pluckNumber(safeMin(
              [bearStyle['stroke-width'], bullStyle['stroke-width']],
              Number.parseFloat
            ));

          // Parsing the hoverOut styles from hover style and highlight style
          for (styles in Object.assign(Object.assign({}, bullHoverInStyle), bullHighlightStyle)) {
            bullHoverOutStyle[styles] = pluck(bullStyle[styles], '');
          }

          // Stroke dash-array behave differently when applied on element and group, hence removing it
          defined(bullHoverOutStyle['stroke-dasharray']) && (bullHoverOutStyle['stroke-dasharray'] = '');

          for (styles in Object.assign(Object.assign({}, bearHoverInStyle), bearHighlightStyle)) {
            bearHoverOutStyle[styles] = bearStyle[styles] || '';
          }

          defined(bearHoverOutStyle['stroke-dasharray']) && (bullHoverOutStyle['stroke-dasharray'] = '');

          // Opacities are separately stored and is applied to each element as applying them on the group followed by the elememt (during hover in and out)
          // results into multiplied opacity
          config.bullOpacity = bullStyle.opacity;
          config['bull-fill-opacity'] = bullStyle['fill-opacity'];
          config['bull-stroke-opacity'] = bullStyle['stroke-opacity'];

          config.bearOpacity = bearStyle.opacity;
          config['bear-fill-opacity'] = bearStyle['fill-opacity'];
          config['bear-stroke-opacity'] = bearStyle['stroke-opacity'];

          delete bullStyle.opacity;
          delete bullStyle['fill-opacity'];
          delete bullStyle['stroke-opacity'];

          delete bearStyle.opacity;
          delete bearStyle['fill-opacity'];
          delete bearStyle['stroke-opacity'];
        } else {
          config[prop] = dataObj[prop];
        }
      }
      config.limit = dataset._calculateLimits();
      const chart = dataset.getFromEnv('chart');
      chart.setYScaleLimit(config.scaleY.getId(), dataset.getId(), config.limit.y, config.limit.baseRequired);
      chart.setXScaleLimit(config.scaleX.getId(), dataset.getId(), config.limit.x);
    }

    this.parsePrediction(dataObj);
  }

  parsePrediction (dataObject) {
    this.config.prediction = dataObject.prediction || this.config.prediction;
    this.parsePredictiveStyleAttributes();
  }

  parsePredictiveStyleAttributes () {
    const predictiveStyleAttributes = {},
      config = this.config;

    config.predictiveStyleAttributesBear = {};
    config.predictiveStyleAttributesBull = {};

    if (config.prediction.enabled) {
      // Now overriding properties with properties provided for candle stick
      Object.assign(predictiveStyleAttributes, config.prediction.style.plot);
      Object.assign(config.predictiveStyleAttributesBear, config.defaultBearPredictiveStyle, config.prediction.style.plot, config.prediction.style.bear);
      Object.assign(config.predictiveStyleAttributesBull, config.defaultBullPredictiveStyle, config.prediction.style.plot, config.prediction.style.bull);
    }

    config.predictiveStyleAttributes = predictiveStyleAttributes || {};
  }

  _calculateLimits () {
    let { indices, data, scaleY, scaleX, calculateFromContext } = this.config,
      firstData = data[0],
      lastData = data[data.length - 1],
      showPlotOverTick = scaleX.showPlotOverTick(),
      isVisible = this.config.visibility === 'visible',
      isLog = scaleY.getType() === 'log',
      chart = this.getFromEnv('chart'),
      [focusLimitStart, focusLimitEnd] = calculateFromContext
        ? chart.getContextLimit() : chart.getFocusLimit(),
      returnObj = {},
      padding,
      sanitiseFn = num => {
        if ((isLog && num <= 0)) {
          return null;
        }
        return num;
      },
      minFn = datum => {
        // check if data is withing visible range or not
        if (!(datum[0].end < focusLimitStart) && !(datum[0].start > focusLimitEnd)) {
          return safeMin([datum[indices[1]], datum[indices[2]], datum[indices[3]],
            datum[indices[4]]], sanitiseFn);
        }
      },
      maxFn = datum => {
        // check if data is withing visible range or not
        if (!(datum[0].end < focusLimitStart) && !(datum[0].start > focusLimitEnd)) {
          return safeMax([datum[indices[1]], datum[indices[2]], datum[indices[3]],
            datum[indices[4]]], sanitiseFn);
        }
      };
    padding = this.getPadding();
    returnObj.x = [firstData && (firstData[indices[0]].start - padding),
      lastData && (lastData[indices[0]][showPlotOverTick ? 'start' : 'end'] + padding)];

    returnObj.y = isVisible ? [safeMin(data, minFn), safeMax(data, maxFn)] : UNDEF;
    returnObj.baseRequired = false;
    return returnObj;
  }

  /**
   * Helper function of data markers
   * @return {Object} contains positioning info and the dataInfo
   */
  _getRelevantInfo (index) {
    var config = this.config,
      fill,
      text,
      stroke;
    if (index !== UNDEF) {
      ({ fill, stroke } = config.dataInfo[index].groupId === 'bull'
        ? config.bullStyle
        : config.bearStyle);
      if (rgbaToHex(fill) === '#ffffff') {
        text = {
          fill: stroke
        };
      }
    }
    return {
      firstTimeStamp: config.firstTimeStamp,
      timeStampGap: config.timeStampGap,
      dataInfo: config.dataInfo,
      fill,
      stroke,
      text
    };
  }

  /**
   * Function to create the containers of the dataset with all the cosmetics applioed at group level
   * Two groups are created, one for the bull and the other for the bear
   */
  _createContainer () {
    super._createContainer();
    let dataset = this,
      config = dataset.config,
      bullStyle = config.bullStyle,
      bearStyle = config.bearStyle;

    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group',
        isParent: true
      },
      component: dataset,
      label: 'group',
      id: 'meso',
      attr: {
        name: 'candlestick-dataset-meso'
      }
    });
    // Bull group
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'meso-bull',
      attr: Object.assign({
        name: 'bullGroup-meso',
        visibility: config.visibility
      }, bullStyle)
    });

    // Bear group
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'meso-bear',
      attr: Object.assign({
        name: 'bearGroup-meso',
        visibility: config.visibility
      }, bearStyle)
    });

    // Predictive Bull group
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'meso-bull-predictive',
      attr: Object.assign({
        name: 'predictive-bullGroup-meso',
        visibility: config.visibility
      }, bullStyle, config.predictiveStyleAttributesBull)
    });

    // Predictive Bear group
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'meso-bear-predictive',
      attr: Object.assign({
        name: 'predictive-bearGroup-meso',
        visibility: config.visibility
      }, bearStyle, config.predictiveStyleAttributesBear)
    });
  }

  /**
   * Sets the name of the component
   * @return {string} name
   */
  getName () {
    return 'timeseriesCandlestick';
  }
  /**
   * returns the data plot indices which falls within the range
   * @param {Object} range min and max value range
   * @returns {Array} array of data points
   */
  getHighlightedIndices (range) {
    let dataset = this,
      config = dataset.config,
      dataInfo = config.dataInfo,
      hightlightedPoints = [];
    dataInfo.forEach((data, index) => {
      if (data.closeValuePx <= range.minValue && data.closeValuePx >= range.maxValue) {
        hightlightedPoints.push(index);
      }
    });
    return hightlightedPoints;
  }

  /**
   * Function to allocation the position of the dataplots
   */
  allocatePosition () {
    var dataset = this,
      binDecider = dataset.getFromEnv('binDecider'),
      xScale = dataset.getFromEnv('xScale'),
      yScale = dataset.getFromEnv('yScale'),
      config = dataset.config,
      measures = config.measures[0],
      [xIdx, openIdx, highIdx, lowIdx, closeIdx] = config.indices,
      dateColumn = dataset.getFromEnv('dateColumn'),
      isUTC = dataset.getFromEnv('isUTC'),
      // schema = config.schema,
      height,
      width,
      date,
      x,
      y,
      midX,
      yPos,
      yBase,
      groupId,
      open,
      close,
      high,
      low,
      dataObj,
      pX,
      lowStickYEntend,
      highStickYExtend,
      dataInfo = config.dataInfo,
      availableWidth,
      hoverInfo,
      openPx,
      closePx,
      duration,
      format = dateColumn.format,
      formatter = isUTC ? TimeConverter.utcFormatter(format) : TimeConverter.formatter(format),
      startDate,
      endDate,
      i,
      len,
      bins = xScale.bins,
      rangeThreshold = binDecider.getRangeThreshold()[2],
      [startDomain, endDomain] = xScale.getDomain(),
      paddingInTimestamp,
      data = config.data,
      openValueFormatted,
      highValueFormatted,
      lowValueFormatted,
      closeValueFormatted;

    if ((config.repositioningDone = dataset._isRepositioningNeeded())) {
      config.timeStampGap = rangeThreshold;
      config.availableWidth = availableWidth = 0;
      // Storing info for dataMarker detection
      for (i = 0, len = bins.length; i < len; i++) {
        if (bins[i].clipType !== 2) {
          config.availableWidth = availableWidth = xScale.getRangeValue(bins[i].end) - xScale.getRangeValue(bins[i].start);
          break;
        }
      }

      width = Math.max(availableWidth * (1 - config.plotSpacePercent / 100) - config.plotBorderThickness, 1);

      paddingInTimestamp = +xScale.getDomainValue(availableWidth * (config.plotSpacePercent / 200)) -
        +xScale.getDomainValue(0);
      // Storing value for tooltip
      config.actualStartDomain = +startDomain + paddingInTimestamp;

      // Actual end domain is the scale domain minus the space taken up by the plotspacepercent
      config.actualEndDomain = +endDomain - paddingInTimestamp;

      // Store data related operations
      dataInfo = config.dataInfo = [];
      data.forEach((datum, index) => {
        // drawing the candle
        date = datum[xIdx];

        open = datum[openIdx];
        close = datum[closeIdx];
        high = datum[highIdx];
        low = datum[lowIdx];
        duration = date.config.duration;
        startDate = date.start;
        endDate = date.end;

        if (!index) {
          config.firstTimeStamp = date.start;
        }
        closePx = yScale.getRangeValue(close);
        openPx = yScale.getRangeValue(open);
        highStickYExtend = yScale.getRangeValue(high);
        lowStickYEntend = yScale.getRangeValue(low);
        if (isValidNumber(openPx) || isValidNumber(closePx) ||
        isValidNumber(highStickYExtend) || isValidNumber(lowStickYEntend)) {
          // Find rectangle height and starting y, only if both open and close are present
          if (isValidNumber(openPx) && isValidNumber(closePx)) {
            if (open > close) {
              yBase = closePx;
              yPos = openPx;
              groupId = BEAR;
            } else {
              yPos = closePx;
              yBase = openPx;
              groupId = BULL;
            }

            height = Math.max(Math.abs(yBase - yPos), 1);
            y = yPos > yBase ? yBase : yPos;
          }

          midX = xScale.getRangeValue(new Date(date.start), new Date(date.end));

          x = midX - width / 2;

          dataObj = {
            startDate: startDate,
            endDate: endDate
          };

          dataObj.x = x;
          dataObj.endXPosition = x + width;
          dataObj.midX = midX;
          dataObj.colHeight = height || 0;
          // Either the calculated open or close as per bull or bear, when both open and close are present,
          // OR, open, if close absent, OR close, if open absent
          dataObj.colY = y || openPx || closePx;
          dataObj.highStickYExtend = highStickYExtend;
          dataObj.y = safeMin([highStickYExtend, closePx, openPx, lowStickYEntend]);
          dataObj.closeValuePx = closePx;
          dataObj.width = width;
          dataObj.openValue = open;
          dataObj.closeValue = close;
          dataObj.highValue = high;
          dataObj.lowValue = low;
          dataObj.value = close;
          dataObj.lowStickYEntend = lowStickYEntend;
          openValueFormatted = config.formatterFn({
            value: open,
            type: 'tooltip',
            prefix: config.prefix,
            suffix: config.suffix
          });
          closeValueFormatted = config.formatterFn({
            value: close,
            type: 'tooltip',
            prefix: config.prefix,
            suffix: config.suffix
          });
          highValueFormatted = config.formatterFn({
            value: high,
            type: 'tooltip',
            prefix: config.prefix,
            suffix: config.suffix
          });
          lowValueFormatted = config.formatterFn({
            value: low,
            type: 'tooltip',
            prefix: config.prefix,
            suffix: config.suffix
          });
          dataObj.eventArgs = {
            index,
            start: startDate,
            startText: formatter.format(startDate),
            end: endDate,
            endText: formatter.format(endDate),
            binUnit: duration.Unit,
            binMultiplier: duration.number,
            plotType: config.type,
            timeFormatter: format,
            aggregation: config.aggregation,
            measure: measures.close,
            measureHigh: measures.high,
            measureLow: measures.low,
            measureOpen: measures.open,
            measuresClose: measures.close,
            binValue: close,
            binOpen: open,
            binHigh: high,
            binLow: low,
            binClose: close,
            binValueFomatted: closeValueFormatted,
            binOpenFormatted: openValueFormatted,
            binHighFormatted: highValueFormatted,
            binCloseFormatted: closeValueFormatted,
            binLowFormatted: lowValueFormatted
          };
          // Either difference between low and high or 0 if either are missing
          const minmax = extent([openPx, closePx, highStickYExtend, lowStickYEntend]);
          dataObj.height = Math.abs(minmax[0] - minmax[1]) || 0;
          // For missing open/close, always show plot as bull
          dataObj.groupId = groupId || 'bull';
          dataObj.groupConfig = groupId === 'bull' ? 'bullConfig' : 'bearConfig';

          // Get the data index corresponding to the fetched timeStamp
          pX = xScale.getBinIndex(date.start);
          dataInfo[pX] = dataObj;
        }
      });
    }

    // If hovered, show hover effect of the particular plot
    if ((hoverInfo = config.hoverInfo)) {
      hoverInfo.forEach(
        hoveredData => {
          hoveredData.hoverIndices.forEach((index) => {
            if (typeof dataInfo[index] === 'object') {
              dataInfo[index].style = extend2(dataInfo[index].style, hoveredData[dataInfo[index].groupId + 'Style']);
            }
          });
        }
      );
    }
  }

  /**
   * Function to draw the candles and the sticks
   */
  _drawPlot () {
    var dataset = this,
      config = dataset.config,
      attr,
      dataInfo = config.dataInfo,
      definedBullOpacity = defined(config.bullOpacity),
      definedBullFillOpacity = defined(config['bull-fill-opacity']),
      definedBullStrokeOpacity = defined(config['bull-stroke-opacity']),

      predictiveStyleAttributesBull = config.predictiveStyleAttributesBull,
      definedPredictiveBullOpacity = defined(predictiveStyleAttributesBull.opacity),
      definedPredictiveBullFillOpacity = defined(predictiveStyleAttributesBull['fill-opacity']),
      definedPredictiveBullStrokeOpacity = defined(predictiveStyleAttributesBull['stroke-opacity']),

      definedBearOpacity = defined(config.bearOpacity),
      definedBearFillOpacity = defined(config['bear-fill-opacity']),
      definedBearStrokeOpacity = defined(config['bear-stroke-opacity']),

      predictiveStyleAttributesBear = config.predictiveStyleAttributesBear,
      definedPredictiveBearOpacity = defined(predictiveStyleAttributesBear.opacity),
      definedPredictiveBearFillOpacity = defined(predictiveStyleAttributesBear['fill-opacity']),
      definedPredictiveBearStrokeOpacity = defined(predictiveStyleAttributesBear['stroke-opacity']),

      attrBaseBull = {},
      attrBaseBear = {},
      attrBase,
      derivedGroupId = BLANKSTRING,
      isDatumPredictive = false,
      mergePredictiveStylesInDatumGraphics = (attrBear, attrBull) => {
        definedPredictiveBearOpacity && (attrBear.opacity = predictiveStyleAttributesBear.opacity);
        definedPredictiveBearFillOpacity && (attrBear.opacity = predictiveStyleAttributesBear['fill-opacity']);
        definedPredictiveBearStrokeOpacity && (attrBear.opacity = predictiveStyleAttributesBear['stroke-opacity']);

        definedPredictiveBullOpacity && (attrBull.opacity = predictiveStyleAttributesBull.opacity);
        definedPredictiveBullFillOpacity && (attrBull.opacity = predictiveStyleAttributesBull['fill-opacity']);
        definedPredictiveBullStrokeOpacity && (attrBull.opacity = predictiveStyleAttributesBull['stroke-opacity']);
      };

    definedBullOpacity && (attrBaseBull.opacity = config.bullOpacity);
    definedBullFillOpacity && (attrBaseBull.opacity = config['bull-fill-opacity']);
    definedBullStrokeOpacity && (attrBaseBull.opacity = config['bull-stroke-opacity']);

    definedBearOpacity && (attrBaseBear.opacity = config.bearOpacity);
    definedBearFillOpacity && (attrBaseBear.opacity = config['bear-fill-opacity']);
    definedBearStrokeOpacity && (attrBaseBear.opacity = config['bear-stroke-opacity']);

    dataInfo.forEach((datum, index) => {
      if (datum.groupConfig) {
        attrBase = datum.groupId === 'bull' ? attrBaseBull : attrBaseBear;
        isDatumPredictive = dataset.isPlotPredictive(datum);
        derivedGroupId = isDatumPredictive ? datum.groupId + '-predictive' : datum.groupId;
        // If open > close then column/rectangle will have a positive height
        if (datum.colHeight > 0) {
          // If height is a positive value, draw a rectangle
          // Drawing the open-close rect
          attr = Object.assign({
            x: datum.x,
            y: datum.colY,
            height: datum.colHeight,
            width: datum.width
          }, attrBase);
          datum.style && (Object.assign(attr, datum.style));

          if (isDatumPredictive) {
            mergePredictiveStylesInDatumGraphics(attrBaseBear, attrBaseBull);
          }

          dataset.addGraphicalElement({
            el: 'rect',
            component: dataset,
            props: {
              index: index,
              dataLength: dataInfo.length
            },
            container: {
              label: 'group',
              id: 'meso-' + derivedGroupId
            },
            label: 'rect',
            attr: attr
          }, true);
          // If only either of open or close values are present, draw a horizontal path
        } else if (datum.openValue || datum.closeValue) {
          attr = Object.assign({
            path: `M${datum.x},${datum.colY},H${datum.x + datum.width}`
          }, attrBase);
          datum.style && (Object.assign(attr, datum.style));

          if (isDatumPredictive) {
            mergePredictiveStylesInDatumGraphics(attrBaseBear, attrBaseBull);
          }

          dataset.addGraphicalElement({
            el: 'path',
            component: dataset,
            props: {
              index: index,
              dataLength: dataInfo.length
            },
            container: {
              label: 'group',
              id: 'meso-' + derivedGroupId
            },
            label: 'line',
            attr: attr
          }, true);
        }

        if (datum.highStickYExtend) {
          // drawing the high stick starting from -
          //  if open and/or close are present, high to start of column/horizontal path
          //  if open and close are absent, high with length difference between high and low
          attr = Object.assign({
            path: `M${datum.midX},${datum.highStickYExtend},V${datum.colY || (datum.highStickYExtend + datum.height)}`,
            'stroke-linecap': 'round'
          }, attrBase);
          datum.style && (Object.assign(attr, datum.style));
          dataset.addGraphicalElement({
            el: 'path',
            component: dataset,
            props: {
              index: index,
              dataLength: dataInfo.length
            },
            container: {
              label: 'group',
              id: 'meso-' + datum.groupId
            },
            label: 'high',
            attr: attr
          }, true);
        }

        if (datum.lowStickYEntend) {
          // drawing the low stick starting from -
          //  if open and/or close are present, low to end of column/horizontal path
          //  if open and close are absent, high with length difference between high and low
          attr = Object.assign({
            path: `M${datum.midX},${datum.lowStickYEntend},V${(datum.colY + datum.colHeight) || (datum.lowStickYEntend - datum.height)}`,
            'stroke-linecap': 'round'
          }, attrBase);
          datum.style && (Object.assign(attr, datum.style));
          dataset.addGraphicalElement({
            el: 'path',
            component: dataset,
            props: {
              index: index,
              dataLength: dataInfo.length
            },
            container: {
              label: 'group',
              id: 'meso-' + datum.groupId
            },
            label: 'low',
            attr: attr
          }, true);
        }
      }
    });
    config.hoverInfo = [];
  }

  /**
   * Function to form the tooltext
   * @param {Boolean} pointFound If any dataset's data is actually hovered
   * @param {Boolean} datasetHovered If the current dataset is hovered
   * @param {number} dataIndex The index of the data in the current dataset that is hovered
   */
  _getTooltext (pointFound, datasetHovered, dataIndex) {
    var dataset = this,
      config = dataset.config,
      dataSource = dataset.getFromEnv('dataSource'),
      plots = dataset._linkedParent.getChildren('dataset'),
      tooltext = dataSource.tooltip ? dataSource.tooltip.tooltext : null,
      macroIndices,
      parserConfig,
      plotConfig,
      timeNavigator = dataset._linkedParent._linkedParent._components.timeNavigator[0],
      formatter = timeNavigator.config.formatter,
      binStart,
      binEnd,
      containsVolume,
      dataInfo = config.dataInfo,
      currData = dataInfo[dataIndex],
      isDataDefined,
      // Setting logic for opacity.
      // When the data of the currentdataset is hovered then all other tooltip's opacity is set to 0.5
      // Rest all the cases the opacity is 1
      opacity = pointFound ? (datasetHovered ? 1 : 0.5) : 1,
      line1;
    if (dataset._isInvalidTooltext(currData)) {
      return '';
    }
    isDataDefined = defined(currData.groupConfig);
    if (tooltext) {
      containsVolume = dataSource.yaxis.find(function (item) {
        return item.plot.find(function (plotType) {
          return plotType.value === 'Volume';
        });
      });
      // When volume is present, then no need to parse tooltext as it will be parsed in column plot for volume
      if (!containsVolume) {
        // Get the name for all the series
        parserConfig = {
          series: dataSource.yaxis.map(function (series) {
            return {
              name: series.title
            };
          })
        };
        // Get individual plot configurations for tooltip macros
        plotConfig = {
          series: plots.map(function (index) {
            binStart = formatter.format(index.config.dataInfo[dataIndex].startDate);
            binEnd = formatter.format(index.config.dataInfo[dataIndex].endDate);
            return {
              bin: binStart + '-' + binEnd,
              binStart: binStart,
              binEnd: binEnd,
              openValue: index.config.dataInfo[dataIndex].eventArgs.binOpen,
              openDataValue: index.config.dataInfo[dataIndex].eventArgs.binOpenFormatted,
              highValue: index.config.dataInfo[dataIndex].eventArgs.binHigh,
              highDataValue: index.config.dataInfo[dataIndex].eventArgs.binHighFormatted,
              lowValue: index.config.dataInfo[dataIndex].eventArgs.binLow,
              lowDataValue: index.config.dataInfo[dataIndex].eventArgs.binLowFormatted,
              closeValue: index.config.dataInfo[dataIndex].eventArgs.binClose,
              closeDataValue: index.config.dataInfo[dataIndex].eventArgs.binCloseFormatted
            };
          })
        };
        macroIndices = [154, 155, 156, 157, 158, 159, 160, 161, 162, 163, 164, 165, 166, 167, 168, 169, 170, 171];
        tooltext = parseTooltext(tooltext, macroIndices, parserConfig, plotConfig);
        line1 = '<div>' + tooltext + '</div>';
      } else {
        line1 = `<div></div>`;
      }
    } else {
      line1 = `<div style='margin-bottom: 5px; opacity:${opacity}'>
      <div>${parseUnsafeString(config.series)}</div>
      </div>`;
      // Forming the open close high low tooltext
      if (isDataDefined) {
        return (config.series ? line1 : '') + ['open', 'high', 'low', 'close'].reduce((accumulator, currentValue) => {
        // Use `[open/high/low/close]TooltipValue` if present,
        // Else, current of OHLC has a value, then format it and update `[open/high/low/close]TooltipValue`
          let value = currData[currentValue + 'TooltipValue'] || (currData[currentValue + 'TooltipValue'] = currData[currentValue + 'Value'] && config.formatterFn({
            value: currData[currentValue + 'Value'],
            type: 'tooltip',
            prefix: config.prefix,
            suffix: config.suffix
          })
          );
          // Only add a line for O/H/L/C if it has a value calculated from above
          return accumulator + ((value !== UNDEF) ? `<div style = 'overflow: hidden;margin-top:4px;'>
        <div style = 'float: left;'>${currentValue}</div>
        <div style = 'float: right;'>${value}</div>
       </div>` : '');
        }, '');
      }
    }
    return line1;
  }
}

export default CandleStick;
