import GridLine from '../components/grid-lines';
export default canvas => {
  if (canvas.config.enableGridLines) {
    let gridLine = canvas.attachChild(GridLine, 'gridLine');
    gridLine.configure({
      direction: canvas.config.gridLineDirection,
      styleGridLines: canvas.config.styleGridLines,
      defaultStyle: canvas.config.gridLineStyle
    });
  }
};
