import { componentFactory } from '@fusioncharts/core/src/lib';
import SmartToolbar from '../components/smart-toolbar';

export default chart => {
  let toolbar,
    chartAttrib = chart.getFromEnv('dataSource').chart;

  // create an instance of toolbar and attach it as a children of chart
  componentFactory(chart, SmartToolbar, 'selectorToolbar', 1, [Object.assign({
    toolbarhdirection: 1
  }, chartAttrib)]);
  toolbar = chart.getChildren('selectorToolbar')[0];
  // add toolbar to the env of chart for other components to access
  chart.addToEnv('selectorToolbar', toolbar);
};
