import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { PXSTRING } from '@fusioncharts/core/src/lib';

const validDateLen = [2, 2, 4],
  /**
   * appends a 0 in front of a number if the number is less than 10
   *
   * @param {Number} x number to checked
   */
  formatDate = x => (x < 10 ? '0' + x : x) + '';

function blur (event) {
  let manager = this.getLinkedParent(),
    text = event.originalEvent.target.value + '',
    givenDate = text.split('/'),
    i,
    len = givenDate.length;

  if (len === 3) {
    for (i = 0; i < len; i++) {
      if (isNaN(givenDate[i]) || givenDate[i].length !== validDateLen[i]) {
        return;
      }
    }
    manager.updateCalendar({
      day: +givenDate[0],
      month: +givenDate[1],
      year: +givenDate[2]
    }, this.config.id);
  }
}

class DateBox extends SmartRenderer {
  /**
   * Creates an instance of DateBox.
   * @param {any} id id
   * @memberof DateBox
   */
  constructor (id) {
    super(id);

    let dateBox = this;
    this.addEventListener('blur', blur.bind(this));
    this.addEventListener('keyup', function (e) {
      dateBox.setData({
        tempDate: e.originalEvent.target.value
      });
    });
  }

  /**
   * sets default configurations
   *
   * @memberof DateBox
   */
  __setDefaultConfig () {
    super.__setDefaultConfig();

    let dateBoxConfig = this.config;

    dateBoxConfig.enabled = true;
    dateBoxConfig.inputBoxWidth = 65;
    dateBoxConfig.tempDate = false;
    // dateBoxConfig.label = 'From: ';
    dateBoxConfig._labelStyle = {
      'font-size': '12px',
      'font-weight': 'normal',
      'font-style': 'normal',
      color: '#5F5F5F'
    };
    dateBoxConfig._inputStyle = {
      'font-size': '12px',
      border: '1px solid #efefef',
      'background-color': '#fcfcfc',
      color: '#5F5F5F',
      width: dateBoxConfig.inputBoxWidth + PXSTRING,
      padding: '2px'
    };
    dateBoxConfig.date = {
      day: 8,
      month: 10,
      year: 2018
    };
  }

  /**
   * configures the components
   *
   * @param {Object} [inputConfig={}] configuration provided by user
   * @memberof DateBox
   */
  configureAttributes (inputConfig = {}) {
    super.configureAttributes(inputConfig);
    let dateBox = this,
      config = dateBox.config,
      curDate,
      styleDef = dateBox.getFromEnv('getStyleDef'),
      baseTextStyle = dateBox.getFromEnv('baseTextStyle');

    Object.assign(config, inputConfig);

    curDate = config.date;
    config.value = `${formatDate(curDate.day)}/${formatDate(curDate.month)}/${formatDate(curDate.year)}`;
    config._finalLabelStyle = Object.assign({}, config._labelStyle, baseTextStyle, styleDef(config.labelStyle));
    config._finalInputStyle = Object.assign({}, config._inputStyle, baseTextStyle, styleDef(config.inputStyle));
    // for html element line-height should not be applied through css as it distorts the height of the elements
    delete config._finalInputStyle['line-height'];
  }

  /**
   * calculates the space taken by this component
   *
   * @returns {Object} width and height
   * @memberof DateBox
   */
  getDimension () {
    let dateBox = this,
      dateBoxConfig = dateBox.config,
      labelDim,
      smartLabel = dateBox.getFromEnv('smartLabel'),
      fontParser = dateBox.getFromEnv('fontParser'),
      fSize = fontParser(dateBoxConfig._finalInputStyle['font-size']);

    smartLabel.setStyle(Object.assign({}, dateBoxConfig._finalLabelStyle));
    dateBoxConfig.labelDim = labelDim = smartLabel.getOriSize(dateBoxConfig.label);
    smartLabel.setStyle(Object.assign({}, dateBoxConfig._finalInputStyle));
    // add extra 10px space in the input box to make it look good
    dateBoxConfig.inputBoxWidth = smartLabel.getOriSize(dateBoxConfig.value).width + 10;
    dateBoxConfig._finalInputStyle.width = Math.max(65, dateBoxConfig.inputBoxWidth) + PXSTRING;

    return {
      width: labelDim.width + dateBoxConfig.inputBoxWidth,
      height: Math.max(smartLabel._lineHeight, fSize * 1.5)
    };
  }

  /**
   * setter method, takes position where the component is to be placed
   *
   * @param {Number} x pos x
   * @param {Number} y pos y
   * @memberof DateBox
   */
  setTranslation (x, y) {
    this.config.position = {
      x: x,
      y: y
    };
  }

  /**
   * draws the elements
   *
   * @memberof DateBox
   */
  draw () {
    let dateBox = this,
      dateBoxConfig = dateBox.config,
      position = dateBoxConfig.position,
      labelDim = dateBoxConfig.labelDim,
      curDate = dateBoxConfig.date || {},
      value = `${formatDate(curDate.day)}/${formatDate(curDate.month)}/${formatDate(curDate.year)}`;

    // create the label
    dateBox.addGraphicalElement({
      el: 'html',
      attr: {
        x: position.x,
        y: position.y,
        type: 'div',
        text: dateBoxConfig.label
      },
      component: dateBox,
      css: dateBoxConfig._finalLabelStyle,
      container: {
        id: 'box-container',
        label: 'box-container',
        isParent: true
      },
      label: 'label',
      id: 'label'
    });

    dateBoxConfig._finalInputStyle.width = dateBoxConfig.inputBoxWidth + PXSTRING;
    // create the input box
    dateBox.addGraphicalElement({
      el: 'html',
      attr: {
        x: position.x + labelDim.width,
        y: position.y - (labelDim.height / 3),
        type: 'input',
        name: dateBoxConfig.label,
        value: dateBoxConfig.tempDate || value
      },
      component: dateBox,
      css: dateBoxConfig._finalInputStyle,
      container: {
        id: 'box-container',
        label: 'box-container',
        isParent: true
      },
      label: 'box',
      id: 'box'
    });
  }
}

export default DateBox;
