/* eslint-disable good-practices/no-single-usage-variable */
export default (standardBins, intervalIndexMap, units, diffDuration) => {
  let i, j, unit, unitsLen = units.length, currentBin,
    oneMonthinMS = 2592000000,
    sixMonthsandOneDayinMS = 15724800000,
    standardDaysForOneMonth = 30,
    exceptionThreshold = 0.067,
    duration = diffDuration;

  /**
   * Special case,
   * Only in case of month level data, user may enter in form of a string in stead proper value. For eg. Jan, Feb, Mar.
   * And their values vary from 28 days to 31 days.Hence In case of month level binning The time differnce varies.
   * eg. for 2 months binning Jan-Feb,2003 has 59 days, WhereAs Apr-May, 2003 has 61 days, July-August,2003 has 62days differene.
   * eg. for 3 months binning Jan-Mar,2003 has 89 days, WhereAs Apr-Jun, 2004 has 91 days, July-Sep,2004 has 92days differene.
   * As in standardbin we follow month as 30days standard, restricting month level data to 30days or its multiplier.
   * hence values raning between 30-32, 60-62 till 180-182 are restricted to 30, 60 till 180.
   */
  // As month level binning are valid from 1-6 months hence adding check of one to six months
  if (duration > oneMonthinMS && duration <= sixMonthsandOneDayinMS) {
    /**
     * check to findout whether the data is within 30 *(multiplier 1-6) + 2 days range
     * exceptionThreshold is calculated from 2/30 as the max diff we are considering to restrict the data is 30 multiplier + 2 days
     * 2 is considered as July and August both months has 31 days
     */
    if (((duration / standardDaysForOneMonth) % 1) < exceptionThreshold) {
      // Logic to restric them to 30 or its multiplier
      duration = oneMonthinMS * Math.floor(duration / oneMonthinMS);
    }
  }
  for (i = 0; i < unitsLen; i++) {
    unit = units[i];
    j = intervalIndexMap[unit.name.toLowerCase()];
    while (standardBins[j] && standardBins[j][0].name() === unit.name.toLowerCase()) {
      currentBin = standardBins[j];
      // if the duration is less or an exact match with an interval, simply return the interval
      if (duration <= currentBin[2]) {
        return {
          unit: unit,
          multiplier: currentBin[1]
        };
      }
      j++;
    }
  }

  // if it was not resolved above, divide and round the duration by the last unit that was processed
  return {
    unit: units[i - 1],
    multiplier: Math.round(duration / units[i - 1].ms)
  };
};
