import {
  pluckNumber,
  pluck,
  hasTouch,
  TOUCH_THRESHOLD_PIXELS,
  defined,
  CLICK_THRESHOLD_PIXELS,
  getMouseCoordinate,
  extend2,
  UNDEF,
  convertColor,
  parseUnsafeString,
  parseTooltext
} from '@fusioncharts/core/src/lib';
import safeMin from '@fusioncharts/utils/src/array/safe-min';
import safeMax from '@fusioncharts/utils/src/array/safe-max';
import { addDep } from '@fusioncharts/core/src/dependency-manager';
import isValidNumber from '@fusioncharts/utils/src/type/is-valid-number';
import SmartRenderer from '@fusioncharts/core/src/component-interface/smart-renderer';
import TimeConverter from '@fusioncharts/utils/src/time-converter';

import columnAnimation from './column.animation';
import markerFactory from '../../factories/marker-factory';
import extent from '@fusioncharts/utils/src/array/extent';
import timeHour from '@fusioncharts/utils/src/time-intervals/hour';

const ROLLOVER = 'DataPlotRollOver',
  ROLLOUT = 'DataPlotRollOut',
  HIGHLIGHT = 'DataPlotHighlight',
  VISIBLE = 'visible',
  BLANK = '',
  _reducerFn = (s, t) => Object.assign(s, t),
  CLICK = 'DataPlotClick',
  /**
   * Function to get the start and end position of plot.
   */
  getDataPos = (num) => {
    return [num.start, num.end];
  },
  /**
   * Function to check if data is invalid
   * Data is invalid if scale is log and value is less than zero
   * or data start or end is outside the focus limit.
   */
  isDataInvalid = (isLog, num, dataPos, focusLimit) => {
    return ((isLog && num <= 0) || dataPos[1] < focusLimit[0] || dataPos[0] > focusLimit[1]);
  },
  getGenericType = (type) => {
    switch (type) {
      case 'line':
      case 'smooth-line':
      case 'step-line':
        return 'line';
      case 'area':
      case 'smooth-area':
      case 'step-area':
      case 'area-range':
        return 'area';
      default: break;
    }
  },
  checkForValidCoordinated = (coordiante) => {
    let valid = true,
      key;
    for (key in coordiante) {
      if (coordiante.hasOwnProperty(key)) {
        valid = valid && !isNaN(coordiante[key]);
      }
    }
    return valid;
  };

var HTP = hasTouch ? TOUCH_THRESHOLD_PIXELS : CLICK_THRESHOLD_PIXELS;

addDep({
  name: 'timeseriesColumnAnimation',
  type: 'animationRule',
  extension: columnAnimation
});

/**
 * This class is used to draw the column dataset of timeseries
 */
class Column extends SmartRenderer {
  constructor () {
    super();
    let dataset = this,
      config = this.config;
    this.registerFactory('markerFactory', markerFactory);
    /**
     * Function to get the 1st valid data
     */
    this._getFirstValidData = function _getFirstValidDataFn (dataArr, validatorProp) {
      let i, ii = dataArr.length;
      for (i = 0; i < ii; i++) {
        if (dataArr[0] && dataArr[0][validatorProp] !== UNDEF) {
          return dataArr[0][validatorProp];
        }
      }
      return UNDEF;
    };

    config.hoverInfo = [];
    this.addEventListener('beforeremove', () => {
      dataset.getFromEnv('chart').setXScaleLimit(config.scaleX.getId(), dataset.getId());
      dataset.getFromEnv('chart').setYScaleLimit(config.scaleY.getId(), dataset.getId());
    });
  }

  /**
   * returns the style which is to be applied on the hovered plot
   *
   * @param {Object} hoverIndices indices of hovered plots
   * @returns {Object} configuration
   * @memberof Column
   */
  getHoverInConfig (hoverIndices) {
    return {
      style: this.config.hoverInStyle,
      hoverIndices,
      hovered: true
    };
  };
  /**
   * returns the style which is to be applied on the highlighted plot
   *
   * @param {Object} hoverIndices index of highlighted plots
   * @returns {Object} configuration
   * @memberof Column
   */
  getHighlightConfig (hoverIndices) {
    return {
      style: this.config.highlightStyle,
      hoverIndices,
      hovered: false
    };
  }

  /**
   * returns the style which is to be applied on the hovered out plot
   *
   * @param {Object} hoverIndices index of hovered out plots
   * @returns {Object} configuration
   * @memberof Column
   */
  getHoverOutConfig (hoverIndices) {
    return {
      style: this.config.hoverOutStyle,
      hoverIndices,
      hovered: false
    };
  };

  /**
   * Sets the type of the component
   * @return {string} type
   */
  getType () {
    return 'dataset';
  }
  /**
   * Sets the name of the component
   * @return {string} name
   */
  getName () {
    return 'timeseriesColumn';
  }

  __setDefaultConfig () {
    super.__setDefaultConfig();
    let config = this.config;
    config['default-plotSpacePercent'] = 30;
    config.visibility = VISIBLE;
    config.prevBoundaryInfo = {};
    config.dataMarkerPadding = 0;
    config.style = {};
    config.hoverInStyle = {};
    config.highlightStyle = {};
    config.hoverOutStyle = {};
    config.defaultStyle = {
      'stroke-width': 1,
      'fill-opacity': 1,
      'stroke-opacity': 1,
      'opacity': 1
    };
    config.defaultPredictedDataStyle = {
      'stroke-width': 1,
      'fill-opacity': 0.2,
      'stroke-opacity': 1,
      'stroke-dasharray': 5
    };
    config.defaultHoverInStyle = {
      'opacity': 0.8
    };
    config.defaultHighlightStyle = {
      'opacity': 0.8
    };
    config.dataInfo = [];
    config.prevPerBinYExtents = [];
    config.dataIndices = [1, 2];
  }

  /**
   * Function to configure the cosmemtics of the plots
   */
  configureAttributes (dataObj) {
    let dataset = this,
      prop,
      chartAttr = dataset.getFromEnv('chart-attrib'),
      config = dataset.config;

    // Setting the given configurations
    if (dataObj.hasOwnProperty('hoverIndices')) {
      // If index is provided then, single plot has to be configured
      config.hoverInfo.push(dataObj);
      config.hoverMode = true;
    } else {
      for (prop in dataObj) {
        // Style parsing is done only when primaryColor is provided
        if (prop === 'primaryColor') {
          let plotConfig = dataObj.plotCosmetics,
            genericConfig = dataObj.genericCosmetics,
            primaryColor = dataObj[prop],
            styles,
            getStyleDef = dataset.getFromEnv('getStyleDef'),

            applicableClassStyleGeneric = getStyleDef(genericConfig.style && genericConfig.style.plot),
            applicableClassStyleGenericHover = getStyleDef(genericConfig.style && genericConfig.style['plot:hover']),
            applicableClassStyleGenericHighlight = getStyleDef(genericConfig.style && genericConfig.style['plot:highlight']),

            applicableClassStylePlot = getStyleDef(plotConfig.style && plotConfig.style.plot),
            applicableInlineStylePlot = getStyleDef(dataObj.styleConfig && dataObj.styleConfig.plot),

            applicableClassStyleColumn = getStyleDef(plotConfig.style && plotConfig.style.column),
            applicableInlineStyleColumn = getStyleDef(dataObj.styleConfig && dataObj.styleConfig.column),

            applicableHoverStylePlot = getStyleDef(plotConfig.style && plotConfig.style['plot:hover']),
            applicableHighlightStylePlot = getStyleDef(plotConfig.style && plotConfig.style['plot:highlight']),
            applicableHoverInlineStylePlot = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['plot:hover']),
            applicableHighlightInlineStylePlot = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['plot:highlight']),

            applicableHoverStyleColumn = getStyleDef(plotConfig.style && plotConfig.style['column:hover']),
            applicableHighlightStyleColumn = getStyleDef(plotConfig.style && plotConfig.style['column:highlight']),
            applicableHoverInlineStyleColumn = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['column:hover']),
            applicableHighlightInlineStyleColumn = getStyleDef(dataObj.styleConfig && dataObj.styleConfig['column:highlight']),

            styleObj = config.style,
            hoverOutStyle = config.hoverOutStyle,
            hoverInStyle = config.hoverInStyle,
            highlightStyle = config.highlightStyle;
          // Parsing the plot styling properties with increasing preference
          [styleObj, config.defaultStyle, applicableClassStyleGeneric, applicableClassStylePlot, applicableInlineStylePlot, applicableClassStyleColumn, applicableInlineStyleColumn].reduce(_reducerFn);
          [hoverInStyle, config.defaultHoverInStyle, applicableClassStyleGenericHover, applicableHoverStylePlot, applicableHoverStyleColumn, applicableHoverInlineStylePlot, applicableHoverInlineStyleColumn].reduce(_reducerFn);
          [highlightStyle, config.defaultHighlightStyle, applicableClassStyleGenericHighlight, applicableHighlightStylePlot, applicableHighlightStyleColumn, applicableHighlightInlineStylePlot, applicableHighlightInlineStyleColumn].reduce(_reducerFn);

          // Forming the stroke color
          styleObj.stroke = convertColor(pluck(styleObj.stroke, primaryColor, config['default-stroke']), pluckNumber(styleObj['stroke-opacity'], 1) * 100);

          // Fill is obtained only from palette colors
          styleObj.fill = convertColor(pluck(primaryColor, styleObj.stroke, config['default-fill']), pluckNumber(styleObj['fill-opacity'], 1) * 100);

          hoverInStyle.fill && (hoverInStyle.fill = convertColor(hoverInStyle.fill, pluckNumber(hoverInStyle['fill-opacity'], styleObj['fill-opacity']) * 100));
          hoverInStyle.stroke && (hoverInStyle.stroke = convertColor(hoverInStyle.stroke, pluckNumber(hoverInStyle['stroke-opacity'], styleObj['stroke-opacity']) * 100));

          highlightStyle.fill && (highlightStyle.fill = convertColor(highlightStyle.fill, pluckNumber(highlightStyle['fill-opacity'], styleObj['fill-opacity']) * 100));
          highlightStyle.stroke && (highlightStyle.stroke = convertColor(highlightStyle.stroke, pluckNumber(highlightStyle['stroke-opacity'], styleObj['stroke-opacity']) * 100));

          // Parsing the hoverOut styles from hover style and highlight style
          for (styles in Object.assign(Object.assign({}, hoverInStyle), highlightStyle)) {
            hoverOutStyle[styles] = pluck(styleObj[styles], '');
          }

          // Stroke dash-array behave differently when applied on element and group, hence removing it
          defined(hoverOutStyle['stroke-dasharray']) && (hoverOutStyle['stroke-dasharray'] = null);

          config.plotBorderThickness = styleObj['stroke-width'];

          // Opacities are separately stored and is applied to each element as applying them on the group followed by the elememt (during hover in and out)
          // results into multiplied opacity
          config.opacity = styleObj.opacity;
          config['fill-opacity'] = styleObj['fill-opacity'];
          config['stroke-opacity'] = styleObj['stroke-opacity'];

          delete styleObj.opacity;
          delete styleObj['fill-opacity'];
          delete styleObj['stroke-opacity'];
        } else {
          config[prop] = dataObj[prop];
        }
      }
      config.plotSpacePercent = pluckNumber(chartAttr.plotspacepercent, config['default-plotSpacePercent']);
      config.limit = dataset._calculateLimits();
      const chart = dataset.getFromEnv('chart');
      chart.setYScaleLimit(config.scaleY.getId(), dataset.getId(), config.limit.y, config.limit.baseRequired);
      chart.setXScaleLimit(config.scaleX.getId(), dataset.getId(), config.limit.x);
    }
    this.parsePrediction(dataObj);
  }
  // Method to get timestamp value for a given pixel
  getValueFromPx (pxValue) {
    let binRange = this.getFromEnv('binDecider').getBinRange(),
      axisLength = binRange[1] - binRange[0],
      chart = this.getFromEnv('chart'),
      limit = this.config.calculateFromContext ? chart.getContextLimit() : chart.getFocusLimit(),
      diff = limit[1] - limit[0];
    // The logic for this calculation is as follows:
    // Suppose your focus limit range is FR and you want to add X value of padding.
    // Then your new focus limit is now FR + X (considering both left and right padding).
    // If the axis length is AL pixel, and the pixel value for the timestamp X is known, then
    // apply unitary method to find the value of X.
    return (pxValue * diff) / (axisLength - pxValue);
  }
  // Method to get padding in data limits
  getPadding () {
    let dataset = this,
      config = dataset.config,
      scaleX = config.scaleX,
      curBin = this.getFromEnv('binDecider').getRangeThreshold(),
      scalePadding = 0,
      dataMarkerPadding = dataset.getValueFromPx(config.dataMarkerPadding);
    if (scaleX.showPlotOverTick()) {
      scalePadding = curBin[2] / 2;
    }
    return Math.ceil(Math.max(scalePadding, dataMarkerPadding));
  }
  _calculateLimits () {
    let { indices, data, type, scaleY, scaleX, calculateFromContext } = this.config,
      isContinuous = /continuous/.test(this.getName()),
      showPlotOverTick = scaleX.showPlotOverTick(),
      padding,
      returnObj = {},
      firstData = data[0],
      lastData = data[data.length - 1],
      isLog = scaleY.getType() === 'log',
      isVisible = this.config.visibility === 'visible',
      chart = this.getFromEnv('chart'),
      limit = calculateFromContext ? chart.getContextLimit() : chart.getFocusLimit(),
      minFn = arr => {
        let num = pluckNumber(arr[indices[2]], arr[indices[1]]),
          dataPos = getDataPos(arr[0]),
          isInvalid = isDataInvalid(isLog, num, dataPos, limit);
        return isInvalid ? null : num;
      },
      maxFn = arr => {
        let num = arr[indices[1]],
          dataPos = getDataPos(arr[0]),
          isInvalid = isDataInvalid(isLog, num, dataPos, limit);
        return isInvalid ? null : num;
      };
    padding = this.getPadding();
    if (showPlotOverTick) {
      returnObj.x = [firstData && (firstData[indices[0]].start - padding), lastData && (lastData[indices[0]].start + padding)];
    } else {
      if (isContinuous) {
        returnObj.x = [firstData && ((firstData[indices[0]].start + firstData[indices[0]].end) / 2 - padding), lastData && ((lastData[indices[0]].start + lastData[indices[0]].end) / 2 + padding)];
      } else {
        returnObj.x = [firstData && (firstData[indices[0]].start - padding), lastData && (lastData[indices[0]].end - padding)];
      }
    }

    returnObj.y = isVisible ? [safeMin(data, minFn), safeMax(data, maxFn)] : UNDEF;
    returnObj.baseRequired = !/line/.test(type);
    return returnObj;
  }

  /**
   * The main draw function
   */
  draw () {
    let dataset = this;
    dataset._createContainer();
    dataset._drawPlot();
    // Resetting the hoverInfo after each draw cycle
    dataset.config.hoverInfo = [];
    dataset.config.hoverMode = false;
    dataset.config.repositioningDone = false;
  }

  /**
   * Function to create the containers of the dataset with all the cosmetics applioed at group level.
   * Also, create containers for its data-markers
   */
  _createContainer () {
    let dataset = this;
    // Group for plots
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group',
        isParent: true
      },
      component: dataset,
      label: 'group',
      id: 'meso',
      attr: {
        name: 'dataset-meso'
      }
    }, true);

    // Group for plots
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'thermo',
        label: 'group',
        isParent: true
      },
      component: dataset,
      label: 'group',
      id: 'thermo',
      attr: {
        name: 'dataset-thermo',
        'clip-path': dataset.getFromEnv('dsGroupclipPath')
      }
    }, true);
  }

  /**
   * Function that determines if drawing is required in the current data-cycle
   */
  isDrawingRequired () {
    let dataset = this,
      config = dataset.config;
    if (config.repositioningDone || config.hoverMode) {
      return true;
    }
  }

  /**
   * Function to draw the plots
   */
  _drawPlot () {
    var dataset = this,
      config = dataset.config,
      styleObj = config.style,
      definedOpacity = defined(config.opacity),
      definedStrokeOpacity = defined(config['stroke-opacity']),
      definedFillOpacity = defined(config['fill-opacity']),
      attr,
      isDatumPredictive = false,
      predictiveStyleAttributes = dataset.config.predictiveStyleAttributes,
      definedPredictiveOpacity = defined(predictiveStyleAttributes.predictiveOpacity),
      definedPredictiveStrokeOpacity = defined(predictiveStyleAttributes.predictiveStrokeOpacity),
      definedPredictiveFillOpacity = defined(predictiveStyleAttributes.predictiveFillOpacity),
      dataInfo = config.dataInfo;

    // Group for plots
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'meso-plot',
      attr: Object.assign({
        name: 'column-plot-meso',
        visibility: config.visibility
      }, styleObj)
    }, true);

    // Group for predictive plots
    dataset.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group'
      },
      component: dataset,
      label: 'group',
      id: 'meso-plot-predictive',
      attr: Object.assign({
        name: 'column-predictive-plot-meso',
        visibility: config.visibility
      }, styleObj, predictiveStyleAttributes)
    }, true);

    // Creating the plots
    dataInfo.forEach((datum, index) => {
      if (isValidNumber(datum.value) || (isValidNumber(datum.high) && isValidNumber(datum.low))) {
        attr = {
          x: datum.x,
          y: datum.y,
          width: datum.width,
          height: datum.height
        };

        isDatumPredictive = dataset.isPlotPredictive(datum);

        definedOpacity && (attr.opacity = config.opacity);
        definedStrokeOpacity && (attr['stroke-opacity'] = config['stroke-opacity']);
        definedFillOpacity && (attr['fill-opacity'] = config['fill-opacity']);
        // Apply opacity only if it is applied
        datum.style && (Object.assign(attr, datum.style));

        if (isDatumPredictive) {
          definedPredictiveOpacity && (attr.opacity = predictiveStyleAttributes.predictiveOpacity);
          definedPredictiveStrokeOpacity && (attr['stroke-opacity'] = predictiveStyleAttributes.predictiveStrokeOpacity);
          definedPredictiveFillOpacity && (attr['fill-opacity'] = predictiveStyleAttributes.predictiveFillOpacity);
        }

        // Invalid value checking
        dataset.addGraphicalElement({
          el: 'rect',
          container: {
            label: 'group',
            id: !isDatumPredictive ? 'meso-plot' : 'meso-plot-predictive'
          },
          props: {
            dataLength: dataInfo.length
          },
          label: 'rect',
          attr: attr,
          component: dataset,
          index
        }, true);
        datum.style = UNDEF;
      }
    });
    config.hoverInfo = [];
  }

  /**
   * Helper function to check if positioning is required based on the xScale's domain range
   */
  _isRepositioningNeeded () {
    let dataset = this,
      config = dataset.config,
      xScale = dataset.getFromEnv('xScale'),
      yScale = dataset.getFromEnv('yScale'),
      [xDomainStart, xDomainEnd] = xScale.getDomain(),
      [xRangeStart, xRangeEnd] = xScale.getRange(),
      [yDomainStart, yDomainEnd] = yScale.getDomain(),
      [yRangeStart, yRangeEnd] = yScale.getRange(),
      positioningReq,
      prevBoundaryInfo = config.prevBoundaryInfo,
      // DO NOT inline this invocation into the conditional below. This function
      // needs to be called each time to recompute the limit changes for the
      // next render. Inlining will cause the function to not be invoked due to
      // short circuiting of the OR checks
      hasDataYExtentChangedInAnyBin = this.hasDataYExtentChangedInAnyBin();

    if (+xDomainStart !== +prevBoundaryInfo.xDomainStart || +xDomainEnd !== +prevBoundaryInfo.xDomainEnd ||
      +yDomainStart !== +prevBoundaryInfo.yDomainStart || +yDomainEnd !== +prevBoundaryInfo.yDomainEnd ||
      xRangeStart !== prevBoundaryInfo.xRangeStart || xRangeEnd !== +prevBoundaryInfo.xRangeEnd ||
      yRangeStart !== prevBoundaryInfo.yRangeStart || yRangeEnd !== +prevBoundaryInfo.yRangeEnd ||
      config.prevVisibility !== config.visibility || config.legendInteracted || hasDataYExtentChangedInAnyBin) {
      // once positioning is required the legend interaction must be set false otherwise it will always reamin
      // true and double drawing of dataset will occur during zooming
      config.legendInteracted = false;
      positioningReq = true;
      config.prevBoundaryInfo = {
        xDomainStart,
        xDomainEnd,
        xRangeStart,
        xRangeEnd,
        yDomainStart,
        yDomainEnd,
        yRangeStart,
        yRangeEnd
      };

      config.prevVisibility = config.visibility;
    }

    return positioningReq;
  }

  hasDataYExtentChangedInAnyBin () {
    const { config } = this,
      {
        data,
        prevPerBinYExtents
      } = config,
      indices = this.config.dataIndices,
      perBinYExtents = data.map(
        datum => extent(indices.map(idx => datum[idx]), Number)
      );

    // If the number of bins in the last render and this render are equal,
    // iterate and compare the Y extents of every bin,
    if (perBinYExtents.length === prevPerBinYExtents.length) {
      const result = perBinYExtents.some((yExtent, idx) => {
        const prevYExtent = prevPerBinYExtents[idx];

        return (yExtent[0] !== prevYExtent[0] || yExtent[1] !== prevYExtent[1]);
      });

      config.prevPerBinYExtents = perBinYExtents;

      return result;
    }

    // Else, no comparisons are required. The Y extents have definitely changed.
    //
    // Set the current perBinYExtents as the prevPerBinYExtents for the next
    // render cycle and return true to indicate that Y extents have changed in
    // some bins.
    config.prevPerBinYExtents = perBinYExtents;
    return true;
  }

  /**
   * Function to allocation the position of the dataplots
   */
  allocatePosition () {
    let dataset = this,
      config = dataset.config,
      [xIdx, yIdx, baseIdx, totalStackSum] = config.indices,
      binDecider = dataset.getFromEnv('binDecider'),
      xScale = dataset.getFromEnv('xScale'),
      yScale = dataset.getFromEnv('yScale'),
      dataInfo = config.dataInfo,
      seriesInfo = config.seriesInfo,
      dateColumn = dataset.getFromEnv('dateColumn'),
      isUTC = dataset.getFromEnv('isUTC'),
      height,
      width,
      date,
      value,
      x,
      y,
      yBaseValue,
      yPos,
      yBase,
      hoverInfo,
      isNegative,
      pX,
      xShift,
      endXPosition,
      groupIndex = config.groupIndex || 0,
      groupWidth,
      totalGroups = config.totalGroups || 1,
      availableWidth,
      data = config.data,
      duration,
      eventArgs,
      seriesName,
      format = dateColumn.format,
      formatter = isUTC ? TimeConverter.utcFormatter(format) : TimeConverter.formatter(format),
      startDate,
      endDate,
      midGroup,
      gutter,
      i,
      len,
      bins = xScale.bins,
      plotBorderThickness = config.plotBorderThickness,
      // plot border overlapping is considered only when borders are in different color
      considerPlotBorder = config.fillColor === config.strokeColor && plotBorderThickness > 0,
      paddingInTimestamp,
      rangeThreshold = binDecider.getRangeThreshold()[2],
      [startDomain, endDomain] = xScale.getDomain(),
      dataObj;
    config.firstTimeStamp = data && data[0] && data[0][xIdx] && data[0][xIdx].start;
    if ((config.repositioningDone = dataset._isRepositioningNeeded())) {
      // skip drawing if dataset is not visible
      if (config.visibility !== 'visible') {
        return;
      }

      // Store data related operations
      dataInfo = config.dataInfo = [];

      // Getting the pixel width between two consecutive data
      config.availableWidth = availableWidth = 0;
      // Storing info for dataMarker detection
      for (i = 0, len = bins.length; i < len; i++) {
        if (bins[i].clipType !== 2) {
          config.availableWidth = availableWidth = xScale.getRangeValue(bins[i].end) - xScale.getRangeValue(bins[i].start);
          break;
        }
      }
      config.timeStampGap = rangeThreshold;

      // Avoid negative thickness when border thickness is more than the actual available width
      groupWidth = Math.max(availableWidth * (1 - config.plotSpacePercent / 100), 1);

      // Logic for multi-series
      width = groupWidth / totalGroups;
      midGroup = Math.round(totalGroups / 2);
      gutter = totalGroups % 2 === 0 ? 0 : 0.5;
      xShift = midGroup - groupIndex > 0 ? -width * (midGroup - groupIndex - gutter) : width * (groupIndex - midGroup + gutter);

      paddingInTimestamp = +xScale.getDomainValue(availableWidth * (config.plotSpacePercent / 200)) -
        +xScale.getDomainValue(0);
      // Storing value for tooltip
      config.actualStartDomain = +startDomain + paddingInTimestamp;
      // Actual end domain is the scale domain minus the space taken up by the plotspacepercent
      config.actualEndDomain = +endDomain - paddingInTimestamp;

      // Adjusting column width as per the column border
      considerPlotBorder && (width = Math.max(width - plotBorderThickness, 1));

      data.forEach((datum, index) => {
        date = datum[xIdx];
        value = datum[yIdx];
        duration = date.config.duration;
        startDate = date.start;
        endDate = date.end;
        const tempDate = new Date(startDate),
          winterOffset = new Date(tempDate.getFullYear(), 0).getTimezoneOffset(),
          summerOffset = new Date(tempDate.getFullYear(), 6).getTimezoneOffset();

        // Incase of daylight savings, update the start date
        if (winterOffset > summerOffset) {
          startDate = timeHour.offset(startDate, 1);
        }
        // Storing the 1st time stamp
        if (!index) {
          config.firstTimeStamp = date.start;
        }
        yBaseValue = datum[baseIdx];
        yBase = yScale.getRangeValue(yBaseValue || Math.max(yScale.getDomain()[0], 0));
        yPos = yScale.getRangeValue(value);
        if (isValidNumber(value) && isValidNumber(yBase) && isValidNumber(yPos)) {
          // Find the position where the plot is to be placed
          x = xScale.getRangeValue(new Date(date.start), new Date(date.end));
          // Adjusting column starting position as per the column border
          considerPlotBorder && (x += plotBorderThickness / 2);

          x += xShift;
          endXPosition = x + width;
          // Adding the base to manage the stacking
          height = Math.abs(yBase - yPos);
          isNegative = yPos > yBase;
          y = isNegative ? yBase : yPos;
          dataObj = {
            startDate: startDate,
            endDate: endDate,
            value,
            yBaseValue,
            yBase,
            x,
            endXPosition,
            y,
            width,
            height,
            totalStackSum: defined(datum[totalStackSum]) ? datum[totalStackSum] : UNDEF,
            eventArgs: {
              index,
              dataValue: value
            }
          };

          eventArgs = {
            index,
            dataValue: value,
            start: startDate,
            startText: formatter.format(startDate),
            end: endDate,
            endText: formatter.format(endDate),
            binUnit: duration.Unit,
            binMultiplier: duration.number,
            plotType: config.type,
            timeFormatter: format,
            aggregation: config.aggregation,
            measure: config.measures[0],
            binValue: value,
            binValueFormatted: config.formatterFn && config.formatterFn({
              value: value,
              type: 'tooltip',
              prefix: config.prefix,
              suffix: config.suffix
            })
          };
          seriesName = Object.keys(seriesInfo)[0];
          if (seriesName) {
            eventArgs.series = seriesName;
            eventArgs.seriesValue = seriesInfo[seriesName];
          }
          dataObj.eventArgs = eventArgs;

          // Get the data index corresponding to the fetched timeStamp
          pX = xScale.getBinIndex(date.start);
          dataInfo[pX] = dataObj;
        }
      });
    }

    // If hovered, show hover effect of the particular plot
    if ((hoverInfo = config.hoverInfo)) {
      hoverInfo.forEach(
        hoveredData => {
          hoveredData.hoverIndices.forEach((index) => {
            if (typeof dataInfo[index] === 'object' && typeof hoveredData.style === 'object') {
              dataInfo[index].style = extend2(dataInfo[index].style, hoveredData.style);
            }
          });
        }
      );
    }
  }

  /**
   * Helper function of data markers
   * @return {Object} contains positioning info and the dataInfo
   */
  _getRelevantInfo () {
    var config = this.config;
    return {
      firstTimeStamp: config.firstTimeStamp,
      timeStampGap: config.timeStampGap,
      dataInfo: config.dataInfo,
      fill: config.style.stroke
    };
  }

  /**
   * Function that retunr the nearest plot details
   * @param {number} chartX x-axis position of the mouse cordinate
   * @param {number} chartY x-axis position of the mouse cordinate
   * @return {Object} return an object with details of nearest polt and whether it is hovered or not
   */
  _getHoveredPlot (chartXArg, chartYArg) {
    var dataset = this,
      chartX = chartXArg,
      chartY = chartYArg,
      xScale = dataset.getFromEnv('xScale'),
      xScaleRange = xScale.getRange(),
      canvas = dataset.getLinkedParent(),
      config = dataset.config,
      visible = config.visibility === VISIBLE,
      dataMarkers = [
        dataset.getChildren('dataMarker')
      ],
      dataMarker,
      output,
      returnObj,
      translationObj = canvas.getTranslation(),
      translationX = translationObj ? translationObj.x : 0,
      translationY = translationObj ? translationObj.y : 0,
      pX;
    // Readjusting the chart as the canvas group is translated
    chartX -= translationX;
    chartY -= translationY;
    // if chartX is outside the range of scale, no plots will be hovered
    // as because tootlip expects a proper object or undefined component property is sent
    // if undefined it sent then it will freeze tooltip in its last position.
    if (chartX < xScaleRange[0] || chartX > xScaleRange[1]) {
      return {
        component: dataset
      };
    }
    // Calculation the bin index corresponding to the fteched timeStamp
    pX = xScale.getBinIndex(xScale.getDomainValue(chartX));

    // Special case  when timeStampGap = 1, the levelof aggregation is minimum
    config.timeStampGap <= 1 && pX === -1 && (pX = 0);
    // Iteraring over the datamarkers to check if they are hovered
    dataMarkers.forEach(dataMarkerArg => {
      if (!output && dataMarkerArg) {
        dataMarker = dataMarkerArg[0];
        output = dataMarker._checkPointOverMarker(
          pX,
          chartX,
          chartY,
          config.availableWidth
        );
      }
    });

    // Checking for overlap amongst datamarker and two cosecutive column plots along x-axis
    returnObj = output;
    if (!returnObj) {
      if (visible) {
        returnObj = dataset._checkPointerOverColumn(pX, chartX, chartY);
      } else {
        returnObj = {};
      }
    }
    // if not hovered on any plot then get the nearest plot info.
    if (!returnObj.hovered) {
      // Storing info for crossline
      returnObj.binIndexHovered = xScale.getBinIndex(xScale.getDomainValue(chartX));
      returnObj.pointObj = config.dataInfo[returnObj.binIndexHovered] || xScale.getBinBounds(pX);
      returnObj.component = dataset;
    } else {
      returnObj.binIndexHovered = returnObj.pointIndex;
    }
    config.binIndexHovered = returnObj.binIndexHovered;
    return returnObj;
  }

  /**
   * Dummy helper function for tooltext to get the data index wrt the current dataset that is hovered
   */
  _getHoveredBin () {
    return this.config.binIndexHovered;
  }

  /**
   * Function to form the tooltext
   * @param {Boolean} pointFound If any dataset's data is actually hovered
   * @param {Boolean} datasetHovered If the current dataset is hovered
   * @param {number} dataIndex The index of the data in the current dataset that is hovered
   */
  _getTooltext (pointFound, datasetHovered, dataIndex) {
    var dataset = this,
      config = dataset.config,
      dataSource = dataset.getFromEnv('dataSource'),
      plots = dataset._linkedParent.getChildren('dataset'),
      tooltext = dataSource.tooltip ? dataSource.tooltip.tooltext : null,
      tooltextElement,
      macroIndices,
      parserConfig,
      plotConfig,
      outPutFormatter = config.scaleX.formatters,
      timeFormat = config.scaleX._timeFormat,
      bin,
      binStart,
      binEnd,
      dsName = dataset.getName(),
      dsType = getGenericType(config.type),
      bodyStyle = dataset.getFromEnv('tooltipStyle').body,
      containerLineHeight = dataset.getFromEnv('tooltipStyle').container['line-height'] || 1,
      colorBoxlen = containerLineHeight === 1 ? bodyStyle['font-size'] : containerLineHeight,
      dataInfo = config.dataInfo,
      currData = dataInfo[dataIndex],
      value,
      // Setting logic for opacity.
      // When the data of the currentdataset is hovered then all other tooltip's opacity is set to 0.5
      // Rest all the cases the opacity is 1
      opacity = pointFound ? (datasetHovered ? 1 : 0.5) : 1,
      tooltipShapeFill = dsName === 'continuous' ? (dsType === 'line' ? config.plotStyle.stroke : config.plotStyle.fill) : config.style.fill,
      line2;
    if (dataset._isInvalidTooltext(currData)) {
      return '';
    }

    if (!currData.tooltipValue) {
      // Substracting the base if it is provided
      // value = isValidNumber(currData.yBaseValue)
      // ? currData.yBaseValue >= 0 ? currData.value - currData.yBaseValue
      // : currData.yBaseValue - currData.value : currData.value;
      // FTV3-1076- correcting value in tooltip
      value = isValidNumber(currData.yBaseValue) ? currData.value - currData.yBaseValue : currData.value;
      currData.tooltipValue = isValidNumber(value)
        ? config.formatterFn({
          value: value,
          type: 'tooltip',
          prefix: config.prefix,
          suffix: config.suffix
        }) : '';
    }

    if (tooltext) {
      // get the name of all the series
      parserConfig = {
        series: dataSource.yaxis[0].plot.map(function (series) {
          return {
            name: series.value
          };
        })
      };
      // get conigurations related to plots
      plotConfig = {
        series: plots.map(function (index) {
          let individualPlotConfig, isMajor, plotIdentifierElement;
          // If outputTimeFormat is provided, then update bin, binStart, binEnd
          if (dataSource.xaxis[0].outputtimeformat) {
            // Mock the time converter behaviour of time-axis
            isMajor = timeFormat.minor ? timeFormat.minor : timeFormat.major;
            binStart = outPutFormatter[isMajor].major.format(dataInfo[dataIndex].startDate);
            binEnd = outPutFormatter[isMajor].major.format(dataInfo[dataIndex].endDate);
          } else {
            bin = config.timeFormatterFn({
              type: 'tooltip',
              dateRange: index.config.dataInfo[dataIndex]
            });
            binStart = bin.split('-')[0];
            binEnd = bin.split('-')[1];
          }
          individualPlotConfig = index.config.dataInfo[dataIndex].eventArgs;

          // Create plotIdentifier element dynamically for all plots
          plotIdentifierElement = `<div style='height:10px;width:10px;background-color:${index.config.style.fill};'></div>`;
          return {
            color: index.config.style ? index.config.style.fill : UNDEF,
            plotIdentifier: plotIdentifierElement,
            value: index.config.dataInfo[dataIndex].eventArgs.binValue,
            bin: bin,
            binStart: binStart,
            binEnd: binEnd,
            dataValue: index.config.dataInfo[dataIndex].eventArgs.binValueFormatted,
            volumeValue: dataInfo[dataIndex].eventArgs.binValue,
            volumeDataValue: dataInfo[dataIndex].eventArgs.binValueFormatted,
            openValue: individualPlotConfig.binOpen,
            openDataValue: individualPlotConfig.binOpenFormatted,
            highValue: individualPlotConfig.binHigh,
            highDataValue: individualPlotConfig.binHighFormatted,
            lowValue: individualPlotConfig.binLow,
            lowDataValue: individualPlotConfig.binLowFormatted,
            closeValue: individualPlotConfig.binClose,
            closeDataValue: individualPlotConfig.binCloseFormatted
          };
        })
      };
      macroIndices = [154, 155, 156, 157, 158, 159, 160, 161, 162, 163, 164, 165, 166, 167, 168, 169, 170, 171];
      tooltext = parseTooltext(tooltext, macroIndices, parserConfig, plotConfig);
      tooltextElement = '<div>' + tooltext + '</div>';
    } else {
      tooltextElement = `<div style='margin-top:6px; overflow: hidden; opacity:${opacity}'>
        <span style='float:left; height: ${colorBoxlen}; width: ${colorBoxlen}; position:relative;margin-right: 2px;'>
          <span style='display:block;height:64%;width:64%;position:absolute;top:50%;transform:translateY(-50%);background-color:${tooltipShapeFill};'></span>
        </span>
        <div style='float:left;'>${parseUnsafeString(config.series)}&nbsp</div>
        <div style='float: right;'>&nbsp${currData.tooltipValue || ''}</div>
        </div>`;
    }
    line2 = tooltextElement;
    return line2;
  }

  /**
   * Funtion that determines if tooltext is possible for the current data
   * @param {Object} currData contains info about the current hovered index
   */
  _isInvalidTooltext (currData) {
    let dataset = this,
      config = dataset.config;
    // Checking the data's date range to be inside the range of the visbible axis
    if (!currData || currData.endDate < config.actualStartDomain ||
      currData.startDate > config.actualEndDomain) {
      return true;
    }
  }

  /**
   * Function that sets the date for the tooltext along with the total styack sum if any
   */
  _getDateForToolText (dataIndex) {
    var dataset = this,
      config = dataset.config,
      dataInfo = config.dataInfo,
      line1 = BLANK,
      line2 = BLANK,
      currData = dataInfo[dataIndex];
    if (dataset._isInvalidTooltext(currData)) {
      return {
        body: '',
        header: ''
      };
    }
    if (!currData.tooltextTime) {
      currData.tooltextTime = config.timeFormatterFn({
        type: 'tooltip',
        dateRange: currData
      });
    }

    // Parsing the total stack sum
    !currData.totalStackSumTooltip && isValidNumber(currData.totalStackSum) &&
      (currData.totalStackSumTooltip = config.formatterFn({
        value: currData.totalStackSum,
        type: 'tooltip',
        prefix: config.prefix,
        suffix: config.suffix
      }));
    line1 = `<div style='margin-bottom:8px;'>${currData.tooltextTime}</div>`;
    currData.totalStackSumTooltip &&
      (line2 = `<div style='margin-bottom: 2px;'>Total: ${currData.totalStackSumTooltip}</div>`);
    return {
      header: line1,
      body: line2
    };
  }

  /**
   * function to check if given x,y coordiante is over a column plot or not
   * @param  {number} pX     index of column
   * @param  {number} chartX x co-ordinate
   * @param  {number} chartY y co-ordinate
   * @return {Object}        hovered information
   */
  _checkPointerOverColumn (pX, chartX, chartY) {
    var dataset = this,
      config = dataset.config,
      plotBorderThickness = config.plotBorderThickness,
      dataInfo = config.dataInfo,
      pointObj,
      dx,
      dy,
      width,
      height,
      hovered,
      halfPlotBorderThickness,
      yPos;

    pointObj = dataInfo[pX];
    if (!pointObj) {
      return {
        pointIndex: pX,
        hovered: false,
        component: dataset
      };
    }

    // Setting the tolerance incase of small height or width of column/bar
    height = pointObj.height < HTP ? HTP : pointObj.height;
    width = pointObj.width < HTP ? HTP : pointObj.width;
    // checking if height is less than thresold value then change the yPos of plot too
    yPos = pointObj.height < HTP ? pointObj.y - (HTP - pointObj.height) : pointObj.y;

    if (plotBorderThickness !== UNDEF) {
      halfPlotBorderThickness = plotBorderThickness / 2;
    } else {
      plotBorderThickness = halfPlotBorderThickness = 0;
    }

    dx = chartX - pointObj.x + halfPlotBorderThickness;
    dy = chartY - yPos + halfPlotBorderThickness;

    hovered = dx >= 0 && dx <= width + plotBorderThickness && dy >= 0 && dy <= height + plotBorderThickness;
    return {
      pointIndex: pX,
      hovered: hovered,
      pointObj: pointObj,
      component: dataset
    };
  }

  /**
   * This method handles all mouse events of an dataset.
   * @param {string} eventType    name of the event
   * @param {number} plotIndex    index of the plot where this event has been occured
   * @param {Event}  e            reference of the original mouse event
   */
  _firePlotEvent (eventType, plotIndex, e) {
    let dataset = this,
      dataInfo = dataset.config.dataInfo,
      eventArgs = (dataInfo[plotIndex] && dataInfo[plotIndex].eventArgs) || {},
      chart = dataset.getFromEnv('chart'),
      coordiante = getMouseCoordinate(chart.getFromEnv('chart-container'), e, chart),
      validCoordinates = checkForValidCoordinated(coordiante),
      args = extend2(coordiante, eventArgs),
      env,
      linkClickFN,
      cancelHover = function (c, a) {
        e.FusionChartsPreventEvent = true;
        // Hack for IE as the global event object gets overwritten
        // causing the FusionChartsPreventEvent to be UNDEF.

        // todo - rectify this commented code.
        /* if (isIE && a.toolText && lib.toolTip) {
          lib.toolTip.preventTooltip();
        } */
      };
    if ((plotIndex !== UNDEF) && validCoordinates) {
      switch (eventType) {
        case 'fc-mouseover':
          chart.fireChartInstanceEvent(ROLLOVER, args, UNDEF, UNDEF, cancelHover);
          break;

        case 'fc-mouseout':
          chart.fireChartInstanceEvent(ROLLOUT, args);
          break;

        case 'fc-click':
        case 'touchend':
        case 'mouseup':
          chart.fireChartInstanceEvent(CLICK, args);
          // when chart is getting disposed need to check if env is present
          env = chart.getFromEnv();
          linkClickFN = env && env.linkClickFN;
          args.link && linkClickFN && linkClickFN.call({
            link: args.link
          }, true);
          break;
        default: break;
      }
    }
  }

  /**
   * Function to set hover in effect
   * @param {number} indices data indices
   * @param {Boolean} isHovered boolean value representing whether dataset is hovered
   */
  setHoverInEffect (indices, isHovered) {
    let dataset = this,
      chart = dataset.getFromEnv('chart'),
      dataInfo = dataset.config.dataInfo,
      eventArgs;
    dataset.getFromEnv('animationManager').setAnimationState('mouseOver');
    if (!isHovered) {
      indices.forEach((index) => {
        if (dataInfo[index]) {
          eventArgs = dataInfo[index].eventArgs;
          chart.fireChartInstanceEvent(HIGHLIGHT, eventArgs);
        }
      });
    }
    this.setData(isHovered ? this.getHoverInConfig(indices) : this.getHighlightConfig(indices), true);
  }

  /**
   * Function to set hover out effect
   * @param {number} indices data indices
   */
  setHoverOutEffect (indices) {
    this.getFromEnv('animationManager').setAnimationState('mouseOut');
    this.setData(this.getHoverOutConfig(indices), true);
  }
  /**
   * returns the data plot indices which falls within the range
   * @param {Object} range min and max value range
   * @returns {Array} array of data points
   */
  getHighlightedIndices (range) {
    let dataset = this,
      config = dataset.config,
      dataInfo = config.dataInfo,
      hightlightedPoints = [];
      // FTV3-1175: Highlighting plots falling inside the reference zone in range charts
      // Adding a check for ybase value of datapoint inside reference zone
    dataInfo.forEach((data, index) => {
      if (data.yBase <= range.minValue && data.y >= range.maxValue) {
        hightlightedPoints.push(index);
      }
    });
    return hightlightedPoints;
  }
  /**
   * This function is used to make a dataset visible when clicked on its respective legend.
   */
  show () {
    this.setData({
      'visibility': 'visible'
    }, true);
  }
  /**
   * This function is used to make a dataset hidden when clicked on its respective legend.
   */
  hide () {
    this.setData({
      'visibility': 'hidden'
    }, true);
  }

  isPlotPredictive (datum) {
    return datum.startDate >= this.config.prediction.dateMs;
  }

  parsePrediction (dataObject) {
    this.config.prediction = dataObject.prediction || this.config.prediction;
    this.parsePredictiveStyleAttributes();
  }

  parsePredictiveStyleAttributes () {
    const predictiveStyleAttributes = {},
      config = this.config;

    let predictiveStylePlot,
      predictiveStyleColumn;

    if (config.prediction.enabled) {
      predictiveStylePlot = config.prediction.style.plot;
      predictiveStyleColumn = config.prediction.style.column;

      // First copying all defaults
      Object.assign(predictiveStyleAttributes, config.defaultPredictedDataStyle);
      // Now overriding properties with properties provided in plot base
      Object.assign(predictiveStyleAttributes, predictiveStylePlot);
      // Now overriding properties with properties provided in column
      // A reason why column cannot be base class for all datasets
      Object.assign(predictiveStyleAttributes, predictiveStyleColumn);
    }

    predictiveStyleAttributes.predictiveOpacity = predictiveStyleAttributes.opacity;
    predictiveStyleAttributes.predictiveFillOpacity = predictiveStyleAttributes['fill-opacity'];
    predictiveStyleAttributes.predictiveStrokeOpacity = predictiveStyleAttributes['stroke-opacity'];

    delete predictiveStyleAttributes.opacity;
    delete predictiveStyleAttributes['fill-opacity'];
    delete predictiveStyleAttributes['stroke-opacity'];

    config.predictiveStyleAttributes = predictiveStyleAttributes || {};
  }
}

export default Column;
