import Axis from '../components/axis';
let UNDEF;

export default timeNav => {
  let baseTextStyle = timeNav.getFromEnv('baseTextStyle');

  timeNav.attachChild(Axis, 'timeAxis');
  timeNav.getChildren('timeAxis').forEach(axis => {
    axis.configure({
      skipinfo: {
        minor: true
      },
      overlap: true,
      validateLabelDimensions: 1,
      offset: {
        x: 2,
        y: 2
      },
      style: {
        'label-major': Object.assign({
          fill: '#5f5f5f',
          'font-size': 11,
          'text-anchor': 'start'
        }, baseTextStyle),
        'label-minor': Object.assign({
          'text-anchor': 'start'
        }, baseTextStyle),
        'label-context': Object.assign({
          'text-anchor': 'start'
        }, baseTextStyle)
      },
      ticksize: 0,
      domainline: false,
      align: 'top',
      dayinmajor: true,
      stickycontextlabel: false,
      scalemode: 'random',
      scale: timeNav.config.xConfigs[0].scale,
      tickvisibilitylimit: {
        start: UNDEF,
        end: UNDEF
      }
    });
  });
};
