import Legend from '@fusioncharts/charts/src/_internal/components/legend/discrete';
import { componentFactory, pluck, convertColor, pluckNumber } from '@fusioncharts/core/src/lib';

const COLOR_5F5F5F = '#5f5f5f',
  INHERIT = 'inherit';
export default chart => {
  let legend,
    showLegend = chart.config.showLegend,
    legendTextStyle = chart.config.mergedLegendStyle,
    legendFontColor = legendTextStyle.color = pluck(legendTextStyle.fill, COLOR_5F5F5F),
    legendTextOpacity = pluckNumber(legendTextStyle.opacity, 1),
    legendTextFillOpacity = pluckNumber(legendTextStyle['fill-opacity'], 1),
    styleObj = Object.assign({}, chart.getFromEnv('baseTextStyle'), legendTextStyle);

  // remove px from font-size since legend component of fc accepts font-size as a number
  styleObj['font-size'] = +((styleObj['font-size'] + '').replace(/px/, ''));
  if (showLegend) {
    // create an instance of legend and attach it as a children of chart
    componentFactory(chart, Legend, 'legend', 1, [{
    // static configuration for legend
      'drawcustomlegendicon': 1,
      'legendiconsides': 4,
      'alignlegendwithcanvas': 1,
      'legendborderthickness': 0,
      'legendiconscale': 1.3,
      'legendbgalpha': 0,
      legendFontColor,
      style: {
        text: styleObj
      }
    }]);
    legend = chart.getChildren('legend')[0];
    // add legend to the env of chart for other components to access
    chart.addToEnv('legend', legend);
    // set hover cosmetics for legend
    legend.setStateCosmetics('hover', function (cosmetics, legenditem) {
      // only applied the text hovered color only if text is not hidden.
      if (!legenditem.hasState('hidden')) {
        if (!cosmetics.text) {
          cosmetics.text = {};
        }
        // set the text color and cursor.
        cosmetics.text.fill = convertColor(legendFontColor, (legendTextOpacity * legendTextFillOpacity) * 100);
        cosmetics.text.cursor = INHERIT;
      }
      return cosmetics;
    });
    const createLegend = (info = {}) => {
      for (const key in info) {
        if (info.hasOwnProperty(key)) {
          const itemInfo = info[key];
          if (itemInfo.hasOwnProperty('visibility')) {
            chart._addLegend(itemInfo);
          } else {
            createLegend(itemInfo);
          }
        }
      }
    };
    createLegend(chart.getFromEnv('legendMap'));
  } else if (legend = (chart.getChildren('legend') && chart.getChildren('legend')[0])) {
    legend.remove();
  }
};
