import Crossline from '../components/crossline';
import { pluckNumber } from '@fusioncharts/core/src/lib';
export default canvas => {
  let dataSource = canvas.getFromEnv('dataSource'),
    chartAttr = canvas.getFromEnv('chart-attrib');
  canvas.attachChild(Crossline, 'horizontalCrossLine')
    .configure({
      type: 'horizontal',
      inputStyle: (dataSource.crossline && dataSource.crossline.style) || {},
      showTextOutline: pluckNumber(chartAttr.textoutline, 0)
    });
};
