/**
 * This file helps in the bucketting of a given time into various slots
 */
import pads from './time-converter/pads';

const dashToLast = (a, b) => a === '-' ? 1 : (b === '-' ? -1 : 0),
  BLANK = '',
  VALID_PAD = `%[${Object.keys(pads).sort(dashToLast).join('')}]?`,
  /**
   * Given a token string, creates a RegEx from it which matches all valid
   * time padding modifiers.
   * @param {String} token The time specifier token whcih needs to be converted
   *                       into a regular expression
   * @return A regular expression which matches any valid padded or non-padded
   *         time specifier token
   */
  regexCreator = token => new RegExp(`${VALID_PAD}${token}`),
  /**
   * Accepts an object and returns an object of a similar structure except that
   * the token strings within it are changed to RegExes.
   * @param {Object} obj An object mapping time interval names to applicable
   *                     time specifier tokens
   * @return {Object} An object mapping time interval names to applicable
   *                  time specifier tokens regular expressions
   */
  regexify = obj => {
    const regexedObj = {};
    let interval = BLANK;

    for (interval in obj) {
      if (obj.hasOwnProperty(interval)) {
        regexedObj[interval] = obj[interval].map(regexCreator);
      }
    }

    return regexedObj;
  };

let timeOrderArr = ['millisecond', 'second', 'minute', 'hour', 'day', 'month', 'year'],
  timeOrderObj = {
    'millisecond': 0,
    'second': 1,
    'minute': 2,
    'hour': 3,
    'day': 4,
    'month': 5,
    'year': 6
  },
  // Here hour and minute are given same preference because if aggregation is in
  // minutes level then to show minute
  // This makes sense 12:00
  // And not 12
  minimumPlaceHolderObj = {
    'millisecond': 0,
    'second': 1,
    'minute': 2,
    'hour': 2,
    'day': 4,
    'month': 5,
    'year': 6
  },
  // Map a supported time interval name to all of its possible format specifier
  // token regexes
  placeHolderMap = regexify({
    millisecond: ['L', 'Q'],
    second: ['S', 's'],
    minute: ['M'],
    hour: ['H', 'I', 'p'],
    day: ['d', 'e', 'j', 'a', 'A'],
    month: ['b', 'B', 'm'],
    year: ['y', 'Y']
  }),
  len = timeOrderArr.length;
/**
 * Function to get the minimum timeStamp in a given string of time placeholders
 * @param {String} timeString Conatins time placeholders
 */
function getMinPlaceHolder (timeString = '') {
  let i,
    j,
    currentPlaceHolder,
    currentPlaceHolderLen;
  // Running loop from minimum timeplaceholder to maximum
  for (i = 0; i < len; i++) {
    currentPlaceHolder = placeHolderMap[timeOrderArr[i]];
    currentPlaceHolderLen = currentPlaceHolder.length;
    for (j = 0; j < currentPlaceHolderLen; j++) {
      if (timeString.match(currentPlaceHolder[j])) {
        return timeOrderArr[i];
      }
    }
  }
}

/**
 * Function to get the index of timeStamp in increasing order
 * @param {String} placeHolder conatains the timeStamp
 */
function getPlaceHolderIndex (placeHolder) {
  return timeOrderObj[placeHolder];
}

/**
 * Function to filter out the non applicable placeholder
 * (that is the placeholder larger than the minimumPlaceHolder)
 * from the given format
 * @param {String} _format conatins mix and match of placeHolders
 * @param {String} minimumPlaceHolder the minimum placeHolder
 */
function getFilterdTimeFormat (_format = '', minimumPlaceHolder) {
  let upto = minimumPlaceHolderObj[minimumPlaceHolder],
    currentPlaceHolder,
    format = _format,
    callBack = element => {
      format = format.replace(element, '');
    },
    i;
  // Removing all the non needed timeformatters
  for (i = 0; i < upto; i++) {
    currentPlaceHolder = placeHolderMap[timeOrderArr[i]];
    currentPlaceHolder.forEach(callBack);
  }
  // Removing (:,) at the end of the string or (,) at the start of the string, if any
  return format.replace(/(\W+$)/, '').replace(/^,/, '');
}

export { getMinPlaceHolder, getPlaceHolderIndex, getFilterdTimeFormat };
