let pinPathStr = 'M12.0047766,4 L12.2491793,4.00380682 C16.5521622,4.13545713 20,7.66508574 20,12 C20,16.418278 16.418278,20 12,20 C7.581722,20 4,16.418278 4,12 C4,11.9105612 4.0014677,11.8214651 4.00438034,11.7327347 L4,4 L12,4 L12.0047766,4 Z';
/**
 * Function to draw the FlagMarkers
*/
export default (marker, index, markerDimensions) => {
  let config = marker.config,
    markerDetails = config.markerInfo[index],
    data = config.dataInfo[index],
    markerStyle = marker.getStyleInformation(index),
    pinY,
    pinX,
    currMarker,
    stroke,
    fill,
    id,
    attrObj,
    markerOpacity,
    textOpacity,
    multipleMarkers,
    { pinHeight, pinWidth } = markerDimensions;

  multipleMarkers = markerDetails.multipleMarkers;
  id = markerDetails.id;
  pinY = data.y - (pinHeight + 2);
  pinX = data.x + ((data.width || 0) / 2) - (pinWidth / 2);

  // If multiple markers are in a single index then no style customization can be applied
  if (!multipleMarkers) {
    currMarker = markerDetails.markers[0];
  }
  markerOpacity = markerStyle.marker.opacity;
  textOpacity = markerStyle.text.opacity;

  // Creating the of the pin marker
  attrObj = {
    path: pinPathStr,
    transform: `t${pinX},${pinY}r225`,
    opacity: markerOpacity
  };
  // Applying the style only when it is provided
  stroke && (attrObj.stroke = stroke);
  fill && (attrObj.fill = fill);
  marker.addGraphicalElement({
    el: 'path',
    container: {
      label: 'group-pin'
    },
    id,
    attr: attrObj,
    css: markerStyle.marker,
    label: 'pinMarker'
  }, true);

  // Drawing the text if multiple markers are present
  if (multipleMarkers || currMarker.identifier) {
    attrObj = {
      text: multipleMarkers ? markerDetails.markers.length : currMarker.identifier.charAt(0),
      x: pinX + (pinWidth / 2),
      y: pinY + (pinHeight / 2) + 2,
      opacity: textOpacity
    };
    marker.addGraphicalElement({
      el: 'text',
      container: {
        label: 'group-pin'
      },
      id,
      attr: attrObj,
      css: markerStyle.text,
      label: 'markerText'
    }, true);
  }
};
