import {
  timeMillisecond,
  timeSecond,
  timeMinute,
  timeHour,
  timeDay,
  timeWeek,
  timeMonth,
  timeYear
} from '../time-intervals';
import ScaleCalendarBin, { copyScaleBin } from './calendar-bin';
import { TIME_SPAN } from '../defaults/time';

class ScaleTimeBin extends ScaleCalendarBin {
  constructor () {
    super(
      timeYear,
      timeMonth,
      timeWeek,
      timeDay,
      timeHour,
      timeMinute,
      timeSecond,
      timeMillisecond
    );
    this.setTimeFormat();
    this.setDomain([...TIME_SPAN]);
  }

  getType () {
    return '';
  }

  /**
   * sets the time formatter strings
   *
   * @param {*} [format={}] Object containing format string
   * @memberof ScaleTimeBin
   */
  setTimeFormat (format = {}) {
    let localeConverter = this._localeConverter,
      { millisecond, second, minute, hour, day, month, year } = format;
    this.formatters = {
      millisecond: {
        major: localeConverter.formatter(millisecond || '%I:%M:%S.%L %p'),
        minor: localeConverter.formatter(millisecond || '%L ms'),
        context: localeConverter.formatter(millisecond || '%b %d, %Y, %I:%M:%S.%L %p')
      },
      second: {
        major: localeConverter.formatter(second || '%I:%M:%S %p'),
        minor: localeConverter.formatter(second || '%S s'),
        context: localeConverter.formatter(second || '%b %d, %Y, %I:%M:%S %p')
      },
      minute: {
        major: localeConverter.formatter(minute || '%I:%M %p'),
        minor: localeConverter.formatter(minute || '%M m'),
        context: localeConverter.formatter(minute || '%b %d, %Y, %I:%M %p')
      },
      hour: {
        major: localeConverter.formatter(hour || '%I %p'),
        minor: localeConverter.formatter(hour || '%I %p'),
        context: localeConverter.formatter(hour || '%b %d, %Y, %I %p')
      },
      day: {
        major: localeConverter.formatter(day || '%b %d'),
        minor: localeConverter.formatter(day || '%d'),
        context: localeConverter.formatter(day || '%b %d, %Y')
      },
      month: {
        major: localeConverter.formatter(month || '%b'),
        minor: localeConverter.formatter(month || '%b'),
        context: localeConverter.formatter(month || '%b %Y')
      },
      year: {
        major: localeConverter.formatter(year || '%Y'),
        minor: localeConverter.formatter(year || '%Y'),
        context: localeConverter.formatter(year || '%Y')
      }
    };
  }
  setTimeInterval (timeIntervals) {
    this.timeIntervals = timeIntervals;
    super.setTimeInterval(timeIntervals);
  }
  setUnclippedTimeIntervals (unclippedTimeIntervals) {
    this.unclippedTimeIntervals = unclippedTimeIntervals;
    super.setUnclippedTimeIntervals(unclippedTimeIntervals);
  }
  getTimeInterval () {
    return this.timeIntervals;
  }
  getUnclippedTimeIntervals () {
    return this.unclippedTimeIntervals;
  }
  /**
   * formats the date as per specified formatter strings
   *
   * @param {any} specifier specifier string
   * @returns {function} instance of timeConverter
   * @memberof ScaleTimeBin
   */
  tickFormat (specifier) {
    return specifier
      ? d => this._localeConverter.formatter(specifier).format(d)
      : (d, type, tickType) => {
      /**
       * if scale provides with a formatter string, use it to format the given date
       * else use from the default formatters.
       */
        if (this._timeFormat[type]) {
          return this._localeConverter.formatter(this._timeFormat[type]).format(d);
        } return this.formatters[type][tickType].format(d);
      };
  }

  copy () {
    return copyScaleBin(this, new ScaleTimeBin());
  }
}

export default ScaleTimeBin;
