import flatMap from 'ramda/es/chain';
import merge from 'ramda/es/mergeDeepRight';

import extent from '../array/extent';
import safeMax from '../array/safe-max';
import safeMin from '../array/safe-min';
import constant from '../scale-utils/constant';
import toPairs from '../scale-utils/array/pair';
import AreaGenerator from '../shape-generators/area-generator';
import LineGenerator from '../shape-generators/line-generator';

const BLANK = '',
  // Determines if the first element in the input array is the string 'undef'
  isDefinedChunk = ([chunkName]) => chunkName !== 'undef',
  // Extracts the clipRect objects from a deeply nested array of objects
  extractClipRects = flatMap(([name, { clipRects }]) => clipRects),
  byX = (a, b) => a.x - b.x,
  // Computes the rectangle between two input rectangles which do not have any
  // horizontal overlap
  clipRectBetween = ([l, r]) => ({
    x: l.x + l.width,
    y: safeMin([l.y, r.y, l.y + l.height, r.y + r.height]),
    width: r.x - (l.x + l.width),
    height:
      safeMax([l.y, r.y, l.y + l.height, r.y + r.height]) -
      safeMin([l.y, r.y, l.y + l.height, r.y + r.height])
  }),
  validRects = rect => !(rect.width < 1 || rect.height < 1),
  // Removes zero width clip rect objects from a chunk
  removeInvalidClipRects = ([k, v]) => [
    k,
    Object.assign(v, {
      clipRects: v.clipRects.filter(validRects)
    })
  ],
  DEFAULT_CHUNK_DECIDER = constant('def'),
  // Resolves which chunk an input data point should belong to
  CHUNK_RESOLVER = (chunkNameLeft, chunkNameRight, chunkNames) => {
    return chunkNames.indexOf(chunkNameLeft) >
      chunkNames.indexOf(chunkNameRight)
      ? chunkNameLeft
      : chunkNameRight;
  },
  DEFAULT_CHUNKS = {
    def: {},
    undef: {}
  };

/**
 * A Chunker is able to accept an array of data points and chunk them into
 * separate buckets based on whether the data point is defined. It can also
 * chunk data points into arbitrary named buckets based on some user defined
 * conditions.
 */
class Chunker {
  /**
   * @param {Function} Generator The generator which will be used to generate
   * path strings for the input data.
   */
  constructor (Generator = AreaGenerator) {
    this.generator = new Generator();

    this.defined = constant(true);

    this.chunkDecider = DEFAULT_CHUNK_DECIDER;

    this.chunks = DEFAULT_CHUNKS;

    this.connectUndefineds = false;
  }

  /**
   * Generates named chunks from the input data points based on whether they are
   * defined and based on defined predicates.
   *
   * @param {any[]} data An array of data points which need to be processed.
   *
   * @returns {Object} Always contains atleast two named chunks, named 'def' and
   * 'undef'. Additional named chunks may be present based on user defined
   * conditions. Each named chunk contains two properties - a string called
   * 'path' and an array of objects containing x, y, width and height called
   * 'clipRects'.
   */
  generate (data) {
    const chunks = this._getChunks(),
      definedChunks = Object.entries(chunks).filter(isDefinedChunk),
      defined = this.getDefined(),
      filteredData = data.filter(defined),
      connectUndefineds = this.getConnectUndefineds(),
      definedSegments = this._computeDefinedSegments(
        connectUndefineds ? data : filteredData
      ),
      x = this.getXAccessor(),
      y = this.getYAccessor(),
      yBase = this.getYBaseAccessor(),
      // Converts a series of data segments into rectangle objects based on
      // their x and y extents
      clipGenerator = (segment, { clipAdjustment = 0 }) => {
        const [yMin, yMax] = extent(segment, y),
          [xMin, xMax] = extent(segment, x),
          [yBaseMin, yBaseMax] = extent(segment, yBase),
          [clipX1, clipX2] = extent([xMin, xMax]),
          [clipY1, clipY2] = extent([yMin, yMax, yBaseMin, yBaseMax]),
          clipY = clipY1 - clipAdjustment;

        return {
          x: clipX1,
          y: clipY,
          // TODO: Probably unnecessary Math.abs. Verify and remove.
          width: Math.abs(clipX2 - clipX1),
          height: Math.abs((clipY2 + clipAdjustment) - clipY)
        };
      };

    let path = BLANK,
      definedChunkSegments = [],
      undefinedChunkSegements = [];

    if (connectUndefineds) {
      // If undefined points need to be connected, generate the path after
      // filtering out the null data.
      path = this.generator.generate(filteredData);
    } else {
      // Otherwise, generate the path by setting the defined function and
      // generating the path from the unfiltered data.
      path = this.generator.setDefined(defined).generate(data);
    }

    // create paths and clip rects for all the defined segments
    definedChunkSegments = definedChunks.map(([chunkName, chunkDefinition]) => [
      chunkName,
      {
        path,
        clipRects: this._computeSegmentChunks(chunkName, definedSegments)
          .map(segment => clipGenerator(segment, chunkDefinition))
      }
    ]);

    undefinedChunkSegements = connectUndefineds
      ? toPairs(extractClipRects(definedChunkSegments).sort(byX))
        .map(clipRectBetween)
        .filter(validRects)
      : [];

    // Create paths and clip rects for all the undefined segments and add them
    // to the other segment definitions
    return Object.fromEntries(
      definedChunkSegments.map(removeInvalidClipRects).concat([
        [
          'undef',
          {
            path:
              connectUndefineds && undefinedChunkSegements.length > 0
                ? path
                : '',
            clipRects: undefinedChunkSegements
          }
        ]
      ])
    );
  }

  /**
   * Sets the input accessor function as the x accessor. The x accessor is used
   * to determine how to compute x values when generating chunks.
   *
   * @param {function} accessor Defines how to compute x values when generating
   * chunks.
   *
   * @returns {object} An instance of the current Chunker
   */
  setXAccessor (accessor) {
    this.generator.setXAccessor(accessor);

    return this;
  }

  /**
   * @returns {function} The x accesssor of the chunker
   */
  getXAccessor () {
    return this.generator.getXAccessor();
  }

  /**
   * Sets the input accessor function as the y accessor. The y accessor is used
   * to determine how to compute y values when generating chunks.
   *
   * @param {function} accessor Defines how to compute y values when generating
   * chunks.
   *
   * @returns {object} An instance of the current Chunker
   */
  setYAccessor (accessor) {
    this.generator.setYAccessor(accessor);

    return this;
  }

  /**
   * @returns {function} The y accesssor of the chunker
   */
  getYAccessor () {
    return this.generator.getYAccessor();
  }

  /**
   * If the chunker is created using an AreaGenerator, sets the input accessor
   * function as the y accessor for the top line of an area chunk. The y
   * accessor is used to determine how to compute y values when generating
   * chunks.
   * If the chunker is created using a LineGenerator, this method sets the y
   * accessor of the chunker instead since a line does not have any notion of a
   * y top.
   *
   * @param {function} accessor Defines how to compute y values when generating
   * chunks.
   *
   * @returns {object} An instance of the current Chunker
   */
  setYTopAccessor (accessor) {
    this.generator.setYTopAccessor
      ? this.generator.setYTopAccessor(accessor)
      : this.generator.setYAccessor(accessor);

    return this;
  }

  /**
   * @returns {function} The y top accesssor of the chunker
   */
  getYTopAccessor () {
    return this.generator.getYTopAccessor
      ? this.generator.getYTopAccessor()
      : this.generator.getYAccessor();
  }

  /**
   * If the chunker is created using an AreaGenerator, sets the input accessor
   * function as the y accessor for the bottom line of an area chunk. The y
   * accessor is used to determine how to compute y values when generating
   * chunks.
   * If the chunker is created using a LineGenerator, this method sets the y
   * accessor of the chunker instead since a line does not have any notion of a
   * y top.
   *
   * @param {function} accessor Defines how to compute y values when generating
   * chunks.
   *
   * @returns {object} An instance of the current Chunker
   */
  setYBaseAccessor (accessor) {
    this.generator.setYBaseAccessor
      ? this.generator.setYBaseAccessor(accessor)
      : this.generator.setYAccessor(accessor);

    return this;
  }

  /**
   * @returns {function} The y base accesssor of the chunker
   */
  getYBaseAccessor () {
    return this.generator.getYBaseAccessor
      ? this.generator.getYBaseAccessor()
      : this.generator.getYAccessor();
  }

  /**
   * Sets the defined function for the chunked which determines whether the
   * chunker should consider a point to be undefined or not.
   *
   * @param {function} defined Dictates whether a data point should be
   * considered as a defined point.
   *
   * @returns An instance of the current Chunker
   */
  setDefined (defined = constant(true)) {
    this.defined =
      typeof defined === 'function' ? defined : constant(!!defined);

    return this;
  }

  /**
   * @returns The defined function of the chunker
   */
  getDefined () {
    return this.defined;
  }

  /**
   * Sets the Curve factory which will be used to generate a path string.
   *
   * @param {function} Curve The Curve factory which will be used to generate a
   * path string
   *
   * @returns An instance of the current Chunker
   */
  setCurve (Curve) {
    this.generator.setCurve(Curve);

    return this;
  }

  /**
   * @returns The Curve factory of the chunker
   */
  getCurve () {
    return this.generator.getCurve();
  }

  /**
   * @returns A new Line Chunker with all the properties of the current chunker
   * which has its X accessor set to the chunker's X top accessor and its Y
   * accessor set to the chunker's Y top accessor.
   */
  getLineChunkerYTop () {
    return this._areaLineChunker()
      .setXAccessor(this.getXAccessor())
      .setYAccessor(this.getYTopAccessor());
  }

  /**
   * @returns A new Line Chunker with all the properties of the current chunker
   * which has its X accessor set to the chunker's X top accessor and its Y
   * accessor set to the chunker's Y top accessor.
   */
  getLineChunkerYBase () {
    return this._areaLineChunker()
      .setXAccessor(this.getXAccessor())
      .setYAccessor(this.getYBaseAccessor());
  }

  /**
   * Sets the function which decides the chunk name for a data point
   *
   * @param {function} decider When given a data point, the decider decides the
   * name of the chunk to which the data belongs. The returned name MUST
   * correspond to a name defined in the chunk definitions. If a name is not
   * found, it will be assumed to belong to the 'def' chunk.
   *
   * @returns An instance of the current Chunker
   */
  setChunkDecider (decider = DEFAULT_CHUNK_DECIDER) {
    this.chunkDecider = decider;

    return this;
  }

  /**
   * @returns The chunk decider of the chunker.
   */
  getChunkDecider () {
    return this.chunkDecider;
  }

  /**
   * Sets the chunk definitions of the Chunker. The chunk definitions are used
   * to validate the decisions of the chunk decides and to determine what chunks
   * to create.
   *
   * @param {object} chunks An object containing the names of the chunks as keys
   * and their configuration as the value.
   *
   * @returns An instance of the Chunker
   */
  setChunks (chunks = DEFAULT_CHUNKS) {
    this._userChunks = Object.assign({}, chunks);
    this.chunks = merge(chunks, DEFAULT_CHUNKS);

    return this;
  }

  /**
   * @returns {object} The user defined chunk definitions of the Chunker.
   */
  getChunks () {
    return this._userChunks || this.chunks;
  }

  /**
   * @returns {object} The user defiend chunk definitions merged with the
   * default chunks of the Chunker.
   */
  _getChunks () {
    return this.chunks;
  }

  /**
   * Sets whether or not to connect two chunks when there is an undefined chunk
   * between them.
   *
   * @param {boolean} [connectUndefineds=false] Indicates whether the chunker
   * should create connected paths across undefined chunks or whether the paths
   * should be separated across undefined chunks.
   *
   * @returns An instance of the Chunker
   */
  setConnectUndefineds (connectUndefineds = false) {
    this.connectUndefineds = connectUndefineds;

    return this;
  }

  /**
   * @returns {boolean} Whether the chunker connects chunks across undefined
   * chunks or not.
   */
  getConnectUndefineds () {
    return this.connectUndefineds;
  }

  /**
   * Helper method to instantiate a chunker with a line generator which has all
   * the properties of the current chunker except the x and y accessors.
   */
  _areaLineChunker () {
    return new Chunker(LineGenerator)
      .setChunkDecider(this.getChunkDecider())
      .setChunks(this.getChunks())
      .setConnectUndefineds(this.getConnectUndefineds())
      .setCurve(this.getCurve())
      .setDefined(this.getDefined());
  }

  /**
   * Given an array of data points, this method separates each continuous
   * section of the defined data points into individual chunks.
   *
   * @param {any[]} data The array of data points whose defined segments need to
   * be separated into chunks.
   *
   * @returns {any[]} An array which contains individual sections of continuous,
   * defined data points.
   *
   * @example
   * // Returns [[1, 2], [3, 4], [5]]
   * chunker._computeDefinedSegments([1, 2, null, 3, 4, null, 5]);
   */
  _computeDefinedSegments (data) {
    let startNewSegment = true;

    // split into segments of continuous data
    return data.reduce((acc, d) => {
      // skip if this point has no data
      if (!this.defined(d)) {
        startNewSegment = true;
        return acc;
      }

      if (startNewSegment) {
        // starting a new segment with this point
        acc.push([d]);
        startNewSegment = false;
      } else {
        // add to the last segment
        const lastSegment = acc[acc.length - 1];
        lastSegment.push(d);
      }

      return acc;
    }, []);
  }

  /**
   * Further splits the defined chunk into sub-chunks based on the available
   * chunk definitions and the available chunk decider function.
   *
   * @param {string} chunkName The name of the chunk to be computed
   * @param {any[]} segments An array containting chunks of data split based on
   * the chunk decider function.
   *
   * @returns {any[]} An array of sub-chunks
   *
   * @example
   * // Returns [[1, 3, 5], [7, 8, 9]]
   * chunker._computeSegmentChunks('morethan5', [1, 3, 5, 7, 8, 9])
   */
  _computeSegmentChunks (chunkName, segments) {
    const chunkNames = Object.keys(this.chunks);

    return segments.reduce((segmentChunks, segment) => {
      let startNewSegment = true;

      const chunks = segment.reduce((acc, d, i) => {
        const dataChunkName = this.getChunkDecider()(d),
          dataPrev = segment[i - 1],
          dataNext = segment[i + 1];

        if (dataChunkName === chunkName) {
          if (startNewSegment) {
            // if we are starting a new segment, start it with this point
            acc.push([d]);
            startNewSegment = false;
          } else {
            // otherwise add to the last segment
            acc[acc.length - 1].push(d);
          }
        } else {
          // check if this point belongs in the previous chunk:
          let added = false;
          // doesn't match chunk name, but does it go in the segment? as the
          // end?
          if (dataPrev != null) {
            const segmentChunkName = CHUNK_RESOLVER(
              this.getChunkDecider()(dataPrev),
              dataChunkName,
              chunkNames
            );

            // if it is supposed to be in this chunk, add it in
            if (segmentChunkName === chunkName) {
              if (startNewSegment) {
                // if we are starting a new segment, start it with this point
                acc.push([d]);
                startNewSegment = false;
              } else {
                // otherwise add to the last segment
                acc[acc.length - 1].push(d);
              }

              added = true;
              startNewSegment = false;
            }
          }

          // doesn't belong in previous, so does it belong in next?
          if (!added && dataNext != null) {
            // check if this point belongs in the next chunk
            const nextSegmentChunkName = CHUNK_RESOLVER(
              dataChunkName,
              this.getChunkDecider()(dataNext),
              chunkNames
            );

            // if it's supposed to be in the next chunk, create it
            if (nextSegmentChunkName === chunkName) {
              acc.push([d]);
              added = true;
              startNewSegment = false;
            } else {
              startNewSegment = true;
            }

            // not previous or next
          } else if (!added) {
            startNewSegment = true;
          }
        }

        return acc;
      }, []);

      if (chunks && chunks.length) {
        return segmentChunks.concat(chunks);
      }

      return segmentChunks;
    }, []);
  }
}

export default Chunker;
