import TimeSpanMinMarker from './time-span-min-marker';

let UNDEF;
class TimeSpanFullMarker extends TimeSpanMinMarker {
  setHoverInEffect (index, domainIndex) {
    let timeMarker = this,
      chart = timeMarker.getFromEnv('chart');
    timeMarker.setData({ hoveredIndex: index, hoveredDomainIndex: domainIndex }, true);
    chart.fireEvent('timeSpanFullMarkerHovered', {
      senderTimeMarker: timeMarker,
      hoveredIndex: index,
      hoveredFromOutside: true
    });
  }
  setHoverOutEffect () {
    let timeMarker = this,
      config = timeMarker.config,
      chart = timeMarker.getFromEnv('chart');
    timeMarker.setData({
      hoveredIndex: UNDEF,
      lastHoveredMarkerIndex: config.hoveredIndex,
      lastHoveredDomainIndex: config.hoveredDomainIndex
    }, true);
    chart.fireEvent('timeSpanFullMarkerHovered', {
      senderTimeMarker: timeMarker,
      hoveredIndex: UNDEF,
      hoveredFromOutside: true
    });
  }
  createGroup () {
    let timeMarker = this,
      canvasConfig = timeMarker.getLinkedParent().config,
      padding = canvasConfig.padding,
      canvasLeft = canvasConfig.canvasBGLeft - padding.left,
      canvasTop = canvasConfig.canvasBGTop - padding.top,
      canvasWidth = canvasConfig.canvasBGWidth,
      canvasHeight = canvasConfig.canvasBGHeight,
      clipPath = [`M${canvasLeft},${canvasTop}`, `L${canvasLeft + canvasWidth},${canvasTop}`, `L${canvasLeft + canvasWidth},${canvasTop + canvasHeight}`, `L${canvasLeft},${canvasTop + canvasHeight}Z`];
    timeMarker.addGraphicalElement({
      el: 'group',
      container: {
        id: 'strato',
        label: 'group',
        isParent: true
      },
      component: timeMarker,
      label: 'timeMarker',
      attr: {
        name: 'time-marker-full-group',
        'clip-path': clipPath
      },
      id: 'timeMarker'
    });
  }
  getName () {
    return 'timeSpanFullMarker';
  }
}
export default TimeSpanFullMarker;
