import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { getTouchEvent, toRaphaelColor } from '@fusioncharts/core/src/lib';

const COLOR_FDFDFD = '#fdfdfd',
  COLOR_F7FFFE = '#f7fffe',
  COLOR_E3E3E3 = '#e3e3e3',
  UP = 'up',
  DOWN = 'down',
  M = 'M',
  v = 'v',
  h = 'h',
  NONE = 'none',
  DRAGSTART = 'timeNavBrushStart',
  DRAGEND = 'timeNavBrushEnd',
  DRAG = 'timeNavBrush',
  LABELFORMAT = '%b %d, %Y',
  TRANSLATE = 'translate',
  STROKE_ATTR_LIST = [
    'stroke',
    'stroke-width',
    'stroke-dasharray',
    'stroke-dashoffset',
    'stroke-linecap',
    'stroke-linejoin',
    'stroke-miterlimit',
    'stroke-opacity',
    'stroke-color'
  ];

class Selection extends SmartRenderer {
  constructor () {
    super();
    this._dimensions = {};
  }
  __setDefaultConfig () {
    super.__setDefaultConfig();
    this.config.defaultStyle = {
      'fill': toRaphaelColor({
        FCcolor: {
          color: COLOR_FDFDFD + ',' + COLOR_F7FFFE,
          ratio: '0,100',
          angle: 90,
          alpha: '50,50'
        }
      }),
      'stroke': COLOR_E3E3E3,
      'stroke-width': 1,
      'opacity': 1,
      'fill-opacity': 0.5,
      'cursor': 'move'
    };
  }
  configureAttributes (obj = {}) {
    super.configureAttributes(obj);
    let selection = this,
      selectionConfig = selection.config,
      defaultStyle = selectionConfig.defaultStyle;

    selectionConfig.style = Object.assign({}, defaultStyle, obj.style.mask);
    selectionConfig.orientation = obj.orientation;
  }
  /**
   * Method that sets the dimension of selection component
   * @param {Object} dim dimension
   */
  setDimension (dim) {
    this._dimensions = dim;
  }
  /**
   * Method to get the current dimension of selection component
   * @return {Object} dimension
   */
  getDimension () {
    return this._dimensions;
  }
  /**
   * Method that attaches respective handlers on selction component
   */
  attachHandlers () {
    let selection = this,
      animationManager = selection.getFromEnv('animationManager'),
      brush = selection.getLinkedParent(),
      timeNav = brush.getFromEnv('timeNavigator'),
      timeNavConfig = timeNav.config,
      scale = timeNavConfig.contextScale,
      chart = selection.getFromEnv('chart'),
      currFocusLimit,
      brushDim,
      startX,
      curPx,
      clampDirection,
      offsetX,
      startPx,
      endPx,
      options = {
        timeFormatter: timeNavConfig.formatter
      },
      forceFireOptions = Object.assign({ forceFire: true }, options),
      eventArgs = {
        formatter: LABELFORMAT,
        action: TRANSLATE
      };
    // attach dragstart event.
    selection.addEventListener('fc-dragstart', selection.dragstartHandler || (selection.dragstartHandler = function (e) {
      startX = getTouchEvent(e).clientX;
      curPx = 0;
      brushDim = brush.getCurrentBrushDimensions();
      currFocusLimit = chart.getFocusLimit();
      chart.getFromEnv('fireChartEvents')(DRAGSTART, [+currFocusLimit[0], +currFocusLimit[1]], eventArgs, forceFireOptions);
    }));
    // attach dragmove event.
    selection.addEventListener('fc-dragmove', selection.dragmoveHandler || (selection.dragmoveHandler = function (e) {
      animationManager.setAnimationState('timenavScroll');
      offsetX = getTouchEvent(e).clientX - startX;
      clampDirection = curPx >= offsetX ? DOWN : UP;
      curPx = offsetX;
      startPx = brushDim.x + offsetX;
      endPx = startPx + brushDim.width;
      chart.setPixelLimit(
        [startPx, endPx],
        scale,
        [clampDirection, clampDirection],
        {
          name: DRAG,
          eventArgs,
          options
        }
      );
    }));
    // attach dragend event.
    selection.addEventListener('fc-dragend', selection.dragendHandler || (selection.dragendHandler = function (e) {
      currFocusLimit = chart.getFocusLimit();
      chart.getFromEnv('fireChartEvents')(DRAGEND, [+currFocusLimit[0], +currFocusLimit[1]], eventArgs, forceFireOptions);
    }));
  }
  draw () {
    let selection = this,
      selfConfig = selection.config,
      selectionStyle = Object.assign({}, selfConfig.style),
      selectionBorderStyle = Object.assign({}, selfConfig.style),
      i,
      len = STROKE_ATTR_LIST.length,
      selectionDimensions = selection.getDimension();
      // @todo- mind the orientation

    // remove all stroke related attributes from css of rect
    for (i = 0; i < len; i++) {
      delete selectionStyle[STROKE_ATTR_LIST[i]];
    }
    // selection element rect.
    selection.addGraphicalElement({
      el: 'rect',
      attr: {
        x: selectionDimensions.x,
        y: selectionDimensions.y,
        width: Math.max(selectionDimensions.width, 0),
        height: Math.max(selectionDimensions.height, 0),
        opacity: selectionStyle.opacity,
        stroke: 'none'
      },
      css: selectionStyle,
      id: 'selectionBox',
      label: 'selectionBox',
      component: selection,
      container: {
        id: 'brushGroup',
        label: 'brushGroup',
        isParent: true
      }
    });

    // border of selection border should not have any fill
    selectionBorderStyle.fill = NONE;
    // selection element border.
    selection.addGraphicalElement({
      el: 'path',
      attr: {
        path: [M, selectionDimensions.x, selectionDimensions.y,
          v, selectionDimensions.height,
          h, selectionDimensions.width,
          v, -selectionDimensions.height],
        opacity: selectionBorderStyle.opacity
      },
      id: 'selectionBoxBorder',
      label: 'selectionBoxBorder',
      css: selectionBorderStyle,
      component: selection,
      container: {
        id: 'brushGroup',
        label: 'brushGroup',
        isParent: true
      }
    });
  }
}

export default Selection;
