function getPathValues (path, hPresent) {
  let regExp = /M(.*)V(.*)/g,
    parsed, MXY, VY, MX, MY, HX;
  hPresent && (regExp = /M(.*)H(.*)/g);
  parsed = regExp.exec(path);
  MXY = parsed[1];
  (hPresent && (HX = parsed[2])) || (VY = parsed[2]);
  MX = parseFloat(MXY.split(',')[0]);
  MY = parseFloat(MXY.split(',')[1]);
  VY = parseFloat(VY);
  HX = parseFloat(HX);
  if (hPresent) {
    return {
      MX, MY, HX
    };
  };
  return {
    MX, MY, VY
  };
}

function getStartEndByIndex (index, total) {
  let start,
    timesegment = 0.00002,
    offset = timesegment * index;
  start = offset;
  return {
    start,
    end: 1
  };
}

export default {
  'initial.dataset.timeseries-ohlc': function () {
    const component = this,
      yScale = component.getFromEnv('yScale'),
      yBasePos = yScale.getRangeValue(0);
    let lastIndex = -1, lastValue = null,
      getStartEndByIndexCached = (index, total) => {
        if (index === lastIndex) {
          return lastValue;
        }
        let val = getStartEndByIndex(index, total);
        lastIndex = index;
        lastValue = val;
        return val;
      };
    return {
      'highlow.appearing': function (inputJSON) {
        const pathValues = getPathValues(inputJSON.attr.path),
          index = inputJSON.props.index,
          dataLength = inputJSON.props.dataLength;
        return [
          {
            initialAttr: {
              path: `M${pathValues.MX},${yBasePos}V${pathValues.VY + (yBasePos - pathValues.MY)}`
            },
            startEnd: getStartEndByIndexCached(index, dataLength),
            effect: 'easeInOutQuart'
          }
        ];
      },
      'open.appearing': function (inputJSON) {
        const pathValues = getPathValues(inputJSON.attr.path, true),
          index = inputJSON.props.index,
          dataLength = inputJSON.props.dataLength;
        // return null;
        return [
          {
            initialAttr: {
              path: `M${pathValues.MX},${yBasePos}H${pathValues.HX}`
            },
            startEnd: getStartEndByIndexCached(index, dataLength),
            effect: 'easeInOutQuart'
          }
        ];
      },
      'close.appearing': function (inputJSON) {
        const pathValues = getPathValues(inputJSON.attr.path, true),
          index = inputJSON.props.index,
          dataLength = inputJSON.props.dataLength;
        // return null;
        return [
          {
            initialAttr: {
              path: `M${pathValues.MX},${yBasePos}H${pathValues.HX}`
            },
            startEnd: getStartEndByIndexCached(index, dataLength),
            effect: 'easeInOutQuart'
          }
        ];
      }
    };
  }
};
