import {
  UNDEF,
  pluckNumber,
  getSuggestiveRotation,
  parseUnsafeString
} from '@fusioncharts/core/src/lib';
import safeMax from '@fusioncharts/utils/src/array/safe-max';
import ScaleLinear from '@fusioncharts/utils/src/scales/linear';
import { SmartRenderer } from '@fusioncharts/core/src/component-interface';

const identity = x => x,
  isNil = x => x === null || typeof x === 'undefined',
  // HEIGHT = 'height',
  // WIDTH = 'width',
  MAX_ALLOWED_SPACE = 5,
  TICKMAJOR = 'tick-mark-major',
  TICKMINOR = 'tick-mark-minor',
  LABELMAJOR = 'label-major',
  LABELMINOR = 'label-minor',
  LABELCONTEXT = 'label-context',
  START = 'start',
  END = 'end',
  MIDDLE = 'middle',
  TOP = 'top',
  RIGHT = 'right',
  BOTTOM = 'bottom',
  LEFT = 'left',
  X = 'x',
  Y = 'y',
  MAJOR = 'major',
  MINOR = 'minor',
  CONTEXT = 'context',
  DEFAULT_TICK_SIZE = 12,
  DEFAULT_TICK_PADDING = 1,
  DEFAULT_ROTATION = 90,
  DEFAULT_TRANSFORM = 't0,0',
  DEFAULT_LABEL_PADDING = 5,
  LABEL_NAME_WIDTH_BUFFER = 10,
  GUTTER_2 = 2,
  // MIN_VERTICAL_LABEL_GAP = 5,
  floor = Math.floor,
  mathMax = Math.max,
  BLANK = '',
  /**
   * Creates translation string with y set to 0
   *
   * @param {number} x x value
   */
  translateX = x => `t${x},0`,
  /**
   * Creates translation string with x set to 0
   *
   * @param {number} y y value
   */
  translateY = y => `t0,${y}`,
  number = scale => d => +scale.getRangeValue(d),
  center = scale => d => +scale.getRangeValue(d) + (scale.getBandwidth() / 2),
  /**
   * Returns the time formatter based on tick type
   *
   * @param {Array} timeFormat list of formatters
   * @param {Array} tickType list of ticks
   * @param {Number} index index for which tick is to be calculated
   * @returns {Instance} formatter
   */
  getFormatter = (timeFormat, tickType, index) => {
    if (timeFormat && tickType) {
      return timeFormat[tickType[index]];
    }
  },
  /**
   * checks if two boxes overlaps
   *
   * @param {Object} bbox1 first dimension
   * @param {Object} bbox2 second dimension
   * @returns {boolean} true if two bbox overlaps, else false
   */
  overlaps = (bbox1, bbox2) => {
    if (bbox1.x > bbox2.x + bbox2.width || bbox2.x > bbox1.x + bbox1.width) {
      return false;
    }

    if (bbox1.y > bbox2.y + bbox2.height || bbox2.y > bbox1.y + bbox1.height) {
      return false;
    }
    return true;
  },
  /**
   * sets tick label to blank string and width and height of ticks to 0
   *
   * @param {Object} tick tick object
   */
  resetTickInfo = tick => {
    tick.text = BLANK;
    tick.dim.width = 0;
    tick.dim.height = 0;
  },
  /**
   * decide text anchor to be applied on labels
   *
   * @param {String} orient orientation of axis
   */
  getTextAnchor = (orient) => {
    let anchor;
    switch (orient) {
      case LEFT:
        anchor = END;
        break;

      case RIGHT:
        anchor = START;
        break;

      default:
        anchor = MIDDLE;
    }

    return anchor;
  },
  /**
   * Method to check whether the label lies within its parent's Dimensions
   * @param {Object} smartText smart text
   * @param {String} textAnchor text anchor of the label
   * @param {Object} labelDim dimension of the label
   * @param {Object} parentDim parent's dimension
   */
  isWithinDomain = (smartText, textAnchor, labelDim, parentDim) => {
    let startX;
    if (textAnchor === START) {
      startX = labelDim.x;
    } else if (textAnchor === END) {
      startX = labelDim.x - smartText.width;
    } else {
      startX = labelDim.x - smartText.width / 2;
    }
    if ((startX >= 0) && ((startX + smartText.width) <= parentDim.width)) {
      return true;
    }
    return false;
  },
  isWithinVisibleRange = (value, limits) => {
    return value >= limits.start && value <= limits.end;
  };
/**
 * @todo
 * 1. try to merge cleanseMajorTicks and cleanseMinorTicks
 * 2. Reduce arrays in axis label space management
 */
class Axis extends SmartRenderer {
  getType () {
    return 'axis';
  }

  getName () {
    return 'axis';
  }

  __setDefaultConfig () {
    super.__setDefaultConfig();

    let axisConfig = this.config;
    this.setScale(new ScaleLinear());
    this.setAlignment(LEFT);
    this.setTickArguments();
    this.setTickValues();
    this.setReverse(false);
    this.setTickSize(DEFAULT_TICK_SIZE);
    this.setTickPadding(DEFAULT_TICK_PADDING);
    this.setDomainLine(true);
    this.setTicksDraw(true);
    this.setLabelOffset();
    this.setDayInMajor(false);
    this.setStickyContextLabel(true);
    this.setTickVisibilityLimit({ start: -Infinity, end: Infinity });
    axisConfig.alignDiff = 0;
    axisConfig.maxLabelSpace = 0;
    axisConfig.axisDimension = {};

    // Default styles to be used when user didn't provide any values
    axisConfig.defaultStyle = axisConfig.style = {
      [TICKMINOR]: {
        stroke: '#d3d3d3',
        'stroke-width': '1px'
      },
      [TICKMAJOR]: {
        stroke: '#AAAAAA',
        'stroke-width': '1px'
      },
      [LABELMAJOR]: {
        fill: '#858585',
        'font-size': '12px',
        'font-weight': 'normal',
        'font-style': 'normal'
      },
      [LABELMINOR]: {
        fill: '#9A9A9A',
        'font-size': '11px',
        'font-weight': 'normal',
        'font-style': 'normal'
      },
      [LABELCONTEXT]: {
        fill: '#858585',
        'font-size': '12px',
        'font-weight': 'normal',
        'font-style': 'normal'
      },
      _ticks: {
        minor: {
          stroke: '#d3d3d3'
        },
        major: {
          stroke: '#AAAAAA'
        },
        context: {}
      },
      _text: {
        major: {
          fill: '#858585',
          'font-size': '12px',
          'font-weight': 'normal',
          'font-style': 'normal'
        },
        minor: {
          fill: '#9A9A9A',
          'font-size': '11px',
          'font-weight': 'normal',
          'font-style': 'normal'
        },
        context: {
          fill: '#858585',
          'font-size': '12px',
          'font-weight': 'normal',
          'font-style': 'normal'
        }
      },
      line: {
        fill: '#D6D6D6'
      },
      title: {
        fill: '#5F5F5F',
        'font-size': '12px',
        'font-weight': 'normal',
        'font-style': 'normal'
      }
    };
    if (!this.config.prevTicks) this.config.prevTicks = [];
  }

  /**
   * configures different properties of axis
   *
   * @param {any} inputCfg configurations object
   * @memberof Axis
   */
  configureAttributes (inputCfg) {
    super.configureAttributes(inputCfg);

    const axis = this;

    inputCfg.scale && axis.setScale(inputCfg.scale);
    inputCfg.align && axis.setAlignment(inputCfg.align, inputCfg.overlap);
    inputCfg.reverse && axis.setReverse(inputCfg.reverse);
    inputCfg.tickarguments && axis.setTickArguments(...inputCfg.tickarguments);
    inputCfg.title && axis.setAxisName(parseUnsafeString(inputCfg.title));
    inputCfg.skipinfo && axis.setSkipInfo(inputCfg.skipinfo);
    inputCfg.offset && axis.setLabelOffset(inputCfg.offset);
    inputCfg.dayinmajor && axis.setDayInMajor(inputCfg.dayinmajor);
    inputCfg.scalemode && axis.setScaleMode(inputCfg.scalemode);
    inputCfg.minspace && axis.setminSpace(inputCfg.minspace);
    typeof inputCfg.stickycontextlabel !== 'undefined' && axis.setStickyContextLabel(inputCfg.stickycontextlabel);
    !isNil(inputCfg.ticksize) && axis.setTickSize(inputCfg.ticksize);
    !isNil(inputCfg.tickpadding) && axis.setTickPadding(inputCfg.tickpadding);
    axis.setDomainLine(inputCfg.domainline);
    axis.setTicksDraw(inputCfg.drawticks);
    !isNil(inputCfg.formatterFn) && axis.setTickFormat(inputCfg);

    inputCfg.outputtimeformat && axis.setTimeFormat(inputCfg.outputtimeformat);
    inputCfg.tickvisibilitylimit && axis.setTickVisibilityLimit(inputCfg.tickvisibilitylimit);
    // parse user provided styles
    inputCfg.style && axis.parseStyle(inputCfg.style);
    axis.config.validateLabelDimensions = pluckNumber(inputCfg.validateLabelDimensions, 0);
  }

  /**
   * merges the style provided by the user with the default style
   *
   * @param {any} [style={}] style provided by the user
   * @memberof Axis
   */
  parseStyle (style = {}) {
    let axis = this,
      axisConfig = axis.config,
      defaultStyle = axisConfig.defaultStyle,
      getStyleDef = axis.getFromEnv('getStyleDef'),
      baseTextStyle = axis.getFromEnv('baseTextStyle'),
      genericLabelStyle = getStyleDef(style.label),
      genericTickStyle = getStyleDef(style['tick-mark']),
      majorTickStyle,
      minorTickStyle,
      majorLabelStyle,
      minorLabelStyle,
      contextLabelStyle;

    majorTickStyle = Object.assign({}, defaultStyle['tick-mark-major'], genericTickStyle, getStyleDef(style['tick-mark-major']));
    minorTickStyle = Object.assign({}, defaultStyle['tick-mark-minor'], genericTickStyle, getStyleDef(style['tick-mark-minor']));

    majorLabelStyle = Object.assign({}, defaultStyle['label-major'], baseTextStyle, genericLabelStyle, getStyleDef(style['label-major']));
    minorLabelStyle = Object.assign({}, defaultStyle['label-minor'], baseTextStyle, genericLabelStyle, getStyleDef(style['label-minor']));
    contextLabelStyle = Object.assign({}, defaultStyle['label-context'], baseTextStyle, genericLabelStyle, getStyleDef(style['label-context']));

    axisConfig.style = {
      [TICKMAJOR]: majorTickStyle,
      [TICKMINOR]: minorTickStyle,
      [LABELMAJOR]: majorLabelStyle,
      [LABELMINOR]: minorLabelStyle,
      [LABELCONTEXT]: contextLabelStyle,
      title: Object.assign({}, defaultStyle.title, baseTextStyle, getStyleDef(style.title)),
      line: Object.assign({}, defaultStyle.line, getStyleDef(style.line))
    };
  }
  /**
   * Calculates the total space required by axis
   *
   * @returns {Object} space required by the axis
   * @memberof Axis
   */
  placeAxis () {
    let axis = this,
      axisConfig = axis.config,
      isVertical = axisConfig.isVertical,
      minSpace = pluckNumber(axisConfig.minspace, -1),
      scale = axis.getScale(),
      skipInfo = axis.getSkipInfo() || {},
      tickArguments,
      values,
      format,
      padding = axis.getTickPadding(),
      smartLabel = axis.getFromEnv('smartLabel'),
      processedValues = [],
      i,
      len,
      maxLen = 0,
      maxLabelLen = 0,
      tickLen,
      labelDim,
      nameDim,
      axisName = axis.getAxisName(),
      axisNameDim,
      formatter,
      value,
      text,
      fSize,
      tickType,
      tickLabelDimension,
      labelDimAr = [],
      labelHeight,
      contextStyle,
      fontSize,
      overlapBuffer;

    tickArguments = (axis.getTickArguments() || []).slice(0);
    tickArguments.push(skipInfo);
    tickArguments.push(axisConfig.dayInMajor);
    // creates ticks
    values = isNil(axis.getTickValues())
      ? (scale.ticks ? scale.ticks(...tickArguments) : scale.getDomain())
      : axis.getTickValues();
    // get formatter
    format = isNil(axis.getTickFormat())
      ? (scale.tickFormat ? scale.tickFormat(...axis.getTickArguments()) : identity)
      : axis.getTickFormat();

    contextStyle = axisConfig.style[LABELCONTEXT];

    smartLabel.setStyle(Object.assign({}, contextStyle));
    fontSize = contextStyle['font-size'] || contextStyle.fontSize;
    axisConfig.labelHeight = parseFloat(fontSize, 10) * 1.2;

    // If axis name is provided, calculate its space required as well
    if (axisName) {
      smartLabel.setStyle(Object.assign({}, axisConfig.style.title));
      axisNameDim = axisConfig.axisNameDim = smartLabel.getOriSize(axisName, false);

      nameDim = axisNameDim.height;
      maxLen += nameDim;
    }

    tickType = scale._tickType || [];

    // Space calculations of tick labels
    for (i = 0, len = values.length; i < len; i++) {
      value = values[i];
      formatter = getFormatter(scale._timeFormat, tickType, i);

      smartLabel.setStyle(Object.assign({}, axisConfig.style['label-' + (tickType[i] || MAJOR)]));
      text = axisConfig.isVertical ? format.formatFn({
        value,
        prefix: format.prefix,
        suffix: format.suffix,
        type: 'axis'
      }, formatter) : format(value, formatter, tickType[i]);

      text = parseUnsafeString(text);
      // Find the length of the longest label
      maxLabelLen = mathMax(text.length, maxLabelLen);

      tickLabelDimension = smartLabel.getOriSize(text, false);
      isVertical && (tickLabelDimension.width = axisConfig.vLabelWidth);
      // tickLabelDimension = isVertical ? smartLabel.getSmartText(bufferText) : smartLabel.getSmartText(text);

      tickLen = axis.getTickSize(tickType[i]);

      processedValues.push({
        text,
        value,
        dim: tickLabelDimension,
        tickLen,
        [axisConfig.x]: (padding + tickLen)
      });

      tickType[i] !== CONTEXT && labelDimAr.push(
        (isVertical ? tickLabelDimension.width : tickLabelDimension.height) + tickLen
      );
    }

    if (isVertical && isNil(axisConfig.vLabelWidth)) {
      // only executed once during initial render and for y-axus
      axisConfig.vLabelWidth = 0;
      labelDimAr = [];
      for (i = 0, len = values.length; i < len; i++) {
        smartLabel.setStyle(Object.assign({}, axisConfig.style['label-' + (tickType[i] || MAJOR)]));
        fSize = axisConfig.style['label-' + (tickType[i] || MAJOR)]['font-size'];
        fSize = +(fSize.toString().split('px')[0]);
        // get a buffer text based on the longest label and store its width
        tickLabelDimension = smartLabel.getOriSize(text, false);
        tickLabelDimension.width = mathMax(tickLabelDimension.width + LABEL_NAME_WIDTH_BUFFER, (minSpace - maxLen));

        processedValues[i].dim = tickLabelDimension;
        axisConfig.vLabelWidth = mathMax(tickLabelDimension.width, axisConfig.vLabelWidth);

        tickType[i] !== CONTEXT && labelDimAr.push(
          (isVertical ? tickLabelDimension.width : tickLabelDimension.height) + tickLen
        );
      }
    }

    axisConfig.values = processedValues;
    // Get space required when minor and major label overlaps
    overlapBuffer = axis.getLabelOverlapBuffer();

    // Find the max space taken by the labels and the ticks
    labelDim = axisConfig.prevMaxLen = pluckNumber(safeMax(labelDimAr), axisConfig.prevMaxLen, 0);
    maxLen += labelDim;

    // Take space for context tick labels
    labelHeight = isVertical ? 0 : axisConfig.labelHeight;
    axisConfig.maxLabelSpace = maxLen + padding + overlapBuffer + labelHeight;

    // find the space when tick labels will be placed
    for (i = 0, len = values.length; i < len; i++) {
      value = processedValues[i];
      if (axisConfig.orientation === TOP || axisConfig.orientation === LEFT) {
        value[axisConfig.x] = axisConfig.maxLabelSpace - padding - overlapBuffer - value.tickLen;
      } else {
        value[axisConfig.x] = padding + value.tickLen;
      }
    }

    axisConfig.axisDimension = {
      [axisConfig.align]: axisConfig.maxLabelSpace
    };
    return axisConfig.axisDimension;
  }

  /**
   * Finds the pixels by which major labels should be shifted so that there is
   * no overlapping between minor and major tick labels
   *
   * @returns {number} pixel by which label will be shifted
   * @memberof Axis
   */
  getLabelOverlapBuffer () {
    let axis = this,
      axisConfig = axis.config,
      values = axisConfig.values,
      scale = axis.getScale(),
      tickType = scale._tickType || [],
      majorTick,
      minorTick,
      i,
      len,
      overlap = 0,
      dim;

    // Find a minor and major tick from the list of ticks
    for (i = 0, len = values.length; i < len; i++) {
      if (tickType[i] === MAJOR || !tickType[i]) {
        majorTick = values[i];
      } else if (tickType[i] === MINOR) {
        minorTick = values[i];
      }

      // when both major and minor tick is found, break
      if (majorTick && minorTick) {
        break;
      };
    }

    // If minor tick is available, check for overlap
    if (minorTick) {
      dim = {
        x: minorTick.dim.width,
        y: minorTick.dim.height
      };
      switch (axisConfig.k) {
        case 1:
          overlap = mathMax(minorTick[axisConfig.x] + dim[axisConfig.x] - majorTick[axisConfig.x], 0);
          break;

        case -1:
          overlap = mathMax(majorTick[axisConfig.x] - (minorTick[axisConfig.x] + dim[axisConfig.x]), 0);
          break;
      }
    }
    return (axisConfig.overlapBuffer = (axisConfig.k * overlap) / 3);
  }
  /**
   * makes bbox of label from the configuration of smartlabel and axis range
   *
   * @param {Object} tick tick for which bbox is to be created
   * @returns {Object} bbox of tick label
   * @memberof Axis
   */
  makeBBox (tick, buffer = DEFAULT_LABEL_PADDING) {
    let axisConfig = this.config,
      scale = this.getScale(),
      dim = {
        x: tick.dim.width,
        y: tick.dim.height
      };

    return {
      [axisConfig.x]: tick[axisConfig.x] - buffer - dim[axisConfig.x] / 2,
      [axisConfig.xInverse]: scale.getRangeValue(tick.value) - buffer - dim[axisConfig.xInverse] / 2,
      width: tick.dim.width + buffer,
      height: tick.dim.height + buffer
    };
  }

  /**
   * Finds the number of minor labels to be skipped
   *
   * @memberof Axis
   */
  setMinorSkipIndex () {
    let axis = this,
      axisConfig = axis.config,
      scale = axis.getScale(),
      ticks = axisConfig.values,
      tickType = scale._tickType || [],
      minorTicks = [],
      i,
      j,
      len = ticks.length,
      midPoint,
      startMajorTick,
      endMajorTick,
      startTickBBox,
      endTickBBox,
      curTickBBox,
      midTickBBox,
      start,
      leftSkip = 1,
      rightSkip = 1,
      found = false,
      doesNotOverlap = false;

    // Take out the minor ticks between the first two major ticks
    for (i = 0; i < len; i++) {
      if (tickType[i] === MAJOR || typeof tickType[i] === UNDEF) {
        for (j = i + 1; j < len; j++) {
          if (tickType[j] === MAJOR || typeof tickType[j] === UNDEF) {
            startMajorTick = ticks[i];
            endMajorTick = ticks[j];
            minorTicks = ticks.slice(i + 1, j);
            found = true;
            break;
          }
        }
      }
      if (found) {
        break;
      }
    }

    // minor ticks are optional, cannot be present in many axis
    if (!minorTicks.length) {
      return;
    }

    len = minorTicks.length;
    start = i;
    // find midpoint of the minor ticks
    midPoint = floor(len / 2);

    // major tick on left
    startTickBBox = axis.makeBBox(startMajorTick);
    // major tick on right
    endTickBBox = axis.makeBBox(endMajorTick);
    // minor tick in the middle
    midTickBBox = axis.makeBBox(minorTicks[midPoint]);

    // if the middle minor tick label overlaps with either left or right major tick label, no minor tick
    // labels can be drawn, hence return
    if (overlaps(midTickBBox, startTickBBox) || overlaps(midTickBBox, endTickBBox)) {
      axisConfig.minorSkip = len;
      return;
    }

    // traverse left. find the first minor tick label which do not overlaps with middle minor tick label
    // leftSkip stores the gap between the middle minor tick label and first minor tick label on the left
    // which do not overlaps
    for (i = midPoint - 1; i >= 0; i--) {
      curTickBBox = axis.makeBBox(minorTicks[i]);
      if (overlaps(curTickBBox, midTickBBox)) {
        continue;
      }
      leftSkip = midPoint - i;
      break;
    }

    // traverse right. find the first minor tick label which do not overlaps with middle minor tick label
    // leftSkip stores the gap between the middle minor tick label and first minor tick label on the right
    // which do not overlaps
    for (i = midPoint + 1; i < len + start; i++) {
      curTickBBox = axis.makeBBox(minorTicks[i]);
      if (overlaps(curTickBBox, midTickBBox)) {
        continue;
      }
      rightSkip = i - midPoint;
      break;
    }

    len = minorTicks.length;
    // check for overlap for the rest of the labels
    while (!doesNotOverlap) {
      doesNotOverlap = true;
      for (i = midPoint + rightSkip; i < len; i += rightSkip) {
        if (overlaps(axis.makeBBox(minorTicks[i]), axis.makeBBox(minorTicks[i - rightSkip]))) {
          rightSkip++;
          doesNotOverlap = false;
          break;
        }
      }
      for (i = midPoint - leftSkip; i >= 0; i -= leftSkip) {
        if (overlaps(axis.makeBBox(minorTicks[i]), axis.makeBBox(minorTicks[i + leftSkip]))) {
          leftSkip++;
          doesNotOverlap = false;
          break;
        }
      }
    }

    // take max of the gaps on the left and right and skip based on it. This ensures symmetrical skipping
    // on both sides
    axisConfig.minorSkip = mathMax(leftSkip, rightSkip);
  }

  /**
   * Clear the minor ticks information whose labels are not to be shown
   *
   * @memberof Axis
   */
  cleanseMinorTicks () {
    let axis = this,
      axisConfig = axis.config,
      scale = axis.getScale(),
      tickType = scale._tickType || [],
      i,
      j,
      len,
      majorTickIndex = [],
      minorTicks,
      minorTicksAr = [],
      midPoint,
      leftPointBBox,
      rightPointBBox,
      curTickBBox,
      ticks = axisConfig.values,
      // equalOverlap,
      leftMax = -1,
      rightMin,
      dim = axisConfig.isVertical ? {
        [axisConfig.xInverse]: 'height',
        [axisConfig.x]: 'width'
      } : {
        [axisConfig.xInverse]: 'width',
        [axisConfig.x]: 'height'
      };

    // find the major tick indices
    for (i = 0, len = ticks.length; i < len; i++) {
      if (tickType[i] === MAJOR || !tickType[i]) {
        majorTickIndex.push(i);
      }
    }

    for (i = 0, len = majorTickIndex.length; i < len - 1; i++) {
      // for two consecutive major ticks, find the minor ticks in between
      minorTicks = ticks.slice(majorTickIndex[i] + 1, majorTickIndex[i + 1]);
      minorTicksAr.push(minorTicks);

      // rightMin store the index of the maximum tick label index that can be shown on the right
      rightMin = minorTicks.length;
      // leftMax store the index of the maximum tick label index that can be shown on the left
      leftMax = -1;
      if (!minorTicks.length) {
        continue;
      }
      // middle minor tick
      midPoint = floor(minorTicks.length / 2);

      // major tick on left
      leftPointBBox = axis.makeBBox(ticks[majorTickIndex[i]], 0);

      // major tick on right
      rightPointBBox = axis.makeBBox(ticks[majorTickIndex[i + 1]], 0);
      for (j = midPoint; j >= 0; j -= 1) {
        curTickBBox = axis.makeBBox(minorTicks[j], 1);

        // if the minor tick label overlaps with the left major tick, the label and all the labels
        // to its left should not be drawn.
        if (axisConfig.isVertical) {
          if (leftPointBBox[axisConfig.xInverse] <= curTickBBox[axisConfig.xInverse] + curTickBBox[dim[axisConfig.xInverse]]) {
            leftMax = mathMax(leftMax, j);
            break;
          }
        } else {
          if (curTickBBox[axisConfig.xInverse] <= leftPointBBox[axisConfig.xInverse] + leftPointBBox[dim[axisConfig.xInverse]]) {
            leftMax = mathMax(leftMax, j);
            break;
          }
        }
      }
      for (j = midPoint; j < minorTicks.length; j += 1) {
        curTickBBox = axis.makeBBox(minorTicks[j], 1);

        // if the minor tick label overlaps with the right major tick, the label and all the labels
        // to its right should not be drawn.
        if (axisConfig.isVertical) {
          if (rightPointBBox[axisConfig.xInverse] + rightPointBBox[dim[axisConfig.xInverse]] >= curTickBBox[axisConfig.xInverse]) {
            rightMin = Math.min(rightMin, j);
            break;
          }
        } else {
          if (curTickBBox[axisConfig.xInverse] + curTickBBox[dim[axisConfig.xInverse]] >= rightPointBBox[axisConfig.xInverse]) {
            rightMin = Math.min(rightMin, j);
            break;
          }
        }
      }

      // equalOverlap = mathMax(leftMax + 1, minorTicks.length - rightMin);
      // leftMax = -1 + equalOverlap;
      // rightMin = minorTicks.length - equalOverlap;
      // reset minor ticks information based on the following criteria:
      // 1. minor tick label overlaps with the major ticks
      // 2. minor tick label overlaps with the adjacent labels
      for (j = 0; j < minorTicks.length; j++) {
        if (j <= leftMax || j >= rightMin || Math.abs(midPoint - j) % axisConfig.minorSkip) {
          resetTickInfo(minorTicks[j]);
        }
      }
    }
  }

  /**
   * Clear the major ticks information whose labels are not to be shown
   *
   * @memberof Axis
   */
  cleanseMajorTicks () {
    let axis = this,
      axisConfig = axis.config,
      scale = axis.getScale(),
      values = axisConfig.values,
      majorTickAr = [],
      majorTickIndexAr = [],
      contextTickAr = [],
      contextTickIndexAr = [],
      tickType = scale._tickType || [],
      startIndex = 0,
      leftSkip = 1,
      rightSkip = 1,
      curTickBBox,
      majorSkip,
      i,
      len,
      doesNotOverlap = false;

    len = values.length;
    if (!len) {
      return;
    }
    // find all the major ticks from the ticks array
    for (i = 0; i < len; i++) {
      if (tickType[i] === MAJOR || !tickType[i]) {
        majorTickAr.push(values[i]);
        majorTickIndexAr.push(i);
      } else if (tickType[i] === CONTEXT) {
        contextTickAr.push(values[i]);
        contextTickIndexAr.push(i);
      }
    }

    /**
     * startIndex will be used to iterate the majorticks. Two cases can arise:
     * 1. When context tick is present: On any condition this tick label has to be shown(unless the font size is garbage)
     * 2. When context tick is not present: start from the 0 index and look for overlaps.
     *
     * startIndex stores the index of context label when context tick is present, else 0
     */
    if (contextTickIndexAr.length) {
      for (i = 0, len = majorTickAr.length; i < len; i++) {
        if (+contextTickAr[0].value === +majorTickAr[i].value) {
          startIndex = i;
        }
      }
    } else { startIndex = 0; }

    // Take the tick label at the startIndex of majorTickAr to calculate rest.
    // Lets call it context tick label
    curTickBBox = axis.makeBBox(majorTickAr[0]);

    // traverse right. look for the first major label on the right which do not overlap with context tick label
    for (i = startIndex + 1, len = majorTickAr.length; i < len; i++) {
      if (!overlaps(axis.makeBBox(majorTickAr[i]), curTickBBox)) {
        rightSkip = i - startIndex;
        break;
      }
    }

    // traverse left. look for the first major label on the left which do not overlap with context tick label
    for (i = startIndex - 1; i >= 0; i--) {
      if (!overlaps(axis.makeBBox(majorTickAr[i]), curTickBBox)) {
        leftSkip = startIndex - i;
        break;
      }
    }

    while (!doesNotOverlap) {
      doesNotOverlap = true;
      for (i = startIndex + rightSkip; i < len; i += rightSkip) {
        if (overlaps(axis.makeBBox(majorTickAr[i]), axis.makeBBox(majorTickAr[i - rightSkip]))) {
          rightSkip++;
          doesNotOverlap = false;
          break;
        }
      }
      for (i = startIndex - leftSkip; i >= 0; i -= rightSkip) {
        if (overlaps(axis.makeBBox(majorTickAr[i]), axis.makeBBox(majorTickAr[i + leftSkip]))) {
          leftSkip++;
          doesNotOverlap = false;
          break;
        }
      }
    }
    // take the maximum gap between the labels on left and right, to decide number of labels to be skipped.
    axisConfig.majorSkip = majorSkip = mathMax(leftSkip, rightSkip);

    // reset information of all the ticks which are to be skipped.
    for (i = 0, len = majorTickAr.length; i < len; i++) {
      if (Math.abs(i - startIndex) % majorSkip) {
        resetTickInfo(majorTickAr[i]);
      }
    }
  }

  /**
   * Drawing of ticks and labels
   *
   * @memberof Axis
   */
  draw () {
    const axis = this,
      axisConfig = axis.config,
      chartConfig = axis.getFromEnv('chart').config,
      scale = axis.getScale(),
      parent = axis.getLinkedParent(),
      tickType = scale._tickType || [],
      position = (scale.getBandwidth ? center : number)(scale),
      range = scale.getRange(),
      domain = scale.getDomain(),
      range0 = Number(range[0]),
      range1 = Number(range[range.length - 1]),
      orientation = axis.getOrientation(),
      smartLabel = axis.getFromEnv('smartLabel'),
      { values } = axisConfig,
      lineOffset = orientation === LEFT || orientation === TOP ? axisConfig.maxLabelSpace + GUTTER_2 : 0,
      isVertical = axisConfig.isVertical,
      orient = axisConfig.orientation,
      xInverse = axisConfig.xInverse,
      x = axisConfig.x,
      k = axisConfig.k,
      style = axisConfig.style;

    // Container group for the axis
    axis.addGraphicalElement({
      el: 'group',
      attr: {
        name: `meso-axis`,
        transform: axisConfig.groupTranslation
      },
      container: {
        id: 'meso',
        label: 'group',
        isParent: true
      },
      id: 'meso-axis',
      component: axis,
      label: 'group'
    });

    // Group that will contain the ticks and labels
    axis.addGraphicalElement({
      el: 'group',
      attr: {
        name: `axis-${orient}`
      },
      container: {
        id: 'meso-axis',
        label: 'group'
      },
      id: 'axisGroup',
      component: axis,
      label: 'group'
    });

    let tickValue,
      type,
      axisName,
      axisNameDim,
      xpos,
      ypos,
      index,
      len,
      value,
      majorTickIndex = [],
      majorTickIndexIterator,
      lMajor,
      rMajor,
      contextTickCount = 0,
      visibilityLimit = axis.getTickVisibilityLimit(),
      firstMajorTick,
      textAnchor,
      fontSize,
      linePath,
      tickPath,
      labelDim;

    // Skipping of minor ticks
    axis.setMinorSkipIndex();
    axis.cleanseMinorTicks();

    // skipping of major ticks
    axis.cleanseMajorTicks();

    if ((linePath = axis.getDomainLine())) {
      // draws the axis line
      axis.addGraphicalElement({
        el: 'path',
        attr: {
          path: linePath,
          stroke: '#d6d6d6',
          fill: 'none',
          'stroke-width': 2
        },
        container: {
          id: 'axisGroup',
          label: 'group',
          isParent: false
        },
        css: style.line,
        component: axis,
        label: 'path'
      });
    }

    // draw axis name
    if ((axisName = axis.getAxisName())) {
      // find the lineHeight @todo remove after creating an API for this in smartlabel
      fontSize = parseFloat(axisConfig.style.title['font-size'] || axisConfig.style.title.fontSize, 10);
      smartLabel.setStyle(Object.assign({}, axisConfig.style.title));
      axisNameDim = smartLabel.getSmartText(axisName, Math.abs(range0 - range1), fontSize * 1.2);
      // calculation for vertical and horizontal axis
      if (isVertical) {
        xpos = orientation === LEFT ? 0 + axisNameDim.height / 2 : axisConfig.maxLabelSpace - axisNameDim.height / 2;
        ypos = (range0 - range1) / 2;
      } else {
        xpos = (range1 - range0) / 2;
        ypos = orientation === TOP ? 0 + axisNameDim.height / 2 : axisConfig.maxLabelSpace - axisNameDim.height / 2;
      }
      axis.addGraphicalElement({
        el: 'text',
        attr: {
          text: axisNameDim.text,
          x: xpos,
          y: ypos,
          opacity: style.title.opacity,
          'text-anchor': 'middle',
          transform: isVertical ? getSuggestiveRotation(k * DEFAULT_ROTATION, xpos, ypos) : DEFAULT_TRANSFORM
        },
        container: {
          id: 'axisGroup',
          label: 'group',
          isParent: false
        },
        tooltext: chartConfig.showTooltip ? axisNameDim.tooltext : UNDEF,
        css: style.title,
        component: axis,
        id: 'axis-name',
        label: 'name'
      });
    }

    // Drawing of axis ticks and axis labels
    if (axis.getTicksDraw()) {
      // find number of context ticks
      contextTickCount = tickType.length - tickType.indexOf(CONTEXT);
      // find the first major tick
      firstMajorTick = axisConfig.values.find(
        (tick, pos) => tick.value >= domain[0] && tick.value <= domain[1] && tickType[pos] === MAJOR
      );

      // fetch all the major ticks of time axis
      for (index = 0, len = values.length; index < len; index++) {
        if (tickType[index] === MAJOR) {
          if (values[index].value >= domain[0] && values[index].value <= domain[1]) {
            values[index][xInverse] = position(values[index].value);
          }
          majorTickIndex.push(index);
        }
      }

      lMajor = majorTickIndex[0];
      rMajor = majorTickIndex[1];
      majorTickIndexIterator = 2;
      for (index = 0, len = axisConfig.values.length; index < len; index++) {
        value = axisConfig.values[index];
        type = tickType[index] || MAJOR;

        // if tick is without label then no need to draw it in x axis
        if (!isVertical && value.text.length === 0) {
          continue;
        }
        // for all non-context ticks, if ticks are outside visible domain, skip it
        if (type !== CONTEXT && (value.value < domain[0] || value.value > domain[1])) {
          continue;
        }

        // for context ticks, two conditions are possible
        // 1. when only one context tick is present, draw it.
        // 2. If more than 1 context ticks are present, show context labels present in visible domain
        if (type === CONTEXT && contextTickCount > 1 && (value.value < domain[0] || value.value > domain[1])) {
          continue;
        }

        // limit the context ticks to the left most point of the x-axis when stickyContextLabel is enabled,
        // Else, context label will be placed with the first major tick
        if (type === CONTEXT && contextTickCount === 1) {
          value[xInverse] = (axis.getStickyContextLabel() || typeof firstMajorTick === 'undefined')
            ? mathMax(position(value.value), value.dim.width / 2)
            : position(firstMajorTick.value);
        } else {
          value[xInverse] = value[xInverse] || position(value.value);
        }

        if (index >= rMajor) {
          lMajor = rMajor;
          rMajor = majorTickIndex[majorTickIndexIterator];
          majorTickIndexIterator++;
        }

        // @todo: bring up tick skipping logic here from scale.
        // only for x axis
        if (
          type === MINOR && !isVertical &&
          ((value[xInverse] - values[lMajor][xInverse] < MAX_ALLOWED_SPACE) ||
          (values[rMajor][xInverse] - value[xInverse] < MAX_ALLOWED_SPACE))
        ) {
          continue;
        }
        // value[xInverse] = (type === CONTEXT && (axis.getStickyContextLabel() || firstMajorTick === -1)) ? mathMax(position(value.value), 0) : position(firstMajorTick.value);
        // alignDiff is the difference between the actual width and alloted width.
        value[x] += axisConfig.alignDiff;

        tickValue = value.value;

        // Create the group
        axis.addGraphicalElement({
          container: {
            id: 'axisGroup',
            label: 'group',
            isParent: false
          },
          attr: {
            name: 'tick',
            transform: axisConfig.translate(value[xInverse])
          },
          data: {
            value: tickValue
          },
          el: 'group',
          id: 'tick-group-' + index,
          component: this,
          label: 'group'
        });

        // Create ticks for minor and major intervals
        if (type !== CONTEXT) {
          tickPath = isVertical
            ? `M ${lineOffset} 0 H ${lineOffset + k * axis.getTickSize(tickType[index])}`
            : `M 0 ${lineOffset} V ${lineOffset + k * axis.getTickSize(tickType[index])}`;

          axis.addGraphicalElement({
            el: 'path',
            attr: {
              path: tickPath
            },
            container: {
              id: 'tick-group-' + index,
              label: 'group',
              isParent: false
            },
            id: 'tick-' + index,
            css: style['tick-mark-' + (type || MAJOR)],
            component: axis,
            label: 'path'
          });
        }

        textAnchor = style['label-' + (type || MAJOR)]['text-anchor'] || getTextAnchor(orientation);
        labelDim = {
          [xInverse]: value[xInverse] + axisConfig[xInverse + 'Offset'],
          [x]: value[x] + axisConfig[x + 'Offset'] + (type !== MINOR ? axisConfig.overlapBuffer : 0)
        };
        if (!axisConfig.validateLabelDimensions ||
          isWithinDomain(value.dim, textAnchor, labelDim, parent.getDimension())
        ) {
          if (!isWithinVisibleRange(value.value, visibilityLimit) && type !== CONTEXT) {
            continue;
          }
          if (type === CONTEXT && typeof visibilityLimit.start === 'undefined') {
            continue;
          }
          // Create tick labels
          axis.addGraphicalElement({
            el: 'text',
            attr: {
              text: value.text + '',
              [xInverse]: 0 + axisConfig[xInverse + 'Offset'],
              // add overlapBuffer to shift the major labels so as to overlap with minor tick labels
              [x]: value[x] + axisConfig[x + 'Offset'] + (type !== MINOR ? axisConfig.overlapBuffer : 0),
              'text-anchor': textAnchor,
              'vertical-align': orient === TOP ? 'bottom' : orient === BOTTOM ? 'top' : 'middle',
              opacity: style['label-' + (type || MAJOR)].opacity
            },
            container: {
              id: 'tick-group-' + index,
              label: 'group',
              isParent: false
            },
            component: axis,
            id: 'tick-label-' + index,
            css: style['label-' + (type || MAJOR)],
            label: 'text'
          });
        }
      }
    }
  }

  /**
   * Updates the max space taken by the axis. This is feature is required to sync
   * the horizontal positioning of y-axis names in multivariate
   *
   * @param {Number} newDim new space to be alloted. Always greater than or equal to the actual space.
   * @returns {Object} object containing alignment and dimension
   * @memberof Axis
   */
  updateMaxLabelSpace (newDim) {
    let axis = this,
      axisConfig = axis.config,
      align = axis.getAlignment();

    // calculate the diff between the new dimension and actual dimension. This is diff is used to place
    // ticks and label in proper place
    axisConfig.alignDiff = newDim - axisConfig.maxLabelSpace;
    // update the maxLabelSpace(total space taken by the axis)
    axisConfig.maxLabelSpace = axisConfig.axisDimension[align] = newDim;

    return axisConfig.axisDimension;
  }
  setDayInMajor (dayInMajor) {
    this.config.dayInMajor = dayInMajor;
  }
  /**
   * setter method for skipInfo. skipInfo contains information about the type of ticks that
   * are to be skipped
   *
   * @param {any} [info={}] Object containing the skipping information
   * @memberof Axis
   */
  setSkipInfo (info = {}) {
    this.config.tickInfo = info;
  }

  /**
   * getter method for skipInfo
   *
   * @returns {Object} returns skipInfo
   * @memberof Axis
   */
  getSkipInfo () {
    return this.config.tickInfo;
  }

  /**
   * setter method for stickyContextLabel attribute
   *
   * @param {boolean} [flag=true] value of the attribute
   * @memberof Axis
   */
  setStickyContextLabel (flag = true) {
    this.config.stickyContextLabel = flag;
  }

  /**
   * getter method for stickyContextLabel attribute
   *
   * @returns {boolean} value of stickyContextLabel attribute
   * @memberof Axis
   */
  getStickyContextLabel () {
    return this.config.stickyContextLabel;
  }
  /**
   * setter method for labelnamespace attribute
   *
   */
  setminSpace (minspace) {
    this.config.minspace = minspace;
  }
  /**
   Getter Method for labelnamespace
   */
  getminSpace () {
    return this.config.minspace;
  }
  /**
   * An offset to labels can be provided to shift the labels as and when required
   *
   * @param {any} [offset={}] Object containing the xOffset and yOffset values
   * @memberof Axis
   */
  setLabelOffset (offset = {}) {
    let axisConfig = this.config;
    axisConfig.xOffset = pluckNumber(offset.x, 0);
    axisConfig.yOffset = pluckNumber(offset.y, 0);
  }

  /**
   * getter method for label offset
   *
   * @returns {Object} object containing the x and y offset
   * @memberof Axis
   */
  getLabelOffset () {
    return {
      x: this.config.xOffset,
      y: this.config.yOffset
    };
  }
  /**
   * returns name of axis
   *
   * @returns {string} name of axis
   * @memberof Axis
   */
  getAxisName () {
    return this.config.axisName;
  }

  /**
   * sets name of axis
   *
   * @param {string} [name=''] name of the axis
   * @memberof Axis
   */
  setAxisName (title = '') {
    this.config.axisName = title;
  }

  /**
   * getter method of axis alignment
   *
   * @returns {any} axis alignment
   * @memberof Axis
   */
  getAlignment () {
    return this.config.align;
  }

  /**
   * sets alignment of axis
   *
   * @param {any} align alignment
   * @memberof Axis
   */
  setAlignment (align, overlap = false) {
    let axis = this,
      axisConfig = axis.config,
      orientation,
      isVertical;

    // calculate orientation based on the value of align and overlap
    switch (align) {
      case LEFT:
        orientation = overlap ? RIGHT : LEFT;
        break;

      case RIGHT:
        orientation = overlap ? LEFT : RIGHT;
        break;

      case TOP:
        orientation = overlap ? BOTTOM : TOP;
        break;

      case BOTTOM:
        orientation = overlap ? TOP : BOTTOM;
        break;
    }

    axisConfig.align = align;
    axisConfig.orientation = orientation;
    axisConfig.isVertical = isVertical = align === LEFT || align === RIGHT;
    axisConfig.k = orientation === LEFT || orientation === TOP ? -1 : 1;
    axisConfig.x = isVertical ? X : Y;
    axisConfig.xInverse = isVertical ? Y : X;
    axisConfig.translate = isVertical ? translateY : translateX;
    axisConfig.translateInverse = isVertical ? translateX : translateY;
  }

  /**
   * getter method for orientation
   *
   * @returns {string} orientation
   * @memberof Axis
   */
  getOrientation () {
    return this.config.orientation;
  }

  /**
   * sets the translation of axis
   *
   * @param {number} [x=0] x position
   * @param {number} [y=0] y position
   * @memberof Axis
   */
  setTranslation (x = 0, y = 0) {
    this.config._translatedX = x;
    this.config._translatedY = y;
    this.config.groupTranslation = `t${x}, ${y}`;
  }

  /**
   * getter method for translation
   *
   * @returns {Object} translation
   * @memberof Axis
   */
  getTranslation () {
    return {
      x: this.config._translatedX,
      y: this.config._translatedY
    };
  }

  /**
   * setter method for domainline
   *
   * @param {boolean} [flag=true] whether domainline will be shown
   * @returns {Object} instance of this class
   * @memberof Axis
   */
  setDomainLine (flag = true) {
    this.config.drawDomainLine = flag;

    return this;
  }

  /**
   * calculates the path for axis line and returns it
   *
   * @returns {string} path string of axis line
   * @memberof Axis
   */
  getDomainLine () {
    let axis = this,
      axisConfig = axis.config,
      scale = axis.getScale(),
      range = scale.getRange(),
      isVertical = axisConfig.isVertical,
      orientation = axisConfig.orientation,
      maxLabelSpace = axisConfig.maxLabelSpace,
      lineOffset,
      start = +range[0],
      end = +range[1],
      path;

    if (axisConfig.drawDomainLine) {
      lineOffset = orientation === LEFT || orientation === TOP ? maxLabelSpace + GUTTER_2 : 0;

      if (isVertical) {
        path = `M ${lineOffset} ${start} V ${end}`;
      } else {
        path = `M ${start} ${lineOffset} H ${end}`;
      }
    }
    return path;
  }

  setTicksDraw (flag = true) {
    this.config.drawTicks = flag;

    return this;
  }

  getTicksDraw () {
    return this.config.drawTicks;
  }

  setScale (scale) {
    this.config.scale = scale;
  }

  getScale () {
    return this.config.scale;
  }

  setReverse (isReverse) {
    this.config.isReverse = isReverse;
  }

  getReverse () {
    return this.config.isReverse;
  }

  setTickArguments (...tickArgs) {
    this.config.tickArguments = tickArgs;

    return this;
  }

  getTickArguments () {
    return this.config.tickArguments.slice();
  }

  setTickSize (size) {
    this.config.tickSizeOuter = Number(size);
    this.config.tickSizeInner = this.config.tickSizeOuter * (2 / 3);

    return this;
  }

  /**
   * Returns the tick size corresponding to the tick type
   *
   * @param {any} [type=MAJOR] type of tick
   * @returns {number} tick size
   * @memberof Axis
   */
  getTickSize (type = MAJOR) {
    let axisConfig = this.config;

    switch (type) {
      case CONTEXT: return axisConfig.tickSizeOuter + axisConfig.labelHeight;
      case MINOR: return axisConfig.tickSizeInner;
      case MAJOR: return axisConfig.tickSizeOuter;
    }
  }

  setTickPadding (padding) {
    this.config.tickPadding = Number(padding);

    return this;
  }

  getTickPadding () {
    return this.config.tickPadding;
  }

  setTickValues (tickValues) {
    this.config.tickValues = isNil(tickValues) ? null : tickValues.slice();

    return this;
  }

  getTickValues () {
    return this.config.tickValues && this.config.tickValues.slice();
  }

  setTickFormat (inputObj) {
    this.config.tickFormat = {
      formatFn: inputObj.formatterFn,
      prefix: inputObj.formatLabelPrefix,
      suffix: inputObj.formatLabelSuffix
    };
    return this;
  }

  getTickFormat () {
    return this.config.tickFormat;
  }

  getAxisEndLabelDisplaySpace () {
    const chartTop = 0,
      chartLeft = 0,
      chartRight = +this.getFromEnv('chartWidth'),
      chartBottom = +this.getFromEnv('chartHeight'),
      values = this.config.values,
      isReverse = this.getReverse(),
      isVertical = this.config.isVertical,
      space = {};

    let valueLeft,
      valueRight,
      valueTop,
      valueBottom;

    if (isVertical) {
      valueTop = isReverse ? values[values.length - 1] : values[0];
      valueBottom = isReverse ? values[0] : values[values.length - 1];

      space.top = chartTop - Math.min(chartTop, valueTop.y - valueTop.dim.height);
      space.bottom = mathMax(valueBottom.y + valueBottom.dim.height, chartBottom) - chartBottom;
    } else {
      valueLeft = isReverse ? values[values.length - 1] : values[0];
      valueRight = isReverse ? values[0] : values[values.length - 1];

      space.left = chartLeft - (Math.min(chartLeft, valueLeft.x - valueLeft.dim.width));
      space.right = mathMax(valueRight.x + valueRight.dim.width, chartRight) - chartRight;
    }

    return space;
  }
  getDimension () {
    return this.config.axisDimension;
  }
  setDimension (dim) {
    return this.placeAxis(dim);
  }

  /**
   * setter method for scale mode
   *
   * @param {any} mode mode in which ticks will be calculated
   * @memberof Axis
   */
  setScaleMode (mode) {
    this.config.scale.setMode(mode);
  }

  /**
   * getter method for scale mode
   *
   * @returns {String} mode
   * @memberof Axis
   */
  getScaleMode () {
    return this.config.scale.getMode();
  }

  /**
   * setter API for time format in time scale
   *
   * @param {Object} format
   * @memberof Axis
   */
  setTimeFormat (format) {
    let axisConfig = this.config,
      scale = axisConfig.scale;

    axisConfig.timeFormat = format;
    scale.setTimeFormat && scale.setTimeFormat(format);
  }

  /**
   * getter API for time format
   *
   * @memberof Axis
   */
  getTimeFormat () {
    return this.config.timeFormat;
  }

  setTickVisibilityLimit (limit) {
    this.config.tickVisibilityLimit = { ...limit };
  }

  getTickVisibilityLimit () {
    return this.config.tickVisibilityLimit;
  }
}

export default Axis;
