import ReferenceLine from '../components/reference-line';
export default canvas => {
  canvas.config.yConfigs.forEach(yConfig => {
    let scale = yConfig.scale;
    if (canvas.config.enableReferenceLine && Array.isArray(yConfig.referenceline)) {
      yConfig.referenceline.forEach(referencelineConf => {
        let refLine = canvas.attachChild(ReferenceLine, 'referenceLine');
        refLine.addEventListener('fc-mouseover', refLine.hoverInHandler);
        refLine.addEventListener('fc-mousemove', refLine.moveHandler);
        refLine.addEventListener('fc-mouseout', refLine.hoverOutHandler);
        refLine.addEventListener('fc-click', refLine.clickHandler);
        refLine.configure({
          yScale: scale,
          // direction represent which side the bulb will be on
          // hence, always opposite to the axis alignment
          direction: yConfig.align === 'left' ? 'right' : 'left',
          formatterFn: yConfig.formatterFn,
          prefix: yConfig.formatLabelPrefix,
          suffix: yConfig.formatLabelSuffix,
          referenceLine: referencelineConf
        });
      });
    }
  });
};
