import { UNDEF } from '@fusioncharts/core/src/lib';
import safeMin from '@fusioncharts/utils/src/array/safe-min';
import safeMax from '@fusioncharts/utils/src/array/safe-max';

/**
 * Function to check if the coordinate is inside the radius of the anchor
 * @param   {Object} pointObj The object containing the info about the data
 * point hovered
 * @param   {number} pX x-axis position of the mouse cordinate
 * @param   {number} x the chartX where hovered
 * @param   {number} y the chartY where hovered
 * @param   {String} component component hovered
 * @param   {number} maxRadius maximum radius of the anchor
 * @return {Object} return an object with details of nearest plot and whether
 * it is hovered or not
 */
function isWithinShape (pointObj, pX, x, y, component, maxRadius) {
  var xPos1,
    yPos1,
    xPos2,
    yPos2,
    diff1,
    diff2;

  if (!pointObj) {
    return {
      pointIndex: pX,
      hovered: false,
      component: component
    };
  }

  xPos1 = pointObj.x;
  xPos2 = pointObj.x;
  yPos1 = pointObj.y;
  yPos2 = pointObj.base;

  // Mathematical function to find a point inside a cirlce of given radius
  diff1 = Math.sqrt(Math.pow((x - xPos1), 2) + Math.pow((y - yPos1), 2));
  diff2 = Math.sqrt(Math.pow((x - xPos2), 2) + Math.pow((y - yPos2), 2));

  return {
    pointIndex: pX,
    hovered: diff1 <= maxRadius || diff2 < maxRadius,
    pointObj: pointObj,
    component: component
  };
}

function calculateLimits (dataset) {
  let { indices, data, type, scaleY, scaleX, calculateFromContext } = dataset.config,
    isContinuous = /continuous/.test(dataset.getName()),
    showPlotOverTick = scaleX.showPlotOverTick(),
    padding,
    returnObj = {},
    firstData = data[0],
    lastData = data[data.length - 1],
    isLog = scaleY.getType() === 'log',
    isVisible = dataset.config.visibility === 'visible',
    chart = dataset.getFromEnv('chart'),
    [focusLimitStart, focusLimitEnd] = calculateFromContext
      ? chart.getContextLimit() : chart.getFocusLimit(),
    sanitiseFn = num => {
      if ((isLog && num <= 0)) {
        return null;
      }
      return num;
    },
    minFn = datum => {
      // check if data is withing visible range or not
      if (!(datum[0].end < focusLimitStart) && !(datum[0].start > focusLimitEnd)) {
        return safeMin([datum[indices[1]], datum[indices[2]], datum[indices[3]],
          datum[indices[4]]], sanitiseFn);
      }
      return null;
    },
    maxFn = datum => {
      // check if data is withing visible range or not
      if (!(datum[0].end < focusLimitStart) && !(datum[0].start > focusLimitEnd)) {
        return safeMax([datum[indices[1]], datum[indices[2]], datum[indices[3]],
          datum[indices[4]]], sanitiseFn);
      }
      return null;
    };
  padding = dataset.getPadding();
  if (showPlotOverTick) {
    returnObj.x = [firstData && (firstData[indices[0]].start - padding), lastData && (lastData[indices[0]].start + padding)];
  } else {
    if (isContinuous) {
      returnObj.x = [firstData && ((firstData[indices[0]].start + firstData[indices[0]].end) / 2 - padding), lastData && ((lastData[indices[0]].start + lastData[indices[0]].end) / 2 + padding)];
    } else {
      returnObj.x = [firstData && (firstData[indices[0]].start - padding), lastData && (lastData[indices[0]].end - padding)];
    }
  }

  returnObj.y = isVisible ? [safeMin(data, minFn), safeMax(data, maxFn)] : UNDEF;
  returnObj.baseRequired = true;
  returnObj.type = type; // TODO: Remove this
  return returnObj;
}

/**
  * Function to form the tooltext
  * @param {Object} dataset The dataset's context
  * @param {Boolean} pointFound If any dataset's data is actually hovered
  * @param {Boolean} datasetHovered If the current dataset is hovered
  * @param {number} dataIndex The index of the data in the current dataset that is hovered
*/
function getTooltext (dataset, pointFound, datasetHovered, dataIndex) {
  var config = dataset.config,
    dataInfo = config.dataInfo,
    currData = dataInfo[dataIndex],
    // Setting logic for opacity.
    // When the data of the currentdataset is hovered then all other tooltip's opacity is set to 0.5
    // Rest all the cases the opacity is 1
    opacity = pointFound ? (datasetHovered ? 1 : 0.5) : 1,
    line2;

  if (dataset._isInvalidTooltext(currData)) {
    return '';
  }

  line2 = ['high', 'low'].reduce((accumulator, currentValue) => {
    let value = currData[currentValue + 'TooltipValue'] || (currData[currentValue + 'TooltipValue'] = currData[currentValue + 'Value'] && config.formatterFn({
      value: currData[currentValue + 'Value'],
      type: 'tooltip',
      prefix: config.prefix,
      suffix: config.suffix
    })
    );
      // Only add a line for H/L if it has a value calculated from above
    return accumulator + ((value !== UNDEF) ? `<div style = 'overflow: hidden;margin-top:4px;'>
      <div style = 'float: left; padding-right: 5px'>${currentValue}</div>
      <div style = 'float: right; padding-right: 5px'>${value}</div>
     </div>` : '');
  }, '');

  line2 = `<div style='margin-top:6px; overflow: hidden; opacity:${opacity}'>
              ${line2}
            </div>`;
  return line2;
}

export {
  isWithinShape,
  calculateLimits,
  getTooltext
};
