/* eslint-disable complexity */
import transpose from 'ramda/es/transpose';
import Line from '../components/dataset/line';
import Column from '../components/dataset/column';
import CandleStick from '../components/dataset/candlestick';
import OHLC from '../components/dataset/ohlc';
import StackGenerator from '@fusioncharts/utils/src/stack-generator';
import offsetDiverge from '@fusioncharts/utils/src/stack-generator/offsets/diverge';
import offsetNull from '@fusioncharts/utils/src/stack-generator/offsets/null';
import { UNDEF, extend2, pluck } from '@fusioncharts/core/src/lib';
import ColumnRange from '../components/dataset/column-range';
import AreaRange from '../components/dataset/area-range';
import FCSEP from '@fusioncharts/utils/src/string/fc-time-separator';

const LINE = 'line',
  getDataset = plotType => {
    switch (plotType) {
      case 'column':
        return Column;
      case 'candlestick':
        return CandleStick;
      case 'column-range':
        return ColumnRange;
      case 'area-range':
        return AreaRange;
      case 'ohlc':
        return OHLC;
      default:
        return Line;
    }
  },
  getSeriesName = str => str.split(FCSEP)[0],
  getMeasureOpName = str => str.split(FCSEP).slice(1).join(FCSEP),
  getVisibility = bool => bool ? 'visible' : 'hidden',
  isOHLC = plotType => plotType === 'ohlc' || plotType === 'candlestick',
  isRangedPlot = plotType => plotType === 'column-range' || plotType === 'area-range',
  isPlotStackable = plotType => {
    return plotType === 'area' || plotType === 'smooth-area' ||
      plotType === 'step-area' || plotType === 'column';
  },
  getAggregationName = (str) => {
    if (str === 'avg') {
      return 'average';
    }
    return str;
  },
  getAggregationType = (input) => {
    let aggObj = {},
      values;
    if (typeof input === 'string') {
      values = input.split(FCSEP);
      return getAggregationName(values[values.length - 1]);
    }
    input.forEach((currInput) => {
      if (currInput) {
        values = currInput.split(FCSEP);
        aggObj[values[0].trim()] = getAggregationName(values[1].trim());
      }
    });
    return aggObj;
  },
  selector = plot => {
    if (plot.stack || !plot.tableInfo.filterItem) {
      return {
        [plot.value[0]]: getSeriesName(plot.value[0])
      };
    }
    return {
      [plot.value[0]]: plot.tableInfo.filterItem
    };
  };

export default canvas => {
  const chart = canvas.getFromEnv('chart'),
    legendMap = canvas.getFromEnv('legendMap'),
    canvasConf = canvas.config,
    dataSource = canvas.getFromEnv('dataSource'),
    plotConfigurations = dataSource.plotconfig || {},
    ordinalScale = canvas.getFromEnv('ordinalScale'),
    { isContext, multiSeriesDatasetMap: msDsMap, enableMarkers } = canvasConf,
    xConfigs = isContext
      ? chart.config.contextAxesX
      : chart.config.focusAxesX,
    yConfigs = isContext
      ? chart.config.contextAxesY
      : chart.config.focusAxesY,
    prediction = canvas.getFromEnv('prediction'),
    predictiveDate = prediction
      ? canvas.getFromEnv('baseTimeConverter').parse(prediction.date)
      : UNDEF;

  prediction.dateMs = prediction.enabled
    ? predictiveDate ? predictiveDate.getTime() : Date.now()
    : UNDEF;
  if (isContext) {
    canvasConf.plotConfigs.forEach(({ plots }) => {
      // when there are multiple columns in a single canvas then it will be rendered as overlapped area in
      // navigator
      const isArea = plots.filter(({ plottype }) => plottype === 'column').length > 1 ? 'area' : null;
      plots.forEach((ob) => {
        if (isRangedPlot(ob.plottype)) {
          // as of now when dataset factory is called for time navigator
          // it iterates on value. So there is a need to map ob.high to ob.value
          // such that no error is thrown.
          ob.value = ob.high;
          ob.isRange = true;
          ob.plottype = (typeof ob.typeinnavigator === 'string' && ob.typeinnavigator) || 'line';
        } else if (isOHLC(ob.plottype)) {
          // For plotting the navigator chart, use whichever is present in the below order
          ob.value = ob.close || ob.low || ob.high || ob.open || [];
          ob.plottype = (typeof ob.typeinnavigator === 'string' && ob.typeinnavigator) || 'line';
        } else if (ob.plottype === 'column') {
          ob.plottype = ob.typeinnavigator || isArea || (ob.value.length > 1 ? 'area' : 'line');
        }
      });
    });
  }

  canvasConf.plotConfigs.forEach((plotConfig, k) => {
    const xConfig = xConfigs[plotConfig.x],
      yConfig = yConfigs[plotConfig.y],
      binDecider = xConfig.binDecider,
      scaleX = xConfig.scale,
      scaleY = yConfig.scale,
      plotGenericStyle = yConfig.plotstyle || {},
      // the total no of columns plot will be total group,
      columnPlotInfo = plotConfig.plots.filter(({ plottype }) => plottype === 'column'),
      totalColumnPlots = columnPlotInfo.length,
      // storing the names of the series for non stacking mode only, since in stacking mode
      // there is no indexing problem
      columnSeriesNames = columnPlotInfo.map(selector);

    let currentColumnIndex = 0;

    plotConfig.plots.forEach((plot, j) => {
      let nodeInfo = plot.tableInfo,
        nodeTable = nodeInfo.table,
        filterItem = nodeInfo.filterItem,
        oriPlotInfo = yConfig.plot[plot.plotInAxisIndex],
        plotInlineStyle = oriPlotInfo.style || {},
        mergedInlineStyle,
        connectNullData = oriPlotInfo.connectnulldata,
        plotCosmetics,
        genericCosmetics,
        tableData = nodeTable.getData(),
        data = tableData.data,
        dataTable = nodeTable,
        timeFormatterFn = xConfig.timeFormatterFn,
        plotType,
        groupIndex,
        totalGroups,
        Dataset,
        isVisible,
        measures = [oriPlotInfo.value],
        seriesInfo = {};

      // if a key is present then the info will be set
      oriPlotInfo.group && (seriesInfo[oriPlotInfo.group] = filterItem);

      mergedInlineStyle = extend2(extend2({}, plotGenericStyle), plotInlineStyle);
      if (!isContext && (isOHLC(plot.plottype) || isRangedPlot(plot.plottype))) {
        let arr = plot.close || plot.open || plot.high || plot.low;
        // for stack or group in ohlc it will be represented as line
        if (arr.length > 1 || filterItem) {
          plot.plottype = LINE;
          plot.value = arr;
        }
      }
      plotType = plot.plottype;
      plotCosmetics = plotConfigurations[plotType] || {};
      genericCosmetics = plotConfigurations.generic || {};
      Dataset = getDataset(plotType);

      if (plotType === 'column') {
        // only for column plots the groupIndex and totalGroups will be provided
        groupIndex = currentColumnIndex++;
        totalGroups = totalColumnPlots;
      }

      if (isOHLC(plotType)) {
        const ohlcs = transpose([plot.open || [plot.open], plot.high || [plot.high], plot.low || [plot.low], plot.close || [plot.close]].filter(s => !!s)),
          { open, high, low, close } = oriPlotInfo;

        if (open || high || low || close) {
          measures[0] = {
            open,
            high,
            low,
            close
          };
        }

        ohlcs.forEach((ohlc, i) => {
          // created unique id for better resuse
          let dsInstance = canvas.attachChild(Dataset, 'dataset', '' + plot.value + plotType + i + j + k);

          dsInstance.addToEnv('binDecider', binDecider);
          dsInstance.addToEnv('xScale', scaleX);
          dsInstance.addToEnv('yScale', scaleY);

          dsInstance.configure({
            data,
            scaleX,
            scaleY,
            formatterFn: yConfig.formatterFn,
            timeFormatterFn,
            yAxisAlign: pluck(yConfig.align, 'left'),
            styleConfig: mergedInlineStyle,
            plotCosmetics,
            genericCosmetics,
            aggregation: getAggregationType(ohlc),
            prefix: yConfig.formatLabelPrefix,
            suffix: yConfig.formatLabelSuffix,
            indices: [dataTable.indexOf(nodeInfo.position)].concat(
              ohlc.map(name => dataTable.indexOf(name))
            ),
            enableMarkers,
            // primary color is send as boolean to indicate that DS should configure the color
            primaryColor: true,
            type: plotType,
            series: plot.value,
            measures,
            calculateFromContext: isContext,
            prediction,
            useNullStyles: !isContext
          });
        });
      } else if (isRangedPlot(plotType)) {
        const ranges = transpose([plot.high || [plot.high], plot.low || [plot.low]].filter(s => !!s)),
          { high, low } = oriPlotInfo;

        if (high || low) {
          measures[0] = {
            high,
            low
          };
        }

        if (plot.name) {
          isVisible = legendMap[plot.name] && legendMap[plot.name].visibility;
          isVisible === UNDEF && (isVisible = true);
        } else {
          isVisible = true;
        }

        ranges.forEach((range, i) => {
          // created unique id for better resuse
          let dsInstance = canvas.attachChild(Dataset, 'dataset', '' + plot.value + plotType + i + j + k);

          dsInstance.addToEnv('binDecider', binDecider);
          dsInstance.addToEnv('xScale', scaleX);
          dsInstance.addToEnv('yScale', scaleY);

          dsInstance.configure({
            data,
            scaleX,
            scaleY,
            visibility: getVisibility(isVisible),
            formatterFn: yConfig.formatterFn,
            timeFormatterFn,
            yAxisAlign: pluck(yConfig.align, 'left'),
            styleConfig: mergedInlineStyle,
            plotCosmetics,
            genericCosmetics,
            aggregation: getAggregationType(range),
            prefix: yConfig.formatLabelPrefix,
            suffix: yConfig.formatLabelSuffix,
            connectNullData,
            indices: [dataTable.indexOf(nodeInfo.position)].concat(
              range.map(name => dataTable.indexOf(name))
            ),
            enableMarkers,
            // primary color is send as boolean to indicate that DS should configure the color
            primaryColor: plot.name ? ordinalScale.getRangeValue(plot.name) : ordinalScale.getRangeValue(plot.high[0] + FCSEP + plot.low[0]),
            type: plotType,
            series: plot.value,
            measures,
            calculateFromContext: isContext,
            prediction,
            useNullStyles: !isContext
          });
        });
      } else {
        if (plot.stack && isPlotStackable(plotType)) {
          const xIndex = dataTable.indexOf(nodeInfo.position),
            sumColumn = `${getMeasureOpName(plot.value[0])}${FCSEP}sum`,
            generator = new StackGenerator()
              .setValueAccessor((d, key) => {
                if ((legendMap[getSeriesName(key)] || {}).visibility) {
                  return d[dataTable.indexOf(key)];
                }
                return 0;
              })
              .setKeysAccessor(() =>
                // the keys will be set according to the column names present in the dataTable
                plot.value.filter(name => dataTable.indexOf(name) >= 0)
              )
              .setOffset(scaleY.getType() === 'log' ? offsetNull : offsetDiverge);

          dataTable.addColumns({
            name: sumColumn,
            type: 'number',
            calcFn: (row, cols) => plot.value.reduce(
              (acc, val) => {
                if ((legendMap[getSeriesName(val)] || {}).visibility) {
                  return acc + row[cols[val]];
                }
                return acc;
              },
              0
            )
          });

          // the data mapping for the each DS is created
          let series = generator.generate(dataTable.getData().data),
            seriesLength = series.length;

          series.forEach((serie, index) => {
            // created unique id for better resuse
            let dsInstance = canvas.attachChild(Dataset, 'dataset', serie.key + plotType + index + j + k),
              seriesName = getSeriesName(serie.key),
              serieData = serie.map(s => [
                s.data[xIndex],
                s[0],
                s[1],
                s.data[dataTable.indexOf(sumColumn)]
              ]);
            isVisible = legendMap[seriesName] && legendMap[seriesName].visibility;
            isVisible === UNDEF && (isVisible = true);
            oriPlotInfo.stack && (seriesInfo[oriPlotInfo.stack] = seriesName);

            dsInstance.addToEnv('binDecider', binDecider);
            dsInstance.addToEnv('xScale', scaleX);
            dsInstance.addToEnv('yScale', scaleY);
            // unique id for each dataset in canvas
            if (filterItem) {
              msDsMap[`${filterItem}${FCSEP}${serie.key}`] = dsInstance;
            } else {
              msDsMap[serie.key] = dsInstance;
            }

            dsInstance.configure({
              data: serieData,
              datasetIndex: index,
              seriesLength: seriesLength,
              aggregation: getAggregationType(serie.key),
              visibility: getVisibility(isVisible),
              scaleX,
              scaleY,
              timeFormatterFn,
              legendInteracted: canvasConf.legendInteracted,
              groupIndex,
              yAxisAlign: pluck(yConfig.align, 'left'),
              totalGroups,
              formatterFn: yConfig.formatterFn,
              prefix: yConfig.formatLabelPrefix,
              suffix: yConfig.formatLabelSuffix,
              styleConfig: mergedInlineStyle,
              plotCosmetics,
              genericCosmetics,
              connectNullData,
              indices: [0, 2, 1, 3],
              primaryColor: ordinalScale.getRangeValue(seriesName),
              type: plotType,
              series: seriesName,
              enableMarkers,
              measures,
              calculateFromContext: isContext,
              seriesInfo: Object.assign({}, seriesInfo),
              prediction,
              useNullStyles: !isContext
            });
          });
        } else {
          let meanColumn;
          if (plot.isRange) {
            const ranges = transpose([plot.high || [plot.high], plot.low || [plot.low]].filter(s => !!s));
            let indices;
            meanColumn = `${getMeasureOpName(plot.high[0])}${FCSEP}${getMeasureOpName(plot.low[0])}${FCSEP}mean`;
            ranges.forEach((range) => {
              indices = range.map(name => dataTable.indexOf(name));
              dataTable.addColumns({
                type: 'number',
                'name': meanColumn,
                calcFn: (row) => (row[indices[0]] + row[indices[1]]) / 2
              });
            });
          }
          plot.value.forEach((name, i) => {
            let seriesName = plot.stack ? getSeriesName(name)
                : (plot.isRange ? (plot.name ? plot.name : plot.high[0] + FCSEP + plot.low[0])
                  : (filterItem || getSeriesName(name))),
              // created unique id for better resuse
              dsInstance = canvas.attachChild(Dataset, 'dataset', seriesName + plotType + i + j + k),
              status,
              currentGroupIndex,
              currentTotalGroups;

            isVisible = legendMap[seriesName] && legendMap[seriesName].visibility;
            isVisible === UNDEF && (isVisible = true);

            if (plotType === 'column') {
              // for column plots, we filter the status of with respect to visibility
              // the indexing will be with respect to new status
              status = columnSeriesNames.filter(obj => legendMap[Object.values(obj)[0]].visibility);
              currentGroupIndex = status.findIndex(obj => {
                let key = Object.keys(obj)[0];
                if (filterItem) {
                  if (key === name && obj[key] === filterItem) {
                    return true;
                  }
                  return false;
                }
                return key === name;
              });
              if (currentGroupIndex !== -1) {
                currentTotalGroups = status.length;
              } else {
                currentGroupIndex = UNDEF;
              }
            }

            if (plot.stack) {
              seriesInfo[plot.stack] = seriesName;
            }

            dsInstance.addToEnv('binDecider', binDecider);
            dsInstance.addToEnv('xScale', scaleX);
            dsInstance.addToEnv('yScale', scaleY);
            dsInstance.configure({
              data,
              scaleX,
              scaleY,
              timeFormatterFn,
              legendInteracted: canvasConf.legendInteracted,
              groupIndex: currentGroupIndex,
              totalGroups: currentTotalGroups,
              visibility: getVisibility(isVisible),
              yAxisAlign: pluck(yConfig.align, 'left'),
              formatterFn: yConfig.formatterFn,
              prefix: yConfig.formatLabelPrefix,
              suffix: yConfig.formatLabelSuffix,
              styleConfig: mergedInlineStyle,
              aggregation: getAggregationType(name),
              plotCosmetics,
              genericCosmetics,
              connectNullData,
              indices: [
                dataTable.indexOf(nodeInfo.position),
                plot.isRange ? dataTable.indexOf(meanColumn) : dataTable.indexOf(name)
              ],
              primaryColor: ordinalScale.getRangeValue(seriesName),
              type: plotType,
              series: seriesName,
              enableMarkers,
              measures,
              calculateFromContext: isContext,
              seriesInfo: Object.assign({}, seriesInfo),
              prediction,
              useNullStyles: !isContext
            });
          });
        }
      }
    });
  });
};
