import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import CustomRangeSelectorTool from './selector-tool';

class CustomRangeSelector extends SmartRenderer {
  /**
   * configures the component
   *
   * @param {any} [inputConfig={}] configuration
   * @memberof CustomRangeSelector
   */
  configureAttributes (inputConfig = {}) {
    Object.assign(this.config, inputConfig);
  }

  /**
   * Called when the event 'focusLimitChanged' is triggered
   *
   * @memberof CustomRangeSelector
   */
  updateOnLimitChange () {
    this.setData({
      domain: this.getFromEnv('chart').getFocusLimit(),
      limitChanged: true
    }, true);
  }

  /**
   * returns the tool requirements of this components
   *
   * @returns {Object} requirements
   * @memberof CustomRangeSelector
   */
  getToolInfo () {
    let styleObj = Object.assign({}, this.config.style);
    styleObj['title:hoverout'] = {
      title: styleObj['title-text'],
      icon: styleObj['title-icon']
    };
    return {
      'selector-0': {
        type: 'tool',
        def: CustomRangeSelectorTool,
        configuration: {
          domain: this.config.domain,
          hAlign: 'right',
          scale: 1,
          marginTop: 2,
          marginBottom: 2,
          marginLeft: 2,
          marginRight: 2,
          extStyle: styleObj,
          limitChanged: this.config.limitChanged
        }
      }
    };
  }
}

export default CustomRangeSelector;
