/* eslint require-jsdoc: 'error', valid-jsdoc: 'error' */
const DEFAULT_UNKNOWN = 'implicit';
/**
 * Ordainal class which maps ordered items to the range
 */
class ScaleOrdinal {
  /**
   * constructs the class with default value
   */
  constructor () {
    this.unknown = DEFAULT_UNKNOWN;
    this.domain = [];
    this.ordinalRange = [];
    this.map = new Map();
  }
  /**
   * sets the domain with unique values and creates its mapping
   * @param {Array} inputArr is the input array contianig the domian
   * @return {Object} instance of scale
   */
  setDomain (inputArr = []) {
    var i,
      domainVal,
      domainValStr;
    this.domain = [];
    this.map.clear();
    for (i = 0; i < inputArr.length; ++i) {
      domainVal = inputArr[i];
      domainValStr = domainVal.toString();
      if (!this.map.has(domainVal)) {
        this.map.set(domainValStr, this.domain.push(domainVal));
      }
    }
    return this;
  }
  /**
   * returns the currnet domain
   * @return {Array} domain array
   */
  getDomain () {
    return this.domain.slice();
  }
  /**
   * sets the range
   * @param {Array} inputArr is the input array contianig the range
   * @return {Object} instance of scale
   */
  setRange (inputArr) {
    this.ordinalRange = inputArr.slice();
    return this;
  }
  /**
   * returns the currnet range
   * @return {Array} range array
   */
  getRange () {
    return this.ordinalRange.slice();
  }
  /**
   * sets the output value of the scale for unknown input values
   * @param {any} unknown any value
   * @return {Object} instance of the class
   */
  setUnknown (unknown) {
    this.unknown = unknown;
    return this;
  }
  /**
   * return the unknown value which is by default implicit,
   * thi implicit value enables implicit domain contruction
   * @return {any} unknown
   */
  getUnknown () {
    return this.unknown;
  }
  /**
   * returns a new instance of the class with same domain, range and unkown value
   * @return {Object} new instance
   */
  copy () {
    return new ScaleOrdinal()
      .setDomain(this.getDomain())
      .setRange(this.getRange())
      .setUnknown(this.geUnknownn());
  }
  /**
   * gives the range value corresponding to a domain
   * @param {any} domainKey a value
   * @return {any} corresponding range value
   */
  getRangeValue (domainKey) {
    let map = this.map,
      domainKeyStr = domainKey.toString(),
      range = this.getRange(),
      value = map.get(domainKeyStr);
    if (!value) {
      if (this.getUnknown() !== DEFAULT_UNKNOWN) {
        return this.getUnknown();
      }
      value = this.domain.push(domainKey);
      map.set(domainKeyStr, value);
    }
    return range[(value - 1) % range.length];
  }
  getType () {
    return 'ordinal';
  }
}
export default ScaleOrdinal;
