import { componentFactory } from '@fusioncharts/core/src/lib';
import ToolbarManager from '../../../_internal/components/toolbar-manager';

export default chart => {
  let srs = chart.getChildren('standardRangeSelector'),
    crs = chart.getChildren('customRangeSelector'),
    manager;

  componentFactory(chart, ToolbarManager, 'toolbar-manager');

  manager = chart.getChildren('toolbar-manager')[0];

  srs && srs[0] && !srs[0].getState('removed') ? manager.register('srs', srs[0]) : manager.deRegister('srs');
  crs && crs[0] && !crs[0].getState('removed') ? manager.register('crs', crs[0]) : manager.deRegister('crs');

  manager.configure();
};
