import { addDep } from '@fusioncharts/core/src/dependency-manager';

import Column from './column';
import columnAnimation from './column.animation';
import TimeConverter from '@fusioncharts/utils/src/time-converter';
import isValidNumber from '@fusioncharts/utils/src/type/is-valid-number';
import { extend2 } from '@fusioncharts/core/src/lib';
import { getTooltext, calculateLimits } from './ranged-plot-utils';

addDep({
  name: 'timeseriesColumnAnimation',
  type: 'animationRule',
  extension: columnAnimation
});

const VISIBLE = 'visible';

class ColumnRange extends Column {
  __setDefaultConfig () {
    super.__setDefaultConfig();
    let config = this.config;
    config['default-plotSpacePercent'] = 30;
    config.visibility = VISIBLE;
    config.isRange = true;
    config.prevBoundaryInfo = {};
    config.dataMarkerPadding = 0;
    config.style = {};
    config.hoverInStyle = {};
    config.highlightStyle = {};
    config.hoverOutStyle = {};
    config.defaultStyle = {
      'stroke-width': 1,
      'fill-opacity': 1,
      'stroke-opacity': 1,
      'opacity': 1
    };
    config.defaultPredictedDataStyle = {
      'stroke-width': 1,
      'fill-opacity': 0.2,
      'stroke-opacity': 1,
      'stroke-dasharray': 5
    };
    config.defaultHoverInStyle = {
      'opacity': 0.8
    };
    config.defaultHighlightStyle = {
      'opacity': 0.8
    };
    config.dataInfo = [];
    config.prevPerBinYExtents = [];
    config.dataIndices = [1, 2];
  }

  allocatePosition () {
    let dataset = this,
      config = dataset.config,
      [xIdx, highIdx, lowIdx] = config.indices,
      binDecider = dataset.getFromEnv('binDecider'),
      xScale = dataset.getFromEnv('xScale'),
      yScale = dataset.getFromEnv('yScale'),
      dataInfo = config.dataInfo,
      seriesInfo = config.seriesInfo,
      dateColumn = dataset.getFromEnv('dateColumn'),
      isUTC = dataset.getFromEnv('isUTC'),
      height,
      width,
      date,
      x,
      y,
      high,
      low,
      yBaseValue,
      yPos,
      yBase,
      hoverInfo,
      isNegative,
      pX,
      xShift,
      endXPosition,
      groupIndex = config.groupIndex || 0,
      groupWidth,
      totalGroups = config.totalGroups || 1,
      availableWidth,
      data = config.data,
      duration,
      eventArgs,
      seriesName,
      format = dateColumn.format,
      formatter = isUTC
        ? TimeConverter.utcFormatter(format)
        : TimeConverter.formatter(format),
      startDate,
      endDate,
      midGroup,
      gutter,
      i,
      len,
      bins = xScale.bins,
      plotBorderThickness = config.plotBorderThickness,
      // plot border overlapping is considered only when borders are in different color
      considerPlotBorder =
        config.fillColor === config.strokeColor && plotBorderThickness > 0,
      paddingInTimestamp,
      rangeThreshold = binDecider.getRangeThreshold()[2],
      [startDomain, endDomain] = xScale.getDomain(),
      dataObj;

    config.firstTimeStamp =
      data && data[0] && data[0][xIdx] && data[0][xIdx].start;
    if ((config.repositioningDone = dataset._isRepositioningNeeded())) {
      // skip drawing if dataset is not visible
      if (config.visibility !== 'visible') {
        return;
      }

      // Store data related operations
      dataInfo = config.dataInfo = [];

      // Getting the pixel width between two consecutive data
      config.availableWidth = availableWidth = 0;
      // Storing info for dataMarker detection
      for (i = 0, len = bins.length; i < len; i++) {
        if (bins[i].clipType !== 2) {
          config.availableWidth = availableWidth = xScale.getRangeValue(bins[i].end) - xScale.getRangeValue(bins[i].start);
          break;
        }
      }
      config.timeStampGap = rangeThreshold;

      // Avoid negative thickness when border thickness is more than the actual available width
      groupWidth = Math.max(
        availableWidth * (1 - config.plotSpacePercent / 100),
        1
      );

      // Logic for multi-series
      width = groupWidth / totalGroups;
      midGroup = Math.round(totalGroups / 2);
      gutter = totalGroups % 2 === 0 ? 0 : 0.5;
      xShift =
        midGroup - groupIndex > 0
          ? -width * (midGroup - groupIndex - gutter)
          : width * (groupIndex - midGroup + gutter);

      paddingInTimestamp =
        +xScale.getDomainValue(
          availableWidth * (config.plotSpacePercent / 200)
        ) - +xScale.getDomainValue(0);
      // Storing value for tooltip
      config.actualStartDomain = +startDomain + paddingInTimestamp;
      // Actual end domain is the scale domain minus the space taken up by the plotspacepercent
      config.actualEndDomain = +endDomain - paddingInTimestamp;

      // Adjusting column width as per the column border
      considerPlotBorder && (width = Math.max(width - plotBorderThickness, 1));
      data.forEach((datum, index) => {
        date = datum[xIdx];
        high = Math.max(datum[highIdx], datum[lowIdx]);
        low = Math.min(datum[lowIdx], datum[highIdx]);
        duration = date.config.duration;
        startDate = date.start;
        endDate = date.end;
        // Storing the 1st time stamp
        if (!index) {
          config.firstTimeStamp = date.start;
        }
        yBaseValue = low;
        yBase = yScale.getRangeValue(
          yBaseValue || Math.max(yScale.getDomain()[0], 0)
        );
        yPos = yScale.getRangeValue(high);
        if (
          isValidNumber(high) &&
          isValidNumber(low) &&
          isValidNumber(yBase) &&
          isValidNumber(yPos)
        ) {
          // Find the position where the plot is to be placed
          x = xScale.getRangeValue(new Date(date.start), new Date(date.end));
          // Adjusting column starting position as per the column border
          considerPlotBorder && (x += plotBorderThickness / 2);

          x += xShift;
          endXPosition = x + width;
          // Adding the base to manage the stacking
          height = Math.max(1, Math.abs(yBase - yPos));
          isNegative = yPos > yBase;
          y = isNegative ? yBase : yPos;
          dataObj = {
            startDate,
            endDate,
            high,
            low,
            highValue: high,
            lowValue: low,
            yBaseValue,
            yBase,
            x,
            endXPosition,
            y,
            width,
            height,
            eventArgs: {
              index,
              high,
              low,
              highValue: high,
              lowValue: low
            }
          };

          eventArgs = {
            index,
            high,
            low,
            highValue: high,
            lowValue: low,
            start: startDate,
            startText: formatter.format(startDate),
            end: endDate,
            endText: formatter.format(endDate),
            binUnit: duration.Unit,
            binMultiplier: duration.number,
            plotType: config.type,
            timeFormatter: format,
            aggregation: config.aggregation,
            measure: config.measures[0]
            // binValue: value,
            // binValueFormatted:
            //   config.formatterFn &&
            //   config.formatterFn({
            //     value: value,
            //     type: 'tooltip',
            //     prefix: config.prefix,
            //     suffix: config.suffix
            //   })
          };
          seriesName = false && Object.keys(seriesInfo)[0];
          if (seriesName) {
            eventArgs.series = seriesName;
            eventArgs.seriesValue = seriesInfo[seriesName];
          }
          dataObj.eventArgs = eventArgs;

          // Get the data index corresponding to the fetched timeStamp
          pX = xScale.getBinIndex(date.start);
          dataInfo[pX] = dataObj;
        }
      });
    }

    // If hovered, show hover effect of the particular plot
    if ((hoverInfo = config.hoverInfo)) {
      hoverInfo.forEach(hoveredData => {
        hoveredData.hoverIndices.forEach(index => {
          if (
            typeof dataInfo[index] === 'object' &&
            typeof hoveredData.style === 'object'
          ) {
            dataInfo[index].style = extend2(
              dataInfo[index].style,
              hoveredData.style
            );
          }
        });
      });
    }
  }

  _calculateLimits () {
    return calculateLimits(this);
  }

  /**
   * Sets the name of the component
   * @return {string} name
   */
  getName () {
    return 'column-range';
  }

  _getTooltext (pointFound, datasetHovered, dataIndex) {
    return getTooltext(this, pointFound, datasetHovered, dataIndex);
  }
}

export default ColumnRange;
