import SmartRenderer from '@fusioncharts/core/src/component-interface/smart-renderer';
import timeConverter from '@fusioncharts/utils/src/time-converter';
import { pluck, parseUnsafeString, defined, getMouseCoordinate, extend2, UNDEF } from '@fusioncharts/core/src/lib';
import { convertColor } from '@fusioncharts/core/src/lib/lib-graphics';
import { getMinPlaceHolder, getPlaceHolderIndex } from '@fusioncharts/utils/src/time-bucket';
import { addDep } from '@fusioncharts/core/src/dependency-manager';
import dataMarkerAnimation from './data-marker.animation';
import isArray from '@fusioncharts/utils/src/type/is-array';
import isObject from '@fusioncharts/utils/src/type/is-object';
import _drawFlagMarker from './flag-marker-drawable';
import _drawPinMarker from './pin-marker-drawable';
import equals from 'ramda/es/equals';
import isNumber from '@fusioncharts/utils/src/type/is-number';

const markerDimensions = {
    flagHeight: 16,
    flagWidth: 16,
    stickLength: 10,
    pinHeight: 23,
    pinWidth: 23,
    totalFlagHeight: 26
  },
  DASH_STR = '—',
  VISIBLE = 'visible',
  DEFAULT_FONT_COLOR = '#ffffff',
  DEFAULT_STROKE_COLOR = '#8c8c8c',
  DEFAULT_TOOLTIP_BACKGROUND = '#f2eded',
  ROLLOVER = 'DataMarkerRollOver',
  ROLLOUT = 'DataMarkerRollOut',
  CLICK = 'dataMarkerClick',
  TYPE_PIN = 'pin',
  getSameProperty = (arr = []) => {
    if (!isArray(arr)) return {};
    let output = {},
      key,
      j,
      obj1 = isObject(arr[0]) ? arr[0] : {},
      len = arr.length, // eslint-disable-next-line
      filterFn = obj => isObject(obj) && obj.hasOwnProperty(key) && obj[key] == obj1[key];
    for (key in obj1) {
      if (obj1.hasOwnProperty(key)) {
        let addProp = true;
        for (j = 1; j < len; ++j) {
          if (!filterFn(arr[j])) {
            addProp = false;
            break;
          }
        }
        if (addProp) {
          output[key] = obj1[key];
        }
      }
    }
    return output;
  },
  checkForValidCoordinated = (coordiante) => {
    let valid = true,
      key;
    for (key in coordiante) {
      if (coordiante.hasOwnProperty(key)) {
        valid = valid && !isNaN(coordiante[key]);
      }
    }
    return valid;
  },
  _isMarkerInBounds = function (width, height, chartX, chartY, centerX, y) {
    return (chartX >= centerX - width / 2 && chartX <= centerX + width / 2 &&
      chartY <= y && chartY >= y - height);
  };

addDep({
  name: 'dataMarkerAnimation',
  type: 'animationRule',
  extension: dataMarkerAnimation
});
/**
 * This class is used to manage and draw the flag markers of timeseries
 */
class DataMarker extends SmartRenderer {
  configure (dataObj) {
    super.configure(dataObj);
    var config = this.config,
      styleInfo = config.styleInfo,
      currStyle,
      styleObj;
    if (isArray(dataObj.data)) {
      let oldData = config.unsortedData;
      config.unsortedData = dataObj.data.slice();
      if (!equals(oldData, config.unsortedData)) {
        // data for tooltip will be sorted
        config.data = dataObj.data.slice();
        config.data.sort((a, b) => timeConverter.parser(a.timeformat).parse(a.time) -
        timeConverter.parser(b.timeformat).parse(b.time));
      }
    }
    config.isStock = pluck(dataObj.isStock, config.isStock);
    config.isRange = pluck(dataObj.isRange, config.isRange);
    // Stroring the information in style Object separately to be merged after grouping
    if ((styleObj = dataObj.style)) {
      currStyle = styleInfo[dataObj.index] || (styleInfo[dataObj.index] = {});
      // Setting the given styles for the respective data markers
      for (let styles in styleObj) {
        if (styleObj.hasOwnProperty(styles)) {
          currStyle[styles] = styleObj[styles];
        }
      }
    }

    config.skipGrouping = dataObj.skipGrouping;
    defined(dataObj.visibility) && (config.visibility = dataObj.visibility);
  }

  constructor () {
    super();
    this.config.styleInfo = {};
  }

  /**
   * Function to set hover in effect
   * @param {number} index data index
   */
  setHoverInEffect (index) {
    let marker = this,
      markerConfig = marker.config,
      hoveredMarkers = markerConfig.markerInfo[index].markers,
      style = hoveredMarkers[0].style || {},
      markerHoverStyle = {},
      markerHoverStyles = {},
      textHoverStyle = {},
      textHoverStyles = {},
      styleDef = marker.getFromEnv('getStyleDef');

    if (hoveredMarkers.multipleMarkers) {
      markerHoverStyles = hoveredMarkers.markers.map(markerInfo => (markerInfo.style && styleDef(markerInfo.style['marker:hover'])) || {});
      textHoverStyles = hoveredMarkers.markers.map(markerInfo => (markerInfo.style && styleDef(markerInfo.style['text:hover'])) || {});
      Object.assign(markerHoverStyle, markerConfig.defaultHoverStyle, getSameProperty(markerHoverStyles));
      Object.assign(textHoverStyle, markerConfig.defaultHoverStyle, getSameProperty(textHoverStyles));
    } else {
      Object.assign(markerHoverStyle, markerConfig.defaultHoverStyle, styleDef(style['marker:hover']));
      Object.assign(textHoverStyle, markerConfig.defaultHoverStyle, styleDef(style['text:hover']));
    }
    marker.getFromEnv('animationManager').setAnimationState('mouseOver');
    marker.setData({
      style: {
        marker: markerHoverStyle,
        text: textHoverStyle
      },
      index,
      skipGrouping: true
    }, true);
  }

  /**
   * Function to set hover out effect
   * @param {number} index data index
   */
  setHoverOutEffect (index) {
    let marker = this;

    marker.getFromEnv('animationManager').setAnimationState('mouseOut');
    marker.setData({
      style: {
        marker: {},
        text: {}
      },
      index,
      skipGrouping: true
    }, true);
  }
  /**
   * This method handles all mouse events of a data marker.
   * @param {string} eventType name of the event
   * @param {number} index  index of the data marker where this event has been occured
   * @param {Event}  e  reference of the original mouse event
   * @param {Event}  hoveredInfo the object containing the information about hovering
   */
  _firePlotEvent (eventType, index, e, hoveredInfo) {
    let marker = this,
      markerConfig = marker.config,
      markerInfo = markerConfig.markerInfo,
      eventArgs = (markerInfo[index] && markerInfo[index].eventArgs) || {},
      chart = marker.getFromEnv('chart'),
      coordiante = getMouseCoordinate(chart.getFromEnv('chart-container'), e, chart),
      validCoordinates = checkForValidCoordinated(coordiante),
      args = extend2(coordiante, eventArgs);

    if (validCoordinates && (index !== UNDEF)) {
      switch (eventType) {
        case 'fc-mouseover':
          chart.fireChartInstanceEvent(ROLLOVER, args);
          break;

        case 'fc-mouseout':
          chart.fireChartInstanceEvent(ROLLOUT, args);
          break;

        case 'fc-click':
          chart.fireChartInstanceEvent(CLICK, args);
          break;
        default: break;
      }
    }
  }
  __setDefaultConfig () {
    var config = this.config;
    config.isStock = false;
    config.isRange = false;
    config['default-stroke-width'] = 1;
    config['default-fill-opacity'] = 0.5;
    config['default-stroke-opacity'] = 0.5;
    config['default-font-color'] = DEFAULT_FONT_COLOR;
    config['default-stroke-color-multiple'] = DEFAULT_STROKE_COLOR;
    config['default-tooltip-background'] = DEFAULT_TOOLTIP_BACKGROUND;

    config.defaultMarkerStyle = {
      'stroke-width': 1,
      'fill-opacity': 0.5,
      'stroke-opacity': 0.5
    };
    config.defaultTextStyle = {
      fill: '#ffffff',
      'font-size': '12px',
      'line-height': '14.4px',
      'fill-opacity': 1,
      'stroke-opacity': 1,
      'text-anchor': 'middle',
      'font-weight': 'normal',
      'font-style': 'normal'
    };
    config.defaultHoverStyle = {
      opacity: 1,
      'fill-opacity': 1,
      'stroke-opacity': 1
    };
  }

  draw () {
    if (this.config.visibility === VISIBLE) {
      this._createGroup();
      this._drawMarkers();
    }
  }

  /**
   * Sets the type of the component
   * @return {string} type
   */
  getType () {
    return 'dataMarker';
  }
  /**
   * Sets the name of the component
   * @return {string} name
   */
  getName () {
    return 'dataMarker';
  }

  /**
   * function to check if given x,y coordiante is inside a dataMarker or not
   * @param  {number} pX     Approximate index on the xScale
   * @param  {number} chartX x co-ordinate
   * @param  {number} chartY y co-ordinate
   * @param  {number} catWidth the gap in pixel between two category
   * @return {Object}        hovered information
   */
  _checkPointOverMarker (pX, chartX, chartY, catWidth) {
    var dataMarker = this,
      config = dataMarker.config,
      dataInfo = config.dataInfo,
      currData,
      currentMarker,
      x, y,
      dim = markerDimensions,
      halfWidth,
      centerX,
      returnObj,
      markerInfo = config.markerInfo,
      ceilPx = Math.ceil(pX),
      floorPx = Math.floor(pX),
      sum = catWidth,
      testWidth,
      testHeight,
      // managing overlapping
      indexRange = [ceilPx, floorPx];

    // When the width of the marker covers more than 1 category
    while (sum < dim.flagWidth || sum < dim.pinWidth) {
      // Chance of the marker to be present on the right hand side of the given pX
      indexRange.unshift(++ceilPx);
      // Chance of the marker to be present on the left hand side of the given pX
      indexRange.push(--floorPx);
      sum += catWidth;
    }
    // Traversing over each dataset
    indexRange.forEach(index => {
      currentMarker = markerInfo[index];
      if (!returnObj && currentMarker) {
        currData = dataInfo[index];
        x = currData.x;
        y = currData.y;
        halfWidth = (currData.width || 0) / 2;
        centerX = x + halfWidth;
        if (currentMarker && currentMarker.markers && currentMarker.markers[0].type === TYPE_PIN) {
          testWidth = dim.pinWidth;
          testHeight = dim.pinHeight;
        } else {
          testWidth = dim.flagWidth;
          testHeight = dim.totalFlagHeight;
        }
        // Detection logic for data markers.
        if (_isMarkerInBounds(testWidth, testHeight, chartX, chartY, centerX, y)) {
          returnObj = {
            pointIndex: index,
            hovered: true,
            pointObj: currData,
            markerObj: currentMarker,
            component: dataMarker
          };
        }
      }
    });

    return returnObj;
  }

  /**
   * Getting the tooltext from the data-marker
   * @param {number} index The specific marker that ishovered
   */
  _getTooltext (index) {
    var marker = this,
      config = marker.config,
      markerInfo = config.markerInfo,
      markerObj = markerInfo[index],
      markers = markerObj.markers,
      len = markers.length,
      formatter = marker.getFromEnv('yScale').tickFormat(4, '.2s'),
      multipleMarkers = markerObj.multipleMarkers,
      timeMap = {},
      header,
      body;

    if (!markerObj.formatedTime) {
      markerObj.formatedTime = marker.getFromEnv('xScale').getFormattedTime({
        type: 'tooltip',
        dateRange: markerObj
      });
    }

    header = `<div style='margin-bottom: 6px'>${markerObj.formatedTime}</div>`;

    if (config.isStock) {
      body = `<div style='display: grid; display: -ms-grid; grid-template-columns: auto auto auto auto 1fr; -ms-grid-columns: auto auto auto auto 1fr; grid-template-rows: auto auto; -ms-grid-rows: auto auto; margin-bottom:6px;'>
      <div style='grid-column-start: 1; -ms-grid-column: 1; grid-row-start: 1; -ms-grid-row: 1;'>
      Open:
      </div>
      <div style='grid-column-start: 2; -ms-grid-column: 2; grid-row-start: 1; -ms-grid-row: 1; text-align: center;  margin-left: 6px;'>
      ${isNumber(markerObj.openValue) ? parseUnsafeString(formatter(markerObj.openValue)) : DASH_STR}
      </div>
      <div style='grid-column-start: 3; -ms-grid-column: 3; grid-row-start: 1; -ms-grid-row: 1; margin-left: 12px;'>
      High:
      </div>
      <div style='grid-column-start: 4; -ms-grid-column: 4; grid-row-start: 1; -ms-grid-row: 1; text-align: center; margin-left: 6px;'>
      ${isNumber(markerObj.highValue) ? parseUnsafeString(formatter(markerObj.highValue)) : DASH_STR}
      </div>
      <div style='grid-column-start: 1; -ms-grid-column: 1; grid-row-start: 2; -ms-grid-row: 2;'>
      Low:
      </div>
      <div style='grid-column-start: 2; -ms-grid-column: 2; grid-row-start: 2; -ms-grid-row: 2; text-align: center; margin-left: 6px;'>
      ${isNumber(markerObj.lowValue) ? parseUnsafeString(formatter(markerObj.lowValue)) : DASH_STR}
      </div>
      <div style='grid-column-start: 3; -ms-grid-column: 3; grid-row-start: 2; -ms-grid-row: 2; margin-left: 12px;'>
      Close:
      </div>
      <div style='grid-column-start: 4; -ms-grid-column: 4; grid-row-start: 2; -ms-grid-row: 2; text-align: center; margin-left: 6px;'>
      ${isNumber(markerObj.closeValue) ? parseUnsafeString(formatter(markerObj.closeValue)) : DASH_STR}
      </div>
    </div>`;
    } else if (config.isRange) {
      body = `<div style='display: grid; display: -ms-grid; grid-template-columns: auto auto auto auto 1fr; -ms-grid-columns: auto auto auto auto 1fr; grid-template-rows: auto auto; -ms-grid-rows: auto auto; margin-bottom:6px;'>
      <div style='grid-column-start: 1; -ms-grid-column: 1; grid-row-start: 1; -ms-grid-row: 1;'>
      High:
      </div>
      <div style='grid-column-start: 2; -ms-grid-column: 2; grid-row-start: 1; -ms-grid-row: 1; text-align: center;  margin-left: 6px;'>
      ${isNumber(markerObj.highValue) ? formatter(markerObj.highValue) : DASH_STR}
      </div>
      <div style='grid-column-start: 1; -ms-grid-column: 1; grid-row-start: 2; -ms-grid-row: 2;'>
      Low:
      </div>
      <div style='grid-column-start: 2; -ms-grid-column: 2; grid-row-start: 2; -ms-grid-row: 2; text-align: center; margin-left: 6px;'>
      ${isNumber(markerObj.lowValue) ? formatter(markerObj.lowValue) : DASH_STR}
      </div>
    </div>`;
    } else {
    // Formatting the value to be shown on the tooltip
      if (!markerObj.tooltipValue) {
        markerObj.tooltipValue = formatter(markerObj.value);
      }

      body = `<div style='margin-bottom:6px; height: 14px;'>
    <div style='float: left;'>${parseUnsafeString(markerObj.seriesname)}:<span style='padding-left:8px'>${parseUnsafeString(markerObj.tooltipValue)}</span></div>
    </div>`;
    }
    markers.forEach((currMarker, i) => {
      let margBottom = 6;
      // Setting individual date if multiple markers are present
      if (multipleMarkers && !timeMap[currMarker.time]) {
        timeMap[currMarker.time] = true;
        body +=
        `<div style='clear: both; margin-bottom:2px; font-weight: 600;'>${currMarker.time}</div>`;
        if (i + 1 < len && timeMap[markers[i + 1].time]) {
          margBottom = 2;
        }
      }

      body += currMarker.tooltext ? `<div style='clear: both; ${i < len - 1 ? 'margin-bottom: ' + margBottom + 'px' : ''}'>${parseUnsafeString(currMarker.tooltext, false)}</div>` : '';
    });
    return {
      header,
      body
    };
  }

  /**
   * Function to induce the grouoing of the dataplots
   */
  allocatePosition () {
    !this.config.skipGrouping && this._groupMarkers();
  }

  /**
   * This function determines the grouping and positioning of the data markers
   */
  _groupMarkers () {
    var marker = this,
      parent = marker.getLinkedParent(),
      info = parent._getRelevantInfo(),
      firstTimeStamp = info.firstTimeStamp,
      dataInfo = info.dataInfo,
      config = marker.config,
      markerInfo = (config.markerInfo = {}),
      currMarkerObj,
      index,
      markerTime,
      xScale = marker.getFromEnv('xScale'),
      xScaleDomain = xScale.getDomain(),
      timeGroupLevelDiff,
      binDecider = marker.getFromEnv('binDecider'),
      thresHold = binDecider.getRangeThreshold(),
      currentGrouping = thresHold[0].name(),
      plotInfo,
      seriesInfo,
      seriesName,
      keys,
      data = config.data;

    config.dataInfo = dataInfo;
    data.forEach(currMarker => {
      markerTime = timeConverter.parser(currMarker.timeformat).parse(currMarker.time);
      // Getting the differnce between the current aggregation level and the minimum time stamp entered in datamarker
      timeGroupLevelDiff = getPlaceHolderIndex(currentGrouping) - getPlaceHolderIndex(getMinPlaceHolder(currMarker.timeformat));
      // Checking for the validity of the marker, if it is to be shown in the current aggregation level
      // FTV3-1147: Data marker is drawn at wrong position w.r.t. the specified date
      // Allowing data markers visibility upto two immediate time hierarchy levels
      if (markerTime && timeGroupLevelDiff >= 0 && timeGroupLevelDiff <= 2) {
        markerTime = markerTime.getTime();
        // Markers can be shown only if it is within the starting range
        if (markerTime >= firstTimeStamp && (markerTime >= xScaleDomain[0]) && (markerTime <= xScaleDomain[1])) {
          // Calculation the data index corresponding to the given timestamp
          // FTV3-1147 : Data marker is drawn at wrong position w.r.t. the specified date.
          // passed false parameter
          index = xScale.getBinIndex(markerTime, false);
          // Storing a copy for the datamarker
          if (plotInfo = dataInfo[index]) {
            seriesInfo = currMarker.series || {};
            seriesName = Object.keys(seriesInfo)[0];
            keys = (config.isRange || config.isStock) && (isObject(currMarker.measures[0])
              ? Object.keys(currMarker.measures[0]).toString()
              : currMarker.measures[0]);
            if (!markerInfo[index]) {
              markerInfo[index] = {
                markers: [],
                // Setting unique id of each marker
                id: ((config.isStock || config.isRange)
                  ? keys
                  : currMarker.seriesname) +
                   currMarker.time,
                startDate: plotInfo.startDate,
                seriesname: currMarker.seriesname,
                endDate: dataInfo[index].endDate,
                eventArgs: {
                  timestamp: markerTime,
                  timeText: currMarker.time,
                  tooltipText: [currMarker.tooltext],
                  timeFormatter: currMarker.timeformat,
                  binStart: plotInfo.startDate,
                  binEnd: plotInfo.endDate,
                  binValue: plotInfo.eventArgs.binValue,
                  binValueFormatted: plotInfo.eventArgs.binValueFormatted,
                  measure: currMarker.value,
                  series: seriesName,
                  seriesName: seriesName && seriesInfo[seriesName],
                  identifier: currMarker.identifier
                }
              };
            }
            currMarkerObj = markerInfo[index];
            if (config.isStock) {
              currMarkerObj.openValue = plotInfo.openValue;
              currMarkerObj.highValue = plotInfo.highValue;
              currMarkerObj.lowValue = plotInfo.lowValue;
              currMarkerObj.closeValue = plotInfo.closeValue;
            } else if (config.isRange) {
              currMarkerObj.highValue = plotInfo.highValue;
              currMarkerObj.lowValue = plotInfo.lowValue;
            } else {
              currMarkerObj.value = plotInfo.value;
            }
            currMarkerObj.markers.push(currMarker);
            if (currMarkerObj.markers.length > 1) {
              currMarkerObj.multipleMarkers = true;
              currMarkerObj.eventArgs.tooltipText.push(currMarker.tooltext);
              currMarkerObj.eventArgs.identifier = currMarkerObj.markers.length;
            }
          }
        }
      }
    });
  }
  /**
   * Function to create the group for markers
   */
  _createGroup () {
    var marker = this;
    marker.addGraphicalElement({
      el: 'group',
      container: {
        label: 'group',
        id: 'thermo',
        isParent: true
      },
      component: marker,
      label: 'group',
      attr: {
        name: 'markerGroup-thermo'
      }
    });
    marker.addGraphicalElement({
      el: 'group',
      container: {
        label: 'group',
        id: 'thermo',
        isParent: true
      },
      component: marker,
      label: 'group-pin',
      attr: {
        name: 'markerGroup-thermo-pin'
      }
    });
  }

  _drawMarkers () {
    var marker = this,
      currMarkerObj,
      index,
      forceDrawFlag,
      allPin = true,
      markerInfo = marker.config.markerInfo,
      checkIfAllPins = m => {
        if (m.type !== TYPE_PIN) {
          allPin = false;
        }
      };

    for (index in markerInfo) {
      currMarkerObj = markerInfo[index];
      forceDrawFlag = false;
      allPin = true;
      if (currMarkerObj.multipleMarkers) {
        currMarkerObj.markers.forEach(checkIfAllPins);
        forceDrawFlag = !allPin;
      }

      if (currMarkerObj.markers[0].type === TYPE_PIN && !forceDrawFlag) {
        _drawPinMarker(marker, index, markerDimensions);
      } else {
        _drawFlagMarker(marker, index, markerDimensions);
      }
    }
    // Deleting the information inside style object as drawing is complete
    marker.styleInfo = {};
  }

  /**
   * parsing of style information of data markers
   *
   * @param {any} index the data marker whose style information is to be parsed
   * @returns {Object} object containing the style of data marker text and background
   * @memberof FlagMarker
   */
  getStyleInformation (index) {
    let marker = this,
      config = marker.config,
      styleInfo = config.styleInfo,
      currMarkerObj = config.markerInfo[index],
      getStyleDef = marker.getFromEnv('getStyleDef'),
      markerStyle,
      markerHoverStyle,
      textStyle,
      style,
      currMarker,
      defaultMarkerStyle = config.defaultMarkerStyle,
      defaultTextStyle = config.defaultTextStyle,
      info = marker.getLinkedParent()._getRelevantInfo(index),
      appliedMarkerStyle,
      appliedTextStyle,
      hoveredStyleInfo,
      baseTextStyle = marker.getFromEnv('baseTextStyle'),
      textStyles,
      markerStyles;

    appliedMarkerStyle = {};
    appliedTextStyle = {};
    hoveredStyleInfo = styleInfo[index] || {};
    markerHoverStyle = hoveredStyleInfo.marker || {};
    currMarker = currMarkerObj.markers[0];
    style = currMarker.style || {};
    markerStyle = getStyleDef(style.marker);
    textStyle = getStyleDef(style.text);

    if (currMarkerObj.multipleMarkers) {
      markerStyles = currMarkerObj.markers.map(markerInfo => (markerInfo.style && getStyleDef(markerInfo.style.marker)) || {});
      textStyles = currMarkerObj.markers.map(markerInfo => (markerInfo.style && getStyleDef(markerInfo.style.text)) || {});
      markerStyle = getSameProperty(markerStyles);
      textStyle = getSameProperty(textStyles);

      if (!markerStyle.fill) {
        markerStyle.fill = currMarkerObj.fill = config['default-stroke-color-multiple'];
      }
      if (!markerStyle.stroke) {
        markerStyle.stroke = config['default-stroke-color-multiple'];
      }
    } else {
      // special case for candlestick chart for default styles
      // since the default fill for bull of candle stick is white and defalut text
      // fill of data marker is white so text is not visible
      // if user has not given any fill for marker and text then the text fill
      // will be stroke of candle stick
      if (!markerStyle.fill && !textStyle.fill && config.isStock && info.text) {
        textStyle = Object.assign({}, textStyle, info.text);
      }
      markerStyle.fill = currMarkerObj.fill = convertColor(pluck(markerStyle.fill, info.fill), pluck(markerHoverStyle['fill-opacity'], markerStyle['fill-opacity'], defaultMarkerStyle['fill-opacity']) * 100);
      markerStyle.stroke = currMarkerObj.stroke = convertColor(pluck(markerStyle.stroke, info.stroke, info.fill), pluck(markerHoverStyle['stroke-opacity'], markerStyle['stroke-opacity'], defaultMarkerStyle['fill-opacity']) * 100);
    }

    Object.assign(appliedMarkerStyle, defaultMarkerStyle, markerStyle, hoveredStyleInfo.marker);
    Object.assign(appliedTextStyle, defaultTextStyle, baseTextStyle, textStyle, hoveredStyleInfo.text);

    return {
      marker: appliedMarkerStyle,
      text: appliedTextStyle
    };
  }
}

export default DataMarker;
