import { SmartRenderer } from '@fusioncharts/core/src/component-interface';

class ToolbarManager extends SmartRenderer {
  /**
   * Creates an instance of ToolbarManager.
   * @param {any} id id
   * @memberof ToolbarManager
   */
  constructor (id) {
    super(id);
    let manager = this;
    this._registry = {};
    this._limitChangeHandler = e => {
      let key,
        regdComponents = manager.getRegistry();

      for (key in regdComponents) {
        if (regdComponents.hasOwnProperty(key)) {
          regdComponents[key].updateOnLimitChange && regdComponents[key].updateOnLimitChange(e);
        }
      }

      manager.setData({}, true);
    };
  }

  /**
   * configures the component
   *
   * @param {any} [inputConfig={}] configuration
   * @memberof ToolbarManager
   */
  configureAttributes (inputConfig = {}) {
    let manager = this,
      registeredComponents = manager.getRegistry(),
      key,
      infoList = [{}];

    for (key in registeredComponents) {
      if (registeredComponents.hasOwnProperty(key)) {
        registeredComponents[key] && infoList.push(registeredComponents[key].getToolInfo());
      }
    }

    manager.getFromEnv('selectorToolbar').setData({
      child: Object.assign(...infoList),
      toolbarhdirection: 1
    }, true);
    manager.addExtEventListener('focusLimitChanged', manager._limitChangeHandler, manager.getFromEnv('chart'));
    manager.addExtEventListener('contextLimitChanged', manager._limitChangeHandler, manager.getFromEnv('chart'));
  }

  /**
   * getter function to fetch all the registered components
   *
   * @returns {Object} registered components
   * @memberof ToolbarManager
   */
  getRegistry () {
    return this._registry;
  }

  /**
   * register a component to this manager
   *
   * @param {any} id id
   * @param {any} instance instance of the component which will provide the tool requirements
   * @memberof ToolbarManager
   */
  register (id, instance) {
    this._registry[id] = instance;
  }

  /**
   * deregisters a component
   *
   * @param {any} id id of the component to be deregistered
   * @memberof ToolbarManager
   */
  deRegister (id) {
    delete this._registry[id];
  }
}

export default ToolbarManager;
