const MONTHS_IN_YEAR = 12,
  MILLISECOND = 'millisecond',
  SECOND = 'second',
  MINUTE = 'minute',
  HOUR = 'hour',
  DAY = 'day',
  MONTH = 'month',
  YEAR = 'year',
  GET = 'get',
  SET = 'set',
  MILLISECONDS_IN_SECOND = 1000,
  SECONDS_IN_MINUTE = 60,
  MINUTES_IN_HOUR = 60,
  HOURS_IN_DAY = 24,
  DAYS_IN_MONTH = 30,
  DAYS_IN_YEAR = 365;

/**
 * For a given date it transform the date to the beginning of nearest formatted date.
 *
 * When format is day, it converts the date to the beginning of the day
 * For example, when format is 'day', 24th jan, 2018 08:05:11 is converted to 24th jan, 2018 00:00:00
 *
 * When interval is 2 and format is 'day', all the days of a month are divided in bins of size 2
 * and the date is converted to beginning of the bin in which it belongs.
 *
 * When interval is 2,
 * 24th jan, 2018 08:05:11 is converted to 24th jan, 2018 00:00:00
 * 25th jan, 2018, 08:05:11 is converted to 24th jan, 2018 00:00:00
 *
 * @param {Date} date date to be converted
 * @param {string} [format='day'] the time interval in which the given date should be formatted
 * @param {number} [_interval=1] size of bins for the given format
 * @returns {Date} converted date
 */
function getFloorOfDate (date, format = 'day', _interval = 1, utc = '') {
  var curDate = new Date(date.valueOf()),
    subTime,
    interval = _interval;

  switch (format) {
    case MILLISECOND:
      subTime = curDate[GET + utc + 'Milliseconds']();
      interval = Math.min(interval, MILLISECONDS_IN_SECOND);
      curDate[SET + utc + 'Milliseconds'](Math.floor(subTime / interval) * interval);
      break;

    case SECOND:
      subTime = curDate[GET + utc + 'Seconds']();
      interval = Math.min(interval, SECONDS_IN_MINUTE);
      curDate[SET + utc + 'Seconds'](Math.floor(subTime / interval) * interval, 0);
      break;

    case MINUTE:
      subTime = curDate[GET + utc + 'Minutes']();
      interval = Math.min(interval, MINUTES_IN_HOUR);
      curDate[SET + utc + 'Minutes'](Math.floor(subTime / interval) * interval, 0, 0);
      break;

    case HOUR:
      subTime = curDate[GET + utc + 'Hours']();
      interval = Math.min(interval, HOURS_IN_DAY);
      curDate[SET + utc + 'Hours'](Math.floor(subTime / interval) * interval, 0, 0, 0);
      break;

    case DAY:
      subTime = curDate[GET + utc + 'Date']();
      interval = Math.min(interval, DAYS_IN_MONTH + 1);
      curDate[SET + utc + 'Date'](Math.max(Math.floor(subTime / interval) * interval, 1));
      curDate[SET + utc + 'Hours'](0, 0, 0, 0);
      break;

    case MONTH:
      subTime = curDate[GET + utc + 'Month']();
      interval = Math.min(interval, MONTHS_IN_YEAR);
      curDate[SET + utc + 'Month'](Math.floor(subTime / interval) * interval, 1);
      curDate[SET + utc + 'Hours'](0, 0, 0, 0);
      break;

    case YEAR:
      curDate[SET + utc + 'Month'](0, 1);
      curDate[SET + utc + 'Hours'](0, 0, 0, 0);
  }

  return curDate;
}

/**
 * Increases or decreases the date based on the format and interval.
 *
 * When the format in 'day' and interval is 2, the date is increased by 2 days.
 * For decreasing the date, interval should be negative, interval = -2 decreases the day by 2.
 *
 * When reset = 1, date is incremented and set to beginning of the format,
 * for example, when format = 'day', interval = 2, reset = 1,
 * 24th jan, 2018 08:05:11 is converted to 26th jan, 2018 00:00:00
 *
 * @param {Date} date date to be modified
 * @param {string} [format='day'] time interval based on which increment and decrement will be done
 * @param {number} [interval=1] increment/decrement value
 * @param {boolean} [reset=false] reset flag
 * @returns {Date} modified date
 */
function modifyDate (date, format = 'day', interval = 1, reset = false, utc = '') {
  var curDate = new Date(date.valueOf());

  switch (format) {
    case MILLISECOND:
      curDate[SET + utc + 'Milliseconds'](curDate[GET + utc + 'Milliseconds']() + interval);
      break;

    case SECOND:
      curDate[SET + utc + 'Seconds'](curDate[GET + utc + 'Seconds']() + interval);
      reset && curDate[SET + utc + 'Milliseconds'](0);
      break;

    case MINUTE:
      curDate[SET + utc + 'Minutes'](curDate[GET + utc + 'Minutes']() + interval);
      reset && curDate[SET + utc + 'Seconds'](0, 0);
      break;

    case HOUR:
      curDate[SET + utc + 'Hours'](curDate[GET + utc + 'Hours']() + interval);
      reset && curDate[SET + utc + 'Minutes'](0, 0, 0);
      break;

    case DAY:
      curDate[SET + utc + 'Date'](curDate[GET + utc + 'Date']() + interval);
      reset && curDate[SET + utc + 'Hours'](0, 0, 0, 0);
      break;

    case MONTH:
      curDate[SET + utc + 'Month'](curDate[GET + utc + 'Month']() + interval);
      reset && curDate[SET + utc + 'Date'](0, 0, 0, 0, 0);
      break;

    case YEAR:
      curDate[SET + utc + 'FullYear'](curDate[GET + utc + 'FullYear']() + interval);
      reset && curDate[SET + utc + 'Month'](0, 0, 0, 0, 0, 0);
  }
  return curDate;
};

export {
  getFloorOfDate,
  modifyDate,
  MILLISECONDS_IN_SECOND,
  SECONDS_IN_MINUTE,
  MINUTES_IN_HOUR,
  MONTHS_IN_YEAR,
  HOURS_IN_DAY,
  DAYS_IN_MONTH,
  DAYS_IN_YEAR
};
