import ScrollBar from '@fusioncharts/core/src/toolbox/tools/scrollbar';
import { getDarkColor } from '@fusioncharts/core/src/lib/index.js';
const DRAGSTART = 'timeNavBrushStart',
  DRAGEND = 'timeNavBrushEnd',
  DRAG = 'timeNavBrush',
  UP = 'up',
  DOWN = 'down',
  LABELFORMAT = '%b %d, %Y',
  TRANSLATE = 'translate',
  buttonStyle = {
    fill: '#e3e3e3',
    stroke: 'none'
  },
  trackStyle = {
    fill: '#f8f8f8',
    stroke: 'none'
  },
  anchorStyle = {
    fill: '#e3e3e3',
    stroke: 'none'
  },
  buttonPointerFill = `#${getDarkColor(buttonStyle.fill, 60)}`,
  stripesStroke = `#${getDarkColor(anchorStyle.fill, 60)}`;
export default timeNav => {
  let scrollbar = timeNav.attachChild(ScrollBar, 'scrollbar'),
    chart = timeNav.getFromEnv('chart'),
    currFocusLimit,
    lastScrollPosition,
    timeNavConfig = timeNav.config,
    scale = timeNavConfig.contextScale,
    dataSource = scrollbar.getFromEnv('dataSource'),
    getStyleDef = scrollbar.getFromEnv('getStyleDef'),
    inputStyle = (dataSource.navigator && dataSource.navigator.scrollbar && dataSource.navigator.scrollbar.style) || {},
    style,
    options = {
      timeFormatter: timeNavConfig.formatter
    },
    forceFireOptions = Object.assign({ forceFire: true }, options),
    eventArgs = {
      formatter: LABELFORMAT,
      action: TRANSLATE
    };

  style = {
    button: Object.assign({
      stroke: buttonStyle.stroke,
      fill: buttonStyle.fill
    }, getStyleDef(inputStyle.button)),
    arrow: Object.assign({
      fill: buttonPointerFill
    }, getStyleDef(inputStyle.arrow)),
    scroller: Object.assign({
      stroke: anchorStyle.stroke,
      fill: anchorStyle.fill
    }, getStyleDef(inputStyle.scroller)),
    grip: Object.assign({
      stroke: stripesStroke
    }, getStyleDef(inputStyle.grip)),
    track: Object.assign({
      stroke: trackStyle.stroke,
      fill: trackStyle.fill
    }, getStyleDef(inputStyle.track))
  };

  scrollbar.configure({ isHorizontal: true,
    displayFlat: true,
    width: timeNav.getScrollbarConfig('width'),
    height: timeNav.getScrollbarConfig('height'),
    style,
    drawStripes: 1,
    restrictScrollAnchor: 0,
    scrollRatio: timeNav.getScrollbarConfig('scrollRatio') });
  scrollbar.attachEventHandlers({
    scroll: function (scrollPosition) {
      // calculate the dimensions of scroll anchor and use them to calculate the new domain of brush.
      // calculations are the same as used in scroll-anchor component.
      let scrollbarConf = timeNav.getChildren('scrollbar')[0].config,
        width = scrollbarConf.width,
        height = scrollbarConf.height,
        clampDirection,
        scrollRatio = timeNav.getScrollbarConfig('scrollRatio'),
        scrollButtonWidth = Math.min(height, width * 0.5),
        anchorWidth = (width - 2 * scrollButtonWidth) * scrollRatio - 1, // 1px is for left and right padding of scrollbar
        trackOffset = scrollbarConf.x + scrollButtonWidth + 0.5,
        trackLength = width - 2 * scrollButtonWidth - anchorWidth,
        anchorXPos = trackOffset + trackLength * scrollPosition;

      clampDirection = lastScrollPosition > scrollPosition ? DOWN : UP;
      chart.setPixelLimit(
        [anchorXPos, anchorXPos + anchorWidth],
        scale,
        [clampDirection, clampDirection],
        {
          name: DRAG
        }
      );
    },
    scrollStart: function () {
      currFocusLimit = chart.getFocusLimit();
      chart.getFromEnv('fireChartEvents')(DRAGSTART, [+currFocusLimit[0], +currFocusLimit[1]], eventArgs, forceFireOptions);
    },
    scrollEnd: function () {
      currFocusLimit = chart.getFocusLimit();
      chart.getFromEnv('fireChartEvents')(DRAGEND, [+currFocusLimit[0], +currFocusLimit[1]], eventArgs, forceFireOptions);
    }
  });
};
