import { ToolBar } from '@fusioncharts/core/src/toolbox';
import { pluckNumber, pluck } from '@fusioncharts/core/src/lib';

class SmartToolbar extends ToolBar {
  /**
   * configures the attributes
   *
   * @param {any} [options={}] options
   * @memberof SmartToolbar
   */
  configureAttributes (options = {}) {
    let toolbar = this,
      conf = toolbar.config,
      chartConfig = toolbar.getFromEnv('chartConfig'),
      child,
      key,
      i,
      len,
      tool;

    this.props.width = pluckNumber(options.width, this.props.width, 0);
    this.props.height = pluckNumber(options.height, this.props.height, 0);
    conf.orientation = pluck(options.toolbarorientation, conf.orientation);
    conf.hPadding = pluck(options.toolbarhpadding, conf.hPadding);
    conf.vPadding = pluck(options.toolbarvpadding, conf.vPadding);
    conf.backgroundcolor = pluck(options.toolbarbackgroundcolor, conf.backgroundcolor);
    conf.backgroundalpha = pluck(options.toolbarbackgroundalpha, conf.backgroundalpha);
    conf.bordercolor = pluck(options.toolbarbordercolor, conf.bordercolor);
    conf.borderalpha = pluck(options.toolbarborderalpha, conf.borderalpha);
    conf.borderthickness = pluck(options.toolbarborderthickness, conf.borderthickness);
    conf.hDirection = pluckNumber(options.toolbarhdirection, (chartConfig.toolbarHAlign === 'r' ? -1 : 1));
    conf.vDirection = pluckNumber(options.toolbarvdirection, (chartConfig.toolbarBAlign === 'b' ? -1 : 1));
    conf.hAlign = pluck(options.hAlign, 'center').toLowerCase();
    conf.vAlign = pluck(options.vAlign, 'middle').toLowerCase();
    conf.marginTop = pluckNumber(options.marginTop, options.spacing, conf.marginTop);
    conf.marginLeft = pluckNumber(options.marginLeft, options.spacing, conf.marginLeft);
    conf.marginRight = pluckNumber(options.marginRight, options.spacing, conf.marginRight);
    conf.marginBottom = pluckNumber(options.marginBottom, options.spacing, conf.marginBottom);
    conf.child = options.child || conf.child;

    if (conf.child) {
      child = conf.child;
      key = Object.keys(conf.child);

      for (i = 0, len = key.length; i < len; i++) {
        tool = toolbar.attachChild(child[key[i]].def, child[key[i]].type, key[i]);
        tool.setData(child[key[i]].configuration, true);
        tool.setState('visible', true);
      }
    }
  }
}

export default SmartToolbar;
