import BinDecider, {
  DEFAULT_THRESHOLD_PIXELS
} from '@fusioncharts/utils/src/bin-decider';
import { BLANKSTRING } from '@fusioncharts/core/src/lib/index';
import {
  segregateClips,
  getUnclippedValue
} from '@fusioncharts/utils/src/clip-utils/index.js';
import {
  timeDay,
  dayObj,
  timeHour,
  timeWeek,
  timeYear,
  timeMonth,
  timeMinute,
  timeSecond,
  secondObj,
  timeMillisecond,
  hourObj,
  minuteObj,
  millisecondObj,
  monthObj,
  weekObj,
  yearObj
} from '@fusioncharts/utils/src/time-intervals';
import {
  utcDay,
  utcDayObj,
  utcHour,
  utcHourObj,
  utcWeek,
  utcWeekObj,
  utcYear,
  utcYearObj,
  utcMonth,
  utcMonthObj,
  utcMinute,
  utcMinuteObj,
  utcSecond,
  utcMillisecond
} from '@fusioncharts/utils/src/time-intervals/utc';
import Log from '@fusioncharts/utils/src/scales/ft-log';
import extent from '@fusioncharts/utils/src/array/extent';
import Linear from '@fusioncharts/utils/src/scales/linear';
import isObject from '@fusioncharts/utils/src/type/is-object';
import ScaleTimeBin from '@fusioncharts/utils/src/scales/time-bin';
import timeConverter from '@fusioncharts/utils/src/time-converter';
import ScaleUTCTimeBin from '@fusioncharts/utils/src/scales/utc-bin';
import capsFirst from '@fusioncharts/utils/src/string/caps-first.js';
import { getDefaultBins, getCustomBins } from '@fusioncharts/utils/src/bin-decider/standard-bins';

import { getAtomicity, getFormatStore } from '../../_utils/atomicity';
import { TIME_SPAN } from '@fusioncharts/utils/src/defaults/time';
import { less, pipe, filter } from '@fusioncharts/datatable/src/operators';
import { getMinPlaceHolder } from '@fusioncharts/utils/src/time-bucket';
import TimeInterval from '@fusioncharts/utils/src/time-intervals/time-interval';
import ScaleTime from '@fusioncharts/utils/src/scales/time';
import ScaleUTCTime from '@fusioncharts/utils/src/scales/utc';
import isArray from '@fusioncharts/utils/src/type/is-array';

const TYPES = {
    time: ScaleTimeBin,
    utctime: ScaleUTCTimeBin,
    scaletime: ScaleTime,
    scaleutctime: ScaleUTCTime,
    numeric: Linear,
    log: Log
  },
  SHARE_RATIO = 0.8;
export default chart => {
  let config = chart.config,
    contextAxesX = config.contextAxesX,
    clipDatesObj = isArray(contextAxesX[0].clip) ? contextAxesX[0].clip : [],
    focusAxesX = config.focusAxesX,
    focusAxesY = config.focusAxesY,
    dateColumnName = chart.getFromEnv('dateColumn').name,
    customBin = focusAxesX[0].binning,
    table = config.dataTable,
    { data, schema } = table.getData(),
    isUTC = chart.getFromEnv('UTC'),
    autoClipNullData = contextAxesX[0].autoclipnulldata ? contextAxesX[0].autoclipnulldata.toLowerCase() : null,
    autoClipMultiplier = contextAxesX[0].autoclipmultiplier || 1,
    clipFormat = '%b %d %Y %H:%M:%S:%L',
    timeConstant,
    firstDate,
    timeIntervals = isUTC ? {
      Year: new TimeInterval(utcYearObj),
      Month: new TimeInterval(utcMonthObj),
      Week: new TimeInterval(utcWeekObj),
      Day: new TimeInterval(utcDayObj),
      Hour: new TimeInterval(utcHourObj),
      Minute: new TimeInterval(utcMinuteObj),
      Second: new TimeInterval(secondObj),
      Millisecond: new TimeInterval(millisecondObj)
    } : {
      Year: new TimeInterval(yearObj),
      Month: new TimeInterval(monthObj),
      Week: new TimeInterval(weekObj),
      Day: new TimeInterval(dayObj),
      Hour: new TimeInterval(hourObj),
      Minute: new TimeInterval(minuteObj),
      Second: new TimeInterval(secondObj),
      Millisecond: new TimeInterval(millisecondObj)
    },
    unclippedTimeIntervals = isUTC ? {
      Day: utcDay,
      Hour: utcHour,
      Week: utcWeek,
      Year: utcYear,
      Month: utcMonth,
      Minute: utcMinute,
      Second: utcSecond,
      Millisecond: utcMillisecond
    } : {
      Day: timeDay,
      Hour: timeHour,
      Week: timeWeek,
      Year: timeYear,
      Month: timeMonth,
      Minute: timeMinute,
      Second: timeSecond,
      Millisecond: timeMillisecond
    },
    chartWidth = chart.getFromEnv('chartWidth'),
    range = [0, SHARE_RATIO * chartWidth / focusAxesX.length],
    parsedClipDates,
    repeatClips,
    singleClips,
    clippedRows,
    makeScale = ({ type = 'time' }) => {
      let Scale = ((type === 'time' && isUTC) ? TYPES['utc' + type] : TYPES[type]) || TYPES.time;

      return new Scale();
    },
    makeBin = ({ type = 'time', plot }, index) => {
      let Scale = ((type === 'time' && isUTC) ? TYPES['scaleutc' + type] : TYPES['scale' + type]) || TYPES.scaletime,
        scale = new Scale(),
        bin;
      if (customBin) {
        bin = new BinDecider(
          getCustomBins(timeIntervals.Year,
            timeIntervals.Month,
            timeIntervals.Week,
            timeIntervals.Day,
            timeIntervals.Hour,
            timeIntervals.Minute,
            timeIntervals.Second,
            timeIntervals.Millisecond,
            customBin));
      } else {
        bin = new BinDecider(
          getDefaultBins(timeIntervals.Year,
            timeIntervals.Month,
            timeIntervals.Week,
            timeIntervals.Day,
            timeIntervals.Hour,
            timeIntervals.Minute,
            timeIntervals.Second,
            timeIntervals.Millisecond));
      }
      bin.setScale(scale);

      bin.setBinRange(range);
      bin.setRangeThreshold(config.pixelMultiplier * DEFAULT_THRESHOLD_PIXELS);

      return bin;
    },
    makeScalesBins = (axesX) => {
      let scales = [], bins = [], i, len = axesX.length;
      for (i = 0; i < len; i++) {
        scales[i] = makeScale(axesX[i]);
        scales[i].setTimeInterval(timeIntervals);
        scales[i].setUnclippedTimeIntervals(unclippedTimeIntervals);
        bins[i] = makeBin(axesX[i], i);
        if (parsedClipDates.length) scales[i].setClippings(parsedClipDates);

        // set the standard bins of the scales
        scales[i].setThresholdIntervals(getDefaultBins(timeIntervals.Year,
          timeIntervals.Month,
          timeIntervals.Week,
          timeIntervals.Day,
          timeIntervals.Hour,
          timeIntervals.Minute,
          timeIntervals.Second,
          timeIntervals.Millisecond));
      }

      return { scales, bins };
    },
    contextInfo,
    focusInfo,
    minMaxFn = ({ value }) =>
      [table.min(value), table.max(value)],
    calculateDomain = axis => {
      let arr = axis.plot.map(minMaxFn),
        context = extent([].concat(...arr), Number),
        minBinDuration = contextInfo.bins[0].getBinMin()[2],
        value = axis.plot[0].value,
        format = axis.format || schema.find(({ name }) => name === value).format,
        parser = timeConverter.parser(format),
        initialInterval = isObject(axis.initialinterval) ? axis.initialinterval : {},
        setMin = (userMin, dataMin) => {
          let min = parser.parse(userMin);
          if (min === null) {
            return dataMin;
          }
          return min < dataMin ? +min : dataMin;
        },
        setMax = (userMax, dataMax) => {
          let max = parser.parse(userMax);
          if (max === null) {
            return dataMax;
          }
          return max > dataMax ? +max : dataMax;
        },
        focus;

      // keeping the raw data start
      config.rawDataXStart = context[0];
      if (!data.length && (typeof context[0] === 'undefined' || typeof context[1] === 'undefined')) {
        context[0] = +TIME_SPAN[0];
        context[1] = +TIME_SPAN[1];
      }
      // if the spread is provided then
      // the lower limit will be based on spread
      if (config.timeSpread && config.timeSpread.duration >= minBinDuration * 3) {
        context[0] = +timeIntervals[config.timeSpread.unit.name].offset(
          context[1],
          -config.timeSpread.multiplier
        );
      } else if (data.length < 3) {
        if (data.length === 0) {
          context[0] = +TIME_SPAN[0];
          context[1] = context[0] + minBinDuration * 3;
        } else {
          context[0] = context[1] - minBinDuration * 2;
        }
      }
      focus = context.slice();
      focus[0] = setMax(initialInterval.from, focus[0]);
      focus[1] = setMin(initialInterval.to, focus[1]);
      focus = extent(focus);
      return {
        focus,
        context
      };
    },
    setClipObjects = (_clipDates, _timeIntervals, rawDataXStart, rawDataXEnd) => {
      let clipDates = _clipDates,
        timeInterval = _timeIntervals;
      timeInterval.Day.setClipDates(clipDates);
      timeInterval.Day.setTimeUniverse([rawDataXStart, rawDataXEnd]);
      timeInterval.Hour.setClipDates(clipDates);
      timeInterval.Hour.setTimeUniverse([rawDataXStart, rawDataXEnd]);
      timeInterval.Week.setClipDates(clipDates);
      timeInterval.Week.setTimeUniverse([rawDataXStart, rawDataXEnd]);
      timeInterval.Year.setClipDates(clipDates);
      timeInterval.Year.setTimeUniverse([rawDataXStart, rawDataXEnd]);
      timeInterval.Month.setClipDates(clipDates);
      timeInterval.Month.setTimeUniverse([rawDataXStart, rawDataXEnd]);
      timeInterval.Minute.setClipDates(clipDates);
      timeInterval.Minute.setTimeUniverse([rawDataXStart, rawDataXEnd]);
      timeInterval.Second.setClipDates(clipDates);
      timeInterval.Second.setTimeUniverse([rawDataXStart, rawDataXEnd]);
      timeInterval.Millisecond.setClipDates(clipDates);
      timeInterval.Millisecond.setTimeUniverse([rawDataXStart, rawDataXEnd]);
    },
    // returns the date in a particular format based on different clipping formats
    getFormattedDate = (timestamp) => {
      let addedHours = 0,
        date,
        hour,
        min,
        sec,
        millisec;
      date = new Date(timestamp);
      hour = date.getHours() - addedHours;
      min = date.getMinutes();
      sec = date.getSeconds();
      millisec = date.getMilliseconds();
      return date.toDateString().slice(4) + ' ' + hour + ':' + min + ':' + sec + ':' + millisec;
    },
    // sets the time constant for different time formats
    updateTimeVars = () => {
      switch (autoClipNullData) {
        case 'year':
          timeConstant = 31536000000;
          break;
        case 'month':
          timeConstant = 2678400000;
          break;
        case 'day':
          timeConstant = 86400000;
          break;
        case 'hour':
          timeConstant = 3600000;
          break;
        case 'minute':
          timeConstant = 60000;
          break;
        case 'second':
          timeConstant = 1000;
          break;
        case 'millisecond':
          timeConstant = 1;
          break;
        default:
          timeConstant = 0;
      }
    },
    // select the iterating clip-range values for different clipping formats based on the format of provided data
    selectIntervalRange = () => {
      let intervalRange = 60000,
        timeDelta = 1,
        formatString = schema[0].format;
      if (formatString.search(/%[-,_,0]?[L]/) !== -1) {
        intervalRange = 1;
      } else if (formatString.search(/%[-,_,0]?[s,S]/) !== -1) {
        intervalRange = 1000;
      } else if (formatString.search(/%[-,_,0]?[M]/) !== -1) {
        intervalRange = 60000;
      } else if (formatString.search(/%[-,_,0]?[H,I]/) !== -1) {
        intervalRange = 3600000;
      } else if (formatString.search(/%[-,_,0]?[d,a,A,j]/) !== -1) {
        intervalRange = 86400000;
      } else if (formatString.search(/%[-,_,0]?[b,B,m]/) !== -1) {
        intervalRange = 2678400000;
      } else if (formatString.search(/%[-,_,0]?[y,Y]/) !== -1) {
        intervalRange = 31536000000;
      }
      return {intervalRange, timeDelta};
    },
    setDateForDynamicInterval = (minDate) => {
      if (autoClipNullData === 'year') {
        return minDate.setFullYear(minDate.getFullYear() + autoClipMultiplier) - 1;
      } else if (autoClipNullData === 'month') {
        return minDate.setMonth(minDate.getMonth() + autoClipMultiplier) - 1;
      }
    },
    // select the clipping data as per the provided variables and push them in the clipping object
    addAutoClipping = (rangeInterval, timeDelta, valueIndex) => {
      let chartIndex = 0,
        maxChartIndex = data.length,
        currentMax,
        currentIntervalMin = dataXStart,
        currentIntervalMax = currentMax = currentIntervalMin + (timeConstant * autoClipMultiplier) - timeDelta;
      if (rangeInterval === 2678400000 || rangeInterval === 31536000000) {
        setDateForDynamicInterval(new Date(currentIntervalMin));
      }
      while (currentIntervalMax <= dataXEnd) {
        if (chartIndex < maxChartIndex) {
          if (data[chartIndex][0] >= currentIntervalMin && data[chartIndex][0] <= currentIntervalMax && (data[chartIndex][valueIndex] === 0 || data[chartIndex][valueIndex])) {
            currentMax = currentIntervalMax;
            while (currentIntervalMin <= currentMax) {
              let dt = new Date(currentIntervalMin);
              if (rangeInterval === 2678400000) {
                currentIntervalMin = dt.setMonth(dt.getMonth() + 1);
                currentIntervalMax = setDateForDynamicInterval(new Date(currentIntervalMin));
              } else if (rangeInterval === 31536000000) {
                currentIntervalMin = dt.setFullYear(dt.getFullYear() + 1);
                currentIntervalMax = setDateForDynamicInterval(new Date(currentIntervalMin));
              } else {
                currentIntervalMin = currentIntervalMin + (rangeInterval);
                currentIntervalMax = currentIntervalMax + (rangeInterval);
              }
              while (data[chartIndex][0] < currentIntervalMin) {
                chartIndex++;
                if (chartIndex === maxChartIndex || !(data[chartIndex][valueIndex] === 0 || data[chartIndex][valueIndex])) {
                  break;
                }
              }
              if (data[chartIndex][0] > currentIntervalMax || currentIntervalMin === dataXEnd || !(data[chartIndex][valueIndex] === 0 || data[chartIndex][valueIndex])) {
                currentMax = currentIntervalMax;
                break;
              }
            }
          } else {
            if (data[chartIndex][0] < currentIntervalMin || (data[chartIndex][0] >= currentIntervalMin && data[chartIndex][0] <= currentIntervalMax)) {
              chartIndex++;
            } else {
              // Future Scope(Support-2009): Based on flag, will need to check if no valid value is present for other data-series as well to perform auto-time clipping
              let obj = {
                format: clipFormat,
                from: getFormattedDate(currentIntervalMin),
                to: getFormattedDate(currentIntervalMax)
              };
              clipDatesObj.push(obj);
              currentIntervalMin = currentIntervalMax + timeDelta;
              if (rangeInterval === 2678400000 || rangeInterval === 31536000000) {
                currentIntervalMax = setDateForDynamicInterval(new Date(currentIntervalMin));
              } else {
                currentIntervalMax = currentIntervalMax + (timeConstant * autoClipMultiplier);
              }
              currentMax = currentIntervalMax;
            }
          }
        }
      }
    },
    calculatedDomains,
    dataXStart,
    dataXEnd,
    context,
    arr,
    clips = {},
    ops = [];
  arr = contextAxesX[0].plot.map(minMaxFn);
  context = extent([].concat(...arr), Number);
  dataXStart = context[0];
  dataXEnd = context[1];

  // code to initiate auto time-axis clipping
  if (autoClipNullData) {
    updateTimeVars();
    if (timeConstant !== 0) {
      if (typeof autoClipMultiplier === 'number' && isFinite(autoClipMultiplier) && Math.floor(autoClipMultiplier) === autoClipMultiplier && autoClipMultiplier > 0) {
        let timeVars = selectIntervalRange(),
          yValueIndex;
        for (let yValues = 0; yValues < focusAxesY.length; yValues++) {
          // iterating through all the data series(y-values) for auto time clipping
          for (let posn = 0; posn < schema.length; posn++) {
            if (focusAxesY[yValues].plot[0].value === schema[posn].name) {
              yValueIndex = posn;
              addAutoClipping(timeVars.intervalRange, timeVars.timeDelta, yValueIndex);
              break;
            }
          }
        }
      }
    }
  }

  parsedClipDates = clipDatesObj.map(clip => {
    let parser = config.baseTimeConverter,
      parsedRepeat,
      parsedClipObj,
      level = BLANKSTRING,
      timeInterval,
      from,
      to;

    if (clip.format) {
      parser = isUTC
        ? timeConverter.utcParser(clip.format)
        : timeConverter.parser(clip.format);
    }

    level = getMinPlaceHolder(parser.toString());
    timeInterval = timeIntervals[capsFirst(level)];

    if (clip.repeat) {
      parsedRepeat = {
        ...clip.repeat
      };
      parsedRepeat.multiplier = Math.abs(parseInt(parsedRepeat.multiplier, 10));
    }
    from = +parser.parse(clip.from);
    to = +parser.parse(clip.to);
    if (from > to) {
      [from, to] = [to, from];
    }
    to = +timeInterval.offset(to);
    parsedClipObj = {
      ...clip,
      format: parser.toString(),
      to,
      from,
      level: getMinPlaceHolder(parser.toString())
    };

    if (parsedRepeat) {
      parsedClipObj.repeat = parsedRepeat;
    }
    return parsedClipObj;
  });
  clips = segregateClips(parsedClipDates);
  repeatClips = clips.repeatClips;
  singleClips = clips.singleClips;
  clippedRows = (row, col) => {
    const t = row[col[dateColumnName]];
    return t !== getUnclippedValue(t, repeatClips, singleClips);
  };
  if (parsedClipDates.length) {
    ops = [filter(clippedRows)];

    setClipObjects(parsedClipDates, timeIntervals, dataXStart, dataXEnd);
    chart.cloneTable();
    table = config.dataTable;
    // since new DT is created
    ({ data, schema } = table.getData());
  }

  contextInfo = makeScalesBins(contextAxesX);
  focusInfo = makeScalesBins(focusAxesX);

  table.getDataStore().on('itemsAdded', chart._onDataUpdate);
  chart.addEventListener('beforeremove', chart._offDataUpdate);

  chart.addToEnv('timeIntervals', timeIntervals);
  chart.addToEnv('unclippedTimeIntervals', unclippedTimeIntervals);

  if (config.timeSpread && config.timeSpread.duration) {
    // In case timespread is provided and data is not present, take the start of
    // default time as first date.
    firstDate = data.length
      ? data[data.length - 1][config.dateColumnIndex]
      : +TIME_SPAN[0];

    ops = ops.concat(less(
      dateColumnName,
      // Take minimum time achieved by removing timespread duration vs 3
      // bins
      Math.min(
        config.timeSpread.interval.offset(
          firstDate,
          -config.timeSpread.multiplier
        ),
        contextInfo.bins[0].getRangeThreshold()[0].offset(firstDate, -2)
      )
    ));
  }

  if (ops.length) {
    table.getDataStore().deleteRows(pipe(...ops), table.getID());
  }

  data = table.getData().data;

  if (!config.atomicity) {
    const info = config.atomicity = {
      ...getFormatStore(schema[config.dateColumnIndex].format)
    };
    info.minBin = getAtomicity({
      ...info,
      index: config.dateColumnIndex,
      data,
      bins: contextInfo.bins[0].getStandardBins(),
      intervalIndexMap: contextInfo.bins[0].intervalIndexMap,
      ms: timeIntervals.Millisecond
    });
  }
  focusInfo.bins[0].setBinMin(config.atomicity.minBin);
  focusInfo.scales[0]
    .setBinMin(focusInfo.bins[0].getBinMin())
    .setRangeThreshold(focusInfo.bins[0].getRangeThreshold());
  contextInfo.bins[0].setBinMin(config.atomicity.minBin);
  contextInfo.scales[0]
    .setBinMin(contextInfo.bins[0].getBinMin())
    .setRangeThreshold(contextInfo.bins[0].getRangeThreshold());

  calculatedDomains = calculateDomain(contextAxesX[0]);

  contextInfo.scales.forEach(scale => {
    scale.setFirstData(config.rawDataXStart);
  });
  focusInfo.scales.forEach(scale => {
    scale.setFirstData(config.rawDataXStart);
  });

  chart.addToEnv('contextScalesX', contextInfo.scales);
  chart.addToEnv('focusScalesX', focusInfo.scales);
  chart.addToEnv('contextBins', contextInfo.bins);
  chart.addToEnv('focusBins', focusInfo.bins);

  config.clipDates = parsedClipDates;

  chart.setContextLimit(calculatedDomains.context);
  chart.setFocusLimit(calculatedDomains.focus);
};
