let flagPathStr = 'M12.4444444,0 L1.55555556,0 C0.7,0 0,0.654545455 0,1.45454545 L0,10.8363636 C0,11.3454545 0.233333333,11.7817897 0.7,12.0727273 L7,16 L13.3,12.0727273 C13.6888889,11.7818182 14,11.3454545 14,10.8363636 L14,1.45454545 C14,0.654545455 13.3,0 12.4444444,0 Z';
/**
* Function to draw the FlagMarkers
*/
export default (marker, index, markerDimensions) => {
  let config = marker.config,
    markerDetails = config.markerInfo[index],
    data = config.dataInfo[index],
    markerStyle = marker.getStyleInformation(index),
    y,
    x,
    width,
    currMarker,
    stickX,
    id,
    identifier,
    attrObj,
    stickY,
    strokeWidth = 1,
    multipleMarkers,
    markerOpacity,
    textOpacity,
    { flagHeight, stickLength, flagWidth } = markerDimensions;

  multipleMarkers = markerDetails.multipleMarkers;
  id = markerDetails.id;
  y = data.y;
  x = data.x;
  width = data.width || 0;
  stickX = x + width / 2;
  stickY = y - stickLength;

  currMarker = markerDetails.markers[0];

  markerOpacity = markerStyle.marker.opacity;
  textOpacity = markerStyle.text.opacity;
  // Creating the vertical line of the flag marker
  attrObj = {
    path: ['M', stickX, y, 'V', stickY],
    opacity: markerOpacity
  };
  marker.addGraphicalElement({
    el: 'path',
    container: {
      label: 'group'
    },
    id,
    component: marker,
    attr: attrObj,
    css: markerStyle.marker,
    label: 'flagStick'
  }, true);

  // Creating the remaining of the flag marker(the marker background)
  attrObj = {
    path: flagPathStr,
    transform: `t${stickX - flagWidth / 2 + strokeWidth},${stickY - flagHeight}`,
    opacity: markerOpacity
  };
  marker.addGraphicalElement({
    el: 'path',
    container: {
      label: 'group'
    },
    id,
    component: marker,
    attr: attrObj,
    css: markerStyle.marker,
    label: 'flagTriangle'
  }, true);
  // Drawing the text
  if (multipleMarkers || (identifier = currMarker.identifier)) {
    attrObj = {
      text: multipleMarkers ? markerDetails.markers.length : identifier.charAt(0),
      x: stickX,
      y: stickY - flagHeight / 2,
      opacity: textOpacity
    };

    marker.addGraphicalElement({
      el: 'text',
      container: {
        label: 'group'
      },
      id,
      component: marker,
      attr: attrObj,
      css: markerStyle.text,
      label: 'markerText'
    }, true);
  }
};
