/* eslint-disable good-practices/no-function-dependency */
import merge from 'ramda/es/merge';
import { componentFactory, BLANKSTRING, pluck, UNDEF } from '@fusioncharts/core/src/lib';
import Axis from '../../../_internal/components/axis';
import Canvas from '../../../_internal/components/canvas';
import panelConfig from './_utils/panel-configurer';
import localeENUS from '@fusioncharts/utils/src/number-converter/locales/en-US';
import isValidNumber from '@fusioncharts/utils/src/type/is-valid-number';
import { NumberConverter } from '@fusioncharts/utils/src/number-converter';
import FormatSpecifier from '@fusioncharts/utils/src/number-converter/format-specifier.js';

const BOTTOM = 'bottom',
  canvases = key => key.match(/^canvas/u),
  removeComponent = component => component.remove(),
  validateRound = round => {
    // If round is a string, attempt to parse it as a number
    if (typeof round === 'string') {
      const parsedRound = parseInt(round, 10);

      return Number.isNaN(parsedRound) ? null : parsedRound;
    } else if (isValidNumber(round)) {
      return round;
    }

    return null;
  },
  getSpecifier = ({ value, type, subType, defaultformat = true, round }) => {
    let precision;

    if (round != null) {
      // If round is positive, the precision is set to be the same as round.
      if (round >= 0) {
        precision = round;
      } else {
        // If round is negative, we round the values to the closest power of
        // 10. For example, if the value is 234, a round of -2 would be 200,
        // a round of -1 would be 230 etc.
        precision = Math.max(1, Math.floor(value).toString().length + round + 1);
      }
    }

    // If the user has left the formatting of numbers upto us, we try to figure
    // out the most appropriate formatting
    if (defaultformat) {
      const absValue = Math.abs(value);

      // When the value is greater than 0 but less than 1 OR less than 0 but
      // greater than -1,
      // If the number of decimal places in the value is in [4, Infinity),
      // format the values with scientific notation.
      // Otherwise, when the number of decimal places in the value is in [1,3]
      // show the values as they are.
      if (absValue !== 0 && absValue < 0.0001) {
        switch (type) {
          case 'axis':
            if (round == null) {
              return '$e';
            } else if (round < 0) {
              // trim insignificant zeroes
              return `$.${precision}~e`;
            }
            // preserve insignificant zeroes
            return `$.${precision}e`;
          default:
            if (round == null) {
              return '$.2e';
            } else if (round < 0) {
              // trim insignificant zeroes
              return `$.${precision}~e`;
            }
            // preserve insignificant zeroes
            return `$.${precision}e`;
        }
      } else if ((absValue >= 0.0001 && absValue < 1) || (absValue === 0)) {
        switch (type) {
          case 'axis':
            if (round == null) {
              return '$';
            } else if (round < 0) {
              // trim insignificant zeroes
              return `$.${precision}~`;
            }
            // preserve insignificant zeroes
            return `$.${precision}`;
          default:
            if (round == null) {
              return '$.2';
            } else if (round < 0) {
              // trim insignificant zeroes
              return `$.${precision}~`;
            }
            // preserve insignificant zeroes
            return `$.${precision}`;
        }
      }

      // If the values are greater than 1 or less than -1
      switch (type) {
        case 'axis':
          if (round == null) {
            return '$~s';
          } else if (round < 0) {
            // trim insignificant zeroes
            return `$.${precision}~s`;
          }
          // preserve insignificant zeroes
          return `$.${precision}s`;
        // for tooltip, crossline and ref line/zone
        default:
          if (round == null) {
            return '$.2~s';
          } else if (round < 0) {
            // trim insignificant zeroes
            return `$.${precision}~s`;
          }
          // preserve insignificant zeroes
          return `$.${precision}s`;
      }
    } else {
      // If the user has opted set defaultFormat to false and a value is not provided
      // to round, simply trim away insignificant zeroes and use decimal
      // formatting with up to 12 digits of precision. We could just perform no
      // formatting at all, but that would lead to unsightly values with IEEE
      // Floating Point precision errors.
      if (round == null) return '$.12~r';

      // If a positive value is provided to round, use fixed point precision
      if (round >= 0) return `$.${precision}f`;

      // If a negative value is provided to round, use decimal precision
      return `$.${precision}~r`;
    }
  },
  createFormatter = (formatConfig, { type: subType, converter }) => {
    if (typeof formatConfig.formatter === 'function') {
      // Append blank string to guard against possibly nullish/falsy/object
      // values returnde by the formatter function (especially when it is
      // defined by the user)
      return obj => formatConfig.formatter(obj) + '';
    }

    const round = validateRound(formatConfig.round);

    return obj => {
      const specifier = typeof formatConfig.formatter === 'string'
          ? formatConfig.formatter
          : getSpecifier(Object.assign(
            {},
            formatConfig,
            { subType, value: obj.value, type: obj.type, round }
          )),
        hasSIPrefix = new FormatSpecifier(specifier).type === 's';

      // formatPrefix interprets precision as fixed point precision instead of
      // as decimal precision (significant digits).
      return hasSIPrefix && (round == null || round >= 0)
        ? converter.formatPrefix(specifier, obj.value).format(obj.value)
        : converter.formatter(specifier).format(obj.value);
    };
  },
  visibleAxes = cfg => !!cfg.visible,
  isLeftAligned = ({ align }) => align === 'left',
  isRightAligned = ({ align }) => align === 'right',
  isTopAligned = ({ align }) => align === 'top',
  isBottomAligned = ({ align }) => align === 'bottom',
  isValidYOrientation = orientation => {
    return orientation === 'left' || orientation === 'right';
  },
  /**
   * Reads the X and Y configs of a panel's configuration and appropraiately
   * sets the top, right, bottom and left border configurations within it.
   *
   * @param {Object} panelCfg The configuration of the panel within which the
   * top, right, left and bottom borders have to be added
   *
   * @return {Object} The input panel configuration with the border
   * configurations added within it
   */
  mergeBorderConfig = panelCfg => {
    const borderCfg = {
        leftBorder: false,
        rightBorder: false,
        topBorder: false,
        bottomBorder: false
      },
      { xConfigs, yConfigs } = panelCfg;

    if (xConfigs.find(isLeftAligned) || yConfigs.find(isLeftAligned)) {
      borderCfg.leftBorder = true;
    }
    if (xConfigs.find(isRightAligned) || yConfigs.find(isRightAligned)) {
      borderCfg.rightBorder = true;
    }
    if (xConfigs.find(isTopAligned) || yConfigs.find(isTopAligned)) {
      borderCfg.topBorder = true;
    }
    if (xConfigs.find(isBottomAligned) || yConfigs.find(isBottomAligned)) {
      borderCfg.bottomBorder = true;
    }

    return merge(panelCfg, borderCfg);
  };

export default chart => {
  let usedCanvasIds = [],
    unusedCanvasIds = [],
    canvasIds,
    baseTextStyle = chart.getFromEnv('baseTextStyle'),
    dataSource = chart.getFromEnv('dataSource'),
    DEFAULT_AXIS_X = {
      orientation: 'bottom',
      align: 'bottom',
      tickpadding: 2,
      visible: true,
      overlap: false,
      domainline: false,
      outputtimeformat: {},
      style: {
        'label-major': Object.assign({
          fill: '#818181'
        }, baseTextStyle),
        'label-context': Object.assign({
          fill: '#818181'
        }, baseTextStyle)
      }
    },
    DEFAULT_AXIS_Y = {
      tickarguments: [4, 's'],
      ticksize: 5,
      tickpadding: 7,
      visible: true,
      overlap: false,
      domainline: false,
      style: {
        'label-major': Object.assign({
          'font-size': '11px'
        }, baseTextStyle),
        'tick-mark-major': {
          stroke: '#efefef'
        }
      }
    };

  const hydrateX = merge(DEFAULT_AXIS_X),
    hydrateY = merge(DEFAULT_AXIS_Y),
    chartConfig = chart.config,
    canvasAxisMap = chartConfig.canvasAxisMap,
    focusAxesX = chartConfig.focusAxesX,
    focusAxesY = chartConfig.focusAxesY,
    chartAttrs = chart.getFromEnv('chart-attrib'),
    canvasStyle = chart.getFromEnv('getStyleDef')(chartAttrs.style && chartAttrs.style.canvas),
    focusPanels = chartConfig.focusPanels.map(panel => merge(panel, {
      x: panel.x.map(xCfg => {
        let focusAxisX = focusAxesX[xCfg.index],
          formatObj = pluck(focusAxesX[xCfg.index].format, {});

        // parse any information given by user, if not provided, use default
        focusAxisX.timeFormatterFn = typeof formatObj.formatter === 'function'
          ? options => formatObj.formatter.call(focusAxisX.scale, options)
          : formatterObj => {
            return focusAxisX.scale.getFormattedTime && focusAxisX.scale.getFormattedTime(formatterObj,
              dataSource.tooltip && dataSource.tooltip.outputtimeformat);
          };

        return merge(
          hydrateX(xCfg), panelConfig(focusAxesX, xCfg.index)
        );
      }),
      y: panel.y.map(yCfg => {
        let focusAxisY = focusAxesY[yCfg.index],
          formatObj = Object.assign(
            { defaultformat: true },
            pluck(focusAxesY[yCfg.index].format, {})
          ),
          formatLocale;

        focusAxisY.formatLabelPrefix = formatObj.prefix || BLANKSTRING;
        focusAxisY.formatLabelSuffix = formatObj.suffix || BLANKSTRING;

        formatLocale = Object.assign(
          {},
          localeENUS,
          {
            prefix: focusAxisY.formatLabelPrefix,
            suffix: focusAxisY.formatLabelSuffix
          }
        );

        focusAxisY.scale.setLocale(formatLocale);

        focusAxisY.formatterFn = createFormatter(formatObj, {
          converter: new NumberConverter(formatLocale),
          type: focusAxisY.type
        });

        return merge(
          hydrateY(yCfg), panelConfig(focusAxesY, yCfg.index)
        );
      }
      )
    })),
    createPanelComponents = (
      {
        x: axisConfigsX,
        y: axisConfigsY,
        plots: plotConfigs
      },
      i
    ) => {
      const canvasId = `canvas_${i}`;
      let axisCount = { left: 0, right: 0 };

      if (!canvasAxisMap[canvasId]) {
        canvasAxisMap[canvasId] = { x: [], y: [] };
      }

      axisConfigsX.filter(visibleAxes).forEach((axisConfig, ai) => {
        const axisId = `axesX_${i}_${ai}`;

        // Set alignment for the X-axis
        axisConfig.align = axisConfig.align.toLowerCase();
        if (!['bottom', 'top'].includes(axisConfig.align)) {
          axisConfig.align = BOTTOM;
        }
        axisConfig.tickvisibilitylimit = {
          start: UNDEF,
          end: UNDEF
        };
        componentFactory(chart, Axis, axisId, 1, [axisConfig]);

        if (!canvasAxisMap[canvasId].x.includes(axisId)) {
          canvasAxisMap[canvasId].x.push(axisId);
        }
      });

      axisConfigsY.filter(visibleAxes).forEach((axisConfig, ai) => {
        const axisId = `axesY_${i}_${ai}`;

        // If orientation is provided correctly, change the align value to it.
        if (isValidYOrientation(axisConfig.orientation)) {
          axisConfig.align = axisConfig.orientation;
        }

        // Set orientation for the Y-axis as per the calculated align.
        axisConfig.orientation = axisConfig.align;
        axisConfig.domainline = axisCount[axisConfig.align] > 0;
        if (axisConfig.type === 'log') axisConfig.tickarguments = [4, '~s'];

        axisCount[axisConfig.align]++;
        axisConfig.tickvisibilitylimit = {
          start: UNDEF,
          end: UNDEF
        };

        componentFactory(chart, Axis, axisId, 1, [axisConfig]);

        if (!canvasAxisMap[canvasId].y.includes(axisId)) {
          canvasAxisMap[canvasId].y.push(axisId);
        }
      });

      // Make a list of canvas IDs that are being used/re-used
      usedCanvasIds.push(canvasId);

      componentFactory(chart, Canvas, canvasId, 1, [mergeBorderConfig({
        plotConfigs,
        tableMap: chartConfig.focusTableMap,
        xConfigs: axisConfigsX,
        yConfigs: axisConfigsY,
        enableGridLines: true,
        gridBandSupported: axisConfigsY[0].type !== 'log',
        enableMouseTracking: 1,
        enableMarkers: 1,
        enableInteraction: 1,
        canvasStyle
      })]);
    };

  focusPanels.forEach(createPanelComponents);

  // Remove any unused canvases
  canvasIds = Object.keys(chart.getChildren()).filter(canvases);
  unusedCanvasIds = canvasIds.filter(oldId => !usedCanvasIds.includes(oldId));

  unusedCanvasIds.forEach(unusedId => {
    chart.getChildren(unusedId).forEach(removeComponent);
  });

  chartConfig.focusPanels = focusPanels;
};
