
import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { pluck, pluckNumber } from '@fusioncharts/core/src/lib';

let M = 'M',
  h = 'h',
  v = 'v',
  Z = 'Z',
  L = 'L',
  GUTTER = 1,
  GUTTER_5 = 5,
  GUTTER_1 = 1,
  COLOR_F3F3F3 = '#f3f3f3',
  anchorRadius = 3,
  BOTTOM = 'bottom',
  TOP = 'top',
  ROLLOVER = 'referenceZoneRollOver',
  ROLLOUT = 'referenceZoneRollOut',
  CLICK = 'referenceZoneClick',
  isWithinYRange = (value, range) => (value >= range[1] && value <= range[0]),
  defaultStyle = {
    marker: {
      fill: '#f76260',
      stroke: '#f76260',
      'fill-opacity': '1',
      'stroke-opacity': '1',
      'opacity': '1',
      borderthickness: 0,
      borderpadding: 2,
      borderradius: 0,
      borderdash: 'none',
      'stroke-width': 1
    },
    'marker-zone': {
      'stroke': '#f76260',
      'fill-opacity': 0.2,
      'fill': '#f76260',
      'stroke-opacity': 1,
      'stroke-width': 0
    },
    'marker-zone:hover': {
      'stroke': '#f76260',
      'fill-opacity': 0.3,
      'fill': '#f76260',
      'stroke-opacity': 1,
      'stroke-width': 1
    },
    'marker-notch': {
      'stroke-width': 0,
      'stroke': '#f76260',
      'fill': '#f76260',
      'fill-opacity': 1
    },
    'marker-notch:hover': {
      'stroke-width': 1,
      'stroke': '#f76260',
      'fill': '#f76260',
      'fill-opacity': 0.5
    },
    'marker-tag': {
      fill: '#f76260',
      stroke: '#f76260',
      'fill-opacity': '1',
      'stroke-opacity': '1',
      'opacity': '1',
      borderthickness: 0,
      borderpadding: 2,
      borderradius: 0,
      borderdash: 'none',
      'stroke-width': 1
    },
    'marker-notch-connector': {
      fill: 'none',
      stroke: '#f76260',
      'fill-opacity': '1',
      'stroke-opacity': '1',
      'opacity': '1',
      'stroke-width': 1
    },
    text: {
      fill: '#5F5F5F',
      'vertical-align': 'top',
      'opacity': '1',
      'fill-opacity': '1',
      'stroke-opacity': '1',
      'font-size': '11px',
      'font-style': 'normal'
    },
    'marker-text': {
      fill: COLOR_F3F3F3,
      'vertical-align': 'middle',
      'text-anchor': 'middle',
      'opacity': '1',
      'fill-opacity': '1',
      'stroke-opacity': '1',
      'font-size': '11px',
      'font-style': 'normal'
    }
  };

class ReferenceZone extends SmartRenderer {
  constructor () {
    super();
    let refzone = this,
      config = refzone.config,
      chart,
      scale,
      maxVaueFormatted,
      minValueFormatted;
    refzone.hoverInHandler = e => {
      e.stopPropagation();
      chart = refzone.getFromEnv('chart');
      scale = config.yScale;
      refzone.setState('hovered', true);
      refzone.asyncDraw();
      refzone.fireEvent('referenceZoneHovered', {
        hovered: true,
        scale: config.yScale,
        range: {
          minValue: scale.getRangeValue(config.minValue),
          maxValue: scale.getRangeValue(config.maxValue)
        }
      });
      maxVaueFormatted = config.formatterFn({
        value: config.maxValue,
        type: 'referenceline',
        prefix: config.prefix,
        suffix: config.suffix
      });
      minValueFormatted = config.formatterFn({
        value: config.minValue,
        type: 'referenceline',
        prefix: config.prefix,
        suffix: config.suffix
      });
      chart.fireChartInstanceEvent(ROLLOVER, {
        label: config.label,
        valueMax: config.maxValue,
        valueMin: config.minValue,
        valueMaxFormatted: maxVaueFormatted,
        valueMinFormatted: minValueFormatted
      });
    };
    // eslint-disable-next-line
    refzone.moveHandler = e => {
      e.stopPropagation();
    };
    refzone.hoverOutHandler = e => {
      e.stopPropagation();
      refzone.setState('hovered', false);
      refzone.asyncDraw();
      refzone.fireEvent('referenceZoneHovered', {
        hovered: false,
        scale: config.yScale,
        range: {
          minValue: scale.getRangeValue(config.minValue),
          maxValue: scale.getRangeValue(config.maxValue)
        }
      });
      chart.fireChartInstanceEvent(ROLLOUT, {
        label: config.label,
        valueMax: config.maxValue,
        valueMin: config.minValue,
        valueMaxFormatted: maxVaueFormatted,
        valueMinFormatted: minValueFormatted
      });
    };
    refzone.clickHandler = e => {
      e.stopPropagation();
      chart.fireChartInstanceEvent(CLICK, {
        label: config.label,
        valueMax: config.maxValue,
        valueMin: config.minValue,
        valueMaxFormatted: maxVaueFormatted,
        valueMinFormatted: minValueFormatted
      });
    };
    refzone.referenceZoneHoveredHandler = e => {
      let eventData = e.data,
        canvas = refzone.getLinkedParent();
      canvas.highlightDataPoints(eventData.hovered, eventData.scale, eventData.range);
    };
    refzone.canvasHoveredHandler = e => {
      let canvas = refzone.getLinkedParent(),
        { yScale, minValue, maxValue } = refzone.config;

      if (refzone.getState('hovered')) {
        refzone.setState('hovered', false);
        refzone.asyncDraw();
        canvas.highlightDataPoints(false, yScale, {
          minValue: yScale.getRangeValue(minValue),
          maxValue: yScale.getRangeValue(maxValue)
        });
      }
    };
  }
  __setDefaultConfig () {
    super.__setDefaultConfig();
    this.config.extStyle = {};
  }
  /**
   * Method that configures the component.
   * @param {Object} obj configuration object
   */
  configureAttributes (obj = {}) {
    super.configureAttributes(obj);
    let refZone = this,
      selfConfig = refZone.config,
      refZoneConfig = obj.referenceLine || {},
      minValue,
      maxValue;
    minValue = pluckNumber(refZoneConfig.valuemin, 0);
    maxValue = pluckNumber(refZoneConfig.valuemax, 0);
    selfConfig.label = pluck(refZoneConfig.label, '');
    // swap values if min is greater than max value
    if (minValue > maxValue) {
      [minValue, maxValue] = [maxValue, minValue];
    }
    selfConfig.minValue = minValue;
    selfConfig.maxValue = maxValue;
    for (let key in obj) {
      if (obj.hasOwnProperty(key)) {
        this.config[key] = obj[key];
      }
    }

    selfConfig.formattedMinValue = selfConfig.formatterFn({
      value: selfConfig.minValue,
      type: 'referenceline',
      prefix: selfConfig.prefix,
      suffix: selfConfig.suffix
    });

    selfConfig.formattedMaxValue = selfConfig.formatterFn({
      value: selfConfig.maxValue,
      type: 'referenceline',
      prefix: selfConfig.prefix,
      suffix: selfConfig.suffix
    });

    refZone.createStyleDefinition();
  }
  createStyleDefinition () {
    let refZone = this,
      config = refZone.config,
      extStyle = config.extStyle,
      getStyleDef = refZone.getFromEnv('getStyleDef'),
      baseTextStyle = refZone.getFromEnv('baseTextStyle'),
      extMarkerStyle = getStyleDef(extStyle.marker),
      extMarkerHoverStyle = getStyleDef(extStyle['marker:hover']),
      extMarkerZoneStyle = getStyleDef(extStyle['marker-zone']),
      extMarkerZoneHoverStyle = getStyleDef(extStyle['marker-zone:hover']),
      extMarkerTagStyle = getStyleDef(extStyle['marker-tag']),
      extTextStyle = getStyleDef(extStyle.text),
      extMarkerTextStyle = getStyleDef(extStyle['marker-text']),
      extMarkerNotchStyle = getStyleDef(extStyle['marker-notch']),
      extMarkerNotchHoverStyle = getStyleDef(extStyle['marker-notch:hover']),
      extMarkerNotchConnectorStyle = getStyleDef(extStyle['marker-notch-connector']),
      extMarkerNotchConnectorHoverStyle = getStyleDef(extStyle['marker-notch-connector:hover']);
    config.styleDefinition = {
      marker: Object.assign({}, defaultStyle.marker, extMarkerStyle),
      'marker:hover': Object.assign({}, defaultStyle.marker, extMarkerHoverStyle),
      'marker-zone': Object.assign({}, defaultStyle['marker-zone'], extMarkerStyle, extMarkerZoneStyle),
      'marker-zone:hover': Object.assign({}, extMarkerStyle, extMarkerZoneStyle, defaultStyle['marker-zone:hover'], extMarkerHoverStyle, extMarkerZoneHoverStyle),
      'marker-tag': Object.assign({}, defaultStyle['marker-tag'], extMarkerStyle, extMarkerTagStyle),
      text: Object.assign({}, baseTextStyle, defaultStyle.text, extTextStyle),
      'marker-text': Object.assign({}, baseTextStyle, defaultStyle['marker-text'], extTextStyle, extMarkerTextStyle),
      'marker-notch': Object.assign({}, defaultStyle['marker-notch'], extMarkerStyle, extMarkerNotchStyle),
      'marker-notch:hover': Object.assign({}, extMarkerStyle, extMarkerNotchStyle, defaultStyle['marker-notch:hover'], extMarkerHoverStyle, extMarkerNotchHoverStyle),
      'marker-notch-connector': Object.assign({}, defaultStyle['marker-notch-connector'], extMarkerStyle, extMarkerNotchConnectorStyle),
      'marker-notch-connector:hover': Object.assign({}, defaultStyle['marker-notch-connector'], extMarkerStyle, extMarkerNotchConnectorStyle, extMarkerHoverStyle, extMarkerNotchConnectorHoverStyle)
    };
  }
  /**
   * Method to derive the path array of zone and connector based on the range values and the direction
   * @return {Object} path array
   */
  getMarkerPath () {
    let refLine = this,
      selfConfig = refLine.config,
      direction = selfConfig.direction, // the direction in which the line will be drawn
      canvasConfig = refLine.getLinkedParent().config,
      canvasLeft = canvasConfig.canvasLeft,
      canvasWidth = canvasConfig.canvasWidth,
      canvasTop = canvasConfig.canvasTop,
      canvasHeight = canvasConfig.canvasHeight,
      canvasRight = canvasLeft + canvasWidth,
      canvasBottom = canvasTop + canvasHeight,
      rightBorder = canvasConfig.borderConfig.rightBorder,
      leftBorder = canvasConfig.borderConfig.leftBorder,
      topBorder = canvasConfig.borderConfig.topBorder,
      bottomBorder = canvasConfig.borderConfig.bottomBorder,
      yScale = selfConfig.yScale,
      minRangeValue,
      maxRangeValue,
      minValue = selfConfig.minValue,
      maxValue = selfConfig.maxValue,
      rangeDiffPx,
      zonePathArr = [],
      connectorPathArr = [];
      // get the range value
    minRangeValue = yScale.getRangeValue(minValue);
    maxRangeValue = yScale.getRangeValue(maxValue);
    rangeDiffPx = minRangeValue - maxRangeValue;
    switch (direction) {
      case 'left':
        zonePathArr = [M, canvasRight - rightBorder, maxRangeValue, h, -canvasWidth, v, rangeDiffPx, h, canvasWidth, Z];
        connectorPathArr = [M, canvasRight - rightBorder - canvasWidth, maxRangeValue, v, rangeDiffPx];
        break;
      case 'right':
        zonePathArr = [M, canvasLeft + leftBorder, maxRangeValue, h, canvasWidth, v, rangeDiffPx, h, -canvasWidth, Z];
        connectorPathArr = [M, canvasLeft + leftBorder + canvasWidth, maxRangeValue, v, rangeDiffPx];
        break;
      case 'top':
        zonePathArr = [M, minRangeValue, canvasBottom - bottomBorder, v, -canvasHeight, h, rangeDiffPx, v, canvasHeight, Z];
        connectorPathArr = [M, minRangeValue, canvasBottom - bottomBorder - canvasHeight, h, rangeDiffPx];
        break;
      case 'bottom':
        zonePathArr = [M, minRangeValue, canvasTop + topBorder, v, canvasHeight, h, rangeDiffPx, v, -canvasHeight, Z];
        connectorPathArr = [M, minRangeValue, canvasTop + topBorder + canvasHeight, h, rangeDiffPx];
        break;
    }
    return {
      connectorPath: connectorPathArr,
      zonePath: zonePathArr
    };
  }

  /**
   * Method to derive the x and y position of the text and also its text anchor
   * based on the direction
   * @param {Array} pathArr path array
   * @return {Object} label properties
   */
  getLabelsProps (pathArr, styleDefinition) {
    let refLine = this,
      mergedMarkerStyle = styleDefinition.marker,
      textStyle = styleDefinition.text,
      refLineConfig = refLine.config,
      canvasConfig = refLine.getLinkedParent().config,
      direction = refLineConfig.direction, // the direction in which the line will be drawn and bulb will be placed
      props = {
        isValidLabel: false
      };
    if (pathArr.length && refLineConfig.label) {
      props.isValidLabel = true;
      // label will be placed at the end of the line,
      // given the direction, calculate the end of the line along with some padding, so it doesn't overlap the anchor
      switch (direction) {
        case 'top':
          props.x = pathArr[1];
          props.y = pathArr[2] + pathArr[4];
          props.textAnchor = 'start';
          break;
        case 'bottom':
          props.x = pathArr[1];
          props.y = pathArr[2] + pathArr[4];
          props.textAnchor = 'start';
          break;
        case 'left':
          // Calculate x position using ref line path: start position x + horizontal lineto/length
          props.x = pathArr[1] + pathArr[4] + anchorRadius; // Some padding so it doesn't overlap the bulb
          props.y = pathArr[2] + pathArr[6] + mergedMarkerStyle.borderpadding;
          props.textAnchor = 'start';
          break;
        case 'right':
          // Calculate x position using ref line path: start position x + horizontal lineto/length
          props.x = pathArr[1] + pathArr[4] - mergedMarkerStyle.borderpadding - 1; // Some padding so it doesn't overlap the bulb
          props.y = pathArr[2] + pathArr[6] + mergedMarkerStyle.borderpadding;
          props.textAnchor = 'end';
      }
      // restricting positon of referencezone label when going outside canvas
      if (canvasConfig.canvasBGHeight < props.y) {
        props.y = canvasConfig.canvasBGHeight - 2 * mergedMarkerStyle.borderpadding;
        textStyle['vertical-align'] = BOTTOM;
      } else {
        textStyle['vertical-align'] = TOP;
      }
    }
    return props;
  }

  /**
   * Method to derive center point of bulb depending on the direction of reference line.
   * @param {Array} pathArr path Array
   */
  getBulbPoints (pathArr) {
    let refLineConfig = this.config,
      direction = refLineConfig.direction, // the direction in which the line will be drawn and bulb will be placed
      scale = refLineConfig.yScale,
      scaleRange = scale.getRange(),
      bulbPoints = [];
    if (pathArr) {
      // given the direction, calculate end of the line to place the bulb
      switch (direction) {
        case 'left':
          // push the co-ordinates of the first bulb point
          isWithinYRange(pathArr[2], scaleRange) && bulbPoints.push({
            cx: pathArr[1] + pathArr[4],
            cy: pathArr[2]
          });
          // push the co-ordinates of the second bulb point
          isWithinYRange(pathArr[2] + pathArr[6], scaleRange) && bulbPoints.push({
            cx: pathArr[1] + pathArr[4],
            cy: pathArr[2] + pathArr[6]
          });
          break;
        case 'right':
          // push the co-ordinates of the first bulb point
          isWithinYRange(pathArr[2], scaleRange) && bulbPoints.push({
            cx: pathArr[1] + pathArr[4],
            cy: pathArr[2]
          });
          // push the co-ordinates of the second bulb point
          isWithinYRange(pathArr[2] + pathArr[6], scaleRange) && bulbPoints.push({
            cx: pathArr[1] + pathArr[4],
            cy: pathArr[2] + pathArr[6]
          });
      }
    }
    return bulbPoints;
  }

  getTagProps (mergedMarkerStyle, flagStyle, textStyle) {
    let refLine = this,
      {
        yScale,
        minValue,
        maxValue,
        direction, // the direction in which the line will be drawn and bulb will be placed
        formattedMinValue,
        formattedMaxValue
      } = this.config,
      scaleRange = yScale.getRange(),
      smartLabel = refLine.getFromEnv('smartLabel'),
      // multiply by two to include padding on both left and right sides
      flagBorderPadding = 2 * flagStyle.borderpadding,
      // multiply by two to include padding on both left and right sides
      tagPadding = 2 * (mergedMarkerStyle.borderpadding + GUTTER_1),
      minSmartText,
      maxSmartText,
      {
        canvasLeft,
        canvasWidth,
        borderConfig: { leftBorder: canvasLeftBorder }
      } = refLine.getLinkedParent().config,
      canvasRight = canvasLeft + canvasWidth,
      minRangeValue,
      maxRangeValue,
      tagWidth,
      tagProps = [];

    minRangeValue = yScale.getRangeValue(minValue);
    maxRangeValue = yScale.getRangeValue(maxValue);

    smartLabel.setStyle({
      fontFamily: textStyle['font-family'],
      fontWeight: textStyle['font-weight'],
      fontSize: textStyle['font-size'],
      fontStyle: textStyle['font-style']
    });

    switch (direction) {
      case 'left':
        minSmartText = smartLabel.getSmartText(formattedMinValue);
        maxSmartText = smartLabel.getSmartText(formattedMaxValue);
        tagWidth = Math.max(minSmartText.width, maxSmartText.width) + tagPadding;

        // push dimension of the min value tag
        isWithinYRange(minRangeValue, scaleRange) && tagProps.push({
          tagPath: [
            M, canvasRight, minRangeValue,
            L, canvasRight + GUTTER_5, minRangeValue,
            L, canvasRight + 2 * GUTTER_5, minRangeValue - (minSmartText.height / 2) - flagBorderPadding,
            h, (tagWidth),
            v, minSmartText.height + 4 * flagStyle.borderpadding,
            h, -(tagWidth),
            L, canvasRight + GUTTER_5, minRangeValue,
            Z
          ],
          tagTextProps: {
            x: canvasRight + (2 * GUTTER_5) + (tagWidth / 2),
            y: minRangeValue,
            value: formattedMinValue
          }
        });

        // push dimension of the max value tag
        isWithinYRange(maxRangeValue, scaleRange) && tagProps.push({
          tagPath: [
            M, canvasRight, maxRangeValue,
            L, canvasRight + GUTTER_5, maxRangeValue,
            L, canvasRight + 2 * GUTTER_5, maxRangeValue - (maxSmartText.height / 2) - flagBorderPadding,
            h, (tagWidth),
            v, maxSmartText.height + 4 * flagStyle.borderpadding,
            h, -(tagWidth),
            L, canvasRight + GUTTER_5, maxRangeValue,
            Z
          ],
          tagTextProps: {
            x: canvasRight + (2 * GUTTER_5) + (tagWidth / 2),
            y: maxRangeValue,
            value: formattedMaxValue
          }
        });
        break;
      case 'right':
      default:
        minSmartText = smartLabel.getSmartText(formattedMinValue);
        maxSmartText = smartLabel.getSmartText(formattedMaxValue);
        tagWidth = Math.max(minSmartText.width, maxSmartText.width) + tagPadding;

        // push dimension of the min value tag
        isWithinYRange(minRangeValue, scaleRange) && tagProps.push({
          tagPath: [
            M, canvasLeft + canvasLeftBorder, minRangeValue,
            L, canvasLeft - GUTTER_5, minRangeValue,
            L, canvasLeft - 2 * GUTTER_5, minRangeValue - (minSmartText.height / 2) - flagBorderPadding,
            h, -(tagWidth),
            v, minSmartText.height + 4 * flagStyle.borderpadding,
            h, (tagWidth),
            L, canvasLeft - GUTTER_5, minRangeValue,
            Z
          ],
          tagTextProps: {
            x: canvasLeft - (2 * GUTTER_5) - (tagWidth / 2),
            y: minRangeValue,
            value: formattedMinValue
          }
        });

        // push dimension of the max value tag
        isWithinYRange(maxRangeValue, scaleRange) && tagProps.push({
          tagPath: [
            M, canvasLeft + canvasLeftBorder, maxRangeValue,
            L, canvasLeft - GUTTER_5, maxRangeValue,
            L, canvasLeft - 2 * GUTTER_5, maxRangeValue - (maxSmartText.height / 2) - flagBorderPadding,
            h, -(tagWidth),
            v, maxSmartText.height + 4 * flagStyle.borderpadding,
            h, (tagWidth),
            L, canvasLeft - GUTTER_5, maxRangeValue,
            Z
          ],
          tagTextProps: {
            x: canvasLeft - (2 * GUTTER_5) - (tagWidth / 2),
            y: maxRangeValue,
            value: formattedMaxValue
          }
        });
    }

    return tagProps;
  }

  draw () {
    let refZone = this,
      selfConfig = refZone.config,
      styleDefinition = selfConfig.styleDefinition,
      label = selfConfig.label,
      markerPath,
      zonePath,
      labelProps,
      bulbPoints,
      tagProps,
      tagPath,
      tagTextProps,
      isHovered = refZone.getState('hovered'),
      canvasConfig = refZone.getLinkedParent().config,
      padding = canvasConfig.padding,
      connectorStyle = isHovered
        ? styleDefinition['marker-notch-connector:hover']
        : styleDefinition['marker-notch-connector'],
      strokeWidth = Number.parseFloat(connectorStyle['stroke-width']) + GUTTER,
      // Taking the padding into consideration as the clip-rect is shifed based on the translation applied
      // on the group
      left = canvasConfig.canvasBGLeft - padding.left - anchorRadius -
        (selfConfig.direction === 'left' ? strokeWidth : 0),
      top = canvasConfig.canvasBGTop - padding.top - anchorRadius,
      width = canvasConfig.canvasBGWidth + 2 * anchorRadius + strokeWidth,
      height = canvasConfig.canvasBGHeight + 2 * anchorRadius,
      clipPath = [`M${left},${top}`,
        `L${left + width},${top}`,
        `L${left + width},${top + height}`,
        `L${left},${top + height}Z`];
    // create group for reference zone
    refZone.addGraphicalElement({
      el: 'group',
      container: {
        id: 'exo',
        label: 'group',
        isParent: true
      },
      component: refZone,
      label: 'refZone',
      attr: {
        name: 'reference-zone-exo'
      },
      id: 'exo'
    }, true);
    refZone.addGraphicalElement({
      el: 'group',
      container: {
        id: 'exo',
        label: 'refZone'
      },
      component: refZone,
      label: 'refZone-exo-group',
      attr: {
        name: 'reference-zone-group',
        'clip-path': clipPath
      },
      id: 'exo'
    }, true);
    refZone.addGraphicalElement({
      el: 'group',
      container: {
        id: 'exo',
        label: 'refZone'
      },
      component: refZone,
      label: 'refZone-exo-hover-group',
      attr: {
        name: 'reference-zone-hover-elem-group'
      },
      id: 'exo'
    }, true);

    markerPath = refZone.getMarkerPath();
    zonePath = markerPath.zonePath;

    labelProps = refZone.getLabelsProps(zonePath, styleDefinition);
    bulbPoints = refZone.getBulbPoints(zonePath);

    // draw path only if it is valid.
    if (zonePath.length) {
      // reference zone path
      refZone.addGraphicalElement({
        el: 'path',
        attr: {
          'path': zonePath
        },
        container: {
          label: 'refZone-exo-group'
        },
        css: isHovered ? styleDefinition['marker-zone:hover'] : styleDefinition['marker-zone'],
        component: refZone,
        label: 'line'
      }, true);
      // draw anchor

      // create reference zone bulbs
      bulbPoints.forEach((bulbPoint) => {
        refZone.addGraphicalElement({
          el: 'circle',
          attr: {
            cx: bulbPoint.cx,
            cy: bulbPoint.cy,
            r: anchorRadius
          },
          container: {
            label: 'refZone-exo-hover-group'
          },
          css: isHovered ? styleDefinition['marker-notch:hover'] : styleDefinition['marker-notch'],
          component: refZone,
          label: 'anchor'
        });
      });
      // bulb connector path
      refZone.addGraphicalElement({
        el: 'path',
        attr: {
          path: markerPath.connectorPath
        },
        container: {
          label: 'refZone-exo-group'
        },
        css: connectorStyle,
        component: refZone,
        label: 'anchorConnector'
      });

      if (isHovered) {
        // draw label only if it is valid and anchor is hovered.
        if (labelProps.isValidLabel) {
          refZone.addGraphicalElement({
            el: 'text',
            attr: {
              'text': label,
              'x': labelProps.x,
              'y': labelProps.y - styleDefinition.marker.borderpadding * (styleDefinition.text['vertical-align'] === 'top' ? -1 : 1),
              'text-anchor': labelProps.textAnchor
            },
            css: styleDefinition.text,
            container: {
              label: 'refZone-exo-hover-group'
            },
            outlineText: selfConfig.showTextOutline,
            component: refZone,
            label: 'label'
          }, true);
        }

        // draw tag
        tagProps = refZone.getTagProps(styleDefinition.marker, styleDefinition['marker-tag'], styleDefinition.text);
        tagProps.forEach((tagProp) => {
          tagPath = tagProp.tagPath;
          tagTextProps = tagProp.tagTextProps;

          refZone.addGraphicalElement({
            el: 'path',
            attr: {
              path: tagPath
            },
            container: {
              label: 'refZone-exo-hover-group'
            },
            css: styleDefinition['marker-tag'],
            component: refZone,
            label: 'tag'
          }, true);

          // text

          refZone.addGraphicalElement({
            el: 'text',
            attr: {
              text: tagTextProps.value,
              x: tagTextProps.x,
              y: tagTextProps.y
            },
            css: styleDefinition['marker-text'],
            container: {
              label: 'refZone-exo-hover-group'
            },
            component: refZone,
            label: 'tag-text'
          }, true);
        });
      }
    }
  }
}
export default ReferenceZone;
