import Brush from '../components/brush';

export default timeNav => {
  let brush = timeNav.attachChild(Brush, 'brush'),
    dataSource = timeNav.getFromEnv('dataSource');

  brush.configure({
    values: timeNav.config.values,
    style: (dataSource.navigator && dataSource.navigator.window && dataSource.navigator.window.style) || {}
  });
};
