import {
  utcMillisecond,
  utcSecond,
  utcMinute,
  utcHour,
  utcDay,
  utcWeek,
  utcMonth,
  utcYear
} from '../time-intervals/utc';
import { copyScale } from './continuous';
import ScaleCalendarBin from './calendar-bin';
import { UTC_TIME_SPAN } from '../defaults/time';
// import { tickFormat } from '../time-intervals/ticks';

class ScaleUtcBin extends ScaleCalendarBin {
  constructor () {
    super(
      utcYear,
      utcMonth,
      utcWeek,
      utcDay,
      utcHour,
      utcMinute,
      utcSecond,
      utcMillisecond
    );

    this.setTimeFormat();
    this.setDomain([...UTC_TIME_SPAN]);
  }

  getType () {
    return 'UTC';
  }
  setTimeInterval (timeIntervals) {
    this.timeIntervals = timeIntervals;
    super.setTimeInterval(timeIntervals);
  }
  /**
   * sets the time formatter strings
   *
   * @param {*} [format={}] Object containing format string
   * @memberof ScaleUtcBin
   */
  setTimeFormat (format = {}) {
    let localeConverter = this._localeConverter,
      { millisecond, second, minute, hour, day, month, year } = format;
    this.formatters = {
      millisecond: {
        major: localeConverter.utcFormatter(millisecond || '%I:%M:%S.%L %p'),
        minor: localeConverter.utcFormatter(millisecond || '%L ms'),
        context: localeConverter.utcFormatter(millisecond || '%b %d, %Y, %I:%M:%S.%L %p')
      },
      second: {
        major: localeConverter.utcFormatter(second || '%I:%M:%S %p'),
        minor: localeConverter.utcFormatter(second || '%S s'),
        context: localeConverter.utcFormatter(second || '%b %d, %Y, %I:%M:%S %p')
      },
      minute: {
        major: localeConverter.utcFormatter(minute || '%I:%M %p'),
        minor: localeConverter.utcFormatter(minute || '%M m'),
        context: localeConverter.utcFormatter(minute || '%b %d, %Y, %I:%M %p')
      },
      hour: {
        major: localeConverter.utcFormatter(hour || '%I %p'),
        minor: localeConverter.utcFormatter(hour || '%I %p'),
        context: localeConverter.utcFormatter(hour || '%b %d, %Y, %I %p')
      },
      day: {
        major: localeConverter.utcFormatter(day || '%b %d'),
        minor: localeConverter.utcFormatter(day || '%d'),
        context: localeConverter.utcFormatter(day || '%b %d, %Y')
      },
      month: {
        major: localeConverter.utcFormatter(month || '%b'),
        minor: localeConverter.utcFormatter(month || '%b'),
        context: localeConverter.utcFormatter(month || '%b %Y')
      },
      year: {
        major: localeConverter.utcFormatter(year || '%Y'),
        minor: localeConverter.utcFormatter(year || '%Y'),
        context: localeConverter.utcFormatter(year || '%Y')
      }
    };
  }
  /**
   * formats the date as per specified formatter strings
   *
   * @param {any} specifier specifier string
   * @returns {function} instance of timeConverter
   * @memberof ScaleTimeBin
   */
  tickFormat (specifier) {
    return specifier
      ? d => this._localeConverter.utcFormatter(specifier).format(d)
      : (d, type, tickType) => {
        /**
         * if scale provides with a formatter string, use it to format the given date
         * else use from the default formatters.
         */
        if (this._timeFormat[type]) {
          return this._localeConverter.utcFormatter(this._timeFormat[type]).format(d);
        } return this.formatters[type][tickType].format(d);
      };
  }

  copy () {
    return copyScale(this, new ScaleUtcBin());
  }
}

export default ScaleUtcBin;
