import merge from 'ramda/es/merge';
import all from 'ramda/es/all';
import isObject from '@fusioncharts/utils/src/type/is-object';
import isArray from '@fusioncharts/utils/src/type/is-array';
import isNumber from '@fusioncharts/utils/src/type/is-number';

/**
 * Verifies that a number is a valid index of the given array
 *
 * @param {array} arr The array against which the number will be checked
 * @param {number} idx The number to be checked
 *
 * @return {boolean}
 */
const isValidIndex = (arr, idx) => idx >= 0 && idx < arr.length,
  /**
   * Verifies that an input is a number and a valid index of the given array
   *
   * @param {array} arr The array against which the number will be checked
   * @param {*} num The input to be checked
   *
   * @return {boolean}
   */
  isValidNumber = (arr, num) => isNumber(num) && isValidIndex(arr, num),
  /**
   * Verifies that an input is an object and has an index property which is a
   * valid index of the given array
   *
   * @param {array} arr The array against which the object will be checked
   * @param {*} obj The input to be checked
   *
   * @reutrn {boolean}
   */
  isValidObject = (arr, obj) => isObject(obj) && isValidNumber(arr, +obj.index),
  /**
   * Verifies that the input panel axis configuration is valid
   *
   * @param {array} arr The array of axis configurations
   * @param {*} a The panel specific reference to an axis configuration
   *
   * @return {boolean}
   */
  isValidAxis = (arr, a) => isValidNumber(arr, +a) || isValidObject(arr, a),
  /**
   * Transforms the input to a minimally valid panel axis configuration while
   * not omitting any configuration otherwise specified
   *
   * @param {*} a The input to be converted to a configuration
   *
   * @return {object} A minimally valid panel axis configuration
   */
  expand = a => isObject(a) ? merge(a, { index: +a.index }) : { index: +a },
  /**
   * Transforms the input to a minimally valid panel configuration while not
   * omitting any configuration otherwise specified
   *
   * @param {*} a The input to be converted to a configuration
   *
   * @return {object} A minimally valid panel configuration
   */
  toPanelConfig = a => merge(a, {
    x: isArray(a.x) ? a.x.map(expand) : [expand(a.x)],
    y: isArray(a.y) ? a.y.map(expand) : [expand(a.y)]
  }),
  /**
   * Verifies whether the given panel configuration is valid or not.
   *
   * @param {array} arr An array of axis configurations
   * @param {*} config The panel configuration
   *
   * @return {boolean}
   */
  validate = (arr, config) => isArray(config)
    ? all(x => isValidAxis(arr, x), config)
    : isValidAxis(arr, config);

/**
 * Generates minimally valid panel configurations from the given x, y and panel
 * configurations; failing which it defaults to the provided function to
 * generate a valid canvas configuration
 *
 * @param {array} xs An array of x configurations
 * @param {array} ys An array of y configurations
 * @param {array} panels An array of panel configurations
 * @param {function} defaultCanvases A function, which when passed the x and y
 * configurations returns an array of panel configurations
 *
 * @return {array} The generated minimally valid panel configurations
 */
export default (xs = [], ys = [], panels = [], defaultCanvases) => {
  const isValidPanelObject = panel =>
      isObject(panel) &&
      validate(xs, panel.x) &&
      validate(ys, panel.y),
    getValidPanels = (configs) => {
      if (isArray(configs) && configs.length > 0) {
        return configs.filter(isValidPanelObject).map(toPanelConfig);
      } else if (isValidPanelObject(configs)) {
        return [toPanelConfig(configs)];
      }
      return [];
    },
    panelConfigs = getValidPanels(panels);

  return panelConfigs.length
    ? panelConfigs
    : defaultCanvases(xs, ys);
};
