import { componentFactory, parseUnsafeString } from '@fusioncharts/core/src/lib';
import Caption from '../../../../_internal/components/caption';
const MIDDLE = 'middle',
  LEFT = 'left',
  validPositionsArr = ['left', 'center', 'right'];
export default chart => {
  let dataSource = chart.getFromEnv('dataSource'),
    caption = dataSource.caption || {},
    captionText,
    captionStyle,
    subCaption = dataSource.subcaption || {},
    subCaptionText,
    subCaptionStyle,
    captionPosition,
    subCaptionPosition,
    getStyleDef = chart.getFromEnv('getStyleDef');

  captionPosition = (caption.position || '').toLowerCase();
  if (validPositionsArr.indexOf(captionPosition) < 0) {
    captionPosition = LEFT;
  }
  if (captionPosition === 'center') {
    captionPosition = MIDDLE;
  }

  subCaptionPosition = (subCaption.position || '').toLowerCase();
  if (validPositionsArr.indexOf(subCaptionPosition) < 0) {
    subCaptionPosition = LEFT;
  }
  if (subCaptionPosition === 'center') {
    subCaptionPosition = MIDDLE;
  }

  captionText = parseUnsafeString(caption.text) || '';
  // merge caption style with default text style
  captionStyle = getStyleDef(caption.style && caption.style.text);

  subCaptionText = subCaption.text && parseUnsafeString(subCaption.text);
  // merge caption style with default text style
  subCaptionStyle = getStyleDef(subCaption.style && subCaption.style.text);

  const CONFIGURATION = {
    caption: captionText,
    subCaption: subCaptionText,
    captionAlignment: captionPosition,
    subCaptionAlignment: subCaptionPosition,
    captionCss: captionStyle,
    subCaptionCss: subCaptionStyle
  };
  componentFactory(
    chart,
    Caption,
    'caption',
    1,
    [CONFIGURATION]
  );
};
