/**
 * Determines the clip path needs to be applied on basis of argument type
 * @param  {string} type   type of clip applied
 * @return {string}        the path to be used for clipping
 */
const getClip = (dataset, type) => {
  var yScale = dataset.getFromEnv('yScale'),
    xScale = dataset.getFromEnv('xScale'),
    yAxisLimits = yScale.getDomain(),
    xAxisLimits = xScale.getDomain(),
    clipX = xScale.getRangeValue(xAxisLimits[0]),
    yMinPixel = yScale.getRangeValue(yAxisLimits[0]),
    yMaxPixel = yScale.getRangeValue(yAxisLimits[1]),
    height = yMinPixel - yMaxPixel,
    clipY = height < 0 ? yMinPixel : yMaxPixel,
    clipWidth =
      type === 'init'
        ? 0
        : Math.abs(xScale.getRangeValue(xAxisLimits[1]) - clipX),
    clipHeight = Math.abs(height);

  return `M${clipX},${clipY}L${clipX + clipWidth},${clipY}L${clipX +
    clipWidth},${clipY + clipHeight}L${clipX},${clipY + clipHeight}Z`;
};

export default {
  'initial.dataset.continuous': function () {
    const dataset = this;

    return {
      // Animating the group instead of the dataset paths in order to avoid
      // conflicts with the dataset's paths' clip-paths.
      'group.appearing': () => {
        return [{
          initialAttr: {
            'clip-path': getClip(dataset, 'init')
          },
          finalAttr: {
            'clip-path': getClip(dataset, 'canvas')
          },
          slot: 'middle'
        }];
      },
      '*': null
    };
  }
};
