import SmartRenderer from '@fusioncharts/core/src/component-interface/smart-renderer';
import {
  convertColor
} from '@fusioncharts/core/src/lib/lib-graphics';
import {
  pluckNumber,
  pluck
} from '@fusioncharts/core/src/lib';
import timeMarkerFactory from '../factories/time-marker-factory';
import refLineFactory from '../factories/reference-line-factory';
import refZoneFactory from '../factories/reference-zone-factory';
import gridLineFactory from '../factories/grid-line-factory';
import mouseTracker from '../factories/mouse-tracker-factory';
import MouseActionFactory from '../factories/tooltip-and-hover-factory';
import wheelFactory from '../factories/interactions/wheel-factory';
import dbTapFactory from '../factories/interactions/db-tap-factory';
import swipeFactory from '../factories/interactions/swipe-factory';
import pinchZoomFactory from '../factories/interactions/pinch-zoom-factory';
import datasetFactory from '../factories/dataset-factory';
import verticalCrosslineFactory from '../factories/vertical-crossline-factory';
import horizontalCrosslineFactory from '../factories/horizontal-crossline-factory';
import gridBandFactory from '../factories/grid-band-factory';

let M = 'M',
  H = 'H',
  V = 'V',
  HORIZONTAL = 'horizontal',
  DEFAULT_BORDER_WIDTH = 2,
  BIN_SKIP_LINE_GAP = 2,
  BIN_SKIP_LINE_TRANSX = 4,
  BIN_SKIP_LINE_TRANSY = 6;

const createBinSkipLine = (xPos, yPos) => {
  let firstLineX = xPos - BIN_SKIP_LINE_GAP,
    secondLineX = xPos + BIN_SKIP_LINE_GAP,
    transX = BIN_SKIP_LINE_TRANSX,
    transY = BIN_SKIP_LINE_TRANSY;

  return ([
    `
    M ${firstLineX + transX}, ${yPos - transY} L ${firstLineX - transX}, ${yPos + transY}
    M ${secondLineX + transX}, ${yPos - transY} L ${secondLineX - transX}, ${yPos + transY}
    `,
    `
    M ${xPos + transX}, ${yPos - transY} L ${xPos - transX}, ${yPos + transY}
    `
  ]);
};
/**
 * This class creates the canvas for time-series
 */
class Canvas extends SmartRenderer {
  constructor () {
    super();

    this.registerFactory('dataset', datasetFactory);
    // register grid line factory
    this.registerFactory('gridLine', gridLineFactory);
    // register grid band factory
    this.registerFactory('gridBand', gridBandFactory);
    // register time marker factory
    this.registerFactory('timeMarker', timeMarkerFactory);
    // register reference line factory
    this.registerFactory('referenceLine', refLineFactory);
    this.registerFactory('referenceZone', refZoneFactory, ['mouseTracker']);
    this.registerFactory('mouseTracker', mouseTracker);
    this.registerFactory('tooltip', MouseActionFactory, ['mouseTracker']);
    this.registerFactory('wheel', wheelFactory);
    this.registerFactory('dbTap', dbTapFactory);
    this.registerFactory('swipe', swipeFactory);
    this.registerFactory('pinchZoom', pinchZoomFactory);
    // vertical crossline
    this.registerFactory('verticalCrossline', verticalCrosslineFactory, ['dataset']);
    this.registerFactory('horizontalCrossline', horizontalCrosslineFactory, ['dataset']);
  }
  __setDefaultConfig () {
    super.__setDefaultConfig();
    const config = this.config;
    config.padding = {
      left: 0,
      right: 0,
      top: 0,
      bottom: 0
    };
    // to keep the mapping of datasets when stacked
    config.multiSeriesDatasetMap = {};
    config.enableMouseTracking = 1;
    config.enableMarkers = 1;
    config.enableTimeMarkers = 1;
    config.enableReferenceLine = 1;
    config.enableReferenceZone = 1;
    config.enableInteraction = 0;
    config.includeLegend = 1;
    config.gridLineDirection = HORIZONTAL;
    config.styleGridLines = 1;
  }
  /**
   * Sets the name of the component
   * @return {string} name
   */
  getName () {
    return 'canvas';
  }
  highlightDataPoints (hovered, refZoneScale, refZoneRange) {
    let canvas = this,
      allDataSets = canvas.getChildren('dataset'),
      highlightIndices,
      mouseTrackerComp = canvas.getChildren('mouseTracker')[0],
      filteredDatasets = allDataSets.filter(dataset => dataset.getFromEnv('yScale') === refZoneScale);
    filteredDatasets.forEach((dataset) => {
      highlightIndices = dataset.getHighlightedIndices(refZoneRange);
      if (highlightIndices.length) {
        if (hovered) {
          mouseTrackerComp.deactivate();
          dataset.setHoverInEffect(highlightIndices);
        } else {
          mouseTrackerComp.activate();
          dataset.setHoverOutEffect(highlightIndices);
        }
      }
    });
  }
  /**
   * Sets the type of the component
   * @return {string} type
   */
  getType () {
    return 'canvas';
  }

  /**
   * Function to configure the canvas
   * @param {Object} overrideConfig - Configuration object for canvas
   */
  configureAttributes (overrideConfig = {}) {
    var config = this.config,
      borderConfig = config.borderConfig || (config.borderConfig = {});

    Object.assign(config, overrideConfig);

    const canvasStyle = config.canvasStyle;

    // Setting the canvas border thickness
    ['leftBorder', 'topBorder', 'rightBorder', 'bottomBorder'].forEach(border => {
      // Parsing canvasBorder thickness only if provided by the chart
      borderConfig[border] = overrideConfig[border] ? pluckNumber(canvasStyle['stroke-width'],
        DEFAULT_BORDER_WIDTH) : borderConfig[border] || 0;
    });

    config.canvasBorderColor = convertColor(pluck(canvasStyle.stroke, 'D6D6D6'),
      pluckNumber(canvasStyle['stroke-opacity'] * 100, 100));

    config.canvasBGColor = convertColor(pluck(canvasStyle.fill, 'ffffff'),
      pluckNumber(canvasStyle['fill-opacity'] * 100, 100));
    config.opacity = canvasStyle.opacity;

    config.showGridBand = config.gridBandSupported && pluckNumber(config.yConfigs[0].showgridband, 0);
    config.gridBandStyle = config.yConfigs[0].style ? config.yConfigs[0].style['grid-band'] : {};
  }

  /**
   * Function to set the canvas dimensions
   * @param {Object} dim - Canvas dimensions set by the chart
   */
  setDimension (dim = {}) {
    let canvas = this,
      config = canvas.config,
      borderConfig = config.borderConfig,
      chartConfig = canvas.getFromEnv('chartConfig');
    config.canvasTop = dim.top || 0;
    config.canvasLeft = dim.left || 0;
    config.canvasWidth = dim.width || chartConfig.origRenderWidth;
    config.canvasHeight = dim.height || chartConfig.origRenderHeight;
    // Background dimensions
    config.canvasBGTop = config.canvasTop + borderConfig.topBorder;
    config.canvasBGLeft = config.canvasLeft + borderConfig.leftBorder;
    config.canvasBGWidth = Math.max(0, config.canvasWidth - borderConfig.rightBorder - borderConfig.leftBorder);
    config.canvasBGHeight = Math.max(0, config.canvasHeight - borderConfig.bottomBorder);
  }

  getDimension () {
    let config = this.config;
    return {
      top: config.canvasTop,
      left: config.canvasLeft,
      width: config.canvasWidth,
      height: config.canvasHeight
    };
  }

  /**
   * function to check the given cordinate is with in canvas or not
   * @param {number} x - x coordinate
   * @param {number} y - y coordinate
   * @return {boolean} whether the coordinate is with in canvas or not
   */
  isWithinCanvas (xArg, yArg) {
    let canvas = this,
      x = xArg,
      y = yArg,
      config = canvas.config,
      left = config.canvasBGLeft,
      right = Math.floor(left + config.canvasBGWidth),
      top = config.canvasBGTop,
      padding = config.padding,
      bottom = Math.floor(top + config.canvasBGHeight),
      translationObj = canvas.getTranslation();
    x = x - (translationObj.x || 0) + (padding.left || 0);
    y = y - (translationObj.y || 0) + (padding.top || 0);
    return (x > left && x < right && y > top && y < bottom);
  }

  draw () {
    let canvas = this;
    canvas._createGroup();
    canvas._drawCanvas();
    !canvas.config.isContext && canvas._setTooltipBounding();
  }

  _setTooltipBounding () {
    let canvas = this,
      config = canvas.config,
      translate = canvas.getTranslation(),
      tooltipController = canvas.getFromEnv('toolTipController'),
      left = config.canvasLeft + (translate.x || 0),
      top = config.canvasTop + (translate.y || 0);
    tooltipController._setRelativePosition({
      left: left,
      right: left + config.canvasWidth,
      top: top,
      bottom: top + config.canvasHeight,
      canvas: this
    });
  }

  /**
   * Fucntion to draw the canavs
   */
  _drawCanvas () {
    var canvas = this,
      config = canvas.config,
      borderConfig = config.borderConfig,
      canvasTop = config.canvasTop + borderConfig.topBorder / 2,
      canvasLeft = config.canvasLeft + borderConfig.leftBorder / 2,
      canvasWidth = config.canvasWidth - (borderConfig.rightBorder / 2 + borderConfig.leftBorder / 2),
      canvasHeight = config.canvasHeight - (borderConfig.bottomBorder / 2 + borderConfig.topBorder / 2),
      canvasPaths = {
        leftBorder: [M, canvasLeft, canvasTop, V, canvasTop + canvasHeight],
        rightBorder: [M, canvasLeft + canvasWidth, canvasTop, V, canvasTop + canvasHeight],
        topBorder: [M, canvasLeft, canvasTop, H, canvasLeft + canvasWidth],
        bottomBorder: [M, canvasLeft, canvasTop + canvasHeight, H, canvasLeft + canvasWidth]
      },
      border,
      bottomBorderPaths;

    // Creating the canvas background
    canvas.addGraphicalElement({
      el: 'rect',
      component: canvas,
      label: 'canvas',
      container: {
        id: 'tropo',
        label: 'group',
        isParent: false
      },
      attr: {
        y: config.canvasBGTop,
        x: config.canvasBGLeft,
        width: config.canvasBGWidth,
        height: config.canvasBGHeight,
        fill: config.canvasBGColor,
        'stroke-width': 0,
        'stroke': 'none',
        'opacity': config.opacity
      },
      id: 'background'
    }, true);

    // Creating individual canvas borders
    for (border in borderConfig) {
      if (border === 'bottomBorder') {
        bottomBorderPaths = this.getBottomBorderPaths();
        bottomBorderPaths.normalLine.length && canvas.addGraphicalElement({
          el: 'path',
          component: canvas,
          label: 'canvas',
          container: {
            id: 'tropo',
            label: 'group',
            isParent: false
          },
          attr: {
            path: bottomBorderPaths.normalLine.join(''),
            stroke: config.canvasBorderColor,
            'stroke-width': borderConfig[border],
            'opacity': config.opacity
          },
          id: border + '-normal'
        }, true);
        bottomBorderPaths.dashedLine.length && canvas.addGraphicalElement({
          el: 'path',
          component: canvas,
          label: 'canvas',
          container: {
            id: 'tropo',
            label: 'group',
            isParent: false
          },
          attr: {
            path: bottomBorderPaths.dashedLine.join(''),
            stroke: config.canvasBorderColor,
            'stroke-width': borderConfig[border],
            'opacity': config.opacity,
            'stroke-dasharray': [3, 4]
          },
          id: border + '-dashed'
        }, true);
        if (bottomBorderPaths.clippedLine.length) {
          canvas.addGraphicalElement({
            el: 'path',
            component: canvas,
            label: 'canvas',
            container: {
              id: 'tropo',
              label: 'group',
              isParent: false
            },
            attr: {
              path: bottomBorderPaths.clippedLine.join(''),
              stroke: config.canvasBorderColor,
              'stroke-width': borderConfig[border],
              'opacity': config.opacity
            },
            id: border + '-clipped'
          }, true);
          canvas.addGraphicalElement({
            el: 'path',
            component: canvas,
            label: 'canvas',
            container: {
              id: 'tropo',
              label: 'group',
              isParent: false
            },
            attr: {
              path: bottomBorderPaths.clippedLineMid.join(''),
              stroke: '#ffffff',
              'stroke-width': borderConfig[border],
              'opacity': config.opacity
            },
            id: border + '-clipped'
          }, true);
        }
      } else {
        canvas.addGraphicalElement({
          el: 'path',
          component: canvas,
          label: 'canvas',
          container: {
            id: 'tropo',
            label: 'group',
            isParent: false
          },
          attr: {
            path: canvasPaths[border],
            stroke: config.canvasBorderColor,
            'stroke-width': borderConfig[border],
            'opacity': config.opacity
          },
          id: border
        }, true);
      }
    }
  }

  getBottomBorderPaths () {
    let xScale = this.getFromEnv('focusScalesX')[0],
      config = this.config,
      borderConfig = config.borderConfig,
      bins = xScale.bins,
      domain = xScale.getDomain(),
      range = xScale.getRange(),
      showClippingCue = pluckNumber(config.xConfigs[0].showclippingcue, 1),
      canvasTop = config.canvasTop + borderConfig.topBorder / 2,
      canvasHeight = config.canvasHeight - (borderConfig.bottomBorder / 2 + borderConfig.topBorder / 2),
      j,
      len = bins.length,
      lastNonClippedBinIndex,
      lastClippedBin,
      xPos,
      yVal = canvasTop + canvasHeight,
      pathStr,
      binSkipLines,
      normalLine = [],
      dashedLine = [],
      clippedLine = [],
      clippedLineMid = [];

    for (j = 0, len = bins.length; j < len; j++) {
      if (bins[j].end > +domain[0] && bins[j].start < +domain[1]) {
        pathStr = `M ${xScale.getRangeValue(Math.max(+domain[0], +bins[j].start))}, ${yVal} 
          L ${xScale.getRangeValue(Math.min(+bins[j].end, +domain[1]))}, ${yVal}`;
        if (!bins[j].clipType || !showClippingCue) {
          normalLine.push(pathStr);
          lastNonClippedBinIndex = j;
        } else if (bins[j].clipType === 1) {
          dashedLine.push(pathStr);
          lastNonClippedBinIndex = j;
        }
      }

      if (bins[j].clipType === 2 && showClippingCue) {
        if (lastClippedBin === j - 1) {
          lastClippedBin = j;
          continue;
        }
        if (lastNonClippedBinIndex) {
          xPos = (xScale.getRangeValue(bins[lastNonClippedBinIndex].start) + xScale.getRangeValue(bins[j].end)) / 2;
        } else {
          xPos = xScale.getRangeValue(bins[j].end);
        }

        if (xPos < (range[0] + BIN_SKIP_LINE_GAP) || xPos > (range[1] - BIN_SKIP_LINE_GAP)) {
          continue;
        }
        binSkipLines = createBinSkipLine(xPos, yVal);
        clippedLine.push(binSkipLines[0]);
        clippedLineMid.push(binSkipLines[1]);
        lastClippedBin = j;
      }
    }

    return {
      normalLine,
      dashedLine,
      clippedLine,
      clippedLineMid
    };
  }

  /**
   * Function to create group for its child components
   */
  _createGroup () {
    let canvas = this,
      config = canvas.config,
      padding = config.padding,
      // Taking the padding into consideration as the clip-rect is shifed based on the translation applied
      // on the group
      canvasLeft = config.canvasBGLeft - padding.left,
      canvasTop = config.canvasBGTop - padding.top,
      canvasWidth = config.canvasBGWidth,
      canvasHeight = config.canvasBGHeight,
      clipPath = [`M${canvasLeft},${canvasTop}`, `L${canvasLeft + canvasWidth},${canvasTop}`, `L${canvasLeft + canvasWidth},${canvasTop + canvasHeight}`, `L${canvasLeft},${canvasTop + canvasHeight}Z`];

    // create group for canvas
    canvas.addGraphicalElement({
      el: 'group',
      container: {
        id: 'tropo',
        label: 'group',
        isParent: true
      },
      component: canvas,
      label: 'group',
      attr: {
        name: 'canvas-tropo',
        transform: config.canvasTranslation
      },
      id: 'tropo'
    });
    canvas.addGraphicalElement({
      el: 'group',
      container: {
        id: 'strato',
        label: 'group',
        isParent: true
      },
      component: canvas,
      label: 'strato',
      attr: {
        name: 'canvas-strato',
        transform: config.translate
      },
      id: 'strato'
    });
    canvas.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group',
        isParent: true
      },
      component: canvas,
      label: 'meso',
      attr: {
        name: 'canvas-meso',
        transform: config.translate,
        'clip-path': clipPath
      },
      id: 'meso'
    });
    canvas.addGraphicalElement({
      el: 'group',
      container: {
        id: 'thermo',
        label: 'group',
        isParent: true
      },
      component: canvas,
      label: 'thermo',
      attr: {
        name: 'canvas-thermo',
        transform: config.translate
      },
      id: 'thermo'
    });
    canvas.addGraphicalElement({
      el: 'group',
      container: {
        id: 'exo',
        label: 'group',
        isParent: true
      },
      component: canvas,
      label: 'exo',
      attr: {
        name: 'canvas-exo',
        transform: config.translate
      },
      id: 'exo'
    });
    canvas.addToEnv('dsGroupclipPath', clipPath);
  }
  /**
   * This function calculates the amount of translation that is to be applied on the groups of the canvas
   * The canavs border width is taken into consideration for final x,y translation
   * @param {Number} x Given translation in x
   * @param {Number} y Given translation in y
   */
  setTranslation (x, y) {
    const config = this.config,
      borderConfig = config.borderConfig,
      padding = config.padding;
    config._translateX = x + borderConfig.leftBorder;
    config._translateY = y + borderConfig.topBorder;
    config.canvasTranslation = 't' + config._translateX + ',' + config._translateY;
    config._translateX += padding.left;
    config._translateY += padding.top;
    config.translate = 't' + (config._translateX) + ',' + (config._translateY);
  }

  getCanvasBorder () {
    return this.config.borderConfig;
  }
  setPadding (padding = {}) {
    Object.assign(this.config.padding, padding);
  }
}

export default Canvas;
