import TimeSpanMinMarker from '../components/time-span-min-marker';
import TimeSpanFullMarker from '../components/time-span-full-marker';
import TimeInstanceMarker from '../components/time-instance-marker';
import pluckNumber from '@fusioncharts/utils/src/pluck-number';

export default canvas => {
  let canvasConfig = canvas.config,
    xConfigs = canvasConfig.xConfigs;
  if (canvas.config.enableTimeMarkers) {
    xConfigs.forEach(xConfig => {
      let timeMarkers = xConfig.timemarker || [],
        showTimeSpanMarkerTooltipAt = pluckNumber(xConfig.showtimespanmarkertooltipat, 1),
        columnName = xConfig.plot[0].value,
        chartConfig = canvas.getFromEnv('chartConfig'),
        schema = chartConfig.dataTable.getSchema(),
        defaultFormat = schema.find(el => el.name === columnName).format,
        timeSpanMinMarkersConf = [],
        timeSpanFullMarkersConf = [],
        timeInstanceMarkersConf = [],
        chart = canvas.getFromEnv('chart'),
        scale = canvas.config.xConfigs[0].scale,
        timeSpanMinMarker,
        timeSpanFullMarker,
        timeInstanceMarker;
      timeMarkers.forEach(marker => {
        // time span marker
        if (marker.end) {
          if (marker.type === 'full') {
            timeSpanFullMarkersConf.push(marker);
          } else {
            timeSpanMinMarkersConf.push(marker);
          }
        } else { // time instance marker
          timeInstanceMarkersConf.push(marker);
        }
      });

      timeSpanMinMarker = canvas.attachChild(TimeSpanMinMarker, 'timeSpanMinMarker');
      timeSpanFullMarker = canvas.attachChild(TimeSpanFullMarker, 'timeSpanFullMarker');

      timeSpanMinMarker.configure({
        timeMarker: timeSpanMinMarkersConf,
        defaultFormat,
        xScale: scale,
        showTimeSpanMarkerTooltipAt
      });
      timeSpanMinMarker.addExtEventListener('timeSpanMinMarkerHovered', (e) => {
        if (e.data.senderTimeMarker !== timeSpanMinMarker) {
          timeSpanMinMarker.setData({
            hoveredIndex: e.data.hoveredIndex,
            hoveredFromOutside: true
          }, true);
        }
      }, chart);

      timeSpanFullMarker.configure({
        timeMarker: timeSpanFullMarkersConf,
        defaultFormat,
        xScale: scale,
        showTimeSpanMarkerTooltipAt
      });
      timeSpanFullMarker.addExtEventListener('timeSpanFullMarkerHovered', (e) => {
        if (e.data.senderTimeMarker !== timeSpanFullMarker) {
          timeSpanFullMarker.setData({
            hoveredIndex: e.data.hoveredIndex,
            hoveredFromOutside: true
          }, true);
        }
      }, chart);

      timeInstanceMarker = canvas.attachChild(TimeInstanceMarker, 'timeInstanceMarker');
      timeInstanceMarker.configure({
        timeMarker: timeInstanceMarkersConf,
        defaultFormat,
        xScale: scale
      });
      timeInstanceMarker.addExtEventListener('timeInstanceMarkerHovered', (e) => {
        if (e.data.senderTimeMarker !== timeInstanceMarker) {
          timeInstanceMarker.setData({
            hoveredMarkerIndex: e.data.hoveredMarkerIndex,
            hoveredDomainIndex: e.data.hoveredDomainIndex,
            hoveredFromOutside: true
          }, true);
        }
      }, chart);
    });
  }
};
