import { SmartRenderer } from '@fusioncharts/core/src/component-interface';

// const DEFAULT_BUTTON_PADDING = 10;
const APPLY = 'Apply';
class Button extends SmartRenderer {
  /**
   * Creates an instance of Button.
   * @param {any} id id
   * @memberof Button
   */
  constructor (id) {
    super(id);
    let button = this,
      config = button.config;
    config.clickHandler = function () {
      button.getLinkedParent().submitData(config.type);
    };
    // mouse over handler
    config.mouseoverHandler = function () {
      button.setData({
        buttonStyle: config['button:hover']
      }, true);
    };
    // mouse out handler
    config.mouseoutHandler = function () {
      button.config && button.setData({
        buttonStyle: config['button:hoverout']
      }, true);
    };

    button.addEventListener('fc-click', config.clickHandler);
    button.addEventListener('fc-mouseover', config.mouseoverHandler);
    button.addEventListener('fc-mouseout', config.mouseoutHandler);
  }

  /**
   * sets default configuration
   *
   * @memberof Button
   */
  __setDefaultConfig () {
    let buttonConfig = this.config;

    buttonConfig.height = 10;
    buttonConfig.width = 20;
    buttonConfig.label = APPLY;
    buttonConfig._buttonStyle = {
      '-webkit-border-radius': '2px',
      backgroundColor: '#5648D4',
      border: '1px solid #5648D4',
      borderRadius: '2px',
      color: '#60634E',
      cursor: 'pointer',
      paddingTop: '1px',
      textAlign: 'center',
      zIndex: 21,
      display: 'flex',
      width: 66,
      height: 22,
      'line-height': '9px',
      'justify-content': 'center',
      'align-items': 'center',
      'font-size': '11px'
    };
    buttonConfig.clickHandler = function () {
      this.getLinkedParent().submitData();
    };
  }

  /**
   * configures the button
   *
   * @param {any} [inputConfig={}] configuration provided
   * @memberof Button
   */
  configureAttributes (inputConfig = {}) {
    let button = this,
      buttonConfig = button.config,
      styleDef = button.getFromEnv('getStyleDef');

    Object.assign(buttonConfig, inputConfig);

    buttonConfig._finalStyle = Object.assign({}, buttonConfig._buttonStyle,
      button.getFromEnv('baseTextStyle'), styleDef(buttonConfig.customStyle),
      styleDef(buttonConfig.buttonStyle));
  }

  /**
   * calculates width and height of the buttons
   *
   * @returns {Object} width and height taken by the button
   * @memberof Button
   */
  getDimension () {
    let button = this,
      buttonConfig = button.config,
      fontSize = buttonConfig._finalStyle['font-size'],
      fontParser = button.getFromEnv('fontParser');

    buttonConfig.width = Math.max(+buttonConfig._finalStyle.width || 0, fontParser(fontSize) * 6);
    buttonConfig.height = Math.max(+buttonConfig._finalStyle.height || 0, fontParser(fontSize) * 2);
    return {
      width: buttonConfig.width,
      height: buttonConfig.height
    };
  }

  /**
   * sets the position
   *
   * @param {any} x position x
   * @param {any} y position y
   * @memberof Button
   */
  setTranslation (x, y) {
    this.config.position = {
      x: x,
      y: y
    };
  }

  /**
   * Draws the button
   *
   * @memberof Button
   */
  draw () {
    let button = this,
      buttonConfig = button.config,
      position = buttonConfig.position,
      css = { ...buttonConfig._finalStyle };

    css['margin-bottom'] = window.innerWidth < 576 ? '15px' : '0px';

    button.addGraphicalElement({
      el: 'html',
      attr: {
        text: buttonConfig.label,
        type: 'div',
        width: buttonConfig.width,
        height: buttonConfig.height,
        x: position.x,
        y: position.y
      },
      component: button,
      container: {
        id: 'box-container',
        label: 'box-container',
        isParent: true
      },
      css,
      label: 'button',
      id: 'button'
    });
  }
}

export default Button;
