import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import Calendar from '../../../vendors/fc-calendar/dist/fc-calendar';
import { PXSTRING, DEFAULT_FT_FONT } from '@fusioncharts/core/src/lib';
import DateBox from './date-box';
import TimeBox from './time-box';
import Button from './button';
import isObject from '@fusioncharts/utils/src/type/is-object';

const DEFAULT_CALENDAR_GAP = 25,
  DEFAULT_VERTICAL_PADDING = 8,
  DEFAULT_HORIZONTAL_PADDING = 5,
  DEFAULT_WIDGET_FONT_SIZE = 11,
  CALENDAR_KEY = 'cal-',
  CALENDAR_KEY_LEN = CALENDAR_KEY.length,
  MOBILE = 'mobile',
  DESKTOP = 'desktop',
  getCssObj = (obj, getStyleDef) => {
    let output = {};
    if (isObject(obj)) {
      for (let key in obj) {
        if (obj.hasOwnProperty(key) && key.startsWith(CALENDAR_KEY)) {
          output[key.substring(CALENDAR_KEY_LEN)] = getStyleDef(obj[key]);
        }
      }
    }
    return output;
  },
  /**
   * appends a 0 in front of a number if the number is less than 10
   *
   * @param {Number} x number to checked
   */
  formatDate = x => (x < 10 ? '0' + x : x) + '',
  /**
   * checks if a given date is with two date ranges
   *
   * @param {Object} date date
   * @param {Object} start start date
   * @param {Object} end end date
   * @returns {boolean} whether the date lies between start and end
   */
  isWithinDate = (date, start, end) => {
    var startDate = new Date(start.year, start.month, start.day),
      endDate = new Date(end.year, end.month, end.day),
      curDate = new Date(date.year, date.month, date.day);

    if (+curDate >= +startDate && +curDate <= +endDate) {
      return true;
    } return false;
  };

class CustomRangeManager extends SmartRenderer {
  constructor (id) {
    super(id);

    let manager = this;

    manager.addToEnv('fontParser', fontsizeArg => {
      let fontsize = parseFloat(fontsizeArg);
      return (isNaN(fontsize) ? DEFAULT_WIDGET_FONT_SIZE : fontsize);
    });
  }
  /**
   * sets default configuration
   *
   * @memberof CustomRangeManager
   */
  __setDefaultConfig () {
    let managerConfig = this.config;

    managerConfig.paddingLeft = 10;
    managerConfig.paddingRight = 10;
    managerConfig.paddingTop = 15;
    managerConfig.paddingBottom = 15;
    managerConfig.prevVisibility = 'hidden';
    // default fallback values
    managerConfig.contextStart = {
      year: 1970,
      month: 1,
      day: 1
    };
    managerConfig.contextEnd = {
      year: 2070,
      month: 1,
      day: 1
    };
    managerConfig.calendarDim = {
      height: 191,
      width: 163
    };
    this._calendars = {};
    managerConfig._backgroundStyle = {
      'font-size': DEFAULT_WIDGET_FONT_SIZE + PXSTRING,
      'font-family': 'Verdana,sans-serif',
      border: '1px solid #cdcdcd',
      position: 'absolute',
      'border-radius': '0px',
      'overflow-y': 'visible',
      'background-color': '#ffffff'
    };
    managerConfig._mobileBgStyle = Object.assign({}, managerConfig._backgroundStyle, {
      position: 'fixed',
      'overflow-y': 'scroll',
      'border-radius': '10px'
    });
    managerConfig._pointerStyle = {
      'font-size': DEFAULT_WIDGET_FONT_SIZE + PXSTRING,
      'font-family': 'Verdana,sans-serif',
      'border-left': '1px solid #cdcdcd',
      'border-right': '0px solid #cdcdcd',
      'border-top': '1px solid #cdcdcd',
      'border-bottom': '0px solid #cdcdcd',
      transform: 'rotate(45deg)',
      'background-color': '#ffffff'
    };
  }

  /**
   * configures the components
   *
   * @param {any} [inputConfig={}] configurations
   * @memberof CustomRangeManager
   */
  configureAttributes (inputConfig = {}) {
    let manager = this,
      managerConfig = manager.config,
      calendars = manager.getCalendars(),
      extStyle,
      styleDef = manager.getFromEnv('getStyleDef'),
      baseTextStyle = manager.getFromEnv('baseTextStyle'),
      tempDate,
      leftDateConfig,
      rightDateConfig,
      applyButtonStyle,
      cancelButtonStyle;

    Object.assign(managerConfig, inputConfig);

    extStyle = managerConfig.extStyle;
    if (managerConfig.visibility !== managerConfig.prevVisibility) {
      /**
       * date-selection input box should take the focus limit when the widget is opened
       * by interacting with the crs tool. In other cases, the date input box should always
       * show the temporary date that is typed in it, hence the flag tempDate.
       * */
      tempDate = false;
      managerConfig.blockUpdate = false;
    } else {
      managerConfig.blockUpdate = true;
    }
    managerConfig.prevVisibility = managerConfig.visibility;
    // no need to configure the calendar widget components when it is not displayed
    if (managerConfig.visibility !== 'visible') {
      // remove the calendars if present
      if (calendars.left) {
        calendars.left.dispose();
        delete calendars.left;
      }

      // remove the calendars if present
      if (calendars.right) {
        calendars.right.dispose();
        delete calendars.right;
      }
      return;
    }

    leftDateConfig = {
      label: 'From: ',
      date: managerConfig.leftCalSelectedDate,
      id: 'left',
      labelStyle: extStyle.label,
      inputStyle: extStyle.input
    };
    rightDateConfig = {
      label: 'To: ',
      date: managerConfig.rightCalSelectedDate,
      id: 'right',
      labelStyle: extStyle.label,
      inputStyle: extStyle.input
    };

    if (typeof tempDate !== 'undefined') {
      leftDateConfig.tempDate = rightDateConfig.tempDate = tempDate;
    }
    manager.attachChild(DateBox, 'left-date', 'left-date').configure(leftDateConfig);
    manager.attachChild(DateBox, 'right-date', 'right-date').configure(rightDateConfig);

    // only create time box when either major and/or minor intervals are in time
    if (managerConfig.showTime) {
      manager.attachChild(TimeBox, 'left-time', 'left-time').configure({
        time: managerConfig.leftCalSelectedDate,
        labelStyle: extStyle.label,
        selectStyle: extStyle.select
      });
      manager.attachChild(TimeBox, 'right-time', 'right-time').configure({
        time: managerConfig.rightCalSelectedDate,
        labelStyle: extStyle.label,
        selectStyle: extStyle.select
      });
    }

    if (extStyle['button-apply']) {
      applyButtonStyle = Object.assign({}, baseTextStyle, extStyle['button-apply']);
    } else {
      applyButtonStyle = Object.assign({
        color: '#ffffff',
        'font-size': DEFAULT_WIDGET_FONT_SIZE + PXSTRING
      }, baseTextStyle);
    }
    manager.attachChild(Button, 'apply', 'apply').configure({
      label: 'Apply',
      type: 'submit',
      customStyle: applyButtonStyle,
      buttonStyle: applyButtonStyle,
      'button:hoverout': applyButtonStyle,
      'button:hover': (extStyle['button-apply:hover']) || {}
    });

    if (extStyle['button-cancel']) {
      cancelButtonStyle = Object.assign({}, baseTextStyle, extStyle['button-cancel']);
    } else {
      cancelButtonStyle = Object.assign({
        backgroundColor: '#ffffff',
        color: '#7A7A7A',
        border: '0px',
        'font-size': DEFAULT_WIDGET_FONT_SIZE + PXSTRING
      }, baseTextStyle);
    }
    manager.attachChild(Button, 'cancel', 'cancel').configure({
      label: 'Cancel',
      type: 'cancel',
      customStyle: cancelButtonStyle,
      buttonStyle: cancelButtonStyle,
      'button:hoverout': cancelButtonStyle,
      'button:hover': (extStyle['button-cancel:hover']) || {}
    });

    managerConfig._finalBackgroundStyle = Object.assign({}, managerConfig._backgroundStyle, styleDef(extStyle.container));
    managerConfig._finalMobileBgStyle = Object.assign({}, managerConfig._mobileBgStyle, styleDef(extStyle.container));
    managerConfig._finalPointerStyle = Object.assign({}, managerConfig._pointerStyle, styleDef(extStyle.container));

    if (managerConfig._finalPointerStyle.border) {
      managerConfig._finalPointerStyle['border-left'] = managerConfig._finalPointerStyle['border-top'] = managerConfig._finalPointerStyle.border;
    }
    managerConfig._finalPointerStyle['border-right'] = managerConfig._finalPointerStyle['border-bottom'] = '0px';
    delete managerConfig._finalPointerStyle.border;

    managerConfig.calStyle = getCssObj(managerConfig.extStyle, manager.getFromEnv('getStyleDef'));
  }

  manageSpace (mode = DESKTOP) {
    if (mode === DESKTOP) {
      this.manageSpaceLG();
    } else {
      this.manageSpaceSM();
    }
  }

  manageSpaceSM () {
    let manager = this,
      managerConfig = manager.config,
      clientHeight = document.documentElement.clientHeight,
      clientWidth = document.documentElement.clientWidth,
      maxAllowedHeight = 0.8 * clientHeight,
      fromDate,
      toDate,
      fromTime,
      toTime,
      fromTimeDim = {
        height: 0,
        width: 0
      },
      toTimeDim = {
        height: 0,
        width: 0
      },
      fromLabelBoxDim,
      toLabelBoxDim,
      apply,
      cancel,
      xPos,
      availableWidth,
      paddingLeft = 50,
      paddingRight = 50,
      curYPos = managerConfig.paddingTop,
      calendarDim = managerConfig.calendarDim,
      applyDim,
      cancelDim;

    // no need to configure the calendar widget components when it is not displayed
    if (managerConfig.visibility !== 'visible') {
      return;
    }
    fromDate = manager.getChildren('left-date')[0];
    toDate = manager.getChildren('right-date')[0];
    fromTime = manager.getChildren('left-time');
    toTime = manager.getChildren('right-time');
    apply = manager.getChildren('apply')[0];
    cancel = manager.getChildren('cancel')[0];

    // consider spaces for time selector box when time is to be shown
    if (fromTime) {
      fromTime = fromTime[0];
      fromTimeDim = fromTime.getDimension();
    }

    if (toTime) {
      toTime = toTime[0];
      toTimeDim = toTime.getDimension();
    }

    fromLabelBoxDim = fromDate.getDimension();
    toLabelBoxDim = toDate.getDimension();

    applyDim = apply.getDimension();
    cancelDim = cancel.getDimension();

    availableWidth = Math.max(
      calendarDim.width,
      fromLabelBoxDim.width,
      toLabelBoxDim.width,
      fromTimeDim.width,
      toTimeDim.width,
      applyDim.width + cancelDim.width + DEFAULT_VERTICAL_PADDING
    );

    fromDate.setTranslation(
      paddingLeft,
      curYPos
    );

    xPos = paddingLeft + availableWidth / 2 - calendarDim.width / 2;
    curYPos += fromLabelBoxDim.height + DEFAULT_VERTICAL_PADDING;

    managerConfig.leftCalendarPosition = {
      x: xPos,
      y: curYPos
    };

    curYPos += calendarDim.height + DEFAULT_VERTICAL_PADDING;

    fromTime && fromTime.setTranslation(
      paddingLeft,
      curYPos
    );

    curYPos += fromTimeDim.height + 2 * DEFAULT_VERTICAL_PADDING;

    toDate.setTranslation(
      paddingLeft,
      curYPos
    );

    xPos = paddingLeft + availableWidth / 2 - calendarDim.width / 2;
    curYPos += toLabelBoxDim.height + DEFAULT_VERTICAL_PADDING;

    managerConfig.rightCalendarPosition = {
      x: xPos,
      y: curYPos
    };

    curYPos += calendarDim.height + DEFAULT_VERTICAL_PADDING;

    toTime && toTime.setTranslation(
      paddingLeft,
      curYPos
    );

    curYPos += toTimeDim.height + DEFAULT_VERTICAL_PADDING;

    apply.setTranslation(
      paddingLeft,
      curYPos
    );

    cancel.setTranslation(
      paddingLeft + applyDim.width + DEFAULT_VERTICAL_PADDING,
      curYPos
    );

    if (maxAllowedHeight > curYPos + applyDim.height + managerConfig.paddingBottom) {
      managerConfig.height = curYPos + applyDim.height + managerConfig.paddingBottom;
      managerConfig.y = clientHeight / 2 - managerConfig.height / 2;
    } else {
      managerConfig.height = maxAllowedHeight;
      managerConfig.y = 0.1 * clientHeight;
    }
    managerConfig.width = calendarDim.width + paddingLeft + paddingRight;
    managerConfig.x = clientWidth / 2 - managerConfig.width / 2;
  }
  /**
   * Space management and positioning of all the internal component within this container
   *
   * @memberof CustomRangeManager
   */
  manageSpaceLG () {
    let manager = this,
      managerConfig = manager.config,
      fromLabelBoxDim,
      toLabelBoxDim,
      timeBoxPaddingFactor = 4,
      fromDate,
      toDate,
      fromTime,
      toTime,
      apply,
      cancel,
      maxSpaceTakenOnLeft = 0,
      maxSpaceTakenOnRight = 0,
      fromTimeDim = {
        height: 0,
        width: 0
      },
      toTimeDim = {
        height: 0,
        width: 0
      },
      applyDim,
      cancelDim,
      calendarDim = managerConfig.calendarDim,
      tool = manager.getLinkedParent(),
      position = tool.config.containerPos;

    // no need to configure the calendar widget components when it is not displayed
    if (managerConfig.visibility !== 'visible') {
      return;
    }
    fromDate = manager.getChildren('left-date')[0];
    toDate = manager.getChildren('right-date')[0];
    fromTime = manager.getChildren('left-time');
    toTime = manager.getChildren('right-time');
    apply = manager.getChildren('apply')[0];
    cancel = manager.getChildren('cancel')[0];

    // consider spaces for time selector box when time is to be shown
    if (fromTime) {
      fromTime = fromTime[0];
      fromTimeDim = fromTime.getDimension();
    }

    if (toTime) {
      toTime = toTime[0];
      toTimeDim = toTime.getDimension();
    }

    !fromTimeDim.height && (timeBoxPaddingFactor = 2);

    fromLabelBoxDim = fromDate.getDimension();
    toLabelBoxDim = toDate.getDimension();

    applyDim = apply.getDimension();
    cancelDim = cancel.getDimension();

    maxSpaceTakenOnLeft = Math.max(fromTimeDim.width, fromLabelBoxDim.width, calendarDim.width);

    managerConfig.leftCalendarPosition = {
      x: managerConfig.paddingLeft,
      y: managerConfig.paddingTop + fromLabelBoxDim.height + DEFAULT_VERTICAL_PADDING
    };

    managerConfig.rightCalendarPosition = {
      x: managerConfig.paddingLeft + maxSpaceTakenOnLeft + DEFAULT_CALENDAR_GAP,
      y: managerConfig.paddingTop + toLabelBoxDim.height + DEFAULT_VERTICAL_PADDING
    };

    fromDate.setTranslation(
      managerConfig.paddingLeft,
      managerConfig.paddingTop
    );

    toDate.setTranslation(
      managerConfig.paddingLeft + maxSpaceTakenOnLeft + DEFAULT_CALENDAR_GAP,
      managerConfig.paddingTop
    );

    fromTime && fromTime.setTranslation(
      managerConfig.paddingLeft,
      managerConfig.leftCalendarPosition.y + calendarDim.height + 2 * DEFAULT_VERTICAL_PADDING
    );

    toTime && toTime.setTranslation(
      managerConfig.paddingLeft + maxSpaceTakenOnLeft + DEFAULT_CALENDAR_GAP,
      managerConfig.rightCalendarPosition.y + calendarDim.height + 2 * DEFAULT_VERTICAL_PADDING
    );

    apply.setTranslation(
      managerConfig.paddingLeft,
      managerConfig.leftCalendarPosition.y + calendarDim.height + timeBoxPaddingFactor * DEFAULT_VERTICAL_PADDING + fromTimeDim.height
    );
    cancel.setTranslation(
      managerConfig.paddingLeft + applyDim.width + DEFAULT_HORIZONTAL_PADDING,
      managerConfig.leftCalendarPosition.y + calendarDim.height + timeBoxPaddingFactor * DEFAULT_VERTICAL_PADDING + fromTimeDim.height
    );

    maxSpaceTakenOnRight = Math.max(
      managerConfig.paddingLeft + applyDim.width + DEFAULT_HORIZONTAL_PADDING + cancelDim.width,
      managerConfig.rightCalendarPosition.x + calendarDim.width,
      managerConfig.paddingLeft + maxSpaceTakenOnLeft + DEFAULT_CALENDAR_GAP + toTimeDim.width,
      managerConfig.paddingLeft + maxSpaceTakenOnLeft + DEFAULT_CALENDAR_GAP + toLabelBoxDim.width
    );

    managerConfig.width = maxSpaceTakenOnRight + managerConfig.paddingRight;
    managerConfig.height = managerConfig.leftCalendarPosition.y + calendarDim.height + applyDim.height + timeBoxPaddingFactor * DEFAULT_VERTICAL_PADDING + fromTimeDim.height + managerConfig.paddingBottom;

    managerConfig.x = position.x - managerConfig.width / 2;
    managerConfig.y = position.y + DEFAULT_VERTICAL_PADDING / 2;
  }

  isWithinWidget (x, y) {
    let config = this.config,
      left = config.x,
      top = config.y,
      bottom = top + config.height,
      right = left + config.width;
    return (x > left && x < right && y > top && y < bottom);
  }

  /**
   * draws the container
   *
   * @memberof CustomRangeManager
   */
  draw () {
    let manager = this,
      managerConfig = manager.config,
      {
        _finalMobileBgStyle,
        _finalBackgroundStyle
      } = managerConfig,
      mode = managerConfig.visualMode = window.innerWidth < 576 ? MOBILE : DESKTOP,
      bgStyle = mode === MOBILE ? _finalMobileBgStyle : _finalBackgroundStyle;

    if (managerConfig.visibility === 'hidden') return;

    if (mode === MOBILE) {
      bgStyle['overflow-y'] = 'scroll';
    }
    manager.manageSpace(mode);
    // main container
    manager.addGraphicalElement({
      el: 'html',
      attr: {
        x: managerConfig.x,
        y: managerConfig.y,
        width: managerConfig.width,
        height: managerConfig.height,
        type: 'div',
        fill: 'ffffff',
        display: 'block',
        class: 'fc-crs-widget',
        visibility: managerConfig.visibility
      },
      css: bgStyle,
      component: manager,
      label: 'box-container',
      id: 'box-container'
    });
    (mode !== MOBILE) && manager.addGraphicalElement({
      el: 'html',
      attr: {
        x: managerConfig.width / 2 - 10,
        y: -9,
        width: 15,
        height: 15,
        type: 'div',
        display: 'block',
        visibility: managerConfig.visibility
      },
      css: managerConfig._finalPointerStyle,
      container: {
        label: 'box-container',
        id: 'box-container'
      },
      component: manager,
      label: 'box-pointer',
      id: 'box-pointer'
    });

    managerConfig.drawCalendars && manager.drawCalendars();
    managerConfig.mode = mode;
  }

  /**
   * getter method for calendars
   *
   * @returns {Object} object containing the two calendars
   * @memberof CustomRangeManager
   */
  getCalendars () {
    return this._calendars;
  }

  /**
   * configures and updates the visuals of the calendars
   *
   * @memberof CustomRangeManager
   */
  drawCalendars () {
    let manager = this,
      config = manager.config,
      calendars = manager.getCalendars(),
      leftCalendarPosition = config.leftCalendarPosition,
      rightCalendarPosition = config.rightCalendarPosition,
      container = manager.getGraphicalElement('box-container', 'box-container').element,
      mode = window.innerWidth < 576 ? MOBILE : DESKTOP,
      curCalendarDim,
      calStyle = config.calStyle;

    if (config.mode === mode && config.blockUpdate && calendars.left && calendars.right) {
      return;
    }

    if (!calendars.left) {
      calendars.left = new Calendar({
        weekLabel: ['S', 'M', 'T', 'W', 'T', 'F', 'S'],
        selectedDate: config.leftCalSelectedDate,
        posX: leftCalendarPosition.x,
        posY: leftCalendarPosition.y,
        container,
        id: 'left',
        rangeStart: config.contextStart,
        rangeEnd: config.contextEnd,
        showInactiveMonths: true,
        customCssClass: calStyle,
        events: {
          onDateChange: function (date) {
            manager.updateDate(date, 'left');
          },
          onMonthChange: function (date) {
            manager.updateDate(date, 'left');
          },
          onYearChange: function (date) {
            manager.updateDate(date, 'left');
          }
        },
        'font-family': DEFAULT_FT_FONT + ' !important'
      });
    } else {
      calendars.left.configure({
        weekLabel: ['S', 'M', 'T', 'W', 'T', 'F', 'S'],
        selectedDate: config.leftCalSelectedDate,
        posX: leftCalendarPosition.x,
        posY: leftCalendarPosition.y,
        rangeStart: config.contextStart,
        rangeEnd: config.contextEnd,
        customCssClass: calStyle,
        showInactiveMonths: true,
        container
      });
    }

    if (!calendars.right) {
      calendars.right = new Calendar({
        weekLabel: ['S', 'M', 'T', 'W', 'T', 'F', 'S'],
        selectedDate: config.rightCalSelectedDate,
        posX: rightCalendarPosition.x,
        posY: rightCalendarPosition.y,
        rangeStart: config.contextStart,
        rangeEnd: config.contextEnd,
        showInactiveMonths: true,
        customCssClass: calStyle,
        container,
        id: 'right',
        events: {
          onDateChange: function (date) {
            manager.updateDate(date, 'right');
          },
          onMonthChange: function (date) {
            manager.updateDate(date, 'right');
          },
          onYearChange: function (date) {
            manager.updateDate(date, 'right');
          }
        },
        'font-family': DEFAULT_FT_FONT + ' !important'
      });
    } else {
      calendars.right.configure({
        weekLabel: ['S', 'M', 'T', 'W', 'T', 'F', 'S'],
        selectedDate: config.rightCalSelectedDate,
        posX: rightCalendarPosition.x,
        posY: rightCalendarPosition.y,
        rangeStart: config.contextStart,
        rangeEnd: config.contextEnd,
        customCssClass: calStyle,
        showInactiveMonths: true,
        container
      });
    }

    curCalendarDim = calendars.left.getDimension();

    // if calendar dimension is changed, redraw.
    if (
      curCalendarDim.width !== config.calendarDim.width ||
      curCalendarDim.height !== config.calendarDim.height
    ) {
      config.calendarDim = curCalendarDim;
      manager.asyncDraw();
    }
  }
  /**
   * update the date based on the date chosen in the calendar
   *
   * @param {any} date date
   * @param {any} id id of the calendar
   * @memberof CustomRangeManager
   */
  updateDate (date, id) {
    let manager = this,
      labelBox = manager.getChildren(id + '-date')[0],
      element = labelBox.getGraphicalElement('box', 'box');

    labelBox.setData({
      date,
      tempDate: false
    }, true);
    element.element.value = `${formatDate(date.day)}/${formatDate(date.month)}/${formatDate(date.year)}`;
  }

  /**
   * update the calendar based on the date in the input box
   *
   * @param {any} date date
   * @param {any} id id of the calendar
   * @memberof CustomRangeManager
   */
  updateCalendar (date, id) {
    let manager = this,
      managerConfig = manager.config,
      calendars = manager.getCalendars(),
      selectedDate = managerConfig[id + 'CalSelectedDate'];

    // If date is within the given date, select it. Else ignore
    if (isWithinDate(date, managerConfig.contextStart, managerConfig.contextEnd)) {
      managerConfig[id + 'CalSelectedDate'] = date;
      selectedDate.day = date.day;
      selectedDate.month = date.month;
      selectedDate.year = date.year;
      calendars[id] && calendars[id].configure({
        selectedDate: date
      });
    } else {
      manager.getChildren(id + '-date')[0].setData({}, true);
    }
  }

  /**
   * When apply button is pressed, this api summons the domainValidator method of the chart
   * and updates the visuals
   *
   * @param {string} [type='cancel'] type of operation, can be 'apply' or 'cancel'
   * @memberof CustomRangeManager
   */
  submitData (type = 'cancel') {
    let manager = this,
      rangeSelector = manager.getLinkedParent(),
      chart = rangeSelector.getFromEnv('chart'),
      scale = chart.getFromEnv('contextScalesX')[0],
      curDomain = [],
      calendars = manager.getCalendars(),
      isUTC = chart.getFromEnv('UTC'),
      leftDate = calendars.left.getDate(),
      rightDate = calendars.right.getDate(),
      fromTimeComp = manager.getChildren('left-time'),
      toTimeComp = manager.getChildren('right-time'),
      formattedDateTexts,
      showTimeInLabel,
      d1,
      d2,
      fromTime = {
        hours: 0,
        minutes: 0,
        seconds: 0
      },
      toTime = {
        hours: 0,
        minutes: 0,
        seconds: 0
      };

    // update time if time box is present
    if (fromTimeComp) {
      fromTime = fromTimeComp[0].getTime();
    }

    if (toTimeComp) {
      toTime = toTimeComp[0].getTime();
    }

    rangeSelector.setData({}, true);
    if (type === 'submit') {
      d1 = isUTC
        ? Date.UTC(leftDate.year, leftDate.month - 1, leftDate.day, fromTime.hours, fromTime.minutes, fromTime.seconds)
        : new Date(leftDate.year, leftDate.month - 1, leftDate.day, fromTime.hours, fromTime.minutes, fromTime.seconds);

      d2 = isUTC
        ? Date.UTC(rightDate.year, rightDate.month - 1, rightDate.day, toTime.hours, toTime.minutes, toTime.seconds)
        : new Date(rightDate.year, rightDate.month - 1, rightDate.day, toTime.hours, toTime.minutes, toTime.seconds);

      curDomain.push(
        +d1
      );
      curDomain.push(
        +d2
      );
      chart.setFocusLimit(curDomain);
      showTimeInLabel = rangeSelector.decideTimeInLabel();
      formattedDateTexts = scale.getFormattedTime({
        dateRange: {
          startDate: d1,
          endDate: d2
        },
        type: 'CRS',
        showTimeInLabel
      }).split(' - ');
      chart.fireChartInstanceEvent('customRangeSelect', {
        start: +d1,
        end: +d2,
        startText: formattedDateTexts[0],
        endText: formattedDateTexts[1],
        formatter: '%b %d, %Y' + (showTimeInLabel ? ', %H:%M:%S' : '')// static formatting of date in CRS
      });
    }
    rangeSelector.removeDocumentListener();
  }
}

export { formatDate };
export default CustomRangeManager;
