import { pluckNumber } from '@fusioncharts/core/src/lib';

const appearing = function (inputJSON) {
    return [{
      initialAttr: {
        opacity: 0
      },
      finalAttr: {
        opacity: pluckNumber(inputJSON.attr.opacity, 1)
      },
      slot: 'final'
    }];
  },
  markerAnimation = function () {
    return {
      '*.appearing': appearing
    };
  };

export default {
  'initial.dataMarker.dataMarker': markerAnimation
};
