import Crossline from '../components/crossline';
export default canvas => {
  let crossLine = canvas.attachChild(Crossline, 'verticalCrossLine'),
    dataSource = canvas.getFromEnv('dataSource');
  crossLine.configure({
    scale: canvas.config.xConfigs[0].scale,
    type: 'vertical',
    timeFormatterFn: canvas.config.xConfigs[0].timeFormatterFn,
    inputStyle: (dataSource.crossline && dataSource.crossline.style) || {}
  });
};
