import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { extend2, pluck, pluckNumber } from '@fusioncharts/core/src/lib';
import isValidNumber from '@fusioncharts/utils/src/type/is-valid-number';
const anchorRadius = 3,
  GUTTER_5 = 5,
  M = 'M',
  L = 'L',
  h = 'h',
  v = 'v',
  z = 'z',
  COLOR_F3F3F3 = '#f3f3f3',
  COLOR_595959 = '#595959',
  RIGHT = 'right',
  MIDDLE = 'middle',
  ROLLOVER = 'referenceLineRollOver',
  ROLLOUT = 'referenceLineRollOut',
  CLICK = 'referenceLineClick',
  toString = x => x.toString();
class ReferenceLine extends SmartRenderer {
  /**
   * constructor fn
   */
  constructor () {
    super();
    let refLine = this,
      chart,
      config;
    refLine.hoverInHandler = (e) => {
      e.stopPropagation();
      refLine.setData({ isHovered: true }, true);
      config = refLine.config;
      chart = refLine.getFromEnv('chart');
      chart.fireChartInstanceEvent(ROLLOVER, {
        label: config.label,
        value: config.value,
        valueFormatted: config.formattedMakerText
      });
    };
    // eslint-disable-next-line
    refLine.moveHandler = e => {
      e.stopPropagation();
    };
    refLine.hoverOutHandler = (e) => {
      e.stopPropagation();
      refLine.setData({ isHovered: false }, true);
      chart.fireChartInstanceEvent(ROLLOUT, {
        label: config.label,
        value: config.value,
        valueFormatted: config.formattedMakerText
      });
    };
    refLine.clickHandler = e => {
      e.stopPropagation();
      chart.fireChartInstanceEvent(CLICK, {
        label: config.label,
        value: config.value,
        valueFormatted: config.formattedMakerText
      });
    };
  }
  /**
   * Method that sets the default configuration of the component
   */
  __setDefaultConfig () {
    super.__setDefaultConfig();
    this.config.defaultStyle = {
      text: {
        fill: '#5F5F5F',
        'vertical-align': 'top',
        'opacity': '1',
        'fill-opacity': '1',
        'stroke-opacity': '1',
        'font-size': '11px',
        'font-style': 'normal'
      },
      marker: {
        fill: '#B70000',
        stroke: '#B70000',
        'fill-opacity': '1',
        'stroke-opacity': '1',
        'opacity': '1',
        borderthickness: 0,
        borderpadding: 2,
        borderradius: 0,
        borderdash: 'none',
        'stroke-width': '1'
      }
    };
    this.config.isHovered = false;
    this.config.formatterFn = toString;
    this.config.direction = RIGHT;
  }
  /**
   * Method that configures the component.
   * @param {Object} obj configuration object
   */
  configureAttributes (obj = {}) {
    let refLine = this,
      selfConfig = refLine.config,
      getStyleDef = refLine.getFromEnv('getStyleDef');

    if (obj.referenceLine) {
      const refLineConfig = obj.referenceLine || {};
      // parse and store configuration for label and marker
      if (refLineConfig.style) {
        selfConfig.markerStyle = getStyleDef(refLineConfig.style.marker || {});
        selfConfig.textStyle = getStyleDef(refLineConfig.style.text || {});
      } else {
        selfConfig.markerStyle = {};
        selfConfig.textStyle = {};
      }
      selfConfig.label = pluck(refLineConfig.label, '');
      selfConfig.value = pluckNumber(refLineConfig.value, 0);
    }
    if (obj.yScale) selfConfig.yScale = obj.yScale;
    if (obj.formatterFn) selfConfig.formatterFn = obj.formatterFn;
    if (obj.prefix) selfConfig.prefix = obj.prefix;
    if (obj.suffix) selfConfig.suffix = obj.suffix;
    if (obj.direction) selfConfig.direction = obj.direction;
    if (obj.isHovered != null) selfConfig.isHovered = obj.isHovered;

    selfConfig.formattedValue = selfConfig.formatterFn({
      value: selfConfig.value,
      type: 'referenceline',
      prefix: selfConfig.prefix,
      suffix: selfConfig.suffix
    });
  }
  /**
   * Method to derive the path array based on the value and the direction
   * @return {Array} path array
   */
  getPathArr () {
    let refLine = this,
      selfConfig = refLine.config,
      direction = selfConfig.direction, // the direction in which the line will be drawn
      canvasConfig = refLine.getLinkedParent().config,
      yScale = selfConfig.yScale,
      yScaleDomain = yScale.getDomain(),
      rangeValue,
      value = selfConfig.value,
      pathArr = [];
    // check if the provided domain falls within current domain.
    if (isValidNumber(yScaleDomain[0]) && isValidNumber(yScaleDomain[1]) && value >= yScaleDomain[0] && value <= yScaleDomain[1]) {
      // get the range value
      rangeValue = yScale.getRangeValue(value);
      switch (direction) {
        case 'left':
          pathArr = ['M', (canvasConfig.canvasLeft + canvasConfig.canvasWidth - canvasConfig.borderConfig.rightBorder), rangeValue, 'h', -canvasConfig.canvasWidth];
          break;
        case 'top':
        case 'bottom':
          // vertical line
          pathArr = ['M', rangeValue, canvasConfig.canvasTop, 'v', (canvasConfig.canvasHeight - canvasConfig.borderConfig.bottomBorder)];
          break;
        case 'right':
        default:
          // horizontal line
          pathArr = ['M', (canvasConfig.canvasLeft + canvasConfig.borderConfig.leftBorder), rangeValue, 'h', canvasConfig.canvasWidth];
      }
    }
    return pathArr;
  }
  /**
   * Method to derive the x and y position of the text and also its text anchor
   * based on the direction
   * @param {Array} pathArr path array
   * @return {Object} label properties
   */
  getLabelsProps (pathArr, mergedMarkerStyle) {
    let refLineConfig = this.config,
      direction = refLineConfig.direction, // the direction in which the line will be drawn and bulb will be placed
      props = {
        isValidLabel: false
      };
    if (pathArr.length && refLineConfig.label) {
      props.isValidLabel = true;
      // label will be placed at the end of the line,
      // given the direction, calculate the end of the line along with some padding, so it doesn't overlap the anchor
      switch (direction) {
        case 'top':
          props.x = pathArr[1];
          props.y = pathArr[2];
          props.textAnchor = 'start';
          break;
        case 'bottom':
          props.x = pathArr[1];
          props.y = pathArr[4];
          props.textAnchor = 'start';
          break;
        case 'left':
          // Calculate x position using ref line path: start position x + horizontal lineto/length
          props.x = pathArr[1] + pathArr[4] + anchorRadius; // Some padding so it doesn't overlap the bulb
          props.y = pathArr[2] + mergedMarkerStyle.borderpadding;
          props.textAnchor = 'start';
          break;
        case 'right':
        default:
          // Calculate x position using ref line path: start position x + horizontal lineto/length
          props.x = pathArr[1] + pathArr[4] - mergedMarkerStyle.borderpadding - 1; // Some padding so it doesn't overlap the bulb
          props.y = pathArr[2] + mergedMarkerStyle.borderpadding;
          props.textAnchor = 'end';
      }
    }
    return props;
  }
  /**
   * Method to derive center point of bulb depending on the direction of reference line.
   * @param {Array} pathArr path Array
   */
  getBulbPoints (pathArr) {
    let refLineConfig = this.config,
      direction = refLineConfig.direction, // the direction in which the line will be drawn and bulb will be placed
      bulbPoints = {};
    if (pathArr) {
      // given the direction, calculate end of the line to place the bulb
      switch (direction) {
        case 'left':
          // Calculate x position using ref line path: start position x + horizontal lineto/length
          bulbPoints.cx = pathArr[1] + pathArr[4] - anchorRadius; // Subtracting the bulb radius so it doesn't stick to edge of chart
          bulbPoints.cy = pathArr[2];
          break;
        case 'right':
        default:
          // Calculate x position using ref line path: start position x + horizontal lineto/length
          bulbPoints.cx = pathArr[1] + pathArr[4] + anchorRadius; // Adding the bulb radius so it doesn't stick to edge of chart
          bulbPoints.cy = pathArr[2];
      }
    }
    return bulbPoints;
  }
  getTagProps (mergedMarkerStyle, flagStyle, textStyle) {
    let refLine = this,
      {
        value,
        yScale,
        direction, // the direction in which the line will be drawn and bulb will be placed
        formattedValue
      } = this.config,
      smartLabel = refLine.getFromEnv('smartLabel'),
      smartText,
      {
        canvasLeft,
        canvasWidth,
        borderConfig: { leftBorder: canvasLeftBorder }
      } = refLine.getLinkedParent().config,
      canvasRight = canvasLeft + canvasWidth,
      rangeValue,
      tagWidth,
      tagPath,
      tagTextPosition = {},
      tagProps = { value: formattedValue };

    rangeValue = yScale.getRangeValue(value);

    smartLabel.setStyle({
      fontFamily: textStyle['font-family'],
      fontWeight: textStyle['font-weight'],
      fontSize: textStyle['font-size'],
      fontStyle: textStyle['font-style']
    });

    smartText = smartLabel.getSmartText(formattedValue);

    tagWidth = smartText.width + 2 * mergedMarkerStyle.borderpadding;

    switch (direction) {
      case 'left':
        tagPath = [M, canvasRight, rangeValue,
          L, canvasRight + GUTTER_5, rangeValue,
          L, canvasRight + (2 * GUTTER_5), rangeValue - (smartText.height / 2) - (2 * flagStyle.marker.borderpadding),
          h, (tagWidth),
          v, smartText.height + 4 * flagStyle.marker.borderpadding,
          h, -(tagWidth),
          L, canvasRight + GUTTER_5, rangeValue,
          z
        ];
        tagTextPosition.x = canvasRight + (2 * GUTTER_5) + (tagWidth / 2);
        tagTextPosition.y = rangeValue;
        break;
      case 'right':
      default:
        tagPath = [M, canvasLeft + canvasLeftBorder, rangeValue,
          L, canvasLeft - GUTTER_5, rangeValue,
          L, canvasLeft - (2 * GUTTER_5), rangeValue - (smartText.height / 2) - (2 * flagStyle.marker.borderpadding),
          h, -(tagWidth),
          v, smartText.height + 4 * flagStyle.marker.borderpadding,
          h, (tagWidth),
          L, canvasLeft - GUTTER_5, rangeValue,
          z
        ];
        tagTextPosition.x = canvasLeft - (2 * GUTTER_5) - (tagWidth / 2);
        tagTextPosition.y = rangeValue;
    }

    tagProps.value = formattedValue;
    tagProps.tagPath = tagPath;
    tagProps.tagTextPosition = tagTextPosition;

    return tagProps;
  }
  draw () {
    let refLine = this,
      selfConfig = refLine.config,
      mergedTextStyle,
      mergedMarkerStyle,
      selfDefaultStyle = selfConfig.defaultStyle,
      flagStyle,
      bulbStyle,
      flagStyleMarker,
      flagStyleText,
      textStyle = selfConfig.textStyle,
      markerStyle = selfConfig.markerStyle,
      label = selfConfig.label,
      value = selfConfig.value,
      pathArr,
      labelProps,
      bulbPoints,
      tagProps,
      tagPath,
      tagTextPosition,
      isHovered = selfConfig.isHovered,
      baseTextStyle = refLine.getFromEnv('baseTextStyle');
    // create group for reference line
    refLine.addGraphicalElement({
      el: 'group',
      container: {
        id: 'thermo',
        label: 'group',
        isParent: true
      },
      component: refLine,
      label: 'refLine',
      attr: {
        name: 'reference-line-thermo'
      },
      id: 'thermo'
    }, true);
    // create two groups inside parent group first for path and circle element
    refLine.addGraphicalElement({
      el: 'group',
      container: {
        id: 'thermo',
        label: 'refLine'
      },
      component: refLine,
      label: 'refLine-thermo-hover-group',
      attr: {
        name: 'reference-line-hover-elem-group'
      },
      id: 'thermo'
    }, true);
    // second group is for hovered element like tags.
    refLine.addGraphicalElement({
      el: 'group',
      container: {
        id: 'thermo',
        label: 'refLine'
      },
      component: refLine,
      label: 'refLine-thermo-group',
      attr: {
        name: 'reference-line-group'
      },
      id: 'thermo'
    }, true);

    Object.assign(selfDefaultStyle.text, baseTextStyle);
    // merge chart default styles with refrence line styles
    mergedTextStyle = extend2(extend2({}, selfDefaultStyle.text), textStyle);
    mergedMarkerStyle = extend2(extend2({}, selfDefaultStyle.marker), markerStyle);

    bulbStyle = extend2({}, mergedMarkerStyle);
    pathArr = refLine.getPathArr();
    labelProps = refLine.getLabelsProps(pathArr, mergedMarkerStyle);
    bulbPoints = refLine.getBulbPoints(pathArr);

    // draw path only if it is valid.
    if (pathArr.length) {
      // reference line path
      refLine.addGraphicalElement({
        el: 'path',
        attr: {
          'path': pathArr,
          'stroke': mergedMarkerStyle.stroke || mergedMarkerStyle.fill,
          'fill-opacity': mergedMarkerStyle['fill-opacity'],
          'stroke-opacity': mergedMarkerStyle['stroke-opacity'],
          'stroke-width': mergedMarkerStyle['stroke-width'],
          'stroke-linecap': mergedMarkerStyle['stroke-linecap'],
          'opacity': mergedMarkerStyle.opacity * (isHovered ? 1 : 0.4),
          'stroke-dasharray': mergedMarkerStyle['stroke-dasharray'] || 'none'
        },
        container: {
          label: 'refLine-thermo-group'
        },
        component: refLine,
        label: 'line'
      }, true);
      // draw anchor

      bulbStyle['stroke-width'] = isHovered ? (mergedMarkerStyle['stroke-width'] || 1) : 0;
      bulbStyle.stroke = (markerStyle && markerStyle.stroke) || COLOR_595959;

      refLine.addGraphicalElement({
        el: 'circle',
        attr: {
          cx: bulbPoints.cx,
          cy: bulbPoints.cy,
          r: anchorRadius
        },
        container: {
          label: 'refLine-thermo-group'
        },
        css: bulbStyle,
        component: refLine,
        label: 'anchor'
      });
      selfConfig.formattedMakerText = selfConfig.formatterFn({
        value: value,
        type: 'referenceline',
        prefix: selfConfig.prefix,
        suffix: selfConfig.suffix
      });
      if (isHovered) {
        // draw label only if it is valid and anchor is hovered.
        flagStyle = extend2(extend2(extend2({}, selfDefaultStyle), {
          text: {
            fill: COLOR_F3F3F3
          }
        }), {
          text: textStyle,
          marker: markerStyle
        });
        if (labelProps.isValidLabel) {
          // reference line text

          refLine.addGraphicalElement({
            el: 'text',
            attr: {
              'text': label,
              'x': labelProps.x,
              'y': labelProps.y - mergedMarkerStyle.borderpadding * (mergedTextStyle['vertical-align'] === 'top' ? -1 : 1),
              'opacity': mergedTextStyle.opacity,
              'text-anchor': labelProps.textAnchor,
              'text-bound': [mergedMarkerStyle.fill, mergedMarkerStyle.fill,
                mergedMarkerStyle.borderthickness, mergedMarkerStyle.borderpadding, mergedMarkerStyle.borderradius,
                mergedMarkerStyle.borderdash, (0.2)
              ]
            },
            css: mergedTextStyle,
            container: {
              label: 'refLine-thermo-hover-group'
            },
            component: refLine,
            label: 'label'
          }, true);
        }
        // draw tag
        tagProps = refLine.getTagProps(mergedMarkerStyle, flagStyle, mergedTextStyle);
        tagPath = tagProps.tagPath;
        tagTextPosition = tagProps.tagTextPosition;

        flagStyleMarker = extend2({}, flagStyle.marker);
        !flagStyleMarker.stroke && (flagStyleMarker.stroke = 'none');

        refLine.addGraphicalElement({
          el: 'path',
          attr: {
            path: tagPath
          },
          container: {
            label: 'refLine-thermo-hover-group'
          },
          css: flagStyleMarker,
          component: refLine,
          label: 'tag'
        }, true);

        // text
        flagStyleText = extend2({}, flagStyle.text);
        !flagStyleText['text-anchor'] && (flagStyleText['text-anchor'] = 'middle');
        flagStyleText['vertical-align'] = MIDDLE;
        refLine.addGraphicalElement({
          el: 'text',
          attr: {
            text: selfConfig.formattedValue,
            x: tagTextPosition.x,
            y: tagTextPosition.y
          },
          css: flagStyleText,
          container: {
            label: 'refLine-thermo-hover-group'
          },
          component: refLine,
          label: 'tag-text'
        }, true);
      }
    }
  }
}
export default ReferenceLine;
