import merge from 'ramda/es/merge';
import TimeNavigator from '../../../_internal/components/time-navigator';
import { componentFactory } from '@fusioncharts/core/src/lib';
import panelConfig from './_utils/panel-configurer';

const DEFAULT_AXIS_X = {
    orientation: 'bottom',
    align: 'bottom',
    visible: true,
    overlap: true,
    dayinmajor: true,
    stickycontextlabel: false,
    scalemode: 'random',
    skipinfo: {
      minor: true
    },
    offset: {
      x: 2,
      y: 2
    }
  },
  DEFAULT_AXIS_Y = {
    orientation: 'left',
    align: 'left',
    visible: true,
    overlap: false
  },
  hydrateX = merge(DEFAULT_AXIS_X),
  hydrateY = merge(DEFAULT_AXIS_Y);

export default chart => {
  const chartConfig = chart.config,
    contextAxesX = chartConfig.contextAxesX,
    contextAxesY = chartConfig.contextAxesY,
    contextPanels = chartConfig.contextPanels.map(panel => merge(panel, {
      x: panel.x.map(xCfg => merge(
        hydrateX(xCfg), panelConfig(contextAxesX, xCfg.index)
      )),
      y: panel.y.map(yCfg => merge(
        hydrateY(yCfg), merge(panelConfig(contextAxesY, yCfg.index), { plottype: 'line' })
      ))
    })),
    tableMap = chartConfig.contextTableMap;

  let mergedContextPanels,
    timeNavigator;

  // to make navigator component compatible with using old stored
  // information and make the info as better and explicitly definig
  // keys the x y and plots has been replaced with xConfigs
  // yConfigs and plotConfigs respctively
  mergedContextPanels = contextPanels.map((cfg, i) => {
    let info = merge(cfg, { tableMap });
    componentFactory(
      chart,
      TimeNavigator,
      'timeNavigator',
      chartConfig.navigatorEnabled ? 1 : 0,
      [{
        contextScale: info.x[i].scale.copy(),
        xConfigs: info.x,
        yConfigs: info.y,
        plotConfigs: info.plots,
        dataTable: info.dataTable,
        tableMap: info.tableMap,
        focusScale: chart.getFromEnv('focusScalesX')[0]
      }]
    );
    return info;
  });

  chartConfig.contextPanels = mergedContextPanels;

  if ((timeNavigator = chart.getChildren('timeNavigator') && chart.getChildren('timeNavigator')[0])) {
    if (!timeNavigator || timeNavigator.getState('removed')) return;
    timeNavigator.attachHandlers();
    timeNavigator.addExtEventListener('focusLimitChanged', () => {
      timeNavigator.asyncDraw();
    }, chart);
  }
};
