import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { TRACKER_FILL } from '@fusioncharts/core/src/lib';
const HIDDEN = 'hidden',
  MOBILE = 'mobile',
  DESKTOP = 'desktop',
  BODY = document.body,
  HTML = document.documentElement;
class Cover extends SmartRenderer {
  __setDefaultConfig () {
    super.__setDefaultConfig();
    this.config.visibility = HIDDEN;
  }

  configureAttributes (inputConfig = {}) {
    Object.assign(this.config, inputConfig);
  }

  draw () {
    let cover = this,
      height = +cover.getFromEnv('chartHeight'),
      width = +cover.getFromEnv('chartWidth'),
      mode = window.innerWidth < 576 ? MOBILE : DESKTOP,
      visibility = cover.config.visibility,
      maxHeight = Math.max(BODY.scrollHeight, BODY.offsetHeight,
        HTML.clientHeight, HTML.scrollHeight, HTML.offsetHeight),
      attr,
      css;

    if (mode === MOBILE) {
      attr = {
        x: 0,
        y: 0,
        width: window.innerWidth,
        height: maxHeight,
        visibility,
        type: 'div'
      };
      css = {
        position: 'fixed',
        opacity: 0.6,
        'background-color': '#cdcdcd'
      };
    } else {
      attr = {
        x: 0,
        y: 0,
        width,
        height,
        visibility,
        type: 'div'
      };
      css = {
        opacity: 1,
        'background-color': TRACKER_FILL
      };
    }

    cover.addGraphicalElement({
      el: 'html',
      attr,
      css,
      component: cover,
      id: 'cover',
      label: 'cover'
    });
  }
}

export default Cover;
