import pluckNumber from '@fusioncharts/utils/src/pluck-number';
/**
 * @description This function validates time selection provided by the user, also adds functionality like
 * restricting them to available domain of the chart, swapping the values if startTime > endTime. Holds the previous
 * start or end value if incorrect data provided.
 * @param {Object} timeInterval Expectedd time selection to set
 * @param {Object} currentTimeSelection Current tiem selection of the chart
 * @param {Object} chart FT chart instance
 */
function validateFocusLimit (timeInterval, currentTimeSelection, chart) {
  let domain = chart.getFromEnv('contextBins')[0].getBinDomain(),
    maxDomain = domain[1].getTime(),
    minDomain = domain[0].getTime(),
    { start, end } = timeInterval;

  if (start == null && end == null) {
    return false;
  } else if (isNaN(start) && isNaN(end)) {
    return false;
  } else if (!isNaN(start) && (isNaN(end) || (end === null))) {
    end = currentTimeSelection.end;
  } else if ((isNaN(start) || (start === null)) && !isNaN(end)) {
    start = currentTimeSelection.start;
  }
  if (start > end) {
    start = start + end;
    end = start - end;
    start = start - end;
  }
  start = Math.max(minDomain, start);
  end = Math.min(maxDomain, end);
  return [start, end];
}
/**
 * @description This function filters the standardbin's data which user requires
 * @param {Array} standardBins is an array of array which holds all all possible binning for charts at that moment
 * @returns {Object} binning in a better readable and filtered data which the user only requires
 */
function filterStandarBinsToUserConfig (standardBins) {
  let binObj = {};
  for (let index = 0; index < standardBins.length; index++) {
    let bin = standardBins[index],
      binUnit = bin[0].name();
    if (binObj[binUnit]) {
      binObj[binUnit].push(bin[1]);
    } else {
      binObj[binUnit] = [bin[1]];
    }
  }
  return binObj;
}
/**
 * @description Overrides the binning rules followed by the chart with the binning rules provided to this API.
 * If some time unit is missing from the input binning rules, then the default multipliers for that time unit will be used by the chart.
 * If any incorrect multipliers are provided to a unit, they will be ignored.
 * @param {Object} customBin An object containing:
 * millisecond<Number[]>: All the multipliers applicable for the millisecond unit.
 * second<Number[]>: All the multipliers applicable for the second unit.
 * minute<Number[]>: All the multipliers applicable for the minute unit.
 * hour<Number[]>: All the multipliers applicable for the hour unit.
 * day<Number[]>: All the multipliers applicable for the day unit.
 * month<Number[]>: All the multipliers applicable for the month unit.
 * year<Number[]>: All the multipliers applicable for the year unit.
 * @returns {Object} <FusionCharts>: The instance of FusionCharts upon which the method was invoked.
 */
function setBinningRules (customBin) {
  let FC = this,
    chartData = FC.getJSONData();
  // if xaxis is not present in JSON config, setting blank object to xaxis
  if (chartData.xaxis == null) {
    chartData.xaxis = {};
  }
  chartData.xaxis.binning = customBin;
  FC.setJSONData(chartData);
  return FC;
}
/**
 * @description Verifies given bin unit and multiplier with the standardbins and sends it back else returns false.
 * @param {object} customBin customBinObj An object containing:unit<String>: The unit of time to be represented in each bin - millisecond, second, minute, hour, day, month or year.
 * multiplier<Number>: The multiplier for the unit to be represented in each bin.
 * @param {*} chart
 */
function validateUserBin (customBin, chart) {
  let { unit, multiplier } = customBin, // consider multiplier 1 if not provided
    contextBins = chart.getFromEnv('contextBins')[0],
    atomicity = contextBins.getBinMin()[2],
    standardBins = contextBins.getStandardBins(),
    intervalIndexMap = contextBins.intervalIndexMap;
  multiplier = pluckNumber(multiplier, 1);
  unit = unit.toLowerCase();
  // if unit is invalid return false, intervalIndexMap stores all valid bin unit and their starting position in standarbins
  if (intervalIndexMap[unit] == null) {
    return false;
  }
  // Iterarate from the particular index of bin-unit in standardbin to avoid unnecessary iterations and breaks
  // when other binunit appears (as standard bins are sorted in order)
  for (let index = intervalIndexMap[unit]; index < standardBins.length; index++) {
    const bin = standardBins[index];
    // when other binunit appears break the loop (as standard bins are sorted in order) or break if it goes below atomicity
    if (bin[0].name() !== unit || bin[2] < atomicity) {
      break;
    }
    // rerturns the unit and multiplier
    if (bin[1] === multiplier) {
      return {
        unit,
        multiplier,
        duration: bin[2]
      };
    }
  }
  // after iteration no valid multipliers found, hence returning false
  return false;
}
function calculateDomainForUserCustomBin (customBin, chart) {
  let { duration } = customBin,
    contextBins = chart.getFromEnv('contextBins')[0],
    maxDomainLimit = contextBins.getBinDomain(),
    range = contextBins.getBinRange(),
    currentTimeSelection = chart.getFocusLimit(),
    currentEndTime = currentTimeSelection[1],
    minTime = maxDomainLimit[0].getTime(),
    maxTime = maxDomainLimit[1].getTime(),
    // figuring out how much time to move fixing end limit
    leftTimeToGo = currentEndTime - (duration * range[1]) / contextBins.thresholdPixels,
    // in case moving extreme left also does not fullfill binning requirement, move the currentTime to right till maxtime
    rightTimeToGo = currentEndTime + (minTime - leftTimeToGo);
  // when changing current left time interval fixing current end interval
  if (leftTimeToGo >= minTime) {
    return [leftTimeToGo, currentEndTime];
  } else if (rightTimeToGo <= maxTime) {
  // when changing current left time interval and current end interval also required
    return [minTime, rightTimeToGo];
  }
  // return the max possible time to expand when binning size given is too large compare to possible binning
  return [minTime, maxTime];
}

export {
  validateFocusLimit,
  filterStandarBinsToUserConfig,
  setBinningRules,
  validateUserBin,
  calculateDomainForUserCustomBin
};
