import aggregatorStore from '@fusioncharts/datatable/src/aggregators/index.js';
import arrayHasContent from '@fusioncharts/utils/src/type/array-has-content';
import isArray from '@fusioncharts/utils/src/type/is-array';
import isObject from '@fusioncharts/utils/src/type/is-object';
import isString from '@fusioncharts/utils/src/type/is-string';
import stringHasContent from '@fusioncharts/utils/src/type/string-has-content';
import unique from 'ramda/es/uniq';
import sanitiseAggregationWith from './sanitise-aggregation-with.js';

const VALID_PLOT_TYPES = [
    'column',
    'line',
    'step-line',
    'smooth-line',
    'area',
    'step-area',
    'smooth-area',
    'candlestick',
    'ohlc',
    'column-range',
    'area-range'
  ],
  VALID_CLIP_LEVEL = {
    'millisecond': true,
    'second': true,
    'minute': true,
    'hour': true,
    'day': true,
    'month': true,
    'year': true
  },
  sanitiseAggregation = sanitiseAggregationWith(aggregatorStore),
  getValidClips = clips => {
    return clips
      .map(clip => {
        if (!isString(clip.to)) {
          clip.to = clip.from;
        }

        if (clip.hasOwnProperty('format') && !isString(clip.format)) {
          delete clip.format;
        }

        if (isObject(clip.repeat)) {
          if (isString(clip.repeat.unit) && VALID_CLIP_LEVEL[clip.repeat.unit.toLowerCase()]) {
            clip.repeat.unit = clip.repeat.unit.toLowerCase();
            clip.repeat.multiplier = Number(clip.repeat.multiplier);
            if (!clip.repeat.multiplier) {
              clip.repeat.multiplier = 1;
            }
          } else {
            delete clip.repeat;
          }
        } else {
          delete clip.repeat;
        }
        return clip;
      })
      .filter(
        clip =>
          isString(clip.from) &&
          isObject(clip.repeat) &&
          isString(clip.repeat.unit)
      );
  },
  DEFAULT_PLOT_TYPE = 'line';

let UNDEF,
  truthFn = () => true,
  isValidPlotType = plotType => VALID_PLOT_TYPES.includes(plotType),
  plotExistsIn = obj => stringHasContent(obj.plot) || arrayHasContent(obj.plot) || isObject(obj.plot),
  clipExistsIn = obj => arrayHasContent(obj.clip),
  /**
   * api to check of the object has configuartion to be parsed
   * @param {*} any
   * @return Boolean value
   */
  objectHasContent = param =>
    isObject(param) && (plotExistsIn(param) || clipExistsIn(param)),
  /**
   * api to expand the string configuarion in required format
   * @param {*} any
   * @param {Function} stringValidator validates the string with datatable
   * @return array containing the parsed data
   */
  stringExpando = (str, stringValidator, mergingInfo = {}) => {
    let outputArr = [];
    if (stringHasContent(str) && stringValidator(str)) {
      const mergedPlot = isObject(mergingInfo.plot)
          ? Object.assign({}, mergingInfo.plot)
          : {},
        plotType = mergedPlot.type || mergingInfo.plottype;

      mergedPlot.type = isValidPlotType(plotType)
        ? plotType
        : DEFAULT_PLOT_TYPE;
      mergedPlot.value = str;
      mergedPlot.aggregation = sanitiseAggregation(
        mergedPlot.aggregation || mergingInfo.aggregation
      );

      outputArr.push(Object.assign({}, mergingInfo, { plot: [mergedPlot] }));
    }
    return outputArr;
  },
  /**
   * api to sanitise the 'value' with the schema column names
   * @param {object} obj The object to be processed
   * @param {Function} validator Validates the object attributes like `value`
   * and `OHLC` datatable
   * @return an object with the type, value and OHLC if applicable
   */
  sanitiseObj = (obj, validator) => {
    const type = isValidPlotType(obj.type) ? obj.type : DEFAULT_PLOT_TYPE,
      isOHLC = type === 'candlestick' || type === 'ohlc',
      isRangedData = type === 'column-range' || type === 'area-range',
      sanitisedName = str => validator(str) ? str : UNDEF;

    let value,
      validatedObj = {},
      open,
      close,
      high,
      low;

    // Process the `value` attr, as it is common for both below
    if (isObject(obj.value)) {
      value = sanitisedName(obj.value.value);
    } else {
      value = sanitisedName(obj.value);
    }

    if (isOHLC) {
      // The accepted format being OHLC values will be inside `value` object
      if (isObject(obj.value)) {
        open = sanitisedName(obj.value.open);
        close = sanitisedName(obj.value.close);
        high = sanitisedName(obj.value.high);
        low = sanitisedName(obj.value.low);
      }
      // if all OHLC values are present, use them and set value to null
      if (open && high && low && close) {
        validatedObj = Object.assign({}, obj, {
          type,
          high,
          low,
          open,
          close,
          // value will be overridden since it will not be used
          value: null
        });
        // if none of the OHLC values are present, use value for all
      } else if (!open && !high && !low && !close) {
        validatedObj = Object.assign({}, obj, {
          type,
          high: value,
          low: value,
          open: value,
          close: value,
          value
        });
        // if 3 or less of OHLC value are present, use as is
      } else {
        validatedObj = Object.assign({}, obj, {
          type,
          high,
          low,
          open,
          close,
          // value will be override since it will not be used
          value: null
        });
      }
    } else if (isRangedData) {
      // The accepted format being Range values will be inside `value` object
      if (isObject(obj.value)) {
        high = sanitisedName(obj.value.high);
        low = sanitisedName(obj.value.low);
      }
      // if all range values are present, use them and set value to null
      if (high && low) {
        validatedObj = Object.assign({}, obj, {
          type,
          high,
          low,
          // value will be overridden since it will not be used
          value: null
        });
      // if none of the range values are present, use value for all
      } else if (!high && !low) {
        validatedObj = Object.assign({}, obj, {
          type,
          high: value,
          low: value,
          value
        });
      // if high or low value are present, use as is
      }
    } else {
      validatedObj = Object.assign({}, obj, {
        type,
        value
      });
    }

    validatedObj.typeinnavigator = isValidPlotType(validatedObj.typeinnavigator)
      ? validatedObj.typeinnavigator
      : UNDEF;

    if ((isOHLC && (value || open || close || high || low)) || value) {
      return validatedObj;
    } else if (isRangedData && (high && low)) {
      return validatedObj;
    }
  },
  /**
   * api to expand the object configuarion in required format
   * @param {*} any
   * @param {Function} stringValidator validates the string with datatable
   * @return array containing the parsed data
   */
  objectExpando = (obj, stringValidator) => {
    let outputArr = [],
      plot = obj.plot,
      isPlotObject = isObject(plot),
      action =
        obj.hasOwnProperty('plot') &&
        !(isPlotObject && !plot.hasOwnProperty('value')) &&
        'axis',
      isValidString = str => stringHasContent(str) && stringValidator(str);
    if (objectHasContent(obj)) {
      const array = [],
        validClips = arrayHasContent(obj.clip)
          ? getValidClips(obj.clip)
          : [];

      outputArr.push(
        Object.assign(
          {},
          obj,
          obj.aggregation
            ? validClips.length
              ? {
                plot: array,
                aggregation: sanitiseAggregation(obj.aggregation),
                clip: validClips
              }
              : {
                plot: array,
                aggregation: sanitiseAggregation(obj.aggregation)
              }
            : isArray(obj.clips)
              ? { plot: array, clip: validClips }
              : { plot: array }
        )
      );

      if (isPlotObject || stringHasContent(plot)) {
        plot = [plot];
      }

      if (arrayHasContent(plot)) {
        const filteredInfo = plot.filter(ele => !!ele);
        filteredInfo.forEach(ele => {
          let output;
          if (isObject(ele)) {
            const type = ele.type || obj.plottype;
            output = sanitiseObj(
              Object.assign({}, ele, {
                aggregation: sanitiseAggregation(
                  ele.aggregation || obj.aggregation
                ),
                type
              }),
              isValidString
            );
          } else if (isValidString(ele)) {
            output = sanitiseObj(
              {
                value: ele,
                type: obj.plottype,
                aggregation: sanitiseAggregation(obj.aggregation)
              },
              isValidString
            );
          }
          output && array.push(output);
        });
      }
    }
    return outputArr[0] && outputArr[0].plot.length
      ? {
        output: outputArr,
        action:
            isArray(plot) && plot.length > outputArr[0].plot.length
              ? 'plot'
              : false
      }
      : {
        output: [],
        action
      };
  },
  /**
   * api to expand the array configuarion in required format
   * @param {*} any
   * @param {Function} stringValidator validates the string with datatable
   * @return array containing the parsed data
   */
  arrayExpando = (arr, stringValidator, mergingInfo) => {
    let output = [],
      info,
      actions = [];
    arr.forEach(ele => {
      if (isString(ele)) {
        output = output.concat(
          stringExpando(ele, stringValidator, mergingInfo)
        );
      } else if (isObject(ele)) {
        info = objectExpando(ele, stringValidator);
        output = output.concat(info.output);
        info.action && actions.push(info.action);
      }
    });
    return {
      output,
      actions
    };
  },
  /**
   * api to expand the any type of configuartions
   * @param {*} any
   * @param {Function} stringValidator validates the string with datatable
   * @return array containing the parsed data
   */
  expander = (config, stringValidator, mergingInfo) => {
    let output = [],
      actions = [];
    if (isString(config)) {
      output = stringExpando(config, stringValidator);
    } else if (isObject(config)) {
      let action;
      ({ output, action } = objectExpando(config, stringValidator));
      action && actions.push(action);
    } else if (isArray(config)) {
      ({ output, actions } = arrayExpando(
        config,
        stringValidator,
        mergingInfo
      ));
    }
    return {
      output,
      actions
    };
  },
  /**
   * api to expand the configuartion of axis
   * @param {Array} dataTableColumns is an array of strings containing the data
   * table columns name
   * @param {*} config axis data to be parsed
   * @return array containing the parsed data
   */
  expando = (dataTableColumns = [], config, isY = true) => {
    let output = [],
      actions = [],
      outputLen = 0,
      isObjectConfig = isObject(config),
      stringValidator = str => dataTableColumns.indexOf(str) >= 0;
    if (config) {
      ({ output, actions } = expander(config, stringValidator));
      outputLen = output.length;
      const containsAxisAction = actions.includes('axis');
      if (isObjectConfig) {
        containsAxisAction && (actions = ['fullaxis']);
      } else if (isArray(config) && outputLen < config.length) {
        if (outputLen) {
          !containsAxisAction && actions.push('axis');
        } else {
          actions = ['fullaxis'];
        }
      }
    }
    // if the config does not contain the info to build the axis info then
    // all the info from datatable is parsed
    if (!output.length) {
      ({ output } = expander(
        dataTableColumns,
        truthFn,
        isObjectConfig && config
      ));
    }
    // default title will only be created for y Axis
    isY && output.forEach(obj => {
      if (!isString(obj.title)) {
        let nameArr = [];
        for (let i = 0; i < obj.plot.length; i++) {
          if (obj.plot[i].value) {
            nameArr.push(obj.plot[i].value);
          }
        }
        obj.title = nameArr.join(' - ');
      }
    });
    return {
      expandedAxis: output,
      actions: unique(actions)
    };
  };
export default expando;
