const getSign = x => x < 0 ? -1 : 1;

class CurveMonotoneX {
  constructor (ctx) {
    this._ctx = ctx;
  }

  areaStart () {
    this._line = 0;
  }

  areaEnd () {
    this._line = NaN;
  }

  lineStart () {
    this._x0 = this._x1 = NaN;
    this._y0 = this._y1 = NaN;
    // Tangent of the knot
    this._t0 = NaN;
    this._point = 0;
  }

  lineEnd () {
    switch (this._point) {
      case 2:
        this._ctx.lineTo(this._x1, this._y1);
        break;
      case 3:
        this._monotoneCurveTo(this._t0, this._slope2(this._t0));
        break;
    }

    if (this._line || (this._line !== 0 && this._point === 1)) this._ctx.closePath();
    this._line = 1 - this._line;
  }

  point (_x, _y) {
    let t1 = NaN,
      x = +_x,
      y = +_y;

    if (x === this._x1 && y === this._y1) return; // Co-incident points are ignored

    switch (this._point) {
      case 0:
        this._point = 1;
        this._line ? this._ctx.lineTo(x, y) : this._ctx.moveTo(x, y);
        break;
      case 1:
        this._point = 2;
        break;
      case 2:
        this._point = 3;
        t1 = this._slope3(x, y);
        this._monotoneCurveTo(this._slope2(t1), t1);
        break;
      default:
        t1 = this._slope3(x, y);
        this._monotoneCurveTo(this._t0, t1);
    }

    this._x0 = this._x1;
    this._x1 = x;

    this._y0 = this._y1;
    this._y1 = y;

    this._t0 = t1;
  }

  _monotoneCurveTo (t0, t1) {
    let x0 = this._x0,
      y0 = this._y0,
      x1 = this._x1,
      y1 = this._y1,
      dx = (x1 - x0) / 3;

    // Refer https://en.wikipedia.org/wiki/Cubic_Hermite_spline#Representations
    // Quote: "express cubic Hermite interpolation in terms of cubic BÃ©zier curves
    // with respect to four values p0, p0 + (m0 / 3), p1 - (m1 / 3), p1".
    this._ctx.bezierCurveTo(x0 + dx, y0 + dx * t0, x1 - dx, y1 - dx * t1, x1, y1);
  }

  _slope2 (tangent) {
    // Calculate one-sided slope using the given tangent
    var h = this._x1 - this._x0;
    return h ? ((3 * (this._y1 - this._y0)) / h - tangent) / 2 : tangent;
  }

  _slope3 (x2, y2) {
    // Calculate slopes of the tangents (Hermite-type interpolation) based on
    // the paper "Steffen, M. 1990. A Simple Method for Monotonic
    // Interpolation in One Dimension. Astronomy and Astrophysics, Vol. 239, NO.
    // NOV(II), P. 443, 1990"
    var h0 = this._x1 - this._x0,
      h1 = x2 - this._x1,
      s0 = (this._y1 - this._y0) / (h0 || (h1 < 0 && -0)),
      s1 = (y2 - this._y1) / (h1 || (h0 < 0 && -0)),
      p = (s0 * h1 + s1 * h0) / (h0 + h1);

    return (getSign(s0) + getSign(s1)) * Math.min(Math.abs(s0), Math.abs(s1), 0.5 * Math.abs(p)) || 0;
  }
}

export default CurveMonotoneX;
