/**
 * Returns a vertical path's starting x,y coordinates, and vertical lineto values
 * @param {string} path The path to parse
 */
function getVerticalPathValues (path) {
  var regExp = /M(.*)V(.*)/g,
    parsed = regExp.exec(path),
    MXY = parsed[1],
    VY = parsed[2],
    MX = parseFloat(MXY.split(',')[0]),
    MY = parseFloat(MXY.split(',')[1]);
  VY = parseFloat(VY);
  return {
    MX, MY, VY
  };
}
/**
 * Returns a horizontal path's starting x,y coordinates, and horizontal lineto values
 * @param {string} path The path to parse
 */
function getHorizontalPathValues (path) {
  var regExp = /M(.*)H(.*)/g,
    parsed = regExp.exec(path),
    MXY = parsed[1],
    HX = parsed[2],
    MX = parseFloat(MXY.split(',')[0]),
    MY = parseFloat(MXY.split(',')[1]);
  HX = parseFloat(HX);
  return {
    MX, MY, HX
  };
}

function getStartEndByIndex (index, total) {
  let start,
    timesegment = 0.00002,
    offset = timesegment * index;
  start = offset;
  return {
    start,
    end: 1
  };
}

export default {
  'initial.dataset.timeseriesCandlestick': function () {
    const component = this,
      yScale = component.getFromEnv('yScale'),
      yBasePos = yScale.getRangeValue(0);
    let lastIndex = -1, lastValue = null,
      getStartEndByIndexCached = (index, total) => {
        if (index === lastIndex) {
          return lastValue;
        }
        let val = getStartEndByIndex(index, total);
        lastIndex = index;
        lastValue = val;
        return val;
      };

    return {
      'rect.appearing': function (inputJSON) {
        const index = inputJSON.props.index,
          dataLength = inputJSON.props.dataLength;
        return [
          {
            initialAttr: {
              y: yBasePos
            },
            startEnd: function () {
              return getStartEndByIndexCached(index, dataLength);
            },
            effect: 'easeInOutQuart',
            slot: 'middle'
          }
        ];
      },
      'line.appearing': function (inputJSON) {
        const path = inputJSON.attr.path,
          index = inputJSON.props.index,
          dataLength = inputJSON.props.dataLength,
          pathValues = getHorizontalPathValues(path);
        return [
          {
            initialAttr: {
              path: `M${pathValues.MX},${yBasePos}H${pathValues.HX}`
            },
            finalAttr: {
              path: `M${pathValues.MX},${pathValues.MY}H${pathValues.HX}`
            },
            startEnd: function () {
              return getStartEndByIndexCached(index, dataLength);
            },
            effect: 'easeInOutQuart',
            slot: 'middle'
          }
        ];
      },
      'high.appearing': function (inputJSON) {
        const path = inputJSON.attr.path,
          index = inputJSON.props.index,
          dataLength = inputJSON.props.dataLength,
          pathValues = getVerticalPathValues(path);
        return [
          {
            initialAttr: {
              path: `M${pathValues.MX},${yBasePos}V${pathValues.VY + (yBasePos - pathValues.MY)}`
            },
            finalAttr: {
              path: `M${pathValues.MX},${pathValues.MY}V${pathValues.VY}`
            },
            startEnd: function () {
              return getStartEndByIndexCached(index, dataLength);
            },
            effect: 'easeInOutQuart',
            slot: 'middle'
          }
        ];
      },
      'low.appearing': function (inputJSON) {
        const path = inputJSON.attr.path,
          index = inputJSON.props.index,
          dataLength = inputJSON.props.dataLength,
          pathValues = getVerticalPathValues(path);
        return [
          {
            initialAttr: {
              path: `M${pathValues.MX},${yBasePos}V${pathValues.VY + (yBasePos - pathValues.MY)}`
            },
            finalAttr: {
              path: `M${pathValues.MX},${pathValues.MY}V${pathValues.VY}`
            },
            startEnd: function () {
              return getStartEndByIndexCached(index, dataLength);
            },
            effect: 'easeInOutQuart',
            slot: 'middle'
          }
        ];
      }
    };
  }
};
