import { Tool } from '@fusioncharts/core/src/toolbox/tools';

const HOVER = 'hover',
  NORMAL = 'normal',
  VISIBLE = 'visible',
  PRESSED = 'pressed';

function mouseOver () {
  this.setData({
    hoveredState: HOVER
  }, true);
}

function mouseOut () {
  this.setData({
    hoveredState: NORMAL
  }, true);
}

class SRSTool extends Tool {
  /**
   * Creates an instance of SRSTool.
   * @param {any} id id
   * @memberof SRSTool
   */
  constructor (id) {
    super(id);

    this.addEventListener('fc-mouseover', mouseOver.bind(this));

    this.addEventListener('fc-mouseout', mouseOut.bind(this));
  }

  /**
   * sets default configuration
   *
   * @memberof SRSTool
   */
  __setDefaultConfig () {
    super.__setDefaultConfig();

    let toolConfig = this.config;

    toolConfig.containerInfo = {
      id: 'group',
      label: 'group',
      isParent: true
    };
    toolConfig.spaceNotHardCoded = true;
    toolConfig.hoveredState = NORMAL;
  }

  /**
   * configures attributes
   *
   * @param {any} [inputConfig={}] input configurations
   * @memberof SRSTool
   */
  configureAttributes (inputConfig = {}) {
    let tool = this,
      toolConfig = tool.config,
      prop;

    for (prop in inputConfig) {
      toolConfig[prop] = inputConfig[prop];
    }

    // attach the event listeners
    for (prop in toolConfig.listener) {
      tool.addEventListener(prop, toolConfig.listener[prop]);
    }

    tool.setState(VISIBLE, toolConfig.isHidden !== true);
    toolConfig.symbolName = toolConfig.name;
  }

  /**
   * getter method for hoveredState
   *
   * @returns {string} hovered state
   * @memberof SRSTool
   */
  getHoveredState () {
    return this.config.hoveredState;
  }

  /**
   * draws the tool
   *
   * @memberof SRSTool
   */
  draw () {
    let tool = this,
      toolConfig = tool.config,
      x = toolConfig.x + toolConfig.width / 2,
      y = toolConfig.y + toolConfig.height / 2,
      width = toolConfig.width,
      height = toolConfig.height,
      css = toolConfig.css,
      state = tool.getCurrentState(),
      hoveredState = tool.getHoveredState(),
      textCss = css[state].config[hoveredState],
      backgroundCss = css[state].config[`${hoveredState}Background`];

    // if tool is not visible, dont draw
    if (!tool.getState(VISIBLE)) {
      return;
    }

    // draw the group
    tool.addGraphicalElement({
      el: 'group',
      attr: {
        name: 'srs-tool'
      },
      container: toolConfig.containerInfo,
      component: tool,
      id: 'group',
      label: 'group'
    });

    // draw tracker
    tool.addGraphicalElement({
      el: 'rect',
      attr: {
        x: x - width / 2 - 2,
        y: y - height / 2 - 2,
        width: width + 4,
        height: height + 4,
        opacity: backgroundCss.opacity
      },
      css: backgroundCss,
      container: {
        id: 'group',
        label: 'group'
      },
      component: tool,
      id: 'rect',
      label: 'rect'
    }, true);

    // draw the text
    tool.addGraphicalElement({
      el: 'text',
      attr: {
        x,
        y,
        text: toolConfig.text,
        opacity: textCss.opacity
      },
      container: {
        id: 'group',
        label: 'group'
      },
      css: textCss,
      component: tool,
      id: 'text',
      label: 'text'
    }, true);

    // draw the path under the text only when tool is pressed
    state === PRESSED && tool.addGraphicalElement({
      el: 'path',
      attr: {
        path: `M${x - width / 2 - 1}, ${y + height / 2 + 1.5} h ${width + 1}`,
        opacity: textCss.opacity
      },
      container: {
        id: 'group',
        label: 'group'
      },
      css: {
        stroke: textCss.fill,
        'stroke-linecap': 'round',
        'stroke-opacity': textCss['fill-opacity']
      },
      component: tool,
      id: 'path',
      label: 'path'
    }, true);
  }
}

export default SRSTool;
