const clampWithinContextRange = (curRange, focusScale, contextScale) => {
    const maxZoomRange = contextScale.getDomain().map(val => focusScale.getRangeValue(val));

    curRange[0] = Math.max(curRange[0], maxZoomRange[0]);
    curRange[1] = Math.min(curRange[1], maxZoomRange[1]);

    return curRange;
  },
  isWithinMinRange = (curRange, focusScale) => {
    const binMin = focusScale.getBinMin(),
      minZoomDomain = 3 * binMin[2],
      minZoomRangeLen = focusScale.getRangeValue(minZoomDomain) - focusScale.getRangeValue(0);

    if ((curRange[1] - curRange[0]) >= minZoomRangeLen) {
      return true;
    }
    return false;
  };

export {
  clampWithinContextRange,
  isWithinMinRange
};
