import Path from './path';
import constant from '../scale-utils/constant';
import CurveLinear from './curve-factories/linear';

class LineGenerator {
  constructor () {
    this.xAccessor = d => d[0];
    this.yAccessor = d => d[1];
    this.isDefined = constant(true);

    this.Curve = CurveLinear;

    this.ctx = null;
    this._output = null;
  }

  generate (data = []) {
    let i = 0,
      n = data.length,
      datum,
      lineStarted,
      buffer;

    if (this.ctx === null || typeof this.ctx === 'undefined') {
      buffer = new Path();
      this._output = new this.Curve(buffer);
    }

    for (i = 0; i <= n; ++i) {
      datum = data[i];
      if (!lineStarted) {
        if (i < n && this.isDefined(datum, i, data)) {
          lineStarted = true;
          this._output.lineStart();
          this._output.point(this.xAccessor(datum, i, data), this.yAccessor(datum, i, data));
        }
      } else if (i < n && this.isDefined(datum, i, data)) {
        this._output.point(this.xAccessor(datum, i, data), this.yAccessor(datum, i, data));
      } else {
        this._output.lineEnd();
        lineStarted = false;
      }
    }

    // Reference: https://www.w3.org/TR/SVG/paths.html#PathDataBNF
    // Quote: "Note that the EBNF allows the path data string in the d property to be empty. This is not an error, instead it disables rendering of the path. Rendering is also disabled when the d property has the value none."
    // Going with the empty string option since "none" or null does not exactly match the given EBNF in the SVG spec (although "none" is a part of the written specification)

    if (buffer) {
      this._output = null;
      return buffer.toString();
    }
  }

  setXAccessor (accessor = d => d[0]) {
    this.xAccessor = typeof accessor === 'function' ? accessor : constant(+accessor);

    return this;
  }

  getXAccessor () {
    return this.xAccessor;
  }

  setYAccessor (accessor = d => d[1]) {
    this.yAccessor = typeof accessor === 'function' ? accessor : constant(+accessor);

    return this;
  }

  getYAccessor () {
    return this.yAccessor;
  }

  setDefined (isDefined = constant(true)) {
    this.isDefined = typeof isDefined === 'function' ? isDefined : constant(!!isDefined);

    return this;
  }

  getDefined () {
    return this.isDefined;
  }

  setCurve (Curve = CurveLinear) {
    this.Curve = Curve;

    if (typeof this.ctx !== 'undefined' || this.ctx !== null) {
      this._output = new this.Curve(this.ctx);
    }

    return this;
  }

  getCurve () {
    return this.Curve;
  }

  setContext (ctx) {
    if (ctx === null || typeof ctx === 'undefined') {
      this.ctx = null;
      this._output = this.ctx;
    } else {
      this.ctx = ctx;
      this._output = new this.Curve(this.ctx);
    }

    return this;
  }

  getContext () {
    return this.ctx;
  }
}

export default LineGenerator;
