import isArray from '../type/is-array';
import constant from '../scale-utils/constant';
import orderDefault from './orders/default';
import offsetDefault from './offsets/default';

const stackValue = (d, key) => d[key];

class StackGenerator {
  constructor () {
    this.keysAccessor = constant([]);
    this.order = orderDefault;
    this.offset = offsetDefault;
    this.valueAccessor = stackValue;
  }

  generate (data) {
    let keys = this.keysAccessor(data),
      i,
      jj = data.length,
      ii = keys.length,
      series = new Array(ii),
      orders;

    for (i = 0; i < ii; ++i) {
      let ki = keys[i],
        si = new Array(jj),
        sij,
        j;

      series[i] = si;

      for (j = 0; j < jj; ++j) {
        sij = [0, +this.valueAccessor(data[j], ki, j, data)];
        sij.data = data[j];

        si[j] = sij;
      }
      si.key = ki;
    }

    orders = this.order(series);

    for (i = 0; i < ii; ++i) {
      series[orders[i]].index = i;
    }

    this.offset(series, orders);

    return series;
  }

  setKeysAccessor (accessor = constant([])) {
    this.keysAccessor = typeof accessor === 'function'
      ? accessor
      : constant(isArray(accessor) ? accessor.slice() : []);

    return this;
  }

  getKeysAccessor () {
    return this.keysAccessor;
  }

  setValueAccessor (accessor = stackValue) {
    this.valueAccessor = typeof accessor === 'function'
      ? accessor
      : constant(+accessor);

    return this;
  }

  getValueAccessor () {
    return this.valueAccessor;
  }

  setOrder (order = orderDefault) {
    this.order = typeof order === 'function'
      ? order
      : constant(isArray(order) ? order.slice() : []);

    return this;
  }

  getOrder () {
    return this.order;
  }

  setOffset (offset = offsetDefault) {
    this.offset = offset;

    return this;
  }

  getOffset () {
    return this.offset;
  }
}

export default StackGenerator;
