import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import Canvas from '../../components/canvas';
import Line from '../../components/dataset/line';
import timeNavigatorAxisFactory from '../../factories/time-navigator-axis';
import scrollbarFactory from '../../factories/scrollbar-factory';
import brushFactory from '../../factories/time-navigator-brush-factory';
import TimeConverter from '@fusioncharts/utils/src/time-converter';
import { pluckNumber,
  clampNumber
} from '@fusioncharts/core/src/lib';

const DEFAULT_GRID_STYLE = {
    'stroke': '#F1F1F1',
    'stroke-width': '1',
    'stroke-opacity': '1'
  },
  timeNavCanvas = timeNav => {
    const config = timeNav.config,
      styleDef = timeNav.getFromEnv('getStyleDef'),
      chartAttrs = timeNav.getFromEnv('chart-attrib'),
      // Time navigator always creates a single canvas. Hence, ignoring the
      // presence of other X Axis configurations and only using the 0th one to
      // fetch the style of the grid lines and pass it onto the canvas.
      //
      // Ideally, canvas itself should pick up the required grid line styles
      // withinduringduring its configuration. However, due to lack of time and
      // to avoid major last minute changes this is not being changed right now.
      //
      // TODO: Make canvas pick up the required grid line styles.
      xStyle = config.xConfigs[0].style ? config.xConfigs[0].style : {},
      // Merge user given grid line styles and default grid line styles
      gridLineStyle = Object.assign(
        {},
        DEFAULT_GRID_STYLE,
        styleDef(xStyle['grid-line'])
      );

    // @todo- mind the orientation
    timeNav.attachChild(Canvas, 'canvas').configure({
      xConfigs: config.xConfigs,
      yConfigs: config.yConfigs,
      plotConfigs: config.plotConfigs,
      tableMap: config.tableMap,
      enableGridLines: true,
      gridBandSupported: false,
      gridLineDirection: 'vertical',
      enableMarkers: false,
      enableReferenceLine: false,
      enableReferenceZone: false,
      leftBorder: config.border.left,
      bottomBorder: config.border.bottom,
      topBorder: config.border.top,
      rightBorder: config.border.right,
      enableTimeMarkers: false,
      styleGridLines: 0,
      includeLegend: 0,
      enableMouseTracking: 0,
      isContext: true,
      dataTable: config.dataTable,
      gridLineStyle,
      legendInteracted: config.legendInteracted,
      canvasStyle: styleDef(chartAttrs.style && chartAttrs.style.canvas)
    });
  },
  extractScales = cfg => cfg.scale;

class TimeNavigator extends SmartRenderer {
  constructor () {
    super();
    this._dimensions = {};
    this._translation = {};
    this._scrollbarConfig = {};
    this.registerFactory('timeNavigatorAxis', timeNavigatorAxisFactory);
    this.registerFactory('scrollbar', scrollbarFactory);
    this.registerFactory('brush', brushFactory);
    this.registerFactory('canvas', timeNavCanvas);
  }
  __setDefaultConfig (obj = {}) {
    super.__setDefaultConfig(obj);
    let config = this.config;
    // set default dimensions of itself and its child.
    config.startX = 0;
    config.startY = 0;
    config.scrollbarHeight = 18;
    config.scrollbarStart = -17.5;
    config.scrollbarButtonWidth = 17;
    config.yScalePadding = 20;
    config.formatter = TimeConverter.formatter('%b %d, %Y');
    config.border = {
      left: 1,
      right: 1,
      top: 1,
      bottom: 0
    };
    config.labels = {};
  }
  /**
   * Method to set the context scale for time navigator
   */
  setContextScaleLimit () {
    let timeNav = this,
      contextBin = timeNav.getFromEnv('contextBins')[0];

    timeNav.config.contextScale
      .setBinMin(contextBin.getBinMin())
      .setRangeThreshold(contextBin.getRangeThreshold())
      .setDomain(timeNav.getFromEnv('chart').getContextLimit());
  }
  /**
   * Method to set its dimension.
   * @param {Object} dim dimensions
   */
  setDimension (dim = {}) {
    let timeNav = this,
      config = timeNav.config,
      canvas = timeNav.getChildren('canvas')[0],
      brush = timeNav.getChildren('brush')[0],
      border = config.border;
    timeNav._dimensions.width = pluckNumber(dim.width, timeNav._dimensions.width);
    timeNav._dimensions.height = pluckNumber(dim.height, timeNav._dimensions.height);

    // @todo- mind the orientation
    config.xConfigs.map(extractScales).forEach((scale) => {
      scale.setRange([border.left, this._dimensions.width - (border.left + border.right) / 2]);
    });
    config.contextScale.setRange(config.xConfigs[0].scale.getRange());

    brush.setBrushValue(timeNav.getFromEnv('chart').getFocusLimit());
    const labelSpace = brush.getLabelSpace();

    // set canvas dimensions
    canvas.setDimension({
      left: config.startX,
      top: labelSpace,
      width: this._dimensions.width,
      height: Math.max(this._dimensions.height - labelSpace - config.scrollbarHeight, 0)
    });

    timeNav.getChildren('timeAxis').forEach(axis => {
      axis.setTranslation(0, labelSpace);
      axis.placeAxis();
    });

    config.yConfigs.map(extractScales).forEach(
      scale => scale.setRange([
        this._dimensions.height - config.scrollbarHeight - border.top - border.bottom,
        labelSpace
      ])
    );
    timeNav.setChildDimension();
  }
  /**
   * Method to get its translated position
   * @return {Object} translated position
   */
  getPosition () {
    return {
      x: this.config._translateX,
      y: this.config._translateY
    };
  }
  /**
   * Method to set the dimension of its child components
   */
  setChildDimension () {
    let timeNav = this,
      config = timeNav.config,
      brush = timeNav.getChildren('brush')[0],
      labelSpace = brush.getLabelSpace(),
      scrollbar = timeNav.getChildren('scrollbar')[0],
      contextScale = config.contextScale,
      contextRange = contextScale.getRange(),
      focusDomain = timeNav.getFromEnv('chart').getFocusLimit(),
      focusRange = focusDomain.map(val => contextScale.getRangeValue(val));
    // set max allowable dimensions of brush.
    brush.setMaxBrushDimensions({
      x: contextRange[0],
      y: labelSpace,
      width: contextRange[1] - contextRange[0],
      height: this._dimensions.height - labelSpace - config.scrollbarHeight
    });

    // store the current play dimension of brush component as a whole.
    brush.setCurrentBrushDimensions({
      x: focusRange[0],
      y: labelSpace,
      width: focusRange[1] - focusRange[0],
      height: this._dimensions.height - labelSpace - config.scrollbarHeight
    });
    // set scrollbar dimensions
    scrollbar.setDimension({
      x: config.scrollbarStart,
      y: this._dimensions.height - config.scrollbarHeight,
      height: config.scrollbarHeight,
      width: this._dimensions.width + 2 * config.scrollbarButtonWidth + 1 // 1px is for left and right padding of scrollbar
    });
    timeNav.setScrollbarConfig('width', this._dimensions.width + 2 * config.scrollbarButtonWidth + 1);
    scrollbar.config.scrollRatio = timeNav.getScrollRatio();
    timeNav.setScrollbarConfig('scrollRatio', scrollbar.config.scrollRatio);
    scrollbar.config.scrollPosition = timeNav.getScrollPosition();
    timeNav.setScrollbarConfig('scrollPosition', scrollbar.config.scrollPosition);
    timeNav.setScrollbarConfig('height', config.scrollbarHeight);
  }
  /**
   * Method to get its current dimensions
   */
  getDimension () {
    return this._dimensions;
  }
  configureAttributes (obj = {}) {
    super.configureAttributes(obj);
    let timeNav = this,
      timeNavConfig = timeNav.config;

    Object.assign(timeNavConfig, obj);

    timeNav.setContextScaleLimit();
    timeNav.addToEnv('timeNavigator', timeNav);
    timeNav.addToEnv('timeNavigatorConfig', timeNavConfig);
  }
  /**
   * Method that attaches handlers on this component
   */
  attachHandlers () {
    let timeNav = this,
      brush,
      leftLabel,
      rightLabel;
    timeNav.addEventListener('fc-mouseover', timeNav.mouseoverHandler || (timeNav.mouseoverHandler = function () {
      // clear the timer set on mouseout, every time a mouseover is encountered from time nav or its child components
      // through event bubbling.
      clearTimeout(timeNav.mouseoutTimer);
      brush = this.getChildren('brush')[0];
      leftLabel = brush.getChildren('leftLabel')[0];
      rightLabel = brush.getChildren('rightLabel')[0];
      // draw labels only if previously not visible.
      if (!leftLabel.config.showLabel && !rightLabel.config.showLabel) {
        leftLabel.setData({ showLabel: true }, true);
        rightLabel.setData({ showLabel: true }, true);
      }
    }));
    timeNav.addEventListener('fc-mouseup', timeNav.mouseupHandler || (timeNav.mouseupHandler = function () {
      // inform brush that mouse is released within its boundaries and no need to hide the labels.
      brush.config.pointerReleasedOnParent = true;
    }));
    timeNav.addEventListener('fc-mouseout', timeNav.mouseoutHandler || (timeNav.mouseoutHandler = function () {
      // if currently a label is getting dragged then dont hide the labels.
      if (!timeNav.config.labelsGettingDragged) {
        // create a timer every time a mouseout is encountered from time nav or its child components
        // through event bubbling.
        timeNav.mouseoutTimer = setTimeout(function () {
          leftLabel.setData({ showLabel: false }, true);
          rightLabel.setData({ showLabel: false }, true);
        }, 20);
      }
    }));
  }
  draw () {
    let timeNav = this;
    timeNav.addGraphicalElement({
      el: 'group',
      container: {
        id: 'tropo',
        label: 'group',
        isParent: true
      },
      component: timeNav,
      label: 'group',
      attr: {
        name: 'timenav-tropo',
        transform: timeNav.config.translate
      },
      id: 'tropo'
    });
    timeNav.addGraphicalElement({
      el: 'group',
      container: {
        id: 'strato',
        label: 'group',
        isParent: true
      },
      component: timeNav,
      label: 'group',
      attr: {
        name: 'timenav-strato',
        transform: timeNav.config.translate
      },
      id: 'strato'
    });
    timeNav.addGraphicalElement({
      el: 'group',
      container: {
        id: 'meso',
        label: 'group',
        isParent: true
      },
      component: timeNav,
      label: 'group',
      attr: {
        name: 'timenav-meso',
        transform: timeNav.config.translate
      },
      id: 'meso'
    });
    timeNav.addGraphicalElement({
      el: 'group',
      container: {
        id: 'thermo',
        label: 'group',
        isParent: true
      },
      component: timeNav,
      label: 'group',
      attr: {
        name: 'timenav-thermo',
        transform: timeNav.config.translate
      },
      id: 'thermo'
    });

    timeNav.addGraphicalElement({
      el: 'group',
      container: {
        id: 'exo',
        label: 'group',
        isParent: true
      },
      component: timeNav,
      label: 'group',
      attr: {
        name: 'timenav-exo',
        transform: timeNav.config.translate
      },
      id: 'exo'
    });
    // time axis group
    timeNav.addGraphicalElement({
      el: 'group',
      attr: {
        name: 'time-axis-group'
      },
      id: 'axisTopGroup',
      label: 'group',
      container: {
        id: 'strato',
        label: 'group'
      },
      component: timeNav
    }, true);
    // scrollbar group
    timeNav.addGraphicalElement({
      el: 'group',
      attr: {
        name: 'scrollbar-group'
      },
      id: 'scrollbarContainer',
      label: 'scrollbar',
      container: {
        id: 'thermo',
        label: 'group'
      },
      component: timeNav
    }, true);
  }
  /**
   * Method to store its child scrollbar config
   * @param {String} conf configuration name
   * @param {Number} value configuration value
   */
  setScrollbarConfig (conf, value) {
    this._scrollbarConfig[conf] = value;
  }
  /**
   * Method to get its child scrollbar config
   * @param {String} conf configuration name
   */
  getScrollbarConfig (conf) {
    return conf ? this._scrollbarConfig[conf] : this._scrollbarConfig;
  }

  /**
  * Method that informs chart to validate domains
  * @param {Array} values Domain values
  */
  validateDomain (values, direction, obj) {
    let timeNav = this,
      [startPx, endPx] = values,
      scale = timeNav.config.contextScale,
      range = scale.getRange(),
      chart = timeNav.getFromEnv('chart');
    if (startPx > endPx) {
      [startPx, endPx] = [endPx, startPx];
    }
    startPx = clampNumber(startPx, ...range);
    endPx = clampNumber(endPx, ...range);
    chart.setPixelLimit(
      [startPx, endPx],
      scale,
      [...direction],
      {
        name: obj.eventName,
        eventArgs: obj.eventArgs,
        options: obj.eventArgs
      }
    );
  }
  /**
   * Method that smartly calculates the scroll ratio
   */
  getScrollRatio () {
    let timeNav = this,
      scrollbarConf = timeNav.getChildren('scrollbar')[0].config,
      { leftBorder, rightBorder } = timeNav.getChildren('canvas')[0].config,
      width = scrollbarConf.width,
      height = scrollbarConf.height,
      scrollButtonsTotalWidth = 2 * Math.min(height, width * 0.5),
      brush = timeNav.getChildren('brush')[0],
      borderToWidthRatio = (leftBorder + rightBorder) / width,
      selectionWidth = brush.getCurrentBrushDimensions().width;
    // scroll ration will turn to infinity if width - scrollButtonsTotalWidth is 0

    const scrollRatio = (selectionWidth + 1) / Math.max(0.001, width - scrollButtonsTotalWidth); // 1px is for left and right padding of scrollbar
    /**
     * borderToWidthRatio is the ratio of canvas left and right thickness by total timeNav width.
     * When scrollRatio is more than (1 - borderToWidthRatio), it means that scroll bar should take up full
     * space of the scroll track. Hence, the scrollRatio is set to 1.
     */
    if (scrollRatio >= (1 - borderToWidthRatio)) {
      return 1;
    }
    return scrollRatio;
  }
  /**
   * Method that smartly calculates the scroll position
   */
  getScrollPosition () {
    let timeNav = this,
      scrollbarConf = timeNav.getChildren('scrollbar')[0].config,
      width = scrollbarConf.width,
      height = scrollbarConf.height,
      scrollButtonsTotalWidth = 2 * Math.min(height, width * 0.5),
      brush = timeNav.getChildren('brush')[0],
      selectionStart = brush.getCurrentBrushDimensions().x,
      selectionWidth = brush.getCurrentBrushDimensions().width,
      scrollRatio = timeNav.getScrollbarConfig('scrollRatio'),
      anchorWidth = scrollRatio * (width - scrollButtonsTotalWidth),
      scrollPositionRange = width - (scrollButtonsTotalWidth + anchorWidth),
      rangeMinPx = scrollButtonsTotalWidth / 2 + anchorWidth / 2 - timeNav.config.scrollbarButtonWidth;
    return ((selectionStart + selectionWidth / 2) - rangeMinPx) / scrollPositionRange;
  }
  /**
   * This method returns the dataset defination
   */
  getDSdef () {
    return Line;
  }

  getName () {
    return 'TimeNavigator';
  }
}

export default TimeNavigator;
