import { animHelperFN } from '@fusioncharts/core/src/lib';
let UNDEF;

const Y = 'y',
  HEIGHT = 'height';

/**
 * Get the initial state of the rect of the stacked column
 * @param {*} height Height of the element(rect) given by the user.
 * @param {*} y Y point given by the user.
 * @param {*} isNegative is the stacked column is in the negative axis?
 */
function getInitialAttr (height, y, isNegative) {
  return {
    y: !isNegative ? y + height : y,
    height: 0
  };
}

/**
 * Get the start timing and end timing divided across datasets.
 * @param {*} datasetIndex Index of the rect in the stacked column(dataset)
 * @param {*} totalDatasets Total number of datasets.
 */
function getStartEnd (datasetIndex, totalDatasets) {
  let timeblock = 1 / totalDatasets,
    offset = (timeblock * datasetIndex);
  return {
    start: offset,
    end: offset + timeblock
  };
}

/**
 * Gets the animation for the stacked column
 * @param {*} component The dataset component for the rect.
 */
function getStackedAnimation (component) {
  const datasetIndex = component.config.datasetIndex,
    totalDatasets = component.config.seriesLength;
  return {
    'rect.appearing': function (inputJSON) {
      const index = inputJSON.index,
        dataInfo = component.config.dataInfo[index],
        isNegative = dataInfo.yBaseValue < 0;
      return [
        {
          initialAttr: getInitialAttr(inputJSON.attr.height, inputJSON.attr.y, isNegative),
          slot: 'middle',
          startEnd: getStartEnd(datasetIndex, totalDatasets),
          effect: 'linear'
        }
      ];
    }
  };
};

export default {
  'initial.dataset.timeseriesColumn': function () {
    var component = this,
      yAttrKey = Y,
      hAttrKey = HEIGHT,
      yScale = component.getFromEnv('yScale');
    const totalStackSum = component.config.indices[3] !== UNDEF;
    if (totalStackSum) {
      return getStackedAnimation(component, yScale);
    }
    return {
      'rect.appearing': function (inputJSON) {
        var index = (inputJSON.props && inputJSON.props.originalIndex) || inputJSON.index,
          dataObj = component.config.data[index],
          actualFinalAttr = inputJSON.attr,
          yAttrVal,
          hAttrVal,
          yBasePos,
          endPosWRT0,
          //  baseIndex = component.config.indices[2],
          zeroPos = yScale.getRangeValue(0); // yScale.getRangeValue((component.config.data.length > index) ? component.config.data[index][baseIndex] : component.config.data[index - 1][baseIndex]);
          // Store final y and h for later use
        yAttrVal = actualFinalAttr[yAttrKey];
        hAttrVal = actualFinalAttr[hAttrKey];
        endPosWRT0 = Math.sign(yAttrVal + (hAttrVal / 2) - zeroPos);
        // If the plot is below zero plain, the base will be the y + h of the previous one
        // if it is stacked plot else y of the plot
        if (endPosWRT0 === 1) {
          yBasePos = yAttrVal;
        } else {
          // the base will be the y of the previous one if it is stacked plot else y+h of itself
          yBasePos = yAttrVal + hAttrVal;
        }
        dataObj && (dataObj._posWRT0 = endPosWRT0);
        return [{
          initialAttr: function () {
            var initialAttr = {};
            // Replace the y and h with initial positions
            initialAttr[yAttrKey] = yBasePos;
            initialAttr[hAttrKey] = 0;
            return initialAttr;
          },
          finalAttr: {
          },
          startEnd: function () {
            let startEnd = animHelperFN.animByWeight(inputJSON.index, inputJSON.props.dataLength, 0.6);
            return startEnd;
          },
          hookFn: function () {
            this.attr({ opacity: 1 });
          },
          effect: 'easeInOutQuart',
          slot: 'plot'
        }];
      },
      '*': null
    };
  }
};
