
import ScaleTimeBin from '@fusioncharts/utils/src/scales/time-bin';
import Linear from '@fusioncharts/utils/src/scales/linear';
import Log from '@fusioncharts/utils/src/scales/ft-log';

const TYPES = {
  time: ScaleTimeBin,
  linear: Linear,
  log: Log
};
export default chart => {
  let chartConf = chart.config,
    focusAxesY = chartConf.focusAxesY,
    contextAxesY = chartConf.contextAxesY || [],
    makeScales = ({ type, base }) => {
      let Scale = TYPES[type] || TYPES.linear;
      if (Scale === Log && +base > 0) {
        return new Scale()
          .setBase(base);
      }
      return new Scale();
    };

  chart.addToEnv('contextScalesY', contextAxesY.map(makeScales));
  chart.addToEnv('focusScalesY', focusAxesY.map(makeScales));
};
