import DataMarker from './../components/data-marker';
import stringHasContent from '@fusioncharts/utils/src/type/string-has-content';
import arrayHasContent from '@fusioncharts/utils/src/type/array-has-content';
import equals from 'ramda/es/equals';
import isObject from '@fusioncharts/utils/src/type/is-object';
import timeConverter from '@fusioncharts/utils/src/time-converter';
import { getUnclippedValue } from '@fusioncharts/utils/src/clip-utils';
export default function (dataset) {
  let dataSource = dataset.getFromEnv('dataSource'),
    seriesname = dataset.config.series,
    dataMarker,
    { repeatClips, singleClips } = dataset.getFromEnv('focusScalesX')[0].applicableClippings,
    { measures, seriesInfo = {}, enableMarkers, visibility, isStock, isRange } = dataset.config,
    dataMarkerInstance,
    markers = [];
  if (enableMarkers && (dataMarker = dataSource.datamarker)) {
    // Separating the flag and the pin datamarkers
    dataMarker.forEach(data => {
      let cloneData = Object.assign({}, data), // Cloning Done in order to negate the reference to the same object
        { value, series } = cloneData,
        isConditionsMet = true,
        parsedTime = timeConverter.parser(data.timeformat).parse(data.time);

      if (isNaN(+parsedTime) || (getUnclippedValue(parsedTime, repeatClips, singleClips) !== +parsedTime)) {
        return;
      }
      if (value) {
        if (stringHasContent(value)) {
          value = [value];
        }
        if (arrayHasContent(value)) {
          value = value.filter(stringHasContent);
          // for stock charts all the measure names should match with values
          if ((isRange || isStock) && isObject(measures[0])) {
            let measuresArr = Object.values(measures[0]);
            for (let i = 0; i < value.length && isConditionsMet; ++i) {
              isConditionsMet = measuresArr.includes(value[i]);
            }
          } else {
            isConditionsMet = equals(value, measures);
          }
        }
      }
      if (Object.keys(seriesInfo).length && isObject(series)) {
        let keys = Object.keys(series),
          i = keys.length,
          key;
        while (isConditionsMet && i--) {
          key = keys[i];
          if (stringHasContent(series[key])) {
            if (series[key] !== seriesInfo[key]) isConditionsMet = false;
          }
        };
      }
      if (isConditionsMet) {
        // series name will be same as the series in dataset, it will be used internally
        if (isStock || isRange) {
          cloneData.measures = measures;
        } else {
          cloneData.seriesname = seriesname;
        }
        markers.push(cloneData);
      };
    });

    if (markers.length !== 0) {
      dataMarkerInstance = dataset.attachChild(DataMarker, 'dataMarker');
      dataMarkerInstance.configure({
        data: markers,
        isStock,
        isRange,
        visibility
      });
    }
  }
}
