class Step {
  constructor (ctx, factor = 0.5) {
    this._ctx = ctx;
    this._factor = factor;
  }

  areaStart () {
    this._line = 0;
  }

  areaEnd () {
    this._line = NaN;
  }

  lineStart () {
    this._x = this._y = NaN;
    this._point = 0;
  }

  lineEnd () {
    if (this._factor > 0 && this._factor < 1 && this._point === 2) {
      this._ctx.lineTo(this._x, this._y);
    }

    if (this._line || (this._line !== 0 && this._point === 1)) {
      this._ctx.closePath();
    }

    if (this._line >= 0) {
      this._factor = 1 - this._factor;
      this._line = 1 - this._line;
    }
  }

  point (_x, _y) {
    let x = +_x,
      y = +_y;

    switch (this._point) {
      case 0:
        this._point = 1;
        if (this._line) {
          this._ctx.lineTo(x, y);
        } else {
          this._ctx.moveTo(x, y);
        }
        break;
      case 1:
        this._point = 2; // fallthrough
      default:
        if (this._factor <= 0) {
          this._ctx.lineTo(this._x, y);
          this._ctx.lineTo(x, y);
        } else {
          let x1 = (this._x * (1 - this._factor)) + (x * this._factor);
          this._ctx.lineTo(x1, this._y);
          this._ctx.lineTo(x1, y);
        }
    }

    this._x = x;
    this._y = y;
  }
}

class StepAfter extends Step {
  constructor (ctx) {
    super(ctx, 1);
  }
}

class StepBefore extends Step {
  constructor (ctx) {
    super(ctx, 0);
  }
}

export { StepBefore, StepAfter };
export default Step;
