import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { pluck, pluckNumber } from '@fusioncharts/core/src/lib';
import isValidNumber from '@fusioncharts/utils/src/type/is-valid-number';

const VERTICAL = 'vertical',
  END = 'end',
  MIDDLE = 'middle',
  START = 'start',
  GUTTER_5 = 5,
  M = 'M',
  L = 'L',
  l = 'l',
  h = 'h',
  v = 'v',
  z = 'z';
class Crossline extends SmartRenderer {
  __setDefaultConfig () {
    super.__setDefaultConfig();
    let crosslineConfig = this.config;

    crosslineConfig.type = VERTICAL;
    crosslineConfig.values = [];
    crosslineConfig._defaultLineStyle = {
      stroke: '#9a9a9a',
      'stroke-width': 1,
      'stroke-opacity': 1
    };
    crosslineConfig._defaultTextStyle = {
      fill: '#ffffff',
      'font-size': '10px',
      'font-weight': 'normal',
      'font-style': 'normal',
      'text-anchor': 'middle'
    };
    crosslineConfig._defaultTagStyle = {
      fill: '#5f5f5f'
    };
    crosslineConfig._defaultPadding = 2;
  }
  configureAttributes (obj) {
    super.configureAttributes(obj);
    let config = this.config,
      type,
      inputStyle,
      baseTextStyle = this.getFromEnv('baseTextStyle'),
      getStyleDef = this.getFromEnv('getStyleDef');

    for (let key in obj) {
      if (obj.hasOwnProperty(key)) {
        config[key] = obj[key];
      }
    }

    type = config.type;
    inputStyle = config.inputStyle;

    /**
     * Styling precedence for line and tag
     * -> default style < JSON.crossline.style.line < JSON.crossline.style.vertical-line (horizontal-line for horizontal crossline)
     * -> default style < JSON.crossline.style.tag < JSON.crossline.style.vertical-tag (horizontal-tag for horizontal crossline)
     *
     * Styling precedence for label
     * -> default style < baseTextStyle < JSON.crossline.style.label < JSON.crossline.style.vertical-label (horizontal-label for horizontal crossline)
     */
    config._lineStyle = Object.assign({}, config._defaultLineStyle, getStyleDef(inputStyle.line), getStyleDef(inputStyle[type + '-line']));
    config._textStyle = Object.assign({}, config._defaultTextStyle, baseTextStyle, getStyleDef(inputStyle.label), getStyleDef(inputStyle[type + '-label']));
    config._tagStyle = Object.assign({}, config._defaultTagStyle, getStyleDef(inputStyle.tag), getStyleDef(inputStyle[type + '-tag']));
  }
  /**
   * Method to get the path of crossline
   * @return {Array} pathArr array of paths.
   */
  getPathArrs () {
    let crossline = this,
      selfConfig = crossline.config,
      isVertical = selfConfig.type === VERTICAL,
      scale = selfConfig.scale,
      values = selfConfig.values,
      pathArrays = [],
      pathArr,
      canvasConfig = crossline.getLinkedParent().config,
      canvasPadding = canvasConfig.padding,
      canvasLeft = canvasConfig.canvasLeft - canvasPadding.left,
      canvasTop = canvasConfig.canvasTop - canvasPadding.top,
      canvasWidth = canvasConfig.canvasWidth,
      canvasHeight = canvasConfig.canvasHeight;

    values.forEach(value => {
      let rangeVal = scale.getRangeValue(value);
      if (rangeVal) {
        if (isVertical) {
          pathArr = [M, rangeVal, canvasTop, v, canvasHeight];
        } else {
          pathArr = [M, canvasLeft, rangeVal, h, canvasWidth];
        }
        pathArrays.push(pathArr);
      }
    });

    return pathArrays;
  }
  /**
   * Method to draw vertical crossline text and anchor
   */
  drawVerticalCrosslineText () {
    let crossline = this,
      selfConfig = crossline.config,
      dateRange = selfConfig.dateRange,
      text = selfConfig.timeFormatterFn({
        dateRange,
        type: 'crossline'
      }),
      values = selfConfig.values,
      scale = selfConfig.scale,
      padding = pluckNumber(selfConfig.padding, selfConfig._defaultPadding),
      canvasConfig = crossline.getLinkedParent().config,
      canvasPadding = canvasConfig.padding,
      canvasLeft = canvasConfig.canvasLeft - canvasPadding.left,
      canvasTop = canvasConfig.canvasTop - canvasPadding.top,
      canvasWidth = canvasConfig.canvasWidth,
      canvasHeight = canvasConfig.canvasHeight,
      canvasBottom = canvasTop + canvasHeight,
      canvasRight = canvasLeft + canvasWidth,
      smartLabel = crossline.getFromEnv('smartLabel'),
      markerPath,
      textOffset = 0,
      rangeValue,
      smartText,
      crosslineId;
    // draw only if value is valid
    values.forEach((value, index) => {
      if (isValidNumber(value)) {
        smartLabel.setStyle(selfConfig._textStyle);
        smartText = smartLabel.getOriSize(text);
        rangeValue = scale.getRangeValue(value);
        crosslineId = `crossline-${index}`;
        // create group for vertical cross line.
        crossline.addGraphicalElement({
          el: 'group',
          container: {
            id: 'thermo',
            label: 'thermo',
            isParent: true
          },
          component: crossline,
          label: crosslineId,
          attr: {
            name: 'veritcal-crossline-marker-group'
          },
          id: crosslineId
        });
        // crossline path crosses the right edge of the canvas
        if ((rangeValue + smartText.width / 2 + 2 * padding) > canvasRight) {
          markerPath = [M, rangeValue, canvasBottom,
            L, rangeValue, canvasBottom + padding,
            v, smartText.height + 2 * padding,
            h, -(smartText.width + 4 * padding),
            v, -(smartText.height + 2 * padding),
            L, rangeValue - padding, canvasBottom + padding,
            z];
          selfConfig._textStyle['text-anchor'] = END;
          textOffset = -2 * padding;
        } else if ((rangeValue - smartText.width / 2 - 2 * padding) < canvasLeft) {
          // crossline path crosses the left edge of the canvas
          markerPath = [M, rangeValue, canvasBottom,
            L, rangeValue, canvasBottom + padding,
            v, smartText.height + 2 * padding,
            h, (smartText.width + 4 * padding),
            v, -(smartText.height + 2 * padding),
            L, rangeValue + padding, canvasBottom + padding,
            z];
          selfConfig._textStyle['text-anchor'] = START;
          textOffset = 2 * padding;
        } else {
          markerPath = [M, rangeValue, canvasBottom,
            L, rangeValue - padding, canvasBottom + padding,
            L, rangeValue - (smartText.width / 2) - 2 * padding, canvasBottom + padding,
            v, smartText.height + 2 * padding,
            h, smartText.width + 4 * padding,
            v, -(smartText.height + 2 * padding),
            L, rangeValue + padding, canvasBottom + padding,
            z];
          selfConfig._textStyle['text-anchor'] = MIDDLE;
        }
        crossline.addGraphicalElement({
          el: 'path',
          attr: {
            path: markerPath
          },
          css: selfConfig._tagStyle,
          id: 'vertical-crossline-marker-' + index,
          container: {
            label: crosslineId
          }
        });
        // text
        crossline.addGraphicalElement({
          el: 'text',
          attr: {
            text: text,
            x: rangeValue + textOffset,
            y: canvasBottom + 2 * padding + smartText.height / 2
          },
          css: selfConfig._textStyle,
          id: 'vertical-crossline-text-' + index,
          container: {
            label: crosslineId
          }
        });
        smartLabel.setStyle(selfConfig._textStyle);
        smartText = smartLabel.getOriSize(text);
        rangeValue = scale.getRangeValue(value);
      }
    });
  }
  /**
   * Method to draw horizontal crossline text and anchor
   */
  drawHorizontalCrosslineText () {
    let crossline = this,
      selfConfig = crossline.config,
      values = selfConfig.values,
      text,
      crosslineId,
      markerProp;
      // draw only if value is valid
    values.forEach((value, index) => {
      if (isValidNumber(value)) {
        text = selfConfig.formatterFn({
          value: selfConfig.texts[index],
          type: 'crossline',
          prefix: selfConfig.prefix,
          suffix: selfConfig.suffix
        });
        markerProp = this.getHorizontalMarkerProps(value, index);
        crosslineId = `crossline-${index}`;
        // marker
        // create group for horizontal crossline
        crossline.addGraphicalElement({
          el: 'group',
          container: {
            id: 'thermo',
            label: 'thermo',
            isParent: true
          },
          component: crossline,
          label: crosslineId,
          attr: {
            name: 'horizontal-crossline-marker-group'
          },
          id: crosslineId
        });

        selfConfig._tagStyle.fill = pluck(selfConfig.markerFill, selfConfig._tagStyle.fill);
        selfConfig._tagStyle.stroke = pluck(selfConfig._tagStyle.stroke, selfConfig.markerFill);
        crossline.addGraphicalElement({
          el: 'path',
          attr: {
            path: markerProp.markerPath
          },
          css: selfConfig._tagStyle,
          id: 'horizontal-crossline-marker-' + index,
          container: {
            label: crosslineId
          }
        });
        // text
        crossline.addGraphicalElement({
          el: 'text',
          attr: {
            text: text,
            x: markerProp.markerText.x,
            y: markerProp.markerText.y
          },
          css: selfConfig._textStyle,
          id: 'horizontal-crossline-text-' + index,
          outlineText: selfConfig.showTextOutline,
          container: {
            label: crosslineId
          }
        });
      }
    });
  }
  getHorizontalMarkerProps (value, index) {
    let crossline = this,
      selfConfig = crossline.config,
      scale = selfConfig.scale,
      yAxisAlignment = selfConfig.yAxisAlignment,
      padding = pluckNumber(selfConfig.padding, selfConfig._defaultPadding),
      canvasConfig = crossline.getLinkedParent().config,
      text = selfConfig.formatterFn({
        value: selfConfig.texts[index],
        type: 'crossline',
        prefix: selfConfig.prefix,
        suffix: selfConfig.suffix
      }),
      borderConfig = canvasConfig.borderConfig,
      canvasPadding = canvasConfig.padding,
      canvasLeft = canvasConfig.canvasLeft + borderConfig.leftBorder - canvasPadding.left,
      canvasWidth = canvasConfig.canvasWidth - borderConfig.rightBorder + canvasPadding.left,
      canvasRight = canvasLeft + canvasWidth,
      smartLabel = crossline.getFromEnv('smartLabel'),
      rangeValue,
      smartText,
      markerPath,
      markerText = {},
      markerProp = {};

    smartLabel.setStyle(selfConfig._textStyle);
    smartText = smartLabel.getSmartText(text);
    rangeValue = scale.getRangeValue(value);

    switch (yAxisAlignment) {
      case 'left':
        markerPath = [M, canvasLeft, rangeValue,
          h, -GUTTER_5,
          l, -GUTTER_5, -(smartText.height / 2) - 2 * padding,
          h, -(smartText.width + 2 * padding),
          v, smartText.height + 4 * padding,
          h, (smartText.width + 2 * padding),
          l, GUTTER_5, -smartText.height / 2 - 2 * padding
        ];
        markerText.x = canvasLeft - 2 * GUTTER_5 - (smartText.width + 2 * padding) / 2;
        markerText.y = rangeValue;
        break;
      case 'right':
        markerPath = [M, canvasRight, rangeValue,
          h, GUTTER_5,
          l, 2 * GUTTER_5, -(smartText.height / 2) - 2 * padding,
          h, (smartText.width + 2 * padding),
          v, smartText.height + 4 * padding,
          h, -(smartText.width + 2 * padding),
          l, -(2 * GUTTER_5), -smartText.height / 2 - 2 * padding
        ];
        markerText.x = canvasRight + 2 * GUTTER_5 + (smartText.width + 2 * padding) / 2;
        markerText.y = rangeValue;
        break;
    }
    markerProp.markerPath = markerPath;
    markerProp.markerText = markerText;

    return markerProp;
  }
  draw () {
    let crossline = this,
      config = crossline.config;

    crossline.getPathArrs().forEach((pathArr, index) => {
      // create group for vertical cross line.
      crossline.addGraphicalElement({
        el: 'group',
        container: {
          id: 'strato',
          label: 'strato',
          isParent: true
        },
        component: crossline,
        label: 'verticalcrossline-' + index,
        attr: {
          name: 'vertical-crossline-group'
        },
        id: 'crossline-' + index
      });
      // create group for horizontal cross line.
      crossline.addGraphicalElement({
        el: 'group',
        container: {
          id: 'strato',
          label: 'strato',
          isParent: true
        },
        component: crossline,
        label: 'horizontalcrossline-' + index,
        attr: {
          name: 'horizontal-crossline-group'
        },
        id: 'crossline-' + index
      });
      if (pathArr.length) {
        crossline.addGraphicalElement({
          el: 'path',
          attr: {
            path: pathArr
          },
          css: config._lineStyle,
          container: {
            label: config.type + 'crossline-' + index
          },
          id: config.type + '-crossline-path-' + index,
          component: crossline
        });
      }
      if (config.showMarker) {
        config.type === VERTICAL ? crossline.drawVerticalCrosslineText() : crossline.drawHorizontalCrosslineText();
      }
    });
  }
}
export default Crossline;
