import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { extend2 } from '@fusioncharts/core/src/lib';
// import { extend2, pluck, pluckNumber } from '@fusioncharts/core/src/lib';

const HORIZONTAL = 'horizontal',
  VERTICAL = 'vertical',
  M = 'M',
  H = 'H',
  V = 'V',
  BLANKREGEX = /\S/,
  SPACE = ' ',
  STROKEDASH = 'stroke-dasharray',
  // function to check if a value falls within domain
  iswithinDomain = (value, domainArr) => {
    if (+value >= +domainArr[0] && +value <= +domainArr[1]) {
      return true;
    }
    return false;
  };
class GridLine extends SmartRenderer {
  __setDefaultConfig () {
    super.__setDefaultConfig();
    this.config.direction = HORIZONTAL;
    this.config.showgridlines = 1;
    this.config.showboundarygridlines = 1;
    this.config.style = {};
    this.config.defaultStyle = {
      'stroke': '#EFEFEF',
      'stroke-width': '1',
      'stroke-opacity': '1'
    };
  }

  configureAttributes (obj = {}) {
    super.configureAttributes(obj);
    let selfConfig = this.config;
    for (let key in obj) {
      if (obj.hasOwnProperty(key) && typeof obj[key] !== 'undefined') {
        selfConfig[key] = obj[key];
      }
    }
  }
  /**
   * Method that creates all the gridline paths
   * @return {Array} array of paths
   */
  getPathArr () {
    let gridLine = this,
      selfConfig = gridLine.config,
      styleDef = gridLine.getFromEnv('getStyleDef'),
      axisStyle,
      canvasConfig = gridLine.getLinkedParent().config,
      direction = selfConfig.direction,
      pathArr = [],
      axisConfig,
      stretchFromX,
      stretchFromY,
      strechUpto,
      pathCommand,
      scale,
      scaleDomains,
      ticks,
      tickArguments = [],
      i,
      // FT 4: No grid line should appear in time navigator without a label
      timeNavAxis,
      len;
    if (direction !== VERTICAL) {
      axisConfig = canvasConfig.yConfigs[0];
      scale = axisConfig.scale;
      pathCommand = H;
      stretchFromX = canvasConfig.canvasBGLeft;
      stretchFromY = scale.getRangeValue;
      strechUpto = canvasConfig.canvasBGLeft + canvasConfig.canvasBGWidth;
    } else {
      // FT 4: No grid line should appear in time navigator without a label
      // Accessing  the axis of time navigator
      // Added null check and isArray check
      let timeNavigator = this.getLinkedParent().getLinkedParent();
      if (timeNavigator.getChildren('timeAxis') && Array.isArray(timeNavigator.getChildren('timeAxis'))) {
        timeNavAxis = timeNavigator.getChildren('timeAxis')[0];
      }
      axisConfig = canvasConfig.xConfigs[0];
      scale = axisConfig.scale;
      pathCommand = V;
      stretchFromX = scale.getRangeValue;
      stretchFromY = canvasConfig.canvasBGTop + canvasConfig.canvasBGHeight - canvasConfig.padding.top;
      strechUpto = canvasConfig.canvasBGTop - canvasConfig.padding.top;
    }

    // If tickArguments is present, use it for calculation of ticks
    if (axisConfig.tickarguments) {
      tickArguments = axisConfig.tickarguments;
    }

    // store style of gridlines
    axisStyle = axisConfig.style || {};
    selfConfig.style = styleDef(axisStyle['grid-line'] || {});
    // If skipInfo is provided, use it to calculate ticks
    tickArguments.push(axisConfig.skipinfo || {});
    tickArguments.push(axisConfig.dayinmajor || false);
    ticks = scale.ticks(...tickArguments);

    if (selfConfig.showgridlines) {
      scaleDomains = scale.getDomain();
      for (i = 0, len = ticks.length; i < len; i++) {
        if ((!selfConfig.showboundarygridlines && (i === 0 || i === len - 1)) || !iswithinDomain(ticks[i], scaleDomains)) {
          continue;
        }
        // FT 4: No grid line should appear in time navigator without a label
        // placing gridlines only for labels
        if (direction === VERTICAL && timeNavAxis.config.values[i].text.length === 0) {
          continue;
        }
        pathArr.push([
          M,
          (typeof stretchFromX === 'function' ? stretchFromX.call(scale, ticks[i]) : stretchFromX),
          (typeof stretchFromY === 'function' ? stretchFromY.call(scale, ticks[i]) : stretchFromY),
          pathCommand,
          strechUpto
        ]);
      }
    }
    return pathArr;
  }
  draw () {
    let gridLine = this,
      selfConfig = gridLine.config,
      styleGridLines = selfConfig.styleGridLines,
      pathArr = gridLine.getPathArr(),
      mergedStyle = extend2(extend2({}, selfConfig.defaultStyle), styleGridLines && selfConfig.style);
    // if stroke-dasharray is provided as string convert it to array
    if (mergedStyle[STROKEDASH]) {
      mergedStyle[STROKEDASH] = (typeof mergedStyle[STROKEDASH] === 'string')
        ? mergedStyle[STROKEDASH].split(SPACE).filter(c => BLANKREGEX.test(c)).map(c => +c)
        : mergedStyle[STROKEDASH];
    }
    // create the grid line group
    gridLine.addGraphicalElement({
      el: 'group',
      container: {
        id: 'strato',
        label: 'strato',
        isParent: true
      },
      component: gridLine,
      label: 'gridLine',
      attr: {
        name: 'grid-line-group'
      },
      id: 'gridLine'
    });
    pathArr.forEach((path, index) => {
      gridLine.addGraphicalElement({
        el: 'path',
        attr: {
          'path': path
        },
        css: mergedStyle,
        container: {
          label: 'gridLine'
        },
        id: 'grid-line-' + index,
        component: gridLine,
        label: 'line'
      });
    });
  }
}
export default GridLine;
