import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { UNDEF } from '@fusioncharts/core/src/lib';

const LEFT = 'left',
  RIGHT = 'right',
  MIDDLE = 'middle',
  START = 'start',
  END = 'end',
  TOP = 'top',
  DEFAULT_CAPTION_PADDING = 10,
  CAPTION_SUBCAPTION_GAP_RATIO = 0.02,
  CAPTION_HEIGHT_RATIO = 0.7,
  SUBCAPTION_HEIGHT_RATIO = 0.3,
  FONTSTYLE = 'font-style',
  FONTSIZE = 'font-size',
  FONTWEIGHT = 'font-weight',
  TEXT_ANCHOR = 'text-anchor',
  VERTICAL_ALIGN = 'vertical-align',
  FILL = 'fill',
  DEFAULT_CAPTION_CSS = {
    [FONTSIZE]: '16px',
    [FONTSTYLE]: 'normal',
    [FONTWEIGHT]: 'bold',
    [FILL]: '#5F5F5F'
  },
  DEFAULT_SUBCAPTION_CSS = {
    [FONTSIZE]: '12px',
    [FONTSTYLE]: 'normal',
    [FONTWEIGHT]: 'normal',
    [FILL]: '#B1B1B1'
  };

class Caption extends SmartRenderer {
  /**
   * Sets default configurations of caption component
   *
   * @memberof Caption
   */
  __setDefaultConfig () {
    super.__setDefaultConfig();
    let caption = this,
      captionConfig = caption.config;

    captionConfig.captionCss = DEFAULT_CAPTION_CSS;
    captionConfig.subCaptionCss = DEFAULT_SUBCAPTION_CSS;
    captionConfig.alignment = LEFT;
    captionConfig.gap = 0;
  }

  /**
   * configures the caption component
   *
   * @param {any} [options={}] options used to configure
   * @memberof Caption
   */
  configureAttributes (options = {}) {
    super.configureAttributes(options);
    let caption = this,
      captionConfig = caption.config,
      baseTextStyle = caption.getFromEnv('baseTextStyle');

    Object.keys(options).forEach(key => (captionConfig[key] = options[key]));

    captionConfig.captionCss = Object.assign({}, DEFAULT_CAPTION_CSS, baseTextStyle, captionConfig.captionCss || {});
    captionConfig.subCaptionCss = Object.assign({}, DEFAULT_SUBCAPTION_CSS, baseTextStyle, captionConfig.subCaptionCss || {});
  }

  /**
   * Calculation of amount of space required by caption and subcaption.
   *
   * Caption and subCaption takes up the whole width but can take less than or equal to
   * the max-height provided
   *
   * @param {Object} dim {width , height} maximum width and height provided by chart
   * @returns {Object} height and width taken by caption
   * @memberof Caption
   */
  setDimension (dim) {
    let caption = this,
      captionConfig = caption.config,
      smartLabel = caption.getFromEnv('smartLabel'),
      maxHeight = dim.height - DEFAULT_CAPTION_PADDING,
      maxWidth = dim.width,
      availableHeight,
      maxCaptionHeight = 0,
      maxSubCaptionHeight = 0,
      captionDim,
      subCaptionDim,
      gap;

    if (captionConfig.subCaption) {
      // If subCaption is present, allocate 70% of the total height to caption and
      // 30% to subcaption
      gap = captionConfig.gap = maxHeight * CAPTION_SUBCAPTION_GAP_RATIO;
      availableHeight = maxHeight - gap;
      maxCaptionHeight = availableHeight * CAPTION_HEIGHT_RATIO;
      // If caption is not present, subcaption takes full height
      maxSubCaptionHeight = captionConfig.caption ? availableHeight * SUBCAPTION_HEIGHT_RATIO : maxHeight;
    } else {
      // Caption takes up whole space when subcaption is not present
      availableHeight = maxCaptionHeight = maxHeight;
    }

    // Caption and subcaption should have ellipse when truncated
    smartLabel.useEllipsesOnOverflow(true);
    smartLabel.setStyle(captionConfig.captionCss);
    // Get the caption text, height and width for caption text
    captionDim = captionConfig.captionDim = smartLabel.getSmartText(captionConfig.caption, maxWidth, maxCaptionHeight);

    smartLabel.setStyle(captionConfig.subCaptionCss);
    // Get the caption text, height and width for subcaption text
    subCaptionDim = captionConfig.subCaptionDim = smartLabel.getSmartText(captionConfig.subCaption, maxWidth, maxSubCaptionHeight);

    // Allow a gap of 2% of max-height between caption and subCaption
    maxHeight = captionDim.height + (captionConfig.subCaption ? gap : 0) + subCaptionDim.height;
    // Add a padding at the end
    maxHeight += maxHeight === 0 ? 0 : DEFAULT_CAPTION_PADDING;

    // store the total space take by caption
    captionConfig.spaceTaken = {
      width: maxWidth,
      height: maxHeight
    };
    return {
      height: maxHeight,
      width: maxWidth
    };
  }

  /**
   * Drawing of caption and subcaption
   *
   * @memberof Caption
   */
  draw () {
    let caption = this,
      captionConfig = caption.config,
      chartConfig = caption.getFromEnv('chart').config,
      captionDim = captionConfig.captionDim,
      subCaptionDim = captionConfig.subCaptionDim,
      xCaptionPos,
      xSubCaptionPos,
      captionTextAnchor,
      subCaptionTextAnchor,
      spaceTaken = captionConfig.spaceTaken,
      captionAttr = {
        text: captionDim.text
      },
      subCaptionAttr = {
        text: subCaptionDim.text
      };

    // get the caption alignment and calculate x-position based on it
    switch (captionConfig.captionAlignment) {
      case RIGHT:
        // take width as xCaptionPos and 'end' as 'text-anchor' when aligned right
        xCaptionPos = spaceTaken.width;
        captionTextAnchor = END;
        break;
      case MIDDLE:
        // take width as xCaptionPos and 'middle' as 'text-anchor' when aligned middle/center
        xCaptionPos = spaceTaken.width / 2;
        captionTextAnchor = MIDDLE;
        break;
      case LEFT:
      default:
        // take width as xCaptionPos and 'left' as 'text-anchor' when aligned left. This is default behaviour
        xCaptionPos = 0;
        captionTextAnchor = START;
    }
    // get the sub-caption alignment and calculate x-position based on it
    switch (captionConfig.subCaptionAlignment) {
      case RIGHT:
        // take width as xSubCaptionPos and 'end' as 'text-anchor' when aligned right
        xSubCaptionPos = spaceTaken.width;
        subCaptionTextAnchor = END;
        break;
      case MIDDLE:
        // take width as xSubCaptionPos and 'middle' as 'text-anchor' when aligned middle/center
        xSubCaptionPos = spaceTaken.width / 2;
        subCaptionTextAnchor = MIDDLE;
        break;
      case LEFT:
      default:
        // take width as xSubCaptionPos and 'left' as 'text-anchor' when aligned left. This is default behaviour
        xSubCaptionPos = 0;
        subCaptionTextAnchor = START;
    }

    captionAttr.x = xCaptionPos;
    subCaptionAttr.x = xSubCaptionPos;
    captionAttr[TEXT_ANCHOR] = captionTextAnchor;
    subCaptionAttr[TEXT_ANCHOR] = subCaptionTextAnchor;
    captionAttr[VERTICAL_ALIGN] = subCaptionAttr[VERTICAL_ALIGN] = TOP;

    // caption text should be placed at ypos 0.(relative to its containing group. group is translated)
    captionAttr.y = 0;
    subCaptionAttr.y = captionConfig.gap + captionDim.height;

    // create the strato group
    caption.addGraphicalElement({
      el: 'group',
      container: {
        id: 'strato',
        label: 'group',
        isParent: true
      },
      component: caption,
      label: 'group',
      attr: {
        name: 'caption-strato',
        transform: captionConfig.translate
      },
      id: 'strato'
    });

    // create caption text if text is present
    captionConfig.caption && caption.addGraphicalElement({
      el: 'text',
      attr: {
        x: captionAttr.x,
        y: captionAttr.y,
        text: captionAttr.text,
        'vertical-align': captionAttr[VERTICAL_ALIGN],
        opacity: captionConfig.captionCss.opacity,
        'text-anchor': captionAttr[TEXT_ANCHOR]
      },
      css: captionConfig.captionCss,
      container: {
        id: 'strato',
        label: 'group',
        isParent: false
      },
      tooltext: chartConfig.showTooltip ? captionDim.tooltext : UNDEF,
      component: caption,
      id: 'caption',
      label: 'text'
    });

    // create subcaption text if text is present
    captionConfig.subCaption && caption.addGraphicalElement({
      el: 'text',
      attr: {
        x: subCaptionAttr.x,
        y: subCaptionAttr.y,
        text: subCaptionAttr.text,
        'vertical-align': subCaptionAttr[VERTICAL_ALIGN],
        opacity: captionConfig.subCaptionCss.opacity,
        'text-anchor': subCaptionAttr[TEXT_ANCHOR]
      },
      css: captionConfig.subCaptionCss,
      container: {
        id: 'strato',
        label: 'group',
        isParent: false
      },
      tooltext: chartConfig.showTooltip ? subCaptionDim.tooltext : UNDEF,
      component: caption,
      id: 'subCaption',
      label: 'text'
    });
  }
}

export default Caption;
