const DEFAULT_AGGREGATION = 'avg';

export default aggStore => aggregation => {
  const agg = aggregation == null ? DEFAULT_AGGREGATION : aggregation,
    lowerAgg = agg.toLowerCase();

  if (aggStore.getDefaultAggregations()[lowerAgg]) {
    return lowerAgg;
  } else if (aggStore.getRegisteredAggregations()[agg]) {
    return agg;
  }

  return DEFAULT_AGGREGATION;
};
