import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { getTouchEvent } from '@fusioncharts/core/src/lib';

const START = 'start',
  END = 'end',
  SCALE = 'scale',
  DRAGSTART = 'timeNavBrushStart',
  DRAGEND = 'timeNavBrushEnd',
  DRAG = 'timeNavBrush',
  UP = 'up',
  DOWN = 'down',
  LABELFORMAT = '%b %d, %Y';
class Label extends SmartRenderer {
  constructor () {
    super();
    this._dimensions = {};
  }
  configureAttributes (obj = {}) {
    super.configureAttributes(obj);

    let labelConfig = this.config,
      baseTextStyle = this.getFromEnv('baseTextStyle');

    for (let key in obj) {
      if (obj.hasOwnProperty(key)) {
        labelConfig[key] = obj[key];
      }
    }

    labelConfig.style = {
      label: Object.assign({}, labelConfig._defaultLabelStyle, baseTextStyle, labelConfig.style.label),
      labelBg: Object.assign({}, labelConfig._defaultLabelBgStyle, labelConfig.style.labelBg)
    };
  }
  __setDefaultConfig () {
    super.__setDefaultConfig();

    let labelConfig = this.config;
    labelConfig.showLabel = false;
    // default text style of dragable labels
    labelConfig._defaultLabelStyle = {
      'fill': '#f3f3f3',
      'font-size': '11px',
      'font-weight': 600,
      'vertical-align': 'middle',
      cursor: 'move'
    };
    labelConfig._defaultLabelBgStyle = {
      fill: '#5f5f5f',
      cursor: 'move'
    };

    labelConfig['border-horizontal-padding'] = 5;
    labelConfig['border-vertical-padding'] = 3;
  }
  /**
   * Method that sets the dimension of handle
   * @param {Object} dim dimensions
   */
  setDimension (dim) {
    this._dimensions = dim;
  }
  /**
   * Method to get the dimension of handle
   * @return {Object} dimensions
   */
  getDimension () {
    return this._dimensions;
  }
  /**
   * Method that attaches respective handlers on brush component
   */
  attachHandlers () {
    let label = this,
      animationManager = label.getFromEnv('animationManager'),
      brush = label.getLinkedParent(),
      timeNav = brush.getFromEnv('timeNavigator'),
      timeNavConfig = timeNav.config,
      chart = timeNav.getFromEnv('chart'),
      currFocusLimit,
      brushDim,
      dragLabelElem,
      clampDirection,
      startX,
      lastX,
      // startY,
      offsetX,
      startPx,
      endPx,
      options = {
        timeFormatter: timeNavConfig.formatter
      },
      forceFireOptions = Object.assign({ forceFire: true }, options),
      eventArgs = {
        formatter: LABELFORMAT,
        action: SCALE
      };
      // offsetY;

    // attach dragstart event.
    label.addEventListener('fc-dragstart', label.dragstartHandler || (label.dragstartHandler = function (e) {
      startX = getTouchEvent(e).clientX;
      lastX = 0;
      brushDim = brush.getCurrentBrushDimensions();
      // inform time navigator that a label is getting dragged.
      timeNav.config.labelsGettingDragged = true;
      // store the dragable label element.
      dragLabelElem = e.originalEvent.target;
      brush.config.pointerReleasedOnParent = false;
      currFocusLimit = chart.getFocusLimit();
      chart.getFromEnv('fireChartEvents')(DRAGSTART, [+currFocusLimit[0], +currFocusLimit[1]], eventArgs, forceFireOptions);
    }));
    // attach dragmove event
    label.addEventListener('fc-dragmove', label.dragmoveHandler || (label.dragmoveHandler = function (e) {
      animationManager.setAnimationState('timenavSqueeze');
      offsetX = getTouchEvent(e).clientX - startX;
      clampDirection = offsetX >= lastX ? UP : DOWN;
      lastX = offsetX;
      // offsetY = e.originalEvent.clientY - startY;

      // perform operations based on the type of handle.
      if (label.config.type === 'leftLabel') {
        startPx = brushDim.x + offsetX;
        endPx = brushDim.x + brushDim.width;
      } else {
        startPx = brushDim.x;
        endPx = brushDim.x + brushDim.width + offsetX;
      }
      timeNav.validateDomain([startPx, endPx], [clampDirection, clampDirection], {
        eventName: DRAG,
        eventArgs,
        options
      });
    }));
    // attach dragend event.
    label.addEventListener('fc-dragend', label.dragendHandler || (label.dragendHandler = function (e) {
      animationManager.setAnimationState('timenavSqueeze');
      timeNav.config.labelsGettingDragged = false;
      // if current target is not the dragable label then inform its parent to hide both of its labels.
      if (dragLabelElem !== e.originalEvent.target) {
        brush.hideAllLabels();
      }
      currFocusLimit = chart.getFocusLimit();
      chart.getFromEnv('fireChartEvents')(DRAGEND, [+currFocusLimit[0], +currFocusLimit[1]], eventArgs, forceFireOptions);
    }));
  }

  setValue (value) {
    this.config.value = value;
  }

  getValue () {
    return this.config.value;
  }

  /**
   * space taken by this label
   *
   * @returns {Object} Object containing width and height of the label
   * @memberof Label
   */
  getLabelDimension () {
    let label = this,
      labelConfig = label.config,
      smartLabel = label.getFromEnv('smartLabel'),
      formatter = label.getFromEnv('timeNavigatorConfig').formatter;

    smartLabel.setStyle(labelConfig.style.label);
    return smartLabel.getOriSize(formatter.format(labelConfig.value));
  }

  draw () {
    let label = this,
      config = label.config,
      labelStyle = config.style.label,
      labelBgStyle = config.style.labelBg,
      brush = label.getLinkedParent(),
      timeNavConfig = label.getFromEnv('timeNavigatorConfig'),
      smartLabel = label.getFromEnv('smartLabel'),
      brushMaxDim = brush.getMaxBrushDimensions(),
      labelDimensions = label.getDimension(),
      startX = labelDimensions.x,
      inverter, // decides whether the padding will be added or subtracted
      textAnchor,
      formatter = timeNavConfig.formatter,
      smartText,
      timeStamp = label.getValue(),
      text = formatter.format(timeStamp),
      startY = labelDimensions.y,
      labelStartX,
      labelStartY,
      labelWidth,
      labelHeight,
      labelBgWidth,
      labelBgHeight;
    smartLabel.setStyle(labelStyle);
    smartText = smartLabel.getOriSize(text);
    if (config.type === 'leftLabel') {
      // check if the label is within brush dimensions
      if (startX - smartText.width >= brushMaxDim.x) {
        textAnchor = END;
        inverter = 1;
      } else {
        textAnchor = START;
        inverter = -1;
      }
    } else {
      // check if the label is within brush dimensions
      if (startX + smartText.width <= brushMaxDim.x + brushMaxDim.width) {
        textAnchor = START;
        inverter = -1;
      } else {
        textAnchor = END;
        inverter = 1;
      }
    }
    labelStyle['text-anchor'] = textAnchor;
    if (config.showLabel) {
      labelWidth = smartText.width;
      labelHeight = smartText.height;
      labelBgWidth = labelWidth + 2 * config['border-horizontal-padding'];
      labelBgHeight = labelHeight + 2 * config['border-vertical-padding'];
      labelStartX = startX - inverter * config['border-horizontal-padding'];
      labelStartY = startY - (labelHeight + 2 * config['border-vertical-padding']) / 2;
      // @todo- mind the orientation
      // label background
      label.addGraphicalElement({
        el: 'rect',
        attr: {
          x: textAnchor === START ? startX : startX - labelBgWidth,
          y: startY - labelBgHeight,
          width: labelBgWidth,
          height: labelBgHeight
        },
        css: labelBgStyle,
        shadow: [true, 1],
        label: 'dragableLabelBackground',
        id: config.type + 'dragableLabelBackground',
        component: label,
        container: {
          id: 'brushGroup',
          label: 'brushGroup',
          isParent: true
        }
      });
      // label element
      label.addGraphicalElement({
        el: 'text',
        attr: {
          x: labelStartX,
          y: labelStartY,
          text: text
        },
        css: labelStyle,
        id: config.type + 'dragableLabel',
        label: 'dragableLabel',
        component: label,
        container: {
          id: 'brushGroup',
          label: 'brushGroup',
          isParent: true
        }
      });
    }
  }
}

export default Label;
