class CurveLinear {
  constructor (ctx) {
    this._ctx = ctx;
  }

  areaStart () {
    this._line = 0;
  }

  areaEnd () {
    this._line = NaN;
  }

  lineStart () {
    this._point = 0;
  }

  lineEnd () {
    if (this._line || (this._line !== 0 && this._point === 1)) {
      this._ctx.closePath();
    }
    this._line = 1 - this._line;
  }

  point (_x, _y) {
    let x = +_x,
      y = +_y;

    switch (this._point) {
      case 0:
        this._point = 1;
        this._line
          ? this._ctx.lineTo(x, y)
          : this._ctx.moveTo(x, y);
        break;
      case 1:
        this._point = 2;
        // falls through
      default:
        this._ctx.lineTo(x, y);
    }
  }
}

export default CurveLinear;
