import ScaleContinuous, {
  copyScale,
  deInterpolateLinear as deinterpolate
} from './continuous';
import reinterpolate from '../scale-utils/interpolators/number';
import timeTicks, { tickInterval } from '../time-intervals/ticks';
import enUS from '../time-converter/locales/en-US';
import { TimeConverter } from '../time-converter';

const durationSecond = 1000,
  durationMinute = durationSecond * 60,
  durationHour = durationMinute * 60,
  durationDay = durationHour * 24,
  durationWeek = durationDay * 7,
  durationMonth = durationDay * 30,
  durationYear = durationDay * 365,
  date = d => new Date(d),
  number = n => (n instanceof Date ? +n : +new Date(+n)),
  nice = (domain, interval) => {
    const d = domain.slice();
    let firstDomainIdx = 0,
      lastDomainIdx = d.length - 1,
      firstDomainElem = d[firstDomainIdx],
      lastDomainElem = d[lastDomainIdx],
      temp;

    if (lastDomainElem < firstDomainElem) {
      temp = firstDomainIdx;
      firstDomainIdx = lastDomainIdx;
      lastDomainIdx = temp;
      temp = firstDomainElem;
      firstDomainElem = lastDomainElem;
      lastDomainElem = temp;
    }

    d[firstDomainIdx] = interval.floor(firstDomainElem);
    d[lastDomainIdx] = interval.ceil(lastDomainElem);

    return d;
  };

class ScaleCalendar extends ScaleContinuous {
  constructor (year, month, week, day, hour, minute, second, millisecond) {
    super(deinterpolate, reinterpolate);

    this.timeIntervals = {
      year,
      month,
      week,
      day,
      hour,
      minute,
      second,
      millisecond
    };
    this.unclippedTimeIntervals = {
      year,
      month,
      week,
      day,
      hour,
      minute,
      second,
      millisecond
    };
    this.tickIntervals = [
      [this.timeIntervals.second, 1, durationSecond],
      [this.timeIntervals.second, 5, 5 * durationSecond],
      [this.timeIntervals.second, 15, 15 * durationSecond],
      [this.timeIntervals.second, 30, 30 * durationSecond],
      [this.timeIntervals.minute, 1, durationMinute],
      [this.timeIntervals.minute, 5, 5 * durationMinute],
      [this.timeIntervals.minute, 15, 15 * durationMinute],
      [this.timeIntervals.minute, 30, 30 * durationMinute],
      [this.timeIntervals.hour, 1, durationHour],
      [this.timeIntervals.hour, 3, 3 * durationHour],
      [this.timeIntervals.hour, 6, 6 * durationHour],
      [this.timeIntervals.hour, 12, 12 * durationHour],
      [this.timeIntervals.day, 1, durationDay],
      [this.timeIntervals.day, 2, 2 * durationDay],
      [this.timeIntervals.week, 1, durationWeek],
      [this.timeIntervals.month, 1, durationMonth],
      [this.timeIntervals.month, 3, 3 * durationMonth],
      [this.timeIntervals.year, 1, durationYear]
    ];

    this.locale = enUS;
    this._localeConverter = new TimeConverter(enUS);
  }

  setDomain (domain) {
    return super.setDomain(domain.map(number));
  }
  setTimeInterval (timeIntervals) {
    this.timeIntervals = timeIntervals;
  }
  setUnclippedTimeIntervals (unclippedTimeIntervals) {
    this.unclippedTimeIntervals = unclippedTimeIntervals;
  }
  getDomain () {
    return super.getDomain().map(date);
  }

  ticks (interval, count) {
    const domain = super.getDomain();

    return timeTicks(
      domain[0],
      domain[domain.length - 1],
      interval,
      count,
      this.tickIntervals,
      this.timeIntervals
    );
  }

  nice (interval, count) {
    const d = super.getDomain(),
      i = tickInterval(
        d[0],
        d[d.length - 1],
        interval,
        count,
        this.tickIntervals,
        this.timeIntervals
      );

    return i ? super.setDomain(nice(d, i)) : this;
  }

  getDomainValue (range) {
    return new Date(super.getDomainValue(range));
  }

  setLocale (locale = enUS) {
    this.locale = locale;
    this._localeConverter = new TimeConverter(this.locale);

    return this;
  }

  copy () {
    const {
      year,
      month,
      week,
      day,
      hour,
      minute,
      second,
      millisecond
    } = this.timeIntervals;

    return copyScale(this, new ScaleCalendar(
      year,
      month,
      week,
      day,
      hour,
      minute,
      second,
      millisecond
    ));
  }
}

export default ScaleCalendar;
