import { SmartRenderer } from '@fusioncharts/core/src/component-interface';
import { getTouchEvent } from '@fusioncharts/core/src/lib';

const SCALE = 'scale',
  DRAGSTART = 'timeNavBrushStart',
  DRAGEND = 'timeNavBrushEnd',
  DRAG = 'timeNavBrush',
  UP = 'up',
  DOWN = 'down',
  LABELFORMAT = '%b %d, %Y';
class Handle extends SmartRenderer {
  constructor () {
    super();
    this._dimensions = {};
  }
  __setDefaultConfig () {
    super.__setDefaultConfig();

    let config = this.config;

    config._defaultHandleStyle = {
      fill: '#dfdfdf',
      stroke: 'none',
      cursor: 'ew-resize'
    };

    config._defaultHandleGripStyle = {
      cursor: 'ew-resize',
      stroke: '#808080',
      'stroke-width': 1,
      'stroke-linecap': 'round'
    };
  }
  configureAttributes (obj = {}) {
    super.configureAttributes(obj);
    let handle = this,
      handleConfig = handle.config;

    handleConfig.style = {
      handle: Object.assign({}, handleConfig._defaultHandleStyle, obj.style.handle),
      'handle-grip': Object.assign({}, handleConfig._defaultHandleGripStyle, obj.style['handle-grip'])
    };
    handleConfig.orientation = obj.orientation;
    handleConfig.type = obj.type;
  }
  /**
   * Method that sets the dimension of handle.
   * @param {Object} dim dimension
   */
  setDimension (dim) {
    this._dimensions = dim;
  }
  /**
   * Method to get the dimension of handle.
   * @return {Object} dimension
   */
  getDimension () {
    return this._dimensions;
  }
  /**
   * Method that attaches respective handlers on brush component.
   */
  attachHandlers () {
    let handle = this,
      brush = handle.getLinkedParent(),
      timeNav = brush.getFromEnv('timeNavigator'),
      timeNavConfig = timeNav.config,
      chart = handle.getFromEnv('chart'),
      currFocusLimit,
      brushDim,
      lastX,
      clampDirection,
      startX,
      // startY,
      offsetX,
      startPx,
      endPx,
      options = {
        timeFormatter: timeNavConfig.formatter
      },
      forceFireOptions = Object.assign({forceFire: true}, options),
      eventArgs = {
        formatter: LABELFORMAT,
        action: SCALE
      };
      // offsetY;

    // attach dragstart event.
    handle.addEventListener('fc-dragstart', handle.dragstartHandler || (handle.dragstartHandler = function (e) {
      startX = getTouchEvent(e).clientX;
      lastX = 0;
      brushDim = brush.getCurrentBrushDimensions();
      currFocusLimit = chart.getFocusLimit();
      chart.getFromEnv('fireChartEvents')(DRAGSTART, [+currFocusLimit[0], +currFocusLimit[1]], eventArgs, forceFireOptions);
    }));
    // attach dragmove event
    handle.addEventListener('fc-dragmove', handle.dragmoveHandler || (handle.dragmoveHandler = function (e) {
      offsetX = getTouchEvent(e).clientX - startX;
      clampDirection = offsetX >= lastX ? UP : DOWN;
      lastX = offsetX;
      // offsetY = e.originalEvent.clientY - startY;

      // perform operations based on the type of handle.
      if (handle.config.type === 'leftHandle') {
        startPx = brushDim.x + offsetX;
        endPx = brushDim.x + brushDim.width;
      } else {
        startPx = brushDim.x;
        endPx = brushDim.x + brushDim.width + offsetX;
      }
      handle.getFromEnv('animationManager').setAnimationState('timenavSqueeze');
      timeNav.validateDomain([startPx, endPx], [clampDirection, clampDirection], {
        eventName: DRAG,
        eventArgs,
        options
      });
    }));
    // attach dragend event.
    handle.addEventListener('fc-dragend', handle.dragendHandler || (handle.dragendHandler = function (e) {
      currFocusLimit = chart.getFocusLimit();
      chart.getFromEnv('fireChartEvents')(DRAGEND, [+currFocusLimit[0], +currFocusLimit[1]], eventArgs, forceFireOptions);
    }));
  }
  draw () {
    let handle = this,
      handleConfig = handle.config,
      handleStyle = handleConfig.style.handle,
      handleGripStyle = handleConfig.style['handle-grip'],
      handleDimensions = handle.getDimension(),
      startX = handleDimensions.x,
      startY = handleDimensions.y,
      width = handleDimensions.width,
      height = handleDimensions.height;
      // @todo- mind the orientation
    // handle button
    handle.addGraphicalElement({
      el: 'rect',
      attr: {
        x: startX,
        y: startY,
        width,
        height,
        opacity: handleStyle.opacity
      },
      css: handleStyle,
      label: 'handleButton',
      component: handle,
      container: {
        id: 'brushGroup',
        label: 'brushGroup',
        isParent: true
      }
    });
    // handle path
    handle.addGraphicalElement({
      el: 'path',
      attr: {
        path: `M ${startX + 3.5},${startY + 6} v ${height - 12}
               M ${startX + 6.5},${startY + 6} v ${height - 12}`
      },
      label: 'handlePath',
      component: handle,
      css: handleGripStyle,
      container: {
        id: 'brushGroup',
        label: 'brushGroup',
        isParent: true
      }
    });
  }
}

export default Handle;
