import stringHasContent from '@fusioncharts/utils/src/type/string-has-content';
import { UNDEF } from '@fusioncharts/core/src/lib';
/**
 * api to add sanitised series string in the expanded json
 * @param {Array} dataTableColumns is an array of strings containing the data table columns name of type string
 * @param {Array} config expanded json of axis
 * @param {String} seriesType is the series string given in root level of user's json
 */
export default (dataTableColumns = [], config = [], seriesType = '', stackType = '', groupType = '') => {
  let getValidString = str => (stringHasContent(str) && dataTableColumns.indexOf(str) >= 0) ? str : UNDEF,
    rootSeries = getValidString(seriesType),
    rootStack = getValidString(stackType),
    rootGroup = getValidString(groupType);

  return config.map(axis => {
    let series = getValidString(axis.series) || rootSeries,
      stack = getValidString(axis.stack) || rootStack,
      group = getValidString(axis.group) || rootGroup;
    axis.plot = axis.plot.map(plot => {
      const type = plot.type;
      if (type === 'column' || /area/.test(type)) {
        plot.stack = getValidString(plot.stack) || stack || series;
        plot.group = getValidString(plot.group) || group;
      } else {
        plot.stack = getValidString(plot.stack) || stack;
        plot.group = getValidString(plot.group) || group || series;
      }
      return plot;
    });
    return axis;
  });
};
